/*@ngInject*/
function setupNoAccessRoute($stateProvider) {
	$stateProvider.state('app-failure', {
		templateUrl: '_error-app-failure.html',

		/*@ngInject*/
		controller ($rootScope, ssStorage, ssAuthSvc) {
			// if our app fails to load, clear all stored data in case some
			// of it is bad. You never know... ya know?
			ssStorage.clearAll();

			// remove the sidekick components from the dom... because we're invalid
			const skElements = Array.from(document.getElementsByClassName('sidekick'));
			skElements.forEach(el => el.parentNode.removeChild(el));

			$rootScope.go = {
				editSettings: evt => {
					evt.preventDefault();
					ssAuthSvc.editSettings();
				},
				logout: evt => {
					evt.preventDefault();
					ssAuthSvc.signout();
				}
			};
		}
	});
}

/*@ngInject*/
function listenForNoAccessEvents($rootScope, $state, ssEventSvc, ssAppConfigSvc) {
	ssEventSvc.on('error-no-access', function (user = {}) {
		$rootScope.error = {
			title: 'You don\'t have access',
			message: 'It looks like you do not have access. If you feel this is an error, please contact your administrator.',
			isFrontlineUser: (user.isFrontlineUser !== false) // default to true
		};
		$state.transitionTo('app-failure');
		ssEventSvc.emit('preloader-complete');
	}, false);
}

angular.module('super-suit')
	.config(setupNoAccessRoute)
	.run(listenForNoAccessEvents);
