var currentAccessRecord;

/*@ngInject*/
function interceptFlUser() {
	function addFlUser(config) {
		if (config.noFlUser) { return config; }
		if (currentAccessRecord != null) {
			config.headers.FlUser = currentAccessRecord;
		}
		return config;
	}

	return { request: addFlUser };
}

/*@ngInject*/
function populateFlUserForInterceptor(ssActiveUserSvc) {
	ssActiveUserSvc.getUserContext().then(function (userContext) {
		const flUser = Object.assign({}, userContext, {
			UserKey: ssActiveUserSvc.getIdentityByTypeSync('employee')
		});

		currentAccessRecord = JSON.stringify(flUser);
	});
}

angular.module('super-suit')
	.run(populateFlUserForInterceptor)
	.factory('interceptFlUser', interceptFlUser);
