import {
	Enums
}
from 'base-enum';

// #-----------------------------# //
// #---- Enums (/phoneTypes) ----# //

class PhoneTypesEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
				name: 'Work',
				value: 0
			}, {
				name: 'Home',
				value: 1
			}, {
				name: 'Mobile',
				value: 2
			},
			{
				name: 'Other',
				value: 3
			}];
		super(enums);
	}
}

// #---- End Enums (/phoneTypes) ----# //
// #---------------------------------# //

angular.module('super-suit-enums').factory('enumPhoneTypes', () => new PhoneTypesEnum());
