/*@ngInject*/
function packetsApi(ssApiSvc, buildConfig) {
	// #-------------------------------------# //
	// #---- API Endpoint (ssPacketsApi) ----# //

	// base endpoint url
	const base = buildConfig.endpoint.formTemplates;
	const url = (p) => `${base}/packets/${p.id}`;
	const queryUrl = `${base}/organizations/:ORGID:/packets`;
	// model data type
	const model = 'packets';

	// possible relationships
	const rel = [];

	const get = ssApiSvc.endpoint({
		url,
		method: 'GET'
	}, model, rel);
	const add = ssApiSvc.endpoint({
		url: queryUrl,
		method: 'POST'
	}, model, rel);

	// API endpoint methods
	return {
		get, add
	};

	// #-- END API Endpoint (ssPacketsApi) --# //
	// #-------------------------------------# //
}

angular.module('super-suit-apis').factory('ssPacketsApi', packetsApi);
