import { Enums } from 'base-enum';

// #----------------------------# //
// #---- Enums (/ethnicity) ----# //

class EthnicityEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Hispanic or Latino',
			value: 0
		}, {
			name: 'Not Hispanic or Latino',
			value: 1
		}, {
			name: 'Unknown',
			value: 2
		}];
		super(enums);
	}
}

// #---- End Enums (/ethnicity) ----# //
// #--------------------------------# //

angular.module('super-suit-enums').factory('enumEthnicity', () => new EthnicityEnum());
