// #------------------------------# //
// #---- Helper (ssStringHlp) ----# //

// _hlp is the main DI object, it should contain all helper functions
var _hlp = {

	// demo function, please remove
	toDashCase(txt) {
		return splitWords(txt).join('-');
	},

	toCamelCase(txt) {
		return splitWords(txt)
			.map((v,i) => {
				if (i === 0) { return v; }
				return v.charAt(0).toUpperCase() + v.substr(1);
			})
			.join('');
	},

	toProperCase(txt) {
		return splitWords(txt)
			.map((v,i) => v.charAt(0).toUpperCase() + v.substr(1))
			.join('');
	},

	toTitleCase(txt) {
		return splitWords(txt)
			.map((v,i) => v.charAt(0).toUpperCase() + v.slice(1))
			.join(' ');
	}

};

function splitWords(txt) {
	return txt
		.replace(/[\s_-]/g, '{!}')
		.replace(/([A-Z]+)/g, '{!}$1')
		.toLowerCase()
		.split('{!}')
		.filter(v => !!v.length);
}


// #-- END Helper (ssStringHlp) --# //
// #------------------------------# //

function ssStringHlp() { return _hlp; }
angular.module('super-suit-helpers').factory('ssStringHlp', ssStringHlp);
