import { Component } from 'base-component';
// #---------------------------------------# //
// #---- Component (super-input-radio) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=',
	isDisabled: '=',
	label: '@?',
	labelWeight: '@?',
	name: '@?',
	inputId: '@?',
	value: '@',
	editMode: '=?',
	permissions: '=?',
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function inputRadio(ssStringHlp, ssCommonControlHlp) {

	class InputRadio extends Component {

		/*@ngInject*/
		controller($scope, $element, $attrs, $stateParams, enumPropertyAction) {
			$scope.labelWeight = ssCommonControlHlp.labelWeight($scope.labelWeight, $scope.label);

			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				label: $scope.label,
				disabled: false,
				id: `radio-${(Math.ceil(Math.random() * 10000000)).toString()}`
			};

			$scope.$watchGroup(['editMode', 'permissions', 'isDisabled'], (nv, ov) => {
				// index matches expression
				const editMode = nv[0];
				const permissions = nv[1];
				const disabled = nv[2] == null ? false : nv[2];

				const editModeInferred = editMode == null;
				if (editModeInferred) {
					vm.editMode = $stateParams.action && $stateParams.action.endsWith('edit');
				} else {
					vm.editMode = editMode === true; // strict equality to not bind it to the $scope
				}

				if (permissions == null) {
					if ($attrs.permissions === undefined) {
						// if the component wasn't even passed a permissions object assume
						// edit privileges so that we actually show the expected input
						vm.permissions = { view: true, edit: true, redacted: false, raw: 3 };
						if (editModeInferred) { vm.editMode = true; }
					} else {
						// if we expect permissions on the component, but we were
						// passed an empty permissions object, deny edit privileges
						vm.permissions = { view: true, edit: false, redacted: false, raw: 1 };
						if (editModeInferred) { vm.editMode = false; }
					}
				} else {
					vm.permissions = angular.extend({}, permissions);
				}

				if (!vm.permissions.edit) { vm.editMode = false; }
				if (vm.editMode) {
					vm.disabled = disabled === true;
				} else {
					vm.disabled = true;
				}
				if (vm.permissions.raw === enumPropertyAction.notVisible) {
					$element.remove();
				}
			});
		}

		link(scope, elem, attrs) {

			let inputEl = scope.$input = elem.find('input');

			// prevent other listeners from being called if the input is not
			// the target of the event -- e.g. if the label is clicked, two
			// click events are emitted: one for label, one for checkbox.
			// see http://stackoverflow.com/questions/10819699/simple-click-on-labels-makes-double-click#10819803
			elem.on('click', evt => {
				if (evt.target.tagName !== 'INPUT') {
					evt.stopImmediatePropagation();
				}
			});

			attrs.$observe('label', val => {
				scope.vm.label = val;
			});

			if (attrs.inputId) {
				attrs.$observe('inputId', val => {
					scope.vm.id = ssStringHlp.toDashCase(val);
				});
			}

			if (!attrs.label && attrs.ariaLabel) {
				attrs.$observe('ariaLabel', val => {
					inputEl.attr('aria-label', attrs.ariaLabel);
				});
				elem.removeAttr('aria-label');
			}

			if (scope.name != null) {
				elem.find('input').attr('name', scope.name);
			}
		}
	}

	return new InputRadio(props, 'input-radio', config);
}

// #-- END Component (super-input-radio) --# //
// #---------------------------------------# //

angular.module('super-suit-components').directive('superInputRadio', inputRadio);
