import {
	Enums
}
from 'base-enum';

// #------------------------------# //
// #---- Enums (/fieldWidths) ----# //

class FieldWidthsEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Small',
			value: '25%'
		}, {
			name: 'Medium',
			value: '50%'
		}, {
			name: 'Large',
			value: '75%'
		}, {
			name: 'Full',
			value: '100%'
		}];
		super(enums);
	}
}

// #---- End Enums (/fieldWidths) ----# //
// #----------------------------------# //

angular.module('super-suit-enums').factory('enumFieldWidths', () => new FieldWidthsEnum());
