// lockHeader allows you to trigger classes the viewport changes position
(function (plugin) {
// ================= Plugin Name ================= //
//                                                 //
	var name = 'viewport';
//                                                 //
// =============================================== //
	window.ambient.registerPlugin(name, plugin);
})({
// ================= Plugin Code ================= //
//                                                 //
	getCurrentValue: function () {
		var w = window,
			d = document,
			b = d.body,
			e = d.documentElement,

			top = (w.pageYOffset != null) ? w.pageYOffset : (e.clientHeight && e || b).scrollTop,
			left = (w.pageXOffset != null) ? w.pageXOffset : (e.clientWidth && e || b).scrollLeft,
			width = w.innerWidth || b.clientWidth,
			height = w.innerHeight || b.clientHeight;

		// the location of the element could change if the
		// page width changes, so watch that too and include
		// it in the current value so it can be checked
		// for changes
		return top + '-' + (width + left) + '-' + (top + height) + '-' + left;
	},

	getActiveClasses: function (topRightBottomLeft, className, cfg) {
		topRightBottomLeft = topRightBottomLeft.split('-').map(function (valStr) { return parseInt(valStr, 10); });
		var isDefault = true,
			top = topRightBottomLeft[0],
			right = topRightBottomLeft[1],
			bottom = topRightBottomLeft[2],
			left = topRightBottomLeft[3],
			config = {
				minTop: 0,
				maxTop: Infinity,
				minRight: 0,
				maxRight: Infinity,
				minBottom: 0,
				maxBottom: Infinity,
				minLeft: 0,
				maxLeft: Infinity
			},
			keys = Object.keys(cfg),
			i = keys.length, k;


		for(; i-- ;) {
			k = keys[i];
			if (cfg[k] != null) { continue; }

			config[k] = cfg[k];
			isDefault = false;
		}

		if (isDefault) { return [className]; }

		if (top < config.minTop || top > config.maxTop) { return []; }
		if (right < config.minRight || right > config.maxRight) { return []; }
		if (bottom < config.minBottom || bottom > config.maxBottom) { return []; }
		if (left < config.minLeft || left > config.maxLeft) { return []; }

		return [className];
	},
	watchEvent: 'scroll resize'
}
//                                                 //
// =============================================== //
);
