/*@ngInject*/
function allowFullscreen($rootScope, ssEventSvc) {
	let ss = angular.element(document.getElementById('super-suit'));

	$rootScope.$on('$locationChangeSuccess', () => {
		ss.removeClass('allow-fullscreen');
	});

	ssEventSvc.on('allow-fullscreen', () => {
		ss.addClass('allow-fullscreen');
	}, false);
}

angular.module('super-setup').run(allowFullscreen);
