/*@ngInject*/
function assignmentApi(ssApiSvc, buildConfig, ssEffectiveDateHlp, $http) {
// #----------------------------------------# //
// #---- API Endpoint (ssAssignmentApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.employees;
	const convertToValidatingSave = ssEffectiveDateHlp.convertToValidatingSave;

	const queryUrl = `${baseUrl}/organizations/:ORGID:/positions`;
	const url = (p) => `${baseUrl}/positions/${p.id}`;
	const assignmentUrl = (p) => `${baseUrl}/positions/${p.id}/assignments/${p.assignmentId}`;
	const employeeAssignmentsUrl = `${baseUrl}/export/assignments`;

	// model data type
	const model = 'assignments';

	// possible relationships.
	const rel = ['details'];

	const get = convertToValidatingSave(ssApiSvc.endpoint({ url: url, method: 'GET' }, model, rel));
	const query = convertToValidatingSave(ssApiSvc.endpoint({url: queryUrl, method: 'GET'}, model, rel));
	const getAssignment = convertToValidatingSave(ssApiSvc.endpoint({url: assignmentUrl, method: 'GET'}, model, rel));
	const queryEmployeeAssignments = ssApiSvc.endpoint({ url: employeeAssignmentsUrl, method: 'GET' }, model, rel);

	/**
	 * Makes a call to the Employee Export endpoint, and returns the data as a CSC
	 * @param {string} filter The filter (pre-encoded) that will be applied to the report endpoint to get a result set as CSV
	 */
	const downloadEmployeeAssignmentsReportCsv = (filter) => {
		let urlConfig = {
			method: 'GET',
			url: `${baseUrl}/Export/Assignments/csv?${filter}`
		};
		return $http(urlConfig);
	};

	// API endpoint methods
	return {
		get,
		query,
		getAssignment,
		queryEmployeeAssignments,
		downloadEmployeeAssignmentsReportCsv
	};

// #-- END API Endpoint (ssAssignmentApi) --# //
// #----------------------------------------# //
}

angular.module('super-suit-apis').factory('ssAssignmentApi', assignmentApi);
