/*@ngInject*/
function ssListSvc($log, $filter, ssAsync, ssStorage, ssListApi, ssActiveUserSvc, ssEventSvc) {
	// #-----------------------------# //
	// #---- Service (ssListSvc) ----# //

	// External service API
	return {
		getByExternalId,
		mapName
	};

	/**
	 * Maps a key to an item in a list and pulls out the name or ''
	 * @param {string} key The key of the item we are looking for
	 * @param {object[]} Array The list that we are searching through
	 */
	function mapName(key, list) {
		if (key === '') { return; }
		var ret = list.filter(function (v) {
			return v.value === key;
		});

		if (ret.length === 0) {
			$log.warn('Value "' + key + '" not found in list.');
			return '';
		}
		return ret[0].name;
	}

	function getByExternalId(listExternalId) {
		return (ssAsync(function* () {
			// resolve immediately if the data is cached
			const orgId = yield ssActiveUserSvc.getOrgId().catch(err => $log.error(err));
			if (orgId == null) { return []; }
			const cacheKey = `${orgId}_list_${listExternalId}`;
			const cachedList = ssStorage.load(cacheKey);
			if (cachedList != null) { return cachedList; }

			// go get the list
			const listData = yield ssListApi.query({
				externalId: listExternalId,
				include: 'Items'
			});

			if (listData.length === 0) { return []; }

			const list = listData[0];
			var data = list._items.map(function (item) {
				return {
					id: item.id,
					name: item._name,
					description: item._description,
					value: `${list.id}_${item.id}`
				};
			});

			// Store the lists sorted
			data = data.sort((a, b) => {
				var nameA, nameB;
				if (a.hasOwnProperty('name')){
					nameA = a.name.toUpperCase(); // ignore upper and lowercase
					nameB = b.name.toUpperCase(); // ignore upper and lowercase
				} else {
					nameA = a._name.toUpperCase();
					nameB = b._name.toUpperCase();
				}

				if (nameA < nameB) {
					return -1;
				}
				if (nameA > nameB) {
					return 1;
				}

				// names must be equal
				return 0;
			});
			ssStorage.save(cacheKey, data);
			return data;
		})());
	}

	// #-- END Service (ssListSvc) --# //
	// #-----------------------------# //
}

angular.module('super-suit-services').factory('ssListSvc', ssListSvc);
