import {Component} from 'base-component';
// #------------------------------------# //
// #---- Component (super-grid-row) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	selectedValue: '=?',
	isDisabled: '=?'
};

// directive definition object overrides
const config = {
	transclude: true
};

/*@ngInject*/
function gridRow(rAF) {

	class GridRow extends Component {
		/*@ngInject*/
		controller($scope, $log) {

			const gridData = $scope.gridData = $scope.$root.utils.findInParentScope('gridData', $scope); // eslint-disable-line no-unused-vars

			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				selected: false
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars

			};

			if ($scope.gridData.selectable != null && $scope.selectedValue == null) {
				$log.error('super-grid-row :: row must specify a "selected-value" value when inside a "selectable" super-grid');
			}

			$scope.$emit('super-grid-row-init');

			$scope.$watch('vm.selected', function (n, o) {
				if (n === o) { return; }
				$scope.$emit('super-grid-row-selection');
			});
		}

		link(scope, elem, attrs) {
			const gridData = scope.gridData;
			const elStyle = elem[0].style;
			var currentHeight;

			elem.toggleClass('selectable', gridData.selectable != null);
			elem.toggleClass('hoverable', gridData.hoverable != null);
			currentHeight = gridData.defaultRowHeight;
			elStyle.minHeight = currentHeight + 'px';


			elem.on('click mousemove', function (evt) {
				if (currentHeight !== elem[0].offsetHeight) {
					currentHeight = elem[0].offsetHeight;
					// gridData.debounceCheckInView();
				}
			});
		}
	}

	return new GridRow(props, 'grid-row', config);
}

// #-- END Component (super-grid-row) --# //
// #------------------------------------# //

angular.module('super-suit-components').directive('superGridRow', gridRow);
