import {Decorator} from 'base-decorator';
// #--------------------------------# //
// #---- Decorator (ss-pending) ----# //

/*@ngInject*/
function ssPending($compile, $parse, ssElemHlp) {

	class Pending extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {
			const isButton = elem.hasClass('super-button');
			// we don't need a loader if this is a button
			let loaderNeeded = !isButton;

			// add the loader right away if the initial value we are watching
			// starts out with a truthy value
			const initialPendingVal = !!$parse(attrs.ssPending)(scope);
			if (loaderNeeded && initialPendingVal) { addLoader(); }

			elem.addClass('ss-pending');

			scope.$watch(attrs.ssPending, function (newState, oldState) {
				newState = !!newState;
				elem.toggleClass('ss-pending-active', newState);

				if (isButton) {
					elem.attr({'disabled': (newState ? 'disabled': null)});
				}

				// defer adding the loader markup until we actually need it
				// to show up on the page
				if (loaderNeeded && newState) { addLoader(); }
			});

			function addLoader() {
				const elemStyles = ssElemHlp.getStyles(elem);
				const loader = $compile(`<super-loader is-loading="${attrs.ssPending}" status-text="${attrs.ssPendingStatus}" fill></super-loader>`);
				elem.append(loader(scope));

				if (elemStyles.position === 'static') {
					elem.css({position: 'relative'});
				}
				loaderNeeded = false;
			}
		}
	}

	return new Pending();
}

// #-- END Decorator (ss-pending) --# //
// #--------------------------------# //

angular.module('super-suit-decorators').directive('ssPending', ssPending);
