/*@ngInject*/
function ssJsonApi(moment, $log, $http, $rootScope, ssAsync, JsonApiDataStore, ssEventSvc, ssObjectHlp, ssDateHlp, ssMutator, ssToastHlp, enumPropertyAction, ssApiResponseHlp) {
// #-----------------------------# //
// #---- Utility (ssJsonApi) ----# //

	const privateAttrs = ['ownerId', 'ownerVersionNumber'];
	const modelMetaMap = new WeakMap();
	const modelChangesMap = new Map();
	const mutatorDataMap = new WeakMap();
	const ownerVersionMap = new Map();

	class JsonApiModel {
		constructor(model, schema, root) {
			if (root == null) { root = this; }
			tokenReplacement(model);

			this.$permitted = buildPermissions(model);

			// create a single place where all model changes to this object
			// can be tracked. if this is part of the JsonApiModel itself
			// then changes to the model from other JsonApiModel objects will
			// not correctly reflect the changed state of the model
			const modelId = `${schema.model}-${model.id}`;
			if (!modelChangesMap.has(modelId)) {
				modelChangesMap.set(modelId, {
					_changes: new Set(),
					_initialState: new Map()
				});
			}

			// WeakMap the meta data for this model object to the JsonApiModel object
			modelMetaMap.set(this, {
				// proxy requests through to the central changes object
				get _changes() { return modelChangesMap.get(modelId)._changes; },

				// proxy requests through to the central state object
				get _initialState() { return modelChangesMap.get(modelId)._initialState; },

				_model: model,
				_schema: Object.assign({}, schema, { path: (model._links && model._links.self || null) }),
				_root: root
			});

			this.$$resetChangesDetected();
			ownerVersionMap.set(model.ownerId, model.ownerVersionNumber);

			this.$$hashKey = model.id;
			this.$$syncModel(true);
			this.$$applyMutators();
		}

		/**
		 * Submit a Patch to the server to save the updated data.
		 *
		 * @param {boolean} [deepSave=true] Update children objects with parent.
		 * @param {boolean} [refresh=true] Update the object on the way out with the new data.
		 * @param {boolean} [internalErrorHandling=true] Handle the errors internally with the toasts, otherwise rethrow the exception and let the calling code deal with the mess.
		 * @returns Promise with the result of the Ajax call
		 */
		$save(deepSave = true, refresh = true, internalErrorHandling = true) { return (ssAsync(function* () {
			// pull the data we need out of our model meta data
			const $meta = modelMetaMap.get(this);
			const {_changes, _model, _schema} = $meta;

			// make sure we have a path for this object (from link data)
			if (_schema.path == null) { throw noLinkError('save', _model); }

			this.$$syncModel();

			if (_changes.size === 0 && !deepSave) { return; }

			if (_changes.size > 0) {
				ssEventSvc.emit('ss-json-api.preSave.' + _schema.model, this);

				const alsoIncluded = privateAttrs.filter(attr => _model._attributes.includes(attr));
				const changedAttrs = Array.from(_changes.keys());

				// if we are not refreshing, we should reset changes before we
				// send the PATCH, that way changes that happen after we patch
				// are tracked as changes
				if (!refresh) { this.$$resetChangesDetected(); }

				try { // and catch any errors with a toast
					_model.ownerVersionNumber = ownerVersionMap.get(_model.ownerId);
					const payload = _model.serialize({ attributes: changedAttrs.concat(alsoIncluded), relationships: [] });
					const result = yield $http.patch(_schema.path, payload);
					const state = _model._attributes.reduce(function (o, attr) {
						o[attr] = _model[attr];
						return o;
					}, {});


					JsonApiDataStore.store.sync(result.data);

					// Sanity Check - we don't want a null as the ownerVersionMap Key or Value
					if (!_model.ownerId && _model.ownerVersionNumber || _model.ownerId && !_model.ownerVersionNumber) {
						//>log('Unable to $save a model that is missing an ownerId or ownerVersionNumber: ', _model)
						if (!_model.ownerId) { throw '$Save failure due to missing ownerId. Please refresh your page.'; }
						if (!_model.ownerVersionNumber) { throw '$Save failure due to missing ownerVersionNumber. Please refresh your page.'; }
						// if we're missing both, that's cool. Srsly.
					}

					ownerVersionMap.set(_model.ownerId, _model.ownerVersionNumber);

					if (!refresh) { Object.assign(_model, state); }
					this.$$syncModel();

					// if we refreshed, the object is now reset to whatever is
					// on the server so we can assume there are no changes that
					// are valid within this object
					if (refresh) { this.$$resetChangesDetected(); }

					this.$$applyMutators();

					ssEventSvc.emit('ss-json-api.postSave.' + _schema.model, this);
				} catch (err) {
					if (internalErrorHandling) {
						ssApiResponseHlp.handle4XX(err);
					} else {
						throw err;
					}
				}
			}


			if (deepSave) {
				for (let rel of _schema.relationships) {
					yield this[`_${rel}`].$save(false, refresh);
				}
			}

			return this;

		}.bind(this))());}

		/**
		 * Delete (or it's equivalent) the record from the database
		 *
 		 * @param {boolean} [internalErrorHandling=true] Handle the errors internally with the toasts, otherwise rethrow the exception and let the calling code deal with the mess.
		 * @returns Promise with the result of the Ajax call.
		 */
		$remove(internalErrorHandling = true) { return (ssAsync(function* () {
			// pull the data we need out of our model meta data
			const $meta = modelMetaMap.get(this);
			const {_model, _schema} = $meta;

			// make sure we have a path for this object (from link data)
			if (_schema.path == null) { throw noLinkError('remove', _model); }

			try {
				const versionNumber = ownerVersionMap.get(_model.ownerId);
				const actionResult = yield $http.delete(`${_schema.path}?version=${versionNumber}`);
				const collection = _schema.collection;

				if (collection != null) {
					collection.splice(collection.indexOf(this), 1);
					collection.$$state.splice(collection.$$state.indexOf(_model.id), 1);
				}

				if (_model.ownerId !== _model.id) {
					ownerVersionMap.set(_model.ownerId, versionNumber + 1);
				}

				// returns the object as of the latest edit. I wonder if we should send the unchanged object
				ssEventSvc.emit('ss-json-api.removedRecord.' + _schema.model, _model.id, _model);

				JsonApiDataStore.store.destroy(_model);

				return actionResult;
			} catch (err) {
				if (internalErrorHandling) {
					ssToastHlp.errorAjax(err);
				} else {
					throw err;
				}
			}
			return;

		}.bind(this))());}

		/**
		 * Returns true if the model is dirty optionally checking certain fields if specified.
		 *
		 * @param {Boolean} [deep=true] Check relationships
		 * @param {String[]} [fields=[]] Optional list of fields to check for dirtiness
		 *
		 * @return {Boolean}
		 */
		$isDirty({deep = true, fields = []} = {}) { // jshint ignore:line
			const $meta = modelMetaMap.get(this);
			const {_changes, _schema} = $meta;

			if (fields.length) {
				// if there are commonalities from _changes to fields then we are dirty
				if (Array.from(_changes).filter(change => fields.includes(change)).length) { return true; }
			} else {
				if (_changes.size > 0) { return true; }
			}

			// this is here to not make a function in a for loop
			function f(rel) { return fields.length && fields.find(f => f === rel) == null; }

			if (deep) {
				for (let rel of _schema.relationships) {
					// this relationship is not in the non-empty field list so we can skip
					if (f(rel)) { continue; }

					// check relationship if dirty
					if (this[`_${rel}`].$isDirty == null || this[`_${rel}`].$isDirty({deep: false})) { return true; }
				}
			}

			// we're clean
			return false;
		}

		$serialize(opt) {
			return modelMetaMap.get(this)._model.serialize(opt);
		}

		$revert() {
			// pull the data we need out of our model meta data
			const {_changes, _initialState, _model} = modelMetaMap.get(this);

			for (let key of _changes) {
				_model[key] = JSON.parse(_initialState.get(key));
			}

			_changes.clear();
			$rootScope.$applyAsync();
		}

		$refresh() {
			// pull the data we need out of our model meta data
			const $meta = modelMetaMap.get(this);
			const {_root, _model, _schema} = $meta;

			// make sure we have a path for this object (from link data)
			if (_schema.path == null) { throw noLinkError('refresh', _model); }

			const reqParams = {};

			if (_root !== this) { return _root.$refresh(); }
			ssEventSvc.emit('ss-json-api.preRefresh.' + _schema.model, this);
			if (_schema.relationships.length > 0) {
				reqParams.include = _schema.relationships.join(',');
			}
			reqParams.fields = _model._attributes.join(',');

			return $http({ url: _schema.path, params: reqParams }).then((res) => {
				$meta._model = JsonApiDataStore.store.sync(res.data);
				this.$$syncModel(true);
				this.$$resetChangesDetected();
				this.$$applyMutators();
				ssEventSvc.emit('ss-json-api.postRefresh.' + _schema.model, this);
				return this;
			});
		}

		$getLinks() {
			const {_model = {}} = modelMetaMap.get(this);
			return angular.copy(_model._links);
		}

		$getMeta() {
			const {_model = {}} = modelMetaMap.get(this);
			return angular.copy(_model._meta || {});
		}

		$$resetChangesDetected() {
			// pull the data we need out of our model meta data
			const $meta = modelMetaMap.get(this);
			const {_changes, _initialState, _model} = $meta;

			_initialState.clear();
			_model._attributes.forEach(attr => $meta._initialState.set(attr, JSON.stringify(_model[attr])));

			_changes.clear();
		}

		$$forceChangedState(attrName, makeChanged = true) {
			// I understand if you think the attribute has an underscore
			// in front that is, after all, what I gave you.
			if (attrName.startsWith('_')) { attrName = attrName.slice(1); }

			// pull the data we need out of our model meta data
			const $meta = modelMetaMap.get(this);
			const {_changes, _initialState, _model} = $meta;

			// if it ain't in the initial state, give up now
			if (!_initialState.has(attrName)) {
				$log.warn('could not force the changed state for', attrName, 'on', this);
				return;
			}

			if (makeChanged) {
				_initialState.set(attrName, 'null');
				_changes.add(attrName);
			} else if (_changes.has(attrName)) {
				_initialState.set(attrName, JSON.stringify(_model[attrName]));
				_changes.delete(attrName);
			}
		}

		$$applyMutators(applyOnly) {
			const {_model} = modelMetaMap.get(this);
			if (!mutatorDataMap.has(_model)) { mutatorDataMap.set(_model, {}); }
			const data = mutatorDataMap.get(_model);

			if (ssMutator[_model._type] == null) { return; }
			const mutators = ssMutator[_model._type];
			const mutatorNames = Object.keys(mutators).sort();

			if (applyOnly instanceof Array) {
				mutatorNames.filter(m => applyOnly.includes(m));
			}

			mutatorNames.forEach(key => {
				let mutFunc = mutators[key];

				// allow mutators to be prefixed with a ! to mark them as
				// important and have them run first
				if (key.startsWith('!')) { key = key.slice(1); }

				data[key] = mutFunc;

				// Don't apply the property more than once, mkay
				if (this.hasOwnProperty(key)) { return; }

				Object.defineProperty(this, key, {
					enumerable: true,
					get() {
						// Lazy execution until the first time we access the property.
						if (typeof data[key] === 'function') {
							data[key] = data[key](this);
						}
						return data[key];
					},
					set() {
						$log.error(`${_model._type}:${key}`, 'can not be set because it is a mutator value');
					}
				});
			});
		}

		$$syncModel(firstSync = false) {
			// pull the data we need out of our model meta data
			const $meta = modelMetaMap.get(this);
			const {_changes, _model, _initialState, _schema, _root} = $meta;

			// Take server attributes and setup attribute change trackers
			// spin over our attrs from the server and make sure we have
			// attribute trackers in place for all of them
			_model._attributes
				.filter(attr => !privateAttrs.includes(attr))
				.filter(attr => !this.hasOwnProperty(`_${attr}`))
				.filter(attr => !_model._relationships.includes(attr))
				.forEach(attr => Object.defineProperty(this, `_${attr}`, attrTracker.bind(this)(attr, '', attr)));

			// spin over our relationships from the server and create new
			// JsonApiCollection objects for them so we have easy access to them
			// and they can be updated via "save" on the outer object.
			_model._relationships
				.filter(attr => firstSync || !this.hasOwnProperty(`_${attr}`))
				.forEach(attr => {
					const links = _model._links;
					const relSchema = Object.assign({}, _schema, {
						model: attr,
						path: (links && links[attr] && links[attr].self || null),
						relationships: []
					});

					this[`_${attr}`] = new JsonApiCollection(_model[attr] || [], relSchema, _root);
				});

			// attach the read-only id prop
			if (!this.hasOwnProperty('id')) {
				Object.defineProperty(this, 'id', { enumerable: true, value: _model.id });
			}

			function attrTracker(modelAttr, objPath, attr) {

				const baseObj = resolveObj(_model, objPath);
				if (angular.isObject(baseObj[attr]) && !angular.isArray(baseObj[attr])) {
					let obj = {};

					Object.keys(baseObj[attr]).forEach(key => {
						const path = (objPath.length ? `${objPath}.${attr}` : attr);
						Object.defineProperty(obj, key, attrTracker.bind(this)(modelAttr, path, key));
					});

					return {
						enumerable: true,
						value: obj
					};
				} else {
					return {
						enumerable: true,
						get() {
							// get the current object reference
							const obj = resolveObj(_model, objPath);

							// if the root object is null or undefined, return
							// that null or undefined value. Otherwise, return
							// the attribute value.
							return (obj == null ? obj : obj[attr]);
						},
						set(val) {
							const mObj = resolveObj(_model, objPath);

							// if the root object is null or undefined, we can't
							// set data on it. Throw an error and bail out.
							if (mObj == null) {
								$log.error(`JsonApi Object Error: ${objPath}.${attr} can't be set because ${objPath} is null.`);
								return;
							}

							if (isDifferent(val, mObj[attr])) {
								// if we have a date object, convert it to a
								// correctly formatted string before saving it
								if (val instanceof Date) { val = val.toISOString(); }

								// if this is the first change
								if (!_changes.has(modelAttr)) {
									// store the original value
									_changes.add(modelAttr);
								}

								////> log('changed', _model._type, modelAttr, val)
								ssEventSvc.emit('ss-json-api.changedRecord.' + _model._type, _model, modelAttr, modelAttr + objPath, mObj[attr], val);
								// set the new value on the model
								mObj[attr] = val;

								// if the change makes this the same as the
								// original value, mark this field as unchanged
								if (_initialState.get(modelAttr) === JSON.stringify(_model[modelAttr])) {
									////> log('set back =>', modelAttr)
									_changes.delete(modelAttr);
								}
							}
						}
					};
				}
			}

			/**
			 * Looks up a target path within another object using
			 * dot notation. needed in order to ensure that we are always reflecting
			 * changes into the current model object and not an old reference to
			 * a previous model.
			 * @param {object} obj The object to resolve
			 * @param {string} path The path to find the object
			*/
			function resolveObj(obj, path) {
				const pArr = path.split('.');
				const attr = pArr.shift();
				if (obj == null && attr.length > 0) { return undefined; }
				if (attr === '') { return obj; }
				return resolveObj(obj[attr], pArr.join('.'));
			}
		}
	}

	class JsonApiCollection {
		constructor(modelArr, schema, root) {
			const _state = [];
			for (let rec of modelArr) {
				const recSchema = Object.assign({}, schema, {
					path: (rec._links && rec._links.self || null),
					collection: _state
				});
				_state.push(new JsonApiModel(rec, recSchema, root));
			}

			schema.path = (schema.path || null);

			_state.$$state = modelArr.map(obj => obj.id);
			_state.$$schema = schema;
			_state.$$root = root;
			_state.$$constructor = this;
			_state.$$meta = modelArr._meta || {};

			['$save', '$isDirty', '$getMeta'].forEach(attr => _state[attr] = this[attr].bind(_state));

			return _state;
		}

		/**
		 * Determines if the the models are dirty. Dirty is defined as being
		 * a new model (does not have $isDirty) or $isDirty returns true.
		 *
		 * @param {Boolean} [deep=true] Check relationships
		 * @param {String[]} fields
		 *
		 * @return {Boolean}
		 */
		$isDirty({deep = true, fields = []} = {}) { // jshint ignore:line
			var model;

			// if the initial length (represented by this.$$state.length) does not equal the current length (this.length) then we are dirty
			let initialLength = this.$$state.length;
			if (initialLength !== this.length) { return true; }

			// then check children
			for (var i = 0; i < this.length; i++) {
				model = this[i];

				// it is not a JsonApi object or the model is dirty
				if (model.$isDirty == null || model.$isDirty({deep, fields})) {
					return true;
				}
			}

			// we are clean
			return false;
		}

		/**
		 * Saves changes to a JsonApi collection
		 *
		 * @param {boolean|Object} [deepSave=true] Save down the hierarchy or a configuration object of arguments (deepSave, refresh, internalErrorHandling, opOrder)
		 * @param {boolean} [refresh=true] Refresh the object on the way back out.
 		 * @param {boolean} [internalErrorHandling=true] Handle the errors internally with the toasts, otherwise rethrow the exception and let the calling code deal with the mess.
		 * @returns {Promise} Promise resolves when all save operations are complete.
		 */
		$save(...args) { return (ssAsync(function* () {
			// define default function param values
			let props = {
				deepSave: true,
				refresh: true,
				internalErrorHandling: true,
				opOrder: ['add', 'modify', 'remove']
			};
			// args[0] is a config object, apply overrides to props object
			if (args.length === 1 && ssObjectHlp.isObject(args[0])) { Object.assign(props, args[0]); };
			// define vars and assign default values
			let {deepSave, refresh, internalErrorHandling, opOrder} = props;
			// args is the old param interface apply overrides to default values
			if (args.every(p => typeof p === 'boolean')) { ([deepSave = deepSave, refresh = refresh, internalErrorHandling = internalErrorHandling] = args); }

			var state, toRemove;
			const schema = this.$$schema;
			const toAdd = [];

			// create JsonApiModels from plain JS objects in the collection
			// and prep them to be POSTed to the server
			this.forEach(o => {
				// if this object is already a model, it's a preexisting record
				if (o instanceof JsonApiModel) { return; }
				// construct the base model
				const newModel = new JsonApiDataStore.Model(schema.model);

				// populate the base model with values that should be mapped back to the server
				Object.keys(o)
					.filter(attr => attr.startsWith('_')) // filter props to those that should map back (prefixed with _)
					.map(attr => attr.substr(1)) // the raw model does not have the _ prefix, strip it
					.filter(attr => !schema.relationships.includes(attr)) // if the prop is a relationship collection, omit it
					.forEach(attr => newModel.setAttribute(attr, o[`_${attr}`])); // copy the value to the base model

				// setup empty relationship collections that should exist
				schema.relationships.forEach(r => newModel.setRelationship(r, []));
				// queue the model to be added
				toAdd.push({ obj: o, model: newModel });
			});

			// operations that will be performed by this $save function
			const operations = {
				// saves changes to existing records in the collection
				modify: ssAsync(function* () {
					for (let rec of this) {
						if (!(rec instanceof JsonApiModel)) { continue; }
						yield rec.$save(deepSave, refresh);
					}
				}.bind(this)),

				// deletes records on the server that were removed from the collection
				remove: ssAsync(function* () {
					// delete any record that was removed from the collection
					state = this.map(r => r.id).filter(rId => typeof rId === 'string');
					toRemove = this.$$state.filter(rId => !state.includes(rId));
					for (let recId of toRemove) {
						// lookup the apiModel in the object graph
						let model = JsonApiDataStore.store.find(this.$$schema.model, recId);
						// lookup the current version number
						let versionNumber = ownerVersionMap.get(model.ownerId);

						const path = (model._links && model._links.self || null);

						// make sure we have a path for this object (from link data)
						if (path == null) { throw noLinkError('remove', model); }

						// call the delete endpoint
						yield $http.delete(`${path}?version=${versionNumber}`);

						if (model.ownerId !== model.id) {
							ownerVersionMap.set(model.ownerId, versionNumber + 1);
						}
						// remove this model from the jsonApiStore
						JsonApiDataStore.store.destroy(model);

						// returns the object as of the latest edit. I wonder if we should send the unchanged object
						ssEventSvc.emit('ss-json-api.removedRecord.' + schema.model, recId, model);
					}
				}.bind(this)),

				// adds new records for objects added to the collection
				add: ssAsync(function* () {
					for (let rec of toAdd) {
						let postConfig = {};
						if (schema.relationships.length) {
							postConfig.params = {
								include: schema.relationships.join(',')
							};
						}
						let addedRecord = (yield $http.post(schema.path, rec.model.serialize(), postConfig)).data;
						let recModel = JsonApiDataStore.store.sync(addedRecord);
						let recSchema = Object.assign({}, schema, {
							path: (recModel._links && recModel._links.self || null),
							collection: this
						});
						//> log('recSchema', recSchema)
						let newRecord = new JsonApiModel(recModel, recSchema, this.$$root);

						// passively extend the new jsonApi object with other values that
						// are on the original object.
						ssObjectHlp.passiveExtend(newRecord, rec.obj);
						if (!refresh) { ssObjectHlp.update(newRecord, rec.obj); }

						// if we have new objects in this record's relationships
						// we need to copy them over to the relationship arrays so
						// they can be posted when the save fires later
						schema.relationships
							.filter(r => rec.obj[`_${r}`] != null && rec.obj[`_${r}`].length)
							.forEach(r => {
								Array.prototype.splice.apply(
									newRecord[`_${r}`],
									[0, 0].concat(rec.obj[`_${r}`])
								);
							});

						ssEventSvc.emit('ss-json-api.addedRecord.' + schema.model, rec.obj, newRecord);

						this[this.indexOf(rec.obj)] = newRecord;
					}
				}.bind(this)),
			};

			try { // and catch any errors with a toast

				// execute the operations defined above in the order specified
				// default order: add, modify, remove
				for (const op of opOrder) {
					yield operations[op]();
				}

				// update the internal collection state
				this.$$state = state;
			} catch (err) {
				if (internalErrorHandling) {
					$log.error(err);
					ssToastHlp.errorAjax(err);
				} else {
					throw err;
				}
			}
		}.bind(this))());}

		$getMeta() {
			return angular.copy(this.$$meta);
		}

	}

	/**
	* token replacement in link urls
	*/
	function tokenReplacement(model) {
		if (model._links == null) { return; }
		const linkNames = Object.keys(model._links);
		linkNames.forEach(function (name) {
			var url = model._links[name];
			if (typeof url !== 'string') { return; }
			var urlTokens = url.match(/{.*?}/g);

			if (urlTokens == null) { return; }
			urlTokens = urlTokens.map(token => token.slice(1, -1));
			urlTokens.forEach(t => url = url.split(`{${t}}`).join(model[t]));

			model._links[name] = url;
		});
	}

	function buildPermissions(model) {
		if (model._meta == null || model._meta.permissions == null) { return; }

		const permittedObj = {};

		let {permittedProperties, permittedActions} = model._meta.permissions;

		let props = Object.keys(permittedProperties).reduce((prev, key) => {
			Object.defineProperty(prev, key, {
				enumerable: true,
				value: {}
			});

			Object.defineProperty(prev[key], 'view', {
				enumerable: true,
				value: permittedProperties[key] !== enumPropertyAction.notVisible
			});

			Object.defineProperty(prev[key], 'edit', {
				enumerable: true,
				value: [enumPropertyAction.edit,enumPropertyAction.redactedEdit].includes(permittedProperties[key])
			});

			Object.defineProperty(prev[key], 'redacted', {
				enumerable: true,
				value: [enumPropertyAction.redactedView,enumPropertyAction.redactedEdit].includes(permittedProperties[key])
			});

			Object.defineProperty(prev[key], 'raw', {
				enumerable: true,
				value: permittedProperties[key]
			});

			return prev;
		}, {});

		Object.defineProperty(permittedObj, 'props', {
			enumerable: true,
			value: props
		});

		Object.defineProperty(permittedObj, 'actions', {
			enumerable: true,
			value: permittedActions
		});

		return permittedObj;
	}

	function noLinkError(type, model) {
		return Error(`Couldn't ${type} ${model._type} (${model.id}) :: Missing link data`);
	}

	/**
	 * Test for changes to the current object
	 */
	function isDifferent(val1, val2) {
		var v1 = val1;
		var v2 = val2;

		if (ssDateHlp.isDate(v1) || ssDateHlp.isDate(v2)) {
			if (v1 == null || v2 == null) { return true; }
			if (v1 instanceof moment) { v1 = v1.toDate(); }
			if (v2 instanceof moment) { v2 = v2.toDate(); }
			if (typeof v1 === 'string') { v1 = new Date(v1); }
			if (typeof v2 === 'string') { v2 = new Date(v2); }

			return v1.getTime() !== v2.getTime();
		}

		return v1 !== v2;
	}

	// External utility injectable value
	return {
		Model: JsonApiModel,
		Collection: JsonApiCollection
	};

// #-- END Utility (ssJsonApi) --# //
// #-----------------------------# //
}

angular.module('super-suit-utilities').factory('ssJsonApi', ssJsonApi);
