import {
	Enums
}
from 'base-enum';

// #-----------------------------# //
// #---- Enums (/formStatus) ----# //

class FormStatusEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Incomplete',
			value: 0
		}, {
			name: 'Complete',
			value: 1
		}, {
			name: 'Rejected',
			value: 2
		},{
			name: 'Archived',
			value: 3
		},{
			name: 'Completed And Denied',
			value: 4
		}];
		super(enums);
	}
}

// #---- End Enums (/formStatus) ----# //
// #---------------------------------# //

angular.module('super-suit-enums').factory('enumFormStatus', () => new FormStatusEnum());
