/*@ngInject*/
function signatureApi(ssApiSvc, buildConfig) {
   // #------------------------------------# //
// #---- API Endpoint (ssStatusApi) ----# //

const baseUrl = buildConfig.endpoint.formTemplates;
const queryUrl = `${baseUrl}/organizations/:ORGID:/signatures`;
const getUrl = (p) => `${baseUrl}/signatures/${p.id}?resolveFileUrl=true`;


// model data type
const model = 'signatures';

// possible relationships
const rel = [];

// generate default endpoints
const get = ssApiSvc.endpoint({ url: getUrl, method: 'GET' }, model, rel);
const query = ssApiSvc.endpoint({ url: queryUrl, method: 'GET' }, model, rel);


// API endpoint methods
return {
    get,
    query
};

// #-- END API Endpoint (ssStatusApi) --# //
// #------------------------------------# //
}
angular.module('super-suit-apis').factory('ssSignatureApi', signatureApi);
