// #------------------------------------# //
// #---- Mutator (mutatorEmployees) ----# //

/*@ngInject*/
function employeesMutator(ssEnum, ssDateHlp, ssArrayHlp, moment) { return {
	'bestContactNumber': function (emp) {
		var allPhones = emp._phoneNumbers;

		if (allPhones == null) { return; }
		if (allPhones.length === 0) { return; }

		const {personal, work, other} = ssEnum.usageType;
		const isPrimary = x => !x.isEmpty && x._isPrimary === true && x._usageType !== work;
		const isPersonal = x => !x.isEmpty && x._usageType === personal;
		const isOther = x => !x.isEmpty && x._usageType === other;
		const isWork = x => !x.isEmpty && x._usageType === work;

		return allPhones.find(isWork) || allPhones.find(isPrimary) ||
			allPhones.find(isPersonal) || allPhones.find(isOther) || null;
	},

	'bestContactEmail': function (emp) {
		const emails = emp._emails;

		if (emails == null) { return; }
		if (emails.length === 0) { return; }

		const {personal, work, other} = ssEnum.usageType;
		const isPrimary = x => x._isPrimary === true && x._emailAddress && x._emailType === work;
		const isPersonal = x => x._emailAddress && x._emailType === personal;
		const isOther = x => x._emailAddress && x._emailType === other;
		const isWork = x => x._emailAddress && x._emailType === work;

		const email = emails.find(isPrimary) || emails.find(isWork) ||
			emails.find(isPersonal) || emails.find(isOther);

		return (email != null ? email._emailAddress : null);
	},

	/**
	 * TODO Deprecate
	 *
	 * @param {Object} emp
	 *
	 * @return {Object} Current Status
	 */
	'statusCurrent': function(emp) {
		if (emp._statusHistory == null) { return; }
		if (emp._statusHistory.length === 0) { return; }

		return emp._statusHistory.find(s => s.isCurrent);
	},

	/**
	 * TODO Deprecate
	 *
	 * @param {Object} emp
	 *
	 * @return {Array} Future Statuses
	 */
	'statusFuture': function(emp) {
		if (emp._statusHistory == null) { return; }
		if (emp._statusHistory.length === 0) { return; }

		return ssArrayHlp.sortByObjectKey(
			emp._statusHistory.filter(s => s.isFuture),
			'_from', // key
			true	 // reverse order
		);
	},

	/**
	 * TODO Deprecate
	 *
	 * @param {Object} emp
	 *
	 * @return {Array} Past Statuses
	 */
	'statusPast': function(emp) {
		if (emp._statusHistory == null) { return; }
		if (emp._statusHistory.length === 0) { return; }

		return ssArrayHlp.sortByObjectKey(
			emp._statusHistory.filter(s => s.isPast),
			'_from', // key
			true	 // reverse order
		);
	},

	'fullName': function(emp) {
		var name = emp._firstName;
		if (emp._middleName) { name += ' ' + emp._middleName; }
		if (emp._lastName) { name += ' ' + emp._lastName; }
		return name;
	},

	'aliasName': function(emp) {
		var name = emp._aliasFirstName;
		if (emp._aliasMiddleName) { name += ' ' + emp._aliasMiddleName; }
		if (emp._aliasLastName) { name += ' ' + emp._aliasLastName; }
		return name;
	},

	'personalInfo': function(emp) {
		let formattedBirthDate = emp._birthDateUtc ? moment(emp._birthDateUtc).format('M/D/YYYY') : null;
		let maritalStatus = ssEnum.maritalStatus.byValue(emp._maritalStatus);
		let gender = ssEnum.gender.byValue(emp._gender);
		let ethnicity = ssEnum.ethnicity.byValue(emp._ethnicity);
		let race = ssEnum.race.byValue(emp._race);

		return {
			birthDate: formattedBirthDate,
			ssn: emp._sSN || null,
			externalId: emp._externalId || null,
			maritalStatus: maritalStatus || null,
			gender: gender || null,
			ethnicity: ethnicity || null,
			race: race || null
		};
	},

	'phoneNumbers': function(emp) {
		var allPhones = emp._phoneNumbers;
		const defaultObj = {
			work: null,
			workExt: null,
			personal: null,
			personalPhoneType: null,
			personalIsPrimary: null,
			other: null,
			otherPhoneType: null,
			otherIsPrimary: null
		};

		if (allPhones == null) { return defaultObj; }
		if (allPhones.length === 0) { return defaultObj; }

		const {personal, work, other} = ssEnum.usageType;

		let workNumber = allPhones.find((num) => {
			return num._usageType === work;
		});

		let personalNumber = allPhones.find((num) => {
			return num._usageType === personal;
		});

		let otherNumber = allPhones.find((num) => {
			return num._usageType === other;
		});

		return Object.assign(defaultObj, {
			work: workNumber ? workNumber._number : null,
			workExt: workNumber ? workNumber._extension : null,
			personal: personalNumber ? personalNumber._number : null,
			personalPhoneType: personalNumber ? personalNumber._phoneType : null,
			personalIsPrimary: personalNumber ? personalNumber._isPrimary : null,
			other: otherNumber ? otherNumber._number : null,
			otherPhoneType: otherNumber ? otherNumber._phoneType : null,
			otherIsPrimary: otherNumber ? otherNumber._isPrimary : null
		});
	},

	'emails': function(emp) {
		const emails = emp._emails;
		const defaultObj = {
			work: workEmail ? workEmail._emailAddress : null,
			personal: personalEmail ? personalEmail._emailAddress : null,
		};

		if (emails == null) { return defaultObj; }
		if (emails.length === 0) { return defaultObj; }

		const {personal, work} = ssEnum.usageType;

		let workEmail = emails.find((email) => {
			return email._emailType === work;
		});

		let personalEmail = emails.find((email) => {
			return email._emailType === personal;
		});

		return Object.assign(defaultObj, {
			work: workEmail ? workEmail._emailAddress : null,
			personal: personalEmail ? personalEmail._emailAddress : null,
		});
	},

	'address': function(emp) {
			// disabling this for now
			return;

			if (emp._addresses == null) { return; }
			if (emp._addresses.length > 0) { return emp._addresses[0]; }
			var newAddress = {
				_from: ssDateHlp.foreverPastString,_to: ssDateHlp.foreverFutureString,
				_addressType: 0, _isPrimary: true,
				_street1: null, _street2: null,
				_city: null, _state: null, _zip: null, isCurrent: true, isFuture: false, isPast: false
			};
			emp._addresses.push(newAddress);
			return newAddress;
	},

	'emergencyContacts': function(emp) {

		const allContacts = emp._emergencyContacts;
		const defaultObj = {
			primaryContactName: null,
			primaryContactRelationship: null,
			primaryContactPhone: null,
			primaryContactAltPhone: null,
			secondaryContactName: null,
			secondaryContactRelationship: null,
			secondaryContactPhone: null,
			secondaryContactAltPhone: null,
		};

		if (allContacts == null) { return defaultObj; }
		if (allContacts.length === 0) { return defaultObj; }

		let primaryContact = allContacts.find((contact) => {
			return contact._isPrimary;
		});

		let secondaryContact = allContacts.find((contact) => {
			return !contact._isPrimary;
		});

		let primaryRelationship = primaryContact ? ssEnum.emergencyContactRelationship.byValue(primaryContact._relationshipType) : null;

		let secondaryRelationship = secondaryContact ? ssEnum.emergencyContactRelationship.byValue(secondaryContact._relationshipType) : null;

		return Object.assign(defaultObj, {
			primaryContactName: primaryContact ? primaryContact._contactName : null,
			primaryContactRelationship: primaryRelationship,
			primaryContactPhone: primaryContact ? primaryContact._primaryPhone : null,
			primaryContactAltPhone: primaryContact ? primaryContact._alternatePhone : null,
			secondaryContactName: secondaryContact ? secondaryContact._contactName : null,
			secondaryContactRelationship: secondaryRelationship,
			secondaryContactPhone: secondaryContact ? secondaryContact._primaryPhone : null,
			secondaryContactAltPhone: secondaryContact ? secondaryContact._alternatePhone : null,
		});

	},

	// Can be deleted once all effective dated work is out because we deleted the profileCompletenessSvc with this PR
	'profileCompleteness': function(emp) {
		return {};
	},

	// Can be deleted once all effective dated work is out because we deleted the profileCompletenessSvc with this PR
	'profileIncompleteList': function(emp) {
		return {};
	}
};}

// #---- End Mutator (mutatorEmployees) ----# //
// #----------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorEmployees', employeesMutator);
