// #---------------------------------------# //
// #---- Mutator ({ camelCase name }}) ----# //

/*@ngInject*/
function phoneNumbersMutator(ssMutatorDataHlp) { return {
	'isEmpty': function(obj) {
		if (obj._number == null) { obj._number = ''; }
		return obj._number.trim().length === 0;
	},
	// formatted version of the phone number
	'formatted': function (obj) {
		const propertyName = '_number';
		if (!obj.hasOwnProperty(propertyName)) { return ''; }
		if (!obj[propertyName]) { return ''; }

		return ssMutatorDataHlp.formatPhone(obj[propertyName]);
	},
	/* Now with Extensions! */
	'formattedFull': function (obj) {
		let ret = '';
		if (obj._extension != null && obj._extension.length > 0) {
			ret = ' x' + obj._extension;
		}
		return obj.formatted + ret;
	}

};}

// #---- End Mutator ({ camelCase name }}) ----# //
// #-------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorPhoneNumbers', phoneNumbersMutator);
