import {Decorator} from 'base-decorator';
// #-----------------------------------# //
// #---- Decorator (ss-focus-bind) ----# //

/*@ngInject*/
function ssFocusBind($timeout, $parse, ssElemHlp) {

	class FocusBind extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {
			var locked = false;
			var scopeBoundSetter = $parse(attrs.ssFocusBind).assign;
			scope.$watch(attrs.ssFocusBind, function (nVal) {
				if (!locked) {
					if (ssElemHlp.getStyles(elem)['display'] === 'none') {
						$timeout(function () {
							elem[0][!!nVal ? 'focus' : 'blur']();
						}, 10);
					} else {
						elem[0][!!nVal ? 'focus' : 'blur']();
					}
				}
				locked = false;
			});

			elem.on('focus', function () {
				scope.$root.safeApply(function () {
					locked = true;
					scopeBoundSetter(scope, true);
					scope.$emit('focusBind', true);
				});
			});

			elem.on('blur', function () {
				scope.$root.safeApply(function () {
					locked = true;
					scopeBoundSetter(scope, false);
					scope.$emit('focusBind', false);
				});
			});
		}
	}

	return new FocusBind();
}

// #-- END Decorator (ss-focus-bind) --# //
// #-----------------------------------# //

angular.module('super-suit-decorators').directive('ssFocusBind', ssFocusBind);
