// #------------------------------------# //
// #---- Helper (ssOrganizationHlp) ----# //

/*@ngInject*/
function ssOrganizationHlp() { return {

	/**
	 *
	 * @param {object} org - the organization object that comes back from ssOrganizationApi
	 * @param {object[]} org._applications - the array of applications that the org has access to
	 * @param {string} org._applications._applicationId - the name/acronym of a Frontline application
	 * @returns {*|boolean}
     */
	hasCentral(org){
		if(!org){
			throw new Error('the hasCentral function requires org as a parameter');
		}
		if(!org._applications || !Array.isArray(org._applications)){
			throw new Error('you probably forgot to set \'applications\' as the include option for the organizationApi because the org you gave hasCentral() did not have an _applications array');
		}

		return org._applications.some((anApp) => {
			if (typeof anApp._applicationId !== 'string') {
				return;
			}
			return anApp._applicationId.toLowerCase() === 'ec';
		});
	},

};}
// #-- END Helper (ssOrganizationHlp) --# //
// #------------------------------------# //

angular.module('super-suit-helpers').factory('ssOrganizationHlp', ssOrganizationHlp);
