import {Enums} from 'base-enum';

// #-------------------------------------# //
// #---- Enums (/formTemplateStatus) ----# //

class FormTemplateStatusEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Inactive',
			value: 0
		}, {
			name: 'Active',
			value: 1
		}, {
			name: 'Archived',
			value: 2
		}];

		super(enums);
	}
}

// #---- End Enums (/formTemplateStatus) ----# //
// #-----------------------------------------# //

angular.module('super-suit-enums').factory('enumFormTemplateStatus', () => new FormTemplateStatusEnum());
