import {Component} from 'base-component';
// #--------------------------------# //
// #---- Component (super-tabset) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	initActiveTab: '@?', // the `<super-tab>` tab-id value that will start out as active
	onTabChange: '&', // expression that will be executed when the active tab changes. Use $tabId to inject the new active tab-id into the expression (example: `on-tab-change="go.makePanelActive($tabId)"`)
	tabLimit: '@?', // the maximum number of tab spots that can be shown. Tabs that overflow this number will be combined with the last tab in a dropdown of options.
	flat: '@?', // use the flat tab style (with underline)
};

// directive definition object overrides
const config = {
	transclude: true // `<super-tab>` components
};

/*@ngInject*/
function tabset() {

	class Tabset extends Component {
		/*@ngInject*/
		controller($scope, $log) {
			// const {glean} = ssObjectHlp;
			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				activeTab: $scope.initActiveTab || null,
				allTabIds: [],
				overflowTabs: []
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				/**
				* Set the active tab ID on $scope, then call all registered tab callbacks with the new tab ID.
				* Do nothing if the provided tab ID matches the currently active ID.
				* @param {String} newId The tab ID to set as the active one
				*/
				setActiveTab(newId) {
					if (vm.activeTab === newId) { return; }
					vm.activeTab = newId;
					$scope.onTabChange({$tabId: vm.activeTab});
					vm.allTabIds.forEach(key => registeredTabs[key].tabCb(vm.activeTab));
				}
			};

			// mapping of tabId to tab activation callbacks, each of which will be fired when the activeTab is set
			const registeredTabs = {};
			const intTabLimit = parseInt($scope.tabLimit, 10);
			const tabLimit = (Number.isNaN(intTabLimit) ? -1 : intTabLimit);


			$scope.$on('ss-tab-init', (evt, tabId, tabCb) => {
				evt.stopPropagation();

				if (registeredTabs[tabId] != null) {
					$log.error(`super-tabset => tab '${tabId}' is already registered`);
					return;
				}

				const {vm: tabVm} = evt.targetScope;

				if (tabLimit >= 0) {
					// if we have too many tabs, turn the last tab spot into
					// a tab dropdown that contains the tab that used to be in
					// that spot.
					if (vm.allTabIds.length === tabLimit) {
						const lastTabId = vm.allTabIds.slice(-1)[0];
						const {label, subLabel} = registeredTabs[lastTabId].tabVm;
						vm.overflowTabs.push({tabId: lastTabId, label, subLabel});
						//> log('lastTabId', lastTabId)
						registeredTabs[lastTabId].tabVm.visible = false;
					}

					// add the tab to the overflow list
					if (vm.allTabIds.length >= tabLimit) {
						const {label, subLabel} = tabVm;
						vm.overflowTabs.push({tabId, label, subLabel});
						//> log('tabId', tabId)
						evt.targetScope.vm.visible = false;
					}
				}

				registeredTabs[tabId] = {tabCb, tabId, tabVm};
				vm.allTabIds.push(tabId);


				// set active tab to the first initialized tab if unset
				if (vm.activeTab == null) { vm.activeTab = tabId; }

				// initialize each tab's active state uniformly
				tabCb(vm.activeTab);
			});

			$scope.$on('ss-tab-activate', (evt, newId) => {
				evt.stopPropagation();
				go.setActiveTab(newId);
			});
		}

		link(scope, elem, attrs, ctrl, tran) {
			const className = 'ss-tab-list' ;
			const isFlat = (attrs.flat != null || elem.hasClass('flat'));
			elem.addClass(className + (isFlat ? '-flat' : ''));

			elem.on('keydown', function (evt) {
				const {activeTab, allTabIds} = scope.vm;
				const menuMaxIdx = allTabIds.length - 1;
				const selectedIdx = allTabIds.findIndex(tId => tId === activeTab);

				let nextIdx = selectedIdx === -1 ? 0 : selectedIdx + 1;
				if (nextIdx > menuMaxIdx) { nextIdx = 0; }
				let prevIdx = selectedIdx === -1 ? menuMaxIdx : selectedIdx - 1;
				if (prevIdx < 0) { prevIdx = menuMaxIdx; }

				const nextTabId = allTabIds[nextIdx];
				const prevTabId = allTabIds[prevIdx];
				let newActiveTabId = null;

				switch(evt.key) {
					case 'ArrowLeft': newActiveTabId = prevTabId; break;
					case 'ArrowRight': newActiveTabId = nextTabId; break;
					default: break;
				}

				if (newActiveTabId == null) { return; }
				scope.$applyAsync(() => scope.go.setActiveTab(newActiveTabId));
				cancelEvt(evt);
			});

			elem.on('keyup', evt => {
				switch(evt.key) {
					case 'ArrowLeft': case 'ArrowRight': cancelEvt(evt);
					default: break;
				}
			});

			function cancelEvt(evt) {
				evt.stopPropagation();
				evt.preventDefault();
			}

			tran(clone => elem.prepend(clone));
		}
	}

	return new Tabset(props, 'tabset', config);
}

// #-- END Component (super-tabset) --# //
// #--------------------------------# //

angular.module('super-suit-components').directive('superTabset', tabset);
