/*@ngInject*/
function jobTypesApi(ssApiSvc, buildConfig, ssEffectiveDateHlp) {
// #--------------------------------------# //
// #---- API Endpoint (ssJobTypesApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.jobTypes;
	const convertToValidatingSave = ssEffectiveDateHlp.convertToValidatingSave;
	const queryUrl = `${baseUrl}/organizations/:ORGID:/jobTypes`;
	const getUrl = (p) => `${baseUrl}/jobTypes/${p.id}`;


	// model data type
	const model = 'jobTypes';

	// possible relationships
	const rel = ['paySchedules', 'jobTypeDetails'];

	// generate default endpoints
	const query = convertToValidatingSave(ssApiSvc.endpoint({ url: queryUrl, method: 'GET' }, model, rel));
	const get = convertToValidatingSave(ssApiSvc.endpoint({ url: getUrl, method: 'GET' }, model, rel));


	// API endpoint methods
	return {
		query,
		get
	};

// #-- END API Endpoint (ssJobTypesApi) --# //
// #--------------------------------------# //
}

angular.module('super-suit-apis').factory('ssJobTypesApi', jobTypesApi);
