// #------------------------------------# //
// #---- Helper (ssServiceLogicHlp) ----# //

/*@ngInject*/
function ssServiceLogicHlp(buildConfig) { return { // eslint-disable-line

	productIdList(pIdList = []) {
		return pIdList.reduce(function (list, pId) {
			// take into account productId environment mapping
			const pMap = buildConfig.productMap || {};
			const pIdLower = pId.toLowerCase();

			list.push(pId.toUpperCase());
			if (pMap[pIdLower] != null) {
				list.push(pMap[pIdLower].toUpperCase());
			}

			return list;
		}, []);
	},

	productIdEnvMap(pId) {
		const pMap = buildConfig.productMap || {};
		const pIdLower = pId.toLowerCase();
		return (pMap[pIdLower] || pId).toUpperCase();
	}

};} // eslint-disable-line
// #-- END Helper (ssServiceLogicHlp) --# //
// #------------------------------------# //

angular.module('super-suit').factory('ssServiceLogicHlp', ssServiceLogicHlp);
