/*@ngInject*/
function locationsApi(ssApiSvc, buildConfig) {
// #---------------------------------------# //
// #---- API Endpoint (ssLocationsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.locations;
	const queryUrl = `${baseUrl}/organizations/:ORGID:/locations`;
	const getUrl = (p) => `${baseUrl}/locations/${p.id}`;


	// model data type
	const model = 'locations';

	// possible relationships
	const rel = [];

	// generate default endpoints
	const get = ssApiSvc.endpoint({ url: getUrl, method: 'GET' }, model, rel);
	const query = ssApiSvc.endpoint({ url: queryUrl, method: 'GET' }, model, rel);


	// API endpoint methods
	return {
		get,
		query
	};

// #-- END API Endpoint (ssLocationsApi) --# //
// #---------------------------------------# //
}

angular.module('super-suit-apis').factory('ssLocationsApi', locationsApi);
