import {Component} from 'base-component';
// #--------------------------------------------------# //
// #---- Component (super-multi-filter-list-item) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	option: '='
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function multiFilterListItem() {

	class MultiFilterListItem extends Component {
		/*@ngInject*/
		controller($scope) {
			$scope.option.hide = false;

			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				name: $scope.option._name,
				id: `mfli-${(Math.ceil(Math.random() * 10000000)).toString()}`, // "unique" control identifier
			};

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				click() {
					$scope.$emit('multi-filter-list-item-click', $scope.option);
				}
			};
			$scope.$emit('multi-filter-list-item-added', $scope.option);
		}

	}

	return new MultiFilterListItem(props, 'multi-filter-list-item', config);
}

// #-- END Component (super-multi-filter-list-item) --# //
// #--------------------------------------------------# //

angular.module('super-suit-components').directive('superMultiFilterListItem', multiFilterListItem);
