/*@ngInject*/
function resourceLibraryApi($http, buildConfig) {
	// #---------------------------------------------# //
	// #---- API Endpoint (ssResourceLibraryApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.resourceLibrary;
	// USE EXPLICIT URL ONLY FOR LOCALHOST DEVELOPMENT!!!
	// const baseUrl = "https://resourcelibrary-qa.ss.frontlineeducation.com/api"

	const getFilterOptions = () => {
		/* Return a list of Resource Library filter options based on an existing resource metadata */
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/terms`,
		};

		return $http(request);
	};

	const searchResources = (
		searchString,
		searchFilters,
		categoryFilters,
		fromIndex,
		returnCount = 50,
		transactionId = '',
		sortOrder = 'score desc'
	) => {
		let postData = {
			rows: returnCount,
			transactionId: transactionId,
			sort: sortOrder,
		};

		if (searchString !== '' && searchString !== undefined) {
			postData.q = searchString;
		}

		if (searchFilters) {
			postData.filters = searchFilters;
		}

		if (categoryFilters) {
			postData.category = categoryFilters;
		}

		//Start point for paging
		if (fromIndex) {
			postData.start = fromIndex;
		}

		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/search/resources`,
			data: postData,
		};

		return $http(request);
	};

	// a separate endpoint that does not apply licensing logic to the query.
	const searchAllResources = (
		searchString,
		searchFilters,
		categoryFilters,
		fromIndex,
		returnCount = 50,
		transactionId = '',
		sortOrder = 'score desc'
	) => {
		let postData = {
			rows: returnCount,
			transactionId: transactionId,
			sort: sortOrder,
		};

		if (searchString !== '' && searchString !== undefined) {
			postData.q = searchString;
		}

		if (searchFilters) {
			postData.filters = searchFilters;
		}

		if (categoryFilters) {
			postData.category = categoryFilters;
		}

		//Start point for paging
		if (fromIndex) {
			postData.start = fromIndex;
		}

		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/search/all-resources`,
			data: postData,
		};

		return $http(request);
	};

	const getDefaultImgs = () => {
		/* Return the collection of Resource Library's default images & icons */
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/files`,
		};
		//> info( '%c%s' + '%c request:', 'font-weight: bold', 'getDefaultImgs\'', 'font-weight: normal')
		//> info( "request:", request )
		return $http(request);
	};

	/**
	 * Fetches anything you want from AWS (but should really only be used for thumbnails).
	 * @param key AWS object key.
	 * @param prefix AWS bucket prefix.
	 * @returns {*} The file(s) requested from AWS.
	 * @deprecated DO NOT USE. This function is preserved purely for fetching thumbnail data.
	 *             To serve up files from AWS, use getDownloadSignedUrl instead.
	 */
	const getAWSImgs = (key, prefix) => {
		/* Return the collection of Resource Library's default images & icons */
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/files/aws`,
			params: { key, prefix },
		};

		return $http(request);
	};

	const getMcPrivileges = () => {
		/* Return the collection of MicroCredential privileges for the specified user */
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/privileges/mcs`,
		};
		//> info( '%c%s' + '%c request:', 'font-weight: bold', 'getMcPrivileges\'', 'font-weight: normal')
		//> info( "request:", request )
		return $http(request);
	};

	const ltiResources = action => {
		// base lti endpoint url is on the 'lti' path _not_ the 'api' path
		const ltiUrl = baseUrl.replace(/api$/, 'lti');

		/*
		This is a method that will request an LTI or SingleSignOn link
		action will look something like this:
		{
			method: 'POST',
			uri: 'http://resourcelibrary-qa.ss.frontlineeducation.com/lti',
			payload: {
				launch_url: 'https://demo.edivate.com',
				custom_item_type: 'video',
				custom_item_id: '813',
			},
		}
		It will return a formatted URL
		*/
		let request = {
			method: action.method,
			url: ltiUrl,
			data: action.payload,
		};

		return $http(request);
	};

	const trackResourceAction = actionData => {
		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/user/actions`,
			data: actionData,
		};

		return $http(request);
	};

	/* COLLECTION related calls
	   The following calls create, read, update, and delete Resource Library Collections.
	   The two standard pieces of data for these calls are:
	     - collectionId - a GUID generated & provided by the `getCollection` call, e.g.
	       {
		       data: {
			       collectionId: '6a8c042b-40f9-408c-b3a1-7441b8ab6f26'
		       }
	       }
	     - resourceIds - an array of Resource Library resource ids, e.g.
	       {
		       data: {
			       resourceIds: ['6a8c042b-40f9-408c-b3a1-7441b8ab6f26']
		       }
	       }
	*/

	const getCollection = collectionId => {
		/* Return a list of Resource Library resourceIds based on an existing CollectionId */
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/collections/${collectionId}`,
		};

		return $http(request);
	};

	const postCollection = resources => {
		/* Create a new Resource Library Collection based on an array of resources.
		   Returns the new collection's collectionId. */

		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/collections`,
			data: {
				resourceIds: resources,
			},
		};

		return $http(request);
	};

	const postResource = (resource, query) => {
		/* Create a new Resource. */

		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/resources?${query || ''}`,
			data: resource,
		};

		return $http(request);
	};

	const putCollection = (resources, collectionId) => {
		/* Update an existing Resource Library Collection */

		let request = {
			method: 'PUT',
			url: `${baseUrl}/v1/collections/${collectionId}`,
			data: {
				resourceIds: resources,
			},
		};
		//> info( '%c%s' + '%c request:', 'font-weight: bold', 'putCollection\'s', 'font-weight: normal')
		//> info( request )

		return $http(request);
	};

	const deleteCollection = collectionId => {
		/* Delete an existing Resource Library Colletion */

		let request = {
			method: 'DELETE',
			url: `${baseUrl}/v1/collections/${collectionId}`,
		};
		//> info( '%c%s' + '%c request:' + '%o', 'font-weight: bold', 'deleteCollection\'s', 'font-weight: normal', request )

		return $http(request);
	};

	const getTypeDefs = () => {
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/typedefs`,
		};

		return $http(request);
	};

	const getCompletionForResources = resourceArray => {
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/user/status/resources`,
			params: { resourceIds: resourceArray, completed: true },
		};

		return $http(request);
	};

	/* Resource RATING related calls
	   The following calls create, read, and delete thumbsUp ratings on resources.
	   There are no headers for these calls, and the only query parameter passed in for these is:
	     - resourceId - the resource under consideration
	*/

	const getRating = (resourceId, forUser = false) => {
		/*
				Returns _either_
		    - (forUser === false) a count of thumbsUp for the resource (default)
		    or
		    - (forUser === true) a boolean for whether an individual user has
				                     thumbed up the resource
		*/
		const forSpecificUser = forUser ? '/user' : '';
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/rating/${resourceId}${forSpecificUser}`,
		};

		//> info( '%c%s' + '%c request:' + '%o', 'font-weight: bold', 'getRating\'s', 'font-weight: normal', request )

		return $http(request);
	};

	const postRating = resourceId => {
		/*
				Return a boolean indicating the user has a thumbsUp for the resource
		*/
		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/rating/${resourceId}`,
		};

		//> info( '%c%s' + '%c request:' + '%o', 'font-weight: bold', 'postRating\'s', 'font-weight: normal', request )

		return $http(request);
	};

	const deleteRating = resourceId => {
		/*
				Return a boolean indicating the user has a thumbsUp for the resource
		*/
		let request = {
			method: 'DELETE',
			url: `${baseUrl}/v1/rating/${resourceId}`,
		};

		//> info( '%c%s' + '%c request:' + '%o', 'font-weight: bold', 'deleteRating\'s', 'font-weight: normal', request )

		return $http(request);
	};

	const getVideo = (videoId, provider) => {
		/* returns the video details.
			videoId - id of the video,
			provider - source of video ( e.g. edivate, youtube, vimeo )
			*/
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/resources/videos/${videoId}/provider/${provider}`,
		};

		return $http(request);
	};

	const postVideoEvents = (videoId, payload) => {
		/*
		This is a method that record events as well as viewing progress whenever a user interacts witht he video player:
		{
			method: 'POST',
			uri: '${baseUrl}/v1/resources/videos/{{videoId}}/events',
			payload: {
			  "providerId": "edivate",
			  "event": "played",
			  "duration": "string",
			  "viewingHash": "string",
			  "videoURL": "string"
			}
		}
		It will return an object
		*/

		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/resources/videos/${videoId}/events`,
			data: payload,
		};

		return $http(request);
	};

	const getResourceAction = resourceId => {
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/resources/${resourceId}/action`,
		};

		return $http(request);
	};

	const getUploadSignedUrl = (filename, prefix) => {
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/files/presignedurl?filename=${filename}&prefix=${prefix}`,
		};

		return $http(request);
	};

	/**
	 * @deprecated DO NOT USE!
	 */
	const getDownloadSignedUrl = (filename, prefix) => {
		//> error('I AM DEPRECATED! You should not be invoking me!');
			let request = {
				method: 'GET',
				url: `${baseUrl}/v1/files/url/aws?key=${filename}&prefix=${prefix}`,
			};

			return $http(request);
	};

	/**
	 * Given a Resource's UID, produce the pre-signed AWS url for GET-ing the file.
	 * @param resourceUid The Resource's UID value. Required; case-insensitive.
	 * @returns {*} Angular HTTP request to get the URL.
	 */
	const getDownloadSignedUrlV2 = resourceUid => {
		let request = {
			method: 'GET',
			url: `${baseUrl}/v2/files/url/${resourceUid}`,
		};

		return $http(request);
	};

	const getResource = (resourceId, query) => {
		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/resources/${resourceId}?${query || ''}`,
		};

		return $http(request);
	};

	const getResources = (limit, offset, qStr) => {
		/*
		This is a method that returns all resources created by the requesting user
		*/

		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/resources?limit=${limit}&offset=${offset}&q=${qStr}`,
		};

		return $http(request);
	};

	// Return resource objects from an array of ids
	const getResourcesV2 = (resourceIds, limit, offset) => {

		let params = {
			resourceIds
		};

		if(limit){
			params.limit = limit;
		}

		if(offset){
			params.offset = offset;
		}

		let request = {
			method: 'GET',
			url: `${baseUrl}/v2/resources`,
			params,
		};

		return $http(request);
	};

	const deleteResource = (resourceId, type ) => {
		/*
		This is a method that deletes a resource, returns boolean
		*/

		let request = {
			method: 'DELETE',
			url: `${baseUrl}/v1/resources/${resourceId}?type=${type}`,
		};

		return $http(request);
	};

	const getUserInteraction = data => {
		/*
			This is a method to get user interaction for a specific resource.
			Sample Data Object: {
				id: edivate-video-123,
				query: {
					limit: 10,
					offset: 0,
					sortyBy: 'eventDate',
					sortOrder: 'ASC'
				}
			}
		*/

		let query = Object.keys(data.query).map(key => `${key}=${data.query[key]}`).join('&');

		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/user/interactions/${data.id}?${query}`
		};

		return $http(request);
	};

	const postResourceInteraction = (id, payload) => {
		/*
		This is a method that send interaction from resource library to interaction service
		*/

		let request = {
			method: 'POST',
			url: `${baseUrl}/v1/resources/${id}/interaction`,
			data: payload
		};
		return $http(request);
	};

	// **Deprecated**
	const getUserHistory = args => {

		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/user/interactions`,
			params: args
		};

		return $http(request);
	};

	const getUserHistoryV2 = args => {

		let request = {
			method: 'GET',
			url: `${baseUrl}/v2/user/interactions`,
			params: args
		};

		return $http(request);
	};

	// **Deprecated**
	const getUserHistoryExport = args => {

		let request = {
			method: 'GET',
			url: `${baseUrl}/v1/user/interactions`,
			params: args,
			responseType: 'blob'
		};

		return $http(request);
	};

	const getUserHistoryExportV2 = args => {

		let request = {
			method: 'GET',
			url: `${baseUrl}/v2/user/interactions`,
			params: args,
			responseType: 'blob'
		};

		return $http(request);
	};

	/**
	 * Get an org's licenses, along with status info on each license.
	 * @param orgId The flOrgId of the organization.
	 * @param includeNonGlobalLicenses Should non-global licenses (e.g. org-level licenses)
	 *                           be included? Optional; defaults to <code>false</code>
	 * @returns {*} HTTP request to get the licenses.
	 */
	const getLicensesByOrgId = (orgId, includeNonGlobalLicenses=false) => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/organizations/${orgId}/licenses`,
			params: {
				include_non_global_licenses: includeNonGlobalLicenses
			},
		});
	};

	/**
	 * Set the effective duration of a license for a specific organization. Null dates indicate no effective
	 * or expiration (from forever to forever).
	 *
	 * @param {string} orgId - org id from the organization service.
	 * @param {string} licenseId
	 * @param {Object} payload
	 * @param {?string} payload.effectiveDate - ISO 8601 date to make license effective from
	 * @param {?string} payload.expirationDate - ISO 8601 date to make license expire at
	 * @returns {Promise<$httpRes<OrgLicense>>}
	 */
	const postLicenseDuration = (orgId, licenseId, payload) => {
		return $http({
			method: 'POST',
			url: `${baseUrl}/v1/organizations/${orgId}/licenses/${licenseId}/duration`,
			data: payload
		});
	};

	/**
	 * Tag resources to a specific license. Upserts if a resource already exists on the license.
	 *
	 * @param {string} licenseId
	 * @param {Object} payload [<resourceUid>]
	 */
	const postResourcesToLicense = (licenseId, payload) => {
		return $http({
			method: 'POST',
			url: `${baseUrl}/v1/licenses/${licenseId}/resources`,
			data: payload,
		});
	};

	/**
	 * Remove resources from a specific license. Does nothing if no corresponding LicenseResource exists.
	 *
	 * @param {string} licenseId
	 * @param {Object} payload [<resourceUid>]
	 */
	const deleteResourcesFromLicense = (licenseId, payload) => {
		return $http({
			method: 'POST',
			url: `${baseUrl}/v1/licenses/${licenseId}/delete-resources`,
			data: payload,
		});
	};

	// Gets user info i.e. First Name, Last Name for the current signed in user
	const getCurrentUser = () => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/user`,
		});
	};

	// API endpoint methods
	return {
		getFilterOptions,
		searchResources,
		searchAllResources,
		postResource,
		getDefaultImgs,
		getMcPrivileges,
		ltiResources,
		trackResourceAction,
		getCollection,
		putCollection,
		postCollection,
		deleteCollection,
		getTypeDefs,
		getCompletionForResources,
		getAWSImgs,
		getRating,
		postRating,
		deleteRating,
		getVideo,
		getResourceAction,
		getResource,
		getResources,
		getResourcesV2,
		postVideoEvents, // `payload.providerId<String>`=id of the video service provider. (e.g. `edivate`)<br/> `payload.event<String>`=Type of event to be recorded. (e.g.`played`, `paused`, `watched`, `skipped`, `completed` )<br/> `payload.duration<String>`=TimeRanges of the video being watched by the user<br/>`payload.viewingHash <String>`=Id of the user's viewing session<br/>`payload.videoUrl <String>`=The url where the user has watched the video
		getUploadSignedUrl,
		getDownloadSignedUrl ,
		getDownloadSignedUrlV2,
		deleteResource,
		postResourceInteraction,
		getLicensesByOrgId,
		postLicenseDuration,
		getUserHistory,
		getUserHistoryV2,
		getUserInteraction,
		postResourcesToLicense,
		getUserHistoryExport,
		getUserHistoryExportV2,
		deleteResourcesFromLicense,
		getCurrentUser,
	};

	// #-- END API Endpoint (ssResourceLibraryApi) --# //
	// #---------------------------------------------# //
}

angular
	.module('super-suit-apis')
	.factory('ssResourceLibraryApi', resourceLibraryApi);
