/*@ngInject*/
function logic() {
	// logic api
	return {
		isViewOthers
	};


	function isViewOthers({ scEnum, securityScopes }){
		const isViewOtherScope = (scope) => [scEnum.all, scEnum.specific, scEnum.directReports].includes(scope.employeeAdministrationContext);
		if(securityScopes == null) { return true; }
		let result = securityScopes.length ? securityScopes.filter(s => !s.locked).some(isViewOtherScope) : true;
		return result;
	}
}
angular.module('super-suit-config').factory('ssActionBundlesLogic', logic);
