const fieldEventMap = {
	'input': {
		'events': ['focus', 'blur'],
		'text': ['keyup', 'change'],
		'checkbox': ['change'],
		'radio': ['change']
	},
	'textarea': { 'events': ['focus', 'blur', 'keyup', 'change'] },
	'select': { 'events': ['focus', 'blur', 'change'] },
	'button': { 'events': ['click'] }
};

function formChangeEmitter(fieldType) {
	const fieldEvents = fieldEventMap[fieldType];
	return /*@ngInject*/ function (ssValidateHlp) {
		return { restrict: 'E',
			link: function (scope, elem) {
				const subType = elem[0].type;
				// register generic events
				for (let eventType of fieldEvents.events) {
					elem.on(eventType, ssValidateHlp.makeEventEmitter(scope, elem));
				}

				// register subtype events
				if (fieldEvents[subType] == null) { return; }
				for (let eventType of fieldEvents[subType]) {
					elem.on(eventType, ssValidateHlp.makeEventEmitter(scope, elem));
				}
			}
		};
	};
}

angular.module('super-suit-decorators')
	.directive('input', formChangeEmitter('input'))
	.directive('textarea', formChangeEmitter('textarea'))
	.directive('button', formChangeEmitter('button'))
	.directive('select', formChangeEmitter('select'));
