/* globals sidekick */

/*@ngInject*/
function hookupSearch($rootScope, $location, ssAppConfigSvc, ssEmployeeApi, ssDebounce, ssAsync, ssListSvc, ssFlagCCSvc, ssToastHlp) {
	ssAppConfigSvc.getAsync('search').then(function (notificationCfg) {
		let getResults, searchTerm, results;
		var positionTypes = [];

		sidekick.store.set('search', notificationCfg);

		ssListSvc.getByExternalId('position.types').then(function(types) {
			positionTypes = types;
		});

		const debouncedResults = ssDebounce(() => {
			refreshResults();
		}, 350);

		const search = ssDebounce(term => {
			searchTerm = term; //The search term is just what the user typed in.
			debouncedResults();
		}, 350);

		sidekick.events.on('header.search', search);
		sidekick.events.on('header.search.item-clicked', index => {
			let path = '';
			$rootScope.$applyAsync(function () {
				ssFlagCCSvc.checkState('global_data_integration').then((enabled) => {
					if(enabled) {
						if(results[index]._orgPersonId) {
							path = `/ec-directory/person-detail/${results[index]._orgPersonId}/personal`;
						} else {
							ssToastHlp.error('The user does not have a valid ID and therefore their record cannot be accessed');
						}
					} else {
						path = `/ec-employee/record/${results[index].id}/personal`;
					}
					$location.path(path);
				});
			});
		});

		function refreshResults() {
			if (getResults != null) {
				getResults.$abort();
			}

			getResults = ssAsync(function*() {
				const filter = Object.assign({}, {
					include: 'positions,jobtypes,locations,assignments'
				},
				searchFilter(searchTerm, 'fullname'));

				results = yield ssEmployeeApi.query(filter);

				const output = results.map(emp => {
					if (emp._assignments.length) {
						const primaryAssignment = emp._assignments.find(a => a._isPrimary);
						if (primaryAssignment){
							emp.position = emp._positions.find(p => p.id === primaryAssignment._positionId);
						}
						if (emp.position) {
							emp.position.name = ssListSvc.mapName(emp.position._name, positionTypes);
						}
					}

					const name = `<span>${emp._firstName} ${emp._lastName}</span>`;
					const position = emp.position != null ? `<small>${emp.position.name}</small>` : '';
					const thumb = emp.thumb != null ? `<span class="thumb"><img alt="" src="${emp.thumb}"></span>` : '';
					return `<a href="#">${name} ${position} ${thumb}</a>`;
				});
				sidekick.events.emit('header.search.results', output);
				getResults = null;
			})();
		}

		/**
		 * Create a proper querystring parameter given a value, filter property name
		 * @param {string} val The filter value to pass up
		 * @param {string} type The parameter name of the filter object
		 */
		function searchFilter(val, type) {
			var o = {};

			if (val != null) {
				o['filter[' + type + ']'] = val;

				return o;
			} else {
				return null;
			}
		}
	});
}

angular.module('super').run(hookupSearch);
