// #-----------------------------------# //
// #---- Mutator (mutatorMessages) ----# //

/*@ngInject*/
function messagesMutator(ssDateHlp) { return {

	'isRead': function (m) {
		if (m._acknowledgeAtUtc == null) { return false; }
		return !ssDateHlp.isForeverFuture(m._acknowledgeAtUtc);
	},

	'shortContent': function (m) {
		if (m._content == null) { return; }
		if (m._content === '') { return ''; }

		const content = m._content;
		const pleaseIdx = content.toLowerCase().indexOf('please');
		if (pleaseIdx > 0) {
			return content.substr(0, pleaseIdx);
		} else {
			return content;
		}
	}

};}

// #---- End Mutator (mutatorMessages) ----# //
// #---------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorMessages', messagesMutator);
