import {Decorator} from 'base-decorator';
// #----------------------------------# //
// #---- Decorator (ss-permitted) ----# //

const config = {
	priority: 1500, // run before everything
	terminal: true  // prevent all other directives from running
};

/*@ngInject*/
function ssPermitted($compile, ssPermissionsSvc) {

	class Permitted extends Decorator {
		link(scope, elem, attrs) {
			// get the list of permissions
			const perms = attrs.ssPermitted.split(',');
			var p = perms.length;

			for (; p-- ;) {
				// permissions are specified in "group.property" format.
				// use deconstruction to pull out each value
				const [group, prop] = perms[p].split('.');

				// check these values against the permissions service
				// if this action is not permitted, remove the element
				if (ssPermissionsSvc.isNotPermittedAction(group, prop)) {
					return elem.remove();
				}
			}

			// we passed the permissions check remove the terminal
			// decorator and re-compile so this can be seen
			elem.removeAttr('ss-permitted');
			elem.replaceWith($compile(elem[0].outerHTML)(scope));
		}
	}

	return new Permitted(config);
}

// #-- END Decorator (ss-permitted) --# //
// #----------------------------------# //

angular.module('super-suit-decorators').directive('ssPermitted', ssPermitted);
