// #----------------------------# //
// #---- Helper (ssElemHlp) ----# //

/*@ngInject*/
function ssElemHlp(ssStringHlp, $window) {
	return {
		getStyles(element, pseudo = null) {
			// unwrap an angular element
			const domEl = (element.scope == null ? element : element[0]);
			return window.getComputedStyle(domEl, null);
		},
		getBackgroundColor(element) {
			// unwrap an angular element
			const domEl = (element.scope == null ? element : element[0]);
			const isTransparentRe = /^.*?\(\d{1,3}, ?\d{1,3}, ?\d{1,3}, ?0\)$/;
			const isTransparentReIE11 = /^transparent$/i;
			const color = window.getComputedStyle(domEl).getPropertyValue('background-color');

			if (!isTransparentRe.test(color) && !isTransparentReIE11.test(color)) { return color; }
			return this.getBackgroundColor(domEl.parentNode);
		},
		removeAttr(elem, attrs, names) {
			if (typeof names === 'string') { names = [names]; }
			for (let name of names) {
				const nameCamelCase = ssStringHlp.toCamelCase(name);
				const nameDashCase = ssStringHlp.toDashCase(name);

				delete attrs[nameCamelCase];
				delete attrs.$attr[nameCamelCase];
				elem.removeAttr(nameDashCase);
			}
		},
		isOutOfViewport(elem, padAmt = {}) {
			const scrollbarWidth = 15;
			const elemRect = elem[0].getBoundingClientRect();
			const pad = Object.assign({right: 0, bottom: 0}, padAmt);
			return {
				right: scrollbarWidth + elemRect.right + pad.right >= $window.innerWidth,
				bottom: scrollbarWidth + elemRect.bottom + pad.bottom >= $window.innerHeight
			};
		},
		isMyChild
	};

	function isMyChild(parentEl, childEl) {
		const p = childEl.parent();
		if (!p.length) { return false; }
		if (p[0].tagName === 'BODY') { return false; }
		if (p[0] === parentEl[0]) { return true; }
		return isMyChild(parentEl, p);
	}
}
// #-- END Helper (ssElemHlp) --# //
// #----------------------------# //

angular.module('super-suit-helpers').factory('ssElemHlp', ssElemHlp);
