import {Component} from 'base-component';
// #---------------------------------------# //
// #---- Component (super-select-list) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	label: '@?',
	labelWeight: '@?',
	type: '@?',
	model: '=?',
	id: '@?',
	isDisabled: '=?',
	required: '=',
	defaultOptionLabel: '@?',
	defaultOptionValue: '=?',
	permissions: '=?',
	editMode: '=?',
	includeDefaultOption: '=?',
};

// directive definition object overrides
const config = {
	transclude: true
};

/*@ngInject*/
function selectList(ssValidateHlp, rAF, ssCommonControlHlp) {

	class SelectList extends Component {

		/*@ngInject*/
		controller($scope, $element, $attrs, $stateParams, enumPropertyAction) {
			$scope.labelWeight = ssCommonControlHlp.labelWeight($scope.labelWeight, $scope.label);

			// created to allow assigning to model because of ng-if scope
			const cmpt = $scope.cmpt = $scope; // eslint-disable-line no-unused-vars

			const options = new Map();
			const selectTypes = {
				standard: {
					btnType: 'std'
				},
				filter: {
					btnType: 'smp'
				}
			};

			// view model for tracking state
			const vm = $scope.vm = {
				buttonLabel: '',
				buttonClass: 'std',
				show: false,
				inputId: `select-list-${(Math.ceil(Math.random() * 10000000)).toString()}`, // "unique" control identifier
				defaultOptionLabel: $scope.defaultOptionLabel || 'Please Select',
				defaultOptionValue: $scope.defaultOptionValue || null, // don't make it undefined; undefined won't send a value but null will
			};

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				toggleView() {
					vm.show = !vm.show;
				},
				getLabelByValue(val) {
					// we are returning empty string if the default option is included AND the val is null; otherwise maintain backwards compatibility
					if ($scope.includeDefaultOption === true && val === vm.defaultOptionValue) { return ''; }
					return options.get(val);
				}
			};

			if ($scope.type !== undefined) {
				$element.addClass($scope.type);
				vm.buttonClass = selectTypes[$scope.type].btnType;
			}

			$scope.$watchGroup(['editMode', 'permissions'], (nv, ov) => {
				// index matches expression
				const editMode = nv[0];
				const permissions = nv[1];

				const editModeInferred = editMode == null;
				if (editModeInferred) {
					vm.editMode = $stateParams.action && $stateParams.action.endsWith('edit');
				} else {
					vm.editMode = editMode === true; // strict equality to not bind it to the $scope
				}

				if (permissions == null) {
					if ($attrs.permissions === undefined) {
						// if the component wasn't even passed a permissions object assume
						// edit privileges so that we actually show the expected input
						vm.permissions = { view: true, edit: true, redacted: false, raw: 3 };
						if (editModeInferred) { vm.editMode = true; }
					} else {
						// if we expect permissions on the component, but we were
						// passed an empty permissions object, deny edit privileges
						vm.permissions = { view: true, edit: false, redacted: false, raw: 1 };
						if (editModeInferred) { vm.editMode = false; }
					}
				} else {
					vm.permissions = angular.extend({}, permissions);
				}

				if (!vm.permissions.edit) { vm.editMode = false; }
				if (vm.permissions.raw === enumPropertyAction.notVisible) {
					$element.remove();
				}
			});

			$scope.$watch('type', (nv, ov) => {
				if ($element.hasClass(ov)) {
					$element.removeClass(ov);
				}
				$element.addClass(nv);
				if (nv !== undefined) {
					vm.buttonClass = selectTypes[nv].btnType;
				}
			});

			// set the default button label value
			$scope.$watch('model', val => {
				if (options.has(val)) {
					vm.buttonLabel = options.get(val);
				} else {
					vm.buttonLabel = vm.defaultOptionLabel;
				}
			});

			$scope.$on('select-list-item-click', (event, value) => {
				$scope.model = value;
				event.stopPropagation();
			});

			$scope.$on('select-list-item-init', (event, value, label) => {
				options.set(value, label);
				if (JSON.stringify(value) === JSON.stringify($scope.model)) {
					vm.buttonLabel = label;
				}
				event.stopPropagation();
			});
		}

		link(scope, elem, attrs) {
			rAF.raw(function () {
				var btn = elem.find('button');

				// if the button is not visible, this is not editable
				// and validation events should not be triggered
				if (btn.length === 0) { return; }

				btn.on('focus', ssValidateHlp.makeEventEmitter(scope, btn));
				btn.on('click', () => {
					ssValidateHlp.resetValidation(elem);
				});
				btn.on('blur', ssValidateHlp.makeEventEmitter(scope, btn));

				scope.$watch('model', function () {
					scope.$emit('ss-validate-field-event', btn, 'change');
				});
			});

			scope.$applyAsync(() => {
				let el = scope.$el = elem.find('button');
				// Pass through of automation hooks
				if (attrs.name != null) { el.attr('data-auto', attrs.dataAuto); }
			});

			attrs.$observe('label', v => scope.label = v);
		}
	}

	return new SelectList(props, 'select-list', config);
}

// #-- END Component (super-select-list) --# //
// #---------------------------------------# //

angular.module('super-suit-components').directive('superSelectList', selectList);
