import {Component} from 'base-component';
// #---------------------------------------# //
// #---- Component (super-grid-column) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	width: '@',
	sortBy: '@',
	noLeftSeparator: '@?',
	info: '=',
	defaultSortDirection: '@'
};

// directive definition object overrides
const config = {
	transclude: true
};

/*@ngInject*/
function gridColumn() {

	class GridColumn extends Component {
		/*@ngInject*/
		controller($scope) {

			$scope.defaultSortDirection = ($scope.defaultSortDirection === 'desc' ? 'desc' : 'asc');
			const gridData = $scope.gridData = $scope.$root.utils.findInParentScope('gridData', $scope); // eslint-disable-line no-unused-vars

			// view model for tracking state
			const vm = $scope.vm = {
				noLeftSeparator: $scope.noLeftSeparator != null,
				sortIcon: 'sort-up',
				styles: {},
				keyCode: 13,
				tabIndex: ($scope.sortBy ? '0' : '-1')
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				isSorted() {
					return gridData.sortBy === $scope.sortBy && $scope.sortBy != null && gridData.addSortClass;
				},

				sort() {
					if (!$scope.sortBy || $scope.sortBy === null) { return; }
					$scope.$emit('super-grid-column-sort', $scope.sortBy);
				}
			};

			if($scope.info !==null) {
				vm.info = $scope.info;
			}

			$scope.$watchGroup(['gridData.sortBy', 'gridData.sortDirection'], function (s) {
				const [sortBy, sortDirection] = s;

				if (sortBy !== $scope.sortBy) {
					vm.sortIcon = ($scope.defaultSortDirection === 'desc' ? 'sort-down' : 'sort-up');
				} else {
					vm.sortIcon = (sortDirection === 'desc' ? 'sort-down' : 'sort-up');
				}
			});

		}

		link(scope, elem, attrs) {
			const domColumnIndex = Array.from(elem.parent().children()).indexOf(elem[0]);
			scope.$emit('super-grid-column-init', domColumnIndex);
		}
	}

	return new GridColumn(props, 'grid-column', config);
}

// #-- END Component (super-grid-column) --# //
// #---------------------------------------# //

angular.module('super-suit-components').directive('superGridColumn', gridColumn);
