import {
	Enums
}
from 'base-enum';

// #-------------------------------# //
// #---- Enums (/fieldHeights) ----# //

class FieldHeightsEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Single line',
			value: 'super-input-text'
		}, {
			name: 'Multi-line',
			value: 'super-input-textarea'
		}];
		super(enums);
	}
}

// #---- End Enums (/fieldHeights) ----# //
// #-----------------------------------# //

angular.module('super-suit-enums').factory('enumFieldHeights', () => new FieldHeightsEnum());
