import {Decorator} from 'base-decorator';
// #-----------------------------------# //
// #---- Decorator (ss-modal-view) ----# //

/*@ngInject*/
function ssModalView($document, $log, $animate) {

	const body = $document.find('body');
	const fader = angular.element($document[0].getElementById('modal-fade'));
	let faderShowing = false;
	let activeModal = null;
	const modalQueue = [];
	const styleMap = {
		'primary': 'ss-modal-view-pri',
		'pri': 'ss-modal-view-pri',
		'negative': 'ss-modal-view-neg',
		'neg': 'ss-modal-view-neg',
		'positive': 'ss-modal-view-pos',
		'pos': 'ss-modal-view-pos',
	};

	class ModalView extends Decorator {
		link(scope, elem, attrs) {
			// calculate an expression that will tell us when this modal element
			// is visible on the page
			const modalIsVisible = buildVisibilityExpression(attrs);

			// if there's no expression, it means there's no show/hide directive
			if (modalIsVisible === '!!()') {
				$log.error('SS-MODAL-VIEW => ng-if or ng-show/hide is required to show/hide the modal view', elem[0]);
				return;
			}

			// if the modal element's scope is destroyed (maybe via ng-if) then
			// we need to remove it from our list of modals
			scope.$on('$destroy', () => removeModal(elem));
			// handle when the modal enters and leaves the view
			scope.$watch(modalIsVisible, function (nV, oV) {
				if (nV === true) {
					addModal(elem);
				} else if (nV === false) {
					removeModal(elem);
				}
			});

			// make this element a modal
			elem.addClass('ss-modal-view');
			if (attrs.ssModalView.length && styleMap[attrs.ssModalView] != null) {
				// if we have a style modifier, add the appropriate class
				elem.addClass(styleMap[attrs.ssModalView]);
			} else if (attrs.ssModalView.length) {
				// if we have an invalid style, let the developer know
				$log.error(`SS-MODAL-VIEW => ${attrs.ssModalView} is not a valid modal style`, elem[0]);
			}
		}
	}


	/**
	 * buildVisibilityExpression - build an expression that represents this element
	 * being shown or hidden in the view
	 *
	 * @param  {Object} attrs - the attributes on this element
	 * @return {String}       - an angular expressions statement
	 */
	function buildVisibilityExpression(attrs) {
		let visibilityCheck = '';
		if (attrs.ngIf != null) {
			visibilityCheck += `(${attrs.ngIf})`;
		} else if (attrs.ngShow != null) {
			if (visibilityCheck.length) { visibilityCheck += ' && '; }
			visibilityCheck += `(${attrs.ngShow})`;
		} else if (attrs.ngHide != null) {
			if (visibilityCheck.length) { visibilityCheck += ' && '; }
			visibilityCheck += `!(${attrs.ngHide})`;
		}
		return `!!(${visibilityCheck})`;
	}


	/**
	 * addModal - show or queue this modal
	 *
	 * @param  {Element} elem - angular.element to add
	 */
	function addModal(elem) {
		if (activeModal === elem) { return; }
		if (modalQueue.includes(elem)) { return; }
		if (activeModal == null) {
			activeModal = elem;
		} else {
			elem.addClass('ss-modal-view-hidden');
			modalQueue.push(elem);
		}

		if (!faderShowing) {
			faderShowing = true;
			fader.addClass('shown');
			body.css('overflow', 'hidden');
		}
	}


	/**
	 * removeModal - remove this modal element
	 *
	 * @param  {Element} elem - angular.element to remove
	 */
	function removeModal(elem) {
		if (activeModal === elem) {
			activeModal = (modalQueue.length ? modalQueue.shift() : null);
			if (activeModal != null) {
				activeModal.removeClass('ss-modal-view-hidden');
			}
		} else {
			elem.removeClass('ss-modal-view-hidden');
			const mIdx = modalQueue.indexOf(elem);
			if (mIdx >= 0) { modalQueue.splice(mIdx, 1); }
		}

		if (modalQueue.length === 0 && activeModal == null) {
			fader.removeClass('shown');
			faderShowing = false;
			body.css('overflow', 'auto');
		}
	}

	return new ModalView();
}

// #-- END Decorator (ss-modal-view) --# //
// #-----------------------------------# //

angular.module('super-suit-decorators').directive('ssModalView', ssModalView);
