/*@ngInject*/
function departmentsApi(ssApiSvc, buildConfig) {
// #-----------------------------------------# //
// #---- API Endpoint (ssDepartmentsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.departments;
	const url = `${baseUrl}/organizations/:ORGID:/Departments`;
	const patchUrl = (p) => `${baseUrl}/Departments/${p.id}`;

	// model data type
	const model = 'departments';

	// possible relationships
	const rel = [];

	const query = ssApiSvc.endpoint({ url: url, method: 'GET' }, model, rel);
	const save = ssApiSvc.endpoint({ url: patchUrl, method: 'PATCH' }, model, rel);
	const remove = ssApiSvc.endpoint({ url: patchUrl, method: 'PATCH' }, model, rel);
	const add = ssApiSvc.endpoint({ url: url, method: 'POST' }, model, rel);


	// API endpoint methods
	return {
		query,
		save,
		add,
		remove
	};

// #-- END API Endpoint (ssDepartmentsApi) --# //
// #-----------------------------------------# //
}

angular.module('super-suit-apis').factory('ssDepartmentsApi', departmentsApi);
