/*@ngInject*/
function formLibraryApi(ssApiSvc, buildConfig) {
// #-----------------------------------------# //
// #---- API Endpoint (ssFormLibraryApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.formTemplates;
	const getLibraryUrl = `${baseUrl}/organizations/:ORGID:/formtemplates/library`;
	const getTemplateObjectsUrl = `${baseUrl}/organizations/:ORGID:/templates`;
	// model data type
	const model = 'iFormLibraryObjects';
	// possible relationships
	const rel = [

	];

	// generate default endpoints
	const query = ssApiSvc.endpoint({ url: getLibraryUrl, method: 'GET' }, model, rel);
	const queryTemplateObjects = ssApiSvc.endpoint({ url: getTemplateObjectsUrl, method: 'GET' }, model, rel);

	// API endpoint methods
	return {
		query,
		queryTemplateObjects
	};

// #-- END API Endpoint (ssFormLibraryApi) --# //
// #-----------------------------------------# //
}

angular.module('super-suit-apis').factory('ssFormLibraryApi', formLibraryApi);
