import {Component} from 'base-component';
import './lib/fl-custom-pikaday';

// #--------------------------------------------# //
// #---- Component (super-input-datepicker) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=?',
	options: '=?',
	isDisabled: '=?',
	label: '@?',
	labelWeight: '@?',
	inputId: '@',
	name: '@',
	placeholder: '@?',
	dateFormat: '@',
	required: '=?',
	permissions: '=?',
	editMode: '=?'
};

// directive definition object overrides
const config = {};
const slashPattern = new RegExp(/(1[0-2]|0[1-9])\/(3[01]|[12][0-9]|0[1-9])\/[0-9]{4}/, 'i'); //MM/DD/YYYY
const dotPattern = new RegExp(/(1[0-2]|0[1-9])\.(3[01]|[12][0-9]|0[1-9])\.[0-9]{4}/, 'i'); //MM.DD.YYYY
const dashPattern = new RegExp(/(1[0-2]|0[1-9])\-(3[01]|[12][0-9]|0[1-9])\-[0-9]{4}/, 'i'); //MM-DD-YYYY
const redactedRegex = /(^\*+$)/;
const defaultDateFormat = 'L';
var dateFormat;

/*@ngInject*/
function inputDatepicker() {

	class InputDatepicker extends Component {

		/*@ngInject*/
		controller($scope, $element, $attrs, $stateParams, moment, enumPropertyAction) {
			dateFormat = $scope.dateFormat || defaultDateFormat;

			// created to allow assigning to model because of ng-if scope
			const cmpt = $scope.cmpt = $scope; // eslint-disable-line no-unused-vars

			const vm = $scope.vm = {
				displayDate: ''
			};

			$scope.$watchGroup(['editMode', 'permissions'], (nv, ov) => {
				// index matches expression
				const editMode = nv[0];
				const permissions = nv[1];

				const editModeInferred = editMode == null;
				if (editModeInferred) {
					vm.editMode = $stateParams.action && $stateParams.action.endsWith('edit');
				} else {
					vm.editMode = editMode === true; // strict equality to not bind it to the $scope
				}

				if (permissions == null) {
					if ($attrs.permissions === undefined) {
						// if the component wasn't even passed a permissions object assume
						// edit privileges so that we actually show the expected input
						vm.permissions = { view: true, edit: true, redacted: false, raw: 3 };
						if (editModeInferred) { vm.editMode = true; }
					} else {
						// if we expect permissions on the component, but we were
						// passed an empty permissions object, deny edit privileges
						vm.permissions = { view: true, edit: false, redacted: false, raw: 1 };
						if (editModeInferred) { vm.editMode = false; }
					}
				} else {
					vm.permissions = angular.extend({}, permissions);
				}

				if (!vm.permissions.edit) { vm.editMode = false; }
				if (vm.permissions.raw === enumPropertyAction.notVisible) {
					$element.remove();
				}
			});

			$scope.$watch('model', (newVal, oldVal) => {
				if (newVal == null) {
					vm.displayDate = '';
					return;
				}

				if(typeof newVal === 'string' && newVal !== '') {
					$scope.model = newVal = processDateString(newVal);
				}

				vm.displayDate = setDisplayedDate(newVal, dateFormat);

				if (!(newVal instanceof Date) || !moment(newVal).isValid()) {
					return;
				}

				if (vm.calendar) {
					vm.calendar.gotoDate(newVal);
					selectIntended(vm.calendar.el, newVal.getDate());
				}
			});

			$scope.$watch('vm.displayDate', (newVal, oldVal) => {

				if (newVal == null || newVal === '') {
					if (vm.calendar) {
						vm.calendar.setDate(null);
						vm.calendar.gotoToday();
						$scope.model = null;
					}
				}

				if (newVal === oldVal) { return; }

				if (!isValid(newVal)) { return; }

				if(vm.calendar) { clearIntended(vm.calendar.el); }

			});

			$scope.$watch('options', (newOptions, oldOptions) => {
				if(newOptions === oldOptions) { return; }
				if(newOptions.minDate !== undefined) { vm.calendar.setMinDate(newOptions.minDate); }
				if(newOptions.maxDate !== undefined) { vm.calendar.setMaxDate(newOptions.maxDate); }
			}, true);

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				validate(value) {
					if(value == null || value === '') { return true; }
					if(vm.permissions.redacted && isRedacted(value)) { return true; }
					return !isValid(value) ? 'Invalid date' : true;
				}
			};

		}

		link(scope, elem, attrs) {
			scope.$watch('model', m => elem.toggleClass('model-is-empty', !m));
			scope.$watch('editMode', (newVal, oldVal) => {
				if(newVal===oldVal){ return;}
				if(newVal){
					scope.$applyAsync(datePicker);
				}
			});
			scope.$on('show-hide-toggle', (evt, data) => {
				if (data.redacted && data.show) {
					// if we are redacted and the user has clicked the view button
					elem.addClass('ss-unredacted');
				} else {
					// else remove the class
					elem.removeClass('ss-unredacted');
					scope.$applyAsync(datePicker);
				}
			});

			attrs.$observe('label', val => {
				scope.label = val;
			});

			scope.$on('$destroy', () => {
				if (scope.vm.calendar != null) {
					scope.vm.calendar.destroy();
				}
			});

			function datePicker() {
				var inputElem = elem.find('input');

				if (inputElem.length === 0) {return;}

				if (attrs.name != null) {
					inputElem.attr('name', scope.name);
				}

				if (attrs.inputId != null) {
					inputElem.attr('id', attrs.inputId);
				}

				if (attrs.placeholder != null) {
					inputElem.attr('placeholder', scope.placeholder);
				}

				//set up date picker
				var options = {
					field: inputElem[0],
					format: 'MM/DD/YYYY',
					minDate: '1/1/1900',
					maxDate: '12/31/2100',
					showDaysInNextAndPreviousMonths: true,
					container: elem[0],
					onSelect: (selectedDate) => {
						scope.$root.safeApply(() => {
							// if the model date is the same day as the select date, no model update is needed
							const mModelDay = moment(new Date(scope.model)).startOf('day');
							const mSelectedDay = moment(new Date(selectedDate)).startOf('day');
							if (mModelDay.diff(mSelectedDay, 'd') !== 0) {
								scope.model = selectedDate;
							}

							// make sure the displayDate is correct
							scope.vm.displayDate = moment(selectedDate).format(dateFormat);
						});
					}
				};

				// Available config options:
				// https://github.com/dbushell/Pikaday#configuration
				if (scope.options) {
					Object.assign(options, scope.options);
				}

				//attach picker to element
				inputElem[0]._pikaday = new Pikaday(options);
				scope.vm.calendar = inputElem[0]._pikaday;

				//attach handlers
				inputElem.on('blur', function (evt) {
					scope.vm.calendar.el.style.zIndex = '50000';
					setTimeout(() => scope.vm.calendar.el.style.zIndex = '', 200);

					const pikaButtons = Array.from(scope.vm.calendar.el.getElementsByTagName('button'));
					pikaButtons.forEach(b => b.setAttribute('tabindex', '-1'));
				});
				inputElem.on('focus', () => {
					const elemRect = elem[0].getBoundingClientRect();
					elem.toggleClass('align-right', elemRect.right + 260 > window.innerWidth);
					highLightDate();
				});
				inputElem.on('keypress', (evt) => {
					if (!isDateChar(evt.charCode)) {
						evt.preventDefault();
					}
				});
			}

			function highLightDate(evt) {
				let currentDate = scope.vm.calendar.getDate();
				let modelDate = scope.model;

				if (typeof modelDate === 'string') {
					modelDate = new Date(modelDate);
				}

				// if the defaultDate is set, go to it
				if (scope.options && scope.options.defaultDate) {
					scope.vm.calendar.gotoDate(scope.options.defaultDate);
				}

				if (currentDate && modelDate && currentDate.getTime() === modelDate.getTime()) {
					return;
				}

				scope.vm.calendar.setDate(scope.model); //intentionally leaving this out of digest cycle
			}
		}
	}

	const isRedacted = val => redactedRegex.test(val);

	function isValid(value) {
		if(value.length !== 10) { return false; }
		if(isRedacted(value)) { return false; }
		if(!matchDatePattern(value)) { return false; }
		if(!moment(value, dateFormat).isValid()) { return false; }
		return (moment(new Date(value)).year() >= 1900 && moment(new Date(value)).year() <= 2100);
	}

	function matchDatePattern(value) {
		if(slashPattern.test(value)) { return true; }
		if(dotPattern.test(value)) { return true; }
		if(dashPattern.test(value)) { return true; }
		return false;
	}

//	check for numbers (0-9) or dash, period, slash, return/enter, & tab characters
	function isDateChar(value) {
		if(value >= 48 && value <= 57) { return true; }
		if([45, 46, 47, 13, 9].includes(value)) { return true; }
		return false;
	}

	function setDisplayedDate(val, dateFormat){
		if(val === '') { return ''; }
		return isRedacted(val) ? val : moment(val).format(dateFormat);
	};

	function processDateString(val) {
		if (!isRedacted(val)) {
			let d = new Date(val);

			if(!(d instanceof Date)) {
				throw new TypeError('Datepicker requires a valid date object');
			}
			if (isNaN(d.getTime())) {
				throw new TypeError('Datepicker requires a valid date string');
			} else {
				return d;
			}
		} else {
			return val;
		}
	};

	function selectIntended(el, day) {
		var cells = Array.from(angular.element(el).find('td'));
		var intendedSelectionElem = cells.filter((cell) => angular.element(cell).attr('data-day') === day);
		var $intendedSelection = angular.element(intendedSelectionElem[0]);

		if (!$intendedSelection.hasClass('is-empty')) {
			$intendedSelection.addClass('is-intended');
		}
	}

	function clearIntended(el) {
		angular.element(el).find('td').removeClass('is-intended');
	}

	return new InputDatepicker(props, 'input-datepicker', config);
}

// #-- END Component (super-input-datepicker) --# //
// #--------------------------------------------# //

angular.module('super-suit-components').directive('superInputDatepicker', inputDatepicker);
