import {Component} from 'base-component';
// #-----------------------------------# //
// #---- Component (super-popover) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	link: '@?',
	help: '@?'
};

// directive definition object overrides
const config = {
	transclude: true
};

/*@ngInject*/
function popover($window, rAF, ssElemHlp) {

	class Popover extends Component {
		/*@ngInject*/
		controller($scope) {
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				icon: ($scope.help != null ? 'question-circle' : 'info-circle'),
				iconAfter: null,
				link: $scope.link || '',
				onScreen: true,
				menuShowing: false,
				ariaLabel: 'Information',
			};
		}

		link(scope, elem, attrs) {
			const tooltip = angular.element(elem[0].querySelector('div.content'));
			const iconState = {before: true, after: false};

			// if ss-icon is used, set it to vm.icon and clear the ss-icon
			if (attrs.ssIcon != null || attrs.ssIconBefore != null) {
				scope.vm.icon = attrs.ssIcon;
			} else if (attrs.ssIconAfter != null) {
				scope.vm.iconAfter = attrs.ssIconAfter;
				scope.vm.icon = null;
				iconState.after = true;
				iconState.before = false;
			}
			ssElemHlp.removeAttr(elem, attrs, [
				'ss-icon',
				'ss-icon-before',
				'ss-icon-after'
			]);
			// if aria-label is used set it to vm.ariaLabel and clear the aria-label
			if (attrs.ariaLabel != null) {
				scope.vm.ariaLabel = attrs.ariaLabel;
			}

			let container;

			scope.$on('ss-menu-open', function(evt, trigger, menu) {
				if (menu[0] === tooltip[0]) {
					scope.vm.menuShowing = scope.vm.onScreen = true;
					if (container === undefined) {
						container = getContainer(elem[0]);
						doEventHookup();
					}
					scope.$digest();
					rAF.raw(() => tryAdjust(tooltip, elem, container, iconState));
				}
			});

			scope.$on('ss-menu-closed', function(evt, trigger, menu) {
				if (menu[0] === tooltip[0]) {
					scope.vm.menuShowing = false;
					scope.$digest();
				}
			});

			// listen to viewport changes
			ambient.on('update', 'viewport-updates', vpChange);
			scope.$on('$destroy', function() {
				ambient.off('update', 'viewport-updates', vpChange);
			});

			function doEventHookup() {
				// listen to container changes if it isn't the window
				if (container !== $window) {
					// re-calculate layout of the tooltip while we scroll the container
					let rafTimer = null;
					angular.element(container).bind('scroll', () => {
						if (rafTimer !== null) { return; }
						rafTimer = rAF(vpChange, scope).then(() => rafTimer = null);
					});

					// when the mouse wheel is engaged over the tooltip, remove
					// pointer-events while the user is scrolling so that the
					// tooltip does not interfere with the scrolling of the container
					let scrollTimer = null;
					tooltip.bind('wheel', function (evt) {
						if (scrollTimer !== null) {
							clearTimeout(scrollTimer);
						} else {
							evt.preventDefault();
							tooltip.addClass('scrolling');
						}
						scrollTimer = setTimeout(function () {
							scrollTimer = null;
							tooltip.removeClass('scrolling');
						}, 500);
					});
				}
			}

			function vpChange() {
				if (scope.vm.menuShowing) {
					const onScreen = scope.vm.onScreen;
					scope.vm.onScreen = tryAdjust(tooltip, elem, container, iconState);
					if (scope.vm.onScreen !== onScreen) { scope.$digest(); }
				}
			}
		}
	}

	/**
	 * Will go up the chain of elements to find overflow/overflowY.
	 *
	 * @return Will return first ancestor with overflow or window.
	 */
	function getContainer(element) {
		var style;
		while (element) {
			style = $window.getComputedStyle(element);
			if (style.overflow === 'scroll' || style.overflowY === 'scroll') {
				return element;
			}

			if (element.parentElement == null || element.parentElement.nodeName === 'BODY') {
				return $window;
			}
			element = element.parentElement;
		}
	}

	function tryAdjust(tooltip, elem, container, icon) {
		const elemRect = elem[0].getBoundingClientRect();
		const ttWidth = tooltip[0].offsetWidth;
		const ttHeight = tooltip[0].offsetHeight;

		if (container !== $window) {
			const containerRect = container.getBoundingClientRect();
			const offTheTop = containerRect.top > elemRect.bottom;
			const offTheBottom = containerRect.bottom < elemRect.top;
			if (offTheTop || offTheBottom) { return false; }
		}

		if (elemRect.left + ttWidth + 10 >= $window.innerWidth) {
			// tooltip to the left
			const left = (icon.before ? elemRect.left + 22 : elemRect.right + 6);
			tooltip.css('left', `${left - ttWidth}px`);
			tooltip.addClass('flip-x');
		} else {
			// tooltip to the right
			const left = (icon.before ? elemRect.left - 9 : elemRect.right - 24);
			tooltip.css('left', `${left}px`);
			tooltip.removeClass('flip-x');
		}

		if (elemRect.bottom + ttHeight + 10 >= $window.innerHeight) {
			// tooltip on top
			tooltip.css('top', `${elemRect.top - ttHeight - 5}px`);
			tooltip.addClass('flip-y');
		} else {
			// tooltip on bottom
			tooltip.css('top', `${elemRect.bottom + 8}px`);
			tooltip.removeClass('flip-y');
		}

		return true;
	}

	return new Popover(props, 'popover', config);
}

// #-- END Component (super-popover) --# //
// #-----------------------------------# //

angular.module('super-suit-components').directive('superPopover', popover);
