import {Decorator} from 'base-decorator';
// #------------------------------------# //
// #---- Decorator (ss-scroll-list) ----# //

/*@ngInject*/
function ssScrollList(ssElemHlp) {

	class ScrollList extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {

			// set the scroll styles and container styles before we check
			// scrollbar widths and border sizes
			const parent = elem.parent();
			elem.addClass('ss-scroll-list');
			parent.addClass('ss-scroll-list-container');

		}
	}

	return new ScrollList();
}



// #-- END Decorator (ss-scroll-list) --# //
// #------------------------------------# //

angular.module('super-suit-decorators').directive('ssScrollList', ssScrollList);
