// the global event bus
const eventBus = {};

/*@ngInject*/
function ssEventSvc($log, $rootScope) {
// #------------------------------# //
// #---- Service (ssEventSvc) ----# //

	// External service API
	return {
		on,
		once,
		off,
		emit,
	};

	function on(ev, handler, scope) {
		if (eventBus[ev] == null) { eventBus[ev] = []; }

		const offFunc = function () { off(ev, handler); };

		if (scope == null) {
			$log.error('ssEventSvc -- scope not provided when registering event listener');
		} else if (typeof scope.$on === 'function') {
			scope.$on('$destroy', offFunc);
		} else {
			$rootScope.$on('$destroy', offFunc);
		}

		eventBus[ev].push(handler);
		return offFunc;
	}

	function once(ev, handler, scope) {
		const offFunc = on(ev, handleOnce, scope);
		function handleOnce(...args) {
			handler.apply(null, args);
			offFunc();
		}
		return offFunc;
	}

	function off(ev, handler) {
		if (eventBus[ev] != null) {
			eventBus[ev].forEach(function (v, i, a) {
				if (v === handler) { a.splice(i, 1); }
			});
		}
	}

	function emit(ev, ...data) {
		if (eventBus[ev] != null) {
			eventBus[ev].forEach(function (v) {
				v.apply(this, data);
			});
		}
	}

// #-- END Service (ssEventSvc) --# //
// #------------------------------# //
}

angular.module('super-suit-services').factory('ssEventSvc', ssEventSvc);
