/*@ngInject*/
function setupRootTools($rootScope) {
	// only run an apply if there's not already one in progress
	$rootScope.safeApply = function(fn) {
		var phase = $rootScope.$$phase;
		if(phase === '$apply' || phase === '$digest') {
			if(fn && (typeof(fn) === 'function')) {
				fn();
			}
		} else {
			$rootScope.$apply(fn);
		}
	};

	// only run a digest if there's not already one in progress
	$rootScope.safeDigest = function (scope) {
		var phase = scope.$$phase,
			rootPhase = $rootScope.$$phase;

		if(phase !== '$apply' && phase !== '$digest' && rootPhase !== '$apply' && rootPhase !== '$digest') {
			scope.$digest();
		}
	};
}

angular.module('super-suit').run(setupRootTools);
