let activeModule = null;

/**
 * We are checking the userAgent here because when running in PhantomJS for testing, window.ss.events is not loaded
 * before AngularJS framework is loaded which attempts to access window.ss.events and throws the following error
 * when running `npm run develop` or `gulp develop` for example on UI modules:
 *
 * PhantomJS 2.1.1 (Linux 0.0.0) ERROR
 * {
 *   "message": "TypeError: undefined is not an object (evaluating 'window.ss.events')\nat node_modules/fl-ui-framework-angularjs/build/main.js:36524:12",
 *   "str": "TypeError: undefined is not an object (evaluating 'window.ss.events')\nat node_modules/fl-ui-framework-angularjs/build/main.js:36524:12"
 * }
 *
 * window.ss.events is not necessary for local module testing in PhantomJS
 **/
if (!window.navigator.userAgent || window.navigator.userAgent.indexOf('PhantomJS') < 0) {
	window.ss.events.on('ui-module-activate', mod => activeModule = mod.urlValue);
}

/////
// super-suit
//
/*@ngInject*/
function handle404Routes($urlRouterProvider, $stateProvider) {
	// handle the case where we are at an invalid route
	$urlRouterProvider.otherwise(function ($injector) {
		const thisUiModule = window.location.pathname.split('/')[1];
		const ssEventSvc = $injector.get('ssEventSvc');
		const $log = $injector.get('$log');
		if (thisUiModule === activeModule || activeModule == null) {
			$log.error('404');
			ssEventSvc.emit('error-404');
		}
	});
}

// /*@ngInject*/
function handle404Events($window, ssEventSvc) {
	ssEventSvc.on('error-404', function () {
		$window.ss.events.fire('page-error-404');
		ssEventSvc.emit('preloader-complete');
	}, false);
}

angular.module('super-suit').config(handle404Routes).run(handle404Events);

/////
// super-setup
//
/*@ngInject*/
function overrideOtherwiseRule ($urlRouterProvider) {
	// the otherwise rule should only be handled by the ui-module
	// because only the ui-module knows about the routing
	$urlRouterProvider.otherwise(() => {});
}

angular.module('super-setup').config(overrideOtherwiseRule);
