import {Decorator} from 'base-decorator';
// #---------------------------------# //
// #---- Decorator (ss-tool-tip) ----# //

var tipElement = angular.element(document.getElementById('tool-tip-container'));
var tipWidth, tipHeight;

/*@ngInject*/
function ssToolTip($document, $window, $timeout) {

	class ToolTip extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {

			var tip, tipX, tipY, descEl, descId;
			var elemHeight = elem[0].offsetTop;

			if (attrs.ssToolTip !== '' || attrs.ariaLabel !== null) {
				tip = attrs.ssToolTip || attrs.ssToolTipIcon || attrs.ariaLabel;
				elem.addClass('ss-tool-tip');
			} else {
				//> log('You must pass either an aria-label attribute or the "ss-tool-tip" attribute with a value equal to the text that you want to be in the tool-tip');
			}

			//create a random ID for each description element
			descId = Math.floor(Math.random() * 100000);

			//create and append the descriptor element & connect it to its
			descEl = `<span id="${descId}" class="aria">${tip}</span>`;

			elem.append(descEl);
			elem.attr('aria-describedby', descId);

			//handle the interaction
			elem.on('mouseenter', function(evt) {
				setTip();
				elem.on('mousemove', trackTip);
			});

			elem.on('mouseleave', function() {
				resetTip();
			});

			function resetTip() {
				tipElement.removeAttr('style');
				tipElement.removeAttr('class');
				tipElement.text('');
			}

			function setTip() {
				tipElement.append(tip);
				tipWidth = tipElement[0].offsetWidth;
				tipHeight = tipElement[0].offsetHeight;
			}

			function tipConfig() {
				return {
					default: {
						top: (tipY + 30) + 'px',
						left: (tipX - 15) + 'px'
					},
					left: {
						left: (tipX - tipWidth) + 'px'
					},
					top: {
						top: ((tipY - tipHeight) - 30) + 'px',
					}
				};
			}

			function trackTip(evt) {

				tipX = evt.pageX;
				tipY = evt.pageY;

				var config = tipConfig();

				tipElement.css(config.default);

				if (evt.pageX > ($window.innerWidth - tipWidth)) {
					tipElement.addClass('flip-x');
					tipElement.css(config.left);
				} else {
					tipElement.removeClass('flip-x');
					tipElement.css(config.default);
				}

				if (evt.pageY > ($window.innerHeight - elemHeight)) {
					tipElement.addClass('flip-y');
					tipElement.css(config.top);
				} else {
					tipElement.removeClass('flip-y');
				}
			}
		}
	}

	return new ToolTip();
}

// #-- END Decorator (ss-tool-tip) --# //
// #---------------------------------# //

angular.module('super-suit-decorators').directive('ssToolTip', ssToolTip);
