/*@ngInject*/
function notificationsApi(ssApiSvc, buildConfig) {
// #-------------------------------------------# //
// #---- API Endpoint (ssNotificationsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.notifications;
	const url = (p) => `${baseUrl}/employees/${p.employeeId}/notifications`;

	// model data type
	const model = 'messages';
	// possible relationships
	const rel = [];

	// generate default endpoints
	const query = ssApiSvc.endpoint({url, method: 'GET'}, model, rel);

	// API endpoint methods
	return {
		query
	};

// #-- END API Endpoint (ssNotificationsApi) --# //
// #-------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssNotificationsApi', notificationsApi);
