import {Component} from 'base-component';
// #-------------------------------------------# //
// #---- Component (super-employee-search) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=', // Used to bind to the directive
	statusTypes: '@?', // Filter the results to only the status types provided (see enumEmployeeStatus). Works with enum value or name.
	queryLimit: '@?', // Limit the returned results to a number
	limit: '@?', // Limit the number of selected employees
	label: '@', // Label for the directive
	usePill: '=', // Tells the search to use pills to show selected items.
	placeholder: '@', // Attribute to push placeholder text down to the super input
	removeById: '@', // Used to filter that id from the result set.
	excludeEmployeesByIds: '=?', // Array of employee id's that will be excluded from the result set
	includeActiveOnlyStatusFilter: '=?' // Frontline Central Specific - Turns on additional filter for employee status
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function employeeSearch($document, ssAsync, ssEventSvc, ssDebounce, ssEmployeeApi, ssArrayHlp, ssActiveUserSvc, ssDateByOrgHlp, ssOrganizationSvc) {

	class EmployeeSearch extends Component {

		link(scope, elem, attrs) {
			scope.$on('focusBind', function (evt, val) {
				scope.vm.focused = !!val;
			});

			var onClick = function (evt, value) {
				evt.stopPropagation();

				if (scope.vm.disabled) { return; } //We're disabled. leave.
				if (scope.vm.query === '') { return; } // no filter text. leave.
				if (scope.vm.menuOpen === false) { return; } // menu is already closed. leave.

				// clicking the input or the result list should be ignored.
				if ((evt.target.tagName !== 'INPUT' && !evt.target.classList.contains('no-deselect'))) {
					// if we are in single mode, and we are cancelling our action, replace with the last selected value.
					if (!scope.vm.usePill) {
						const firstModel = scope.model && scope.model[0] || {};
						scope.vm.query = firstModel.fullName || null;
						scope.vm.menuOpen = false;
						scope.$apply();
						return;
					}

					// if we were in the middle of making a selection, then reset the input and everything else.  We can't recover
					//	without throwing an error, and most of the time this field isn't required so getting an error seems out of place.
					scope.vm.results = null;
					scope.vm.query = null;
					scope.selectedName = null;
					scope.vm.menuOpen = false;
					scope.$apply();
				}
			};

			// when we click anywhere that isn't the input or the results list do stuff
			$document.on('click', onClick);

			// goodbye click handler
			scope.$on('$destroy', () => {
				$document.off('click', onClick);
			});
		}

		/*@ngInject*/
		controller($scope, ssObjectHlp, enumEmployeeStatus) {
			const {glean} = ssObjectHlp;

			const removeById = $scope.removeById;
			const vm = $scope.vm = {
				disabled: false,
				query: '',
				loading: true,
				statusTypes: $scope.statusTypes || '',
				queryLimit: $scope.queryLimit || 10,
				limit: parseInt($scope.limit, 10) || -1, // default to -1 (unlimited)
				usePill: !!$scope.usePill,
				focused: $scope.focused,
				madeSelection: false, // has the user selected something from the search results?
				menuOpen: false,
				activeOnlyFilter: true
			};

		//	Force selection limit to be 1 if 'use pill' is falsy
			vm.limit = !vm.usePill ? 1 : vm.limit;

			$scope.excludeEmployeesByIds = $scope.excludeEmployeesByIds || [];
			if(removeById != null) {
				$scope.excludeEmployeesByIds.push(removeById);
			};

			if (vm.statusTypes.length) {
				vm.statusTypes = vm.statusTypes.split(',')
					.map(t => {
						const {aValues, aNames} = enumEmployeeStatus;
						// find the value by index (provided as a string)
						const val = glean(t, x=>aValues.find(v => v.toString() === x.trim()), false);
						// find by case insensitive name
						const nameIdx = glean(t, x=>aValues[aNames.findIndex(
							n => n.toLowerCase() === x.trim().toLowerCase()
						)], false);
						if (nameIdx === false && val === false) { return undefined; }
						if (nameIdx !== false) { return nameIdx; }
						if (val !== false) { return val; }
					})
					// filter out anything invalid
					.filter(t => t != null)
					// rejoin into a string to filter with
					.join(',');
			}

			// put the selected name and check so that we don't double search once we make a selection
			let selectedName = '';
			var getResults, searchTerm;

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				add(recipient) {
					// did we find the current selection in the list of selected employees
					var found = false;
					vm.madeSelection = false;

					// If we managed to get here without an empty array fix that
					if ($scope.model == null) { $scope.model = []; }

					if ($scope.model.length < 1 || !vm.usePill) {
						$scope.model = [recipient];
						vm.madeSelection = true;
					} else {
						for (let i = $scope.model.length; i--;) {
							if (recipient.id === $scope.model[i].id) {
								found = true;
								break;
							}
						}

						if (!found) {
							vm.madeSelection = true;
							$scope.model.push(recipient);
						}
					}

					if (!vm.usePill) {
						// in single-select mode, Set the query input to the full name of the selected person
						vm.query = selectedName = recipient._firstName + ' ' + recipient._lastName;
					} else {
						vm.query = '';
					}

					// ensure we have set the correct values for visibility flags
					vm.loading = false;
					vm.menuOpen = false;

					// clear out the results now that we made a selection
					vm.results = [];
				},

				remove(recipient) {
					$scope.model.forEach((v, i) => {
						if (recipient.id === v.id) {
							$scope.model.splice(i, 1);
						}
					});
				}
			};

			$scope.$watch('vm.activeOnlyFilter', () => {
				if ($scope.includeActiveOnlyStatusFilter){
					vm.activeOnlyFilter ? vm.statusTypes = '1' : vm.statusTypes = '';
					refreshResults();
				};
			});

			$scope.$watch('vm.query', (nv, ov) => {
				if (!nv || nv === ov) {
					vm.menuOpen = false;
					vm.results = [];
					if (vm.madeSelection && !vm.usePill) {
						vm.madeSelection = false;
						$scope.model = [];
					}
					return;
				}

				if (getResults != null) {
					getResults.$abort();
					getResults = null;
				}

				// In single-select mode, we don't need to refresh results if the user-entered value is the same as the current selection
				if (!vm.usePill && nv === selectedName) { return; }

				if (nv && nv.length > 0 && nv !== ov) {
					searchTerm = nv.trim();
					refreshResults();
				}

			});

			$scope.$watch('model.length', (nv, ov) => {
				vm.disabled = vm.limit >= 0 && nv >= vm.limit && vm.usePill;

				if (!vm.usePill && $scope.model && $scope.model[0]) {
					vm.madeSelection = true;
					vm.disabled = false;
					selectedName = vm.query = $scope.model[0].fullName;
				} else if (!nv.length) {
					vm.madeSelection = false;
				}
			});

			ssEventSvc.on('entry-cleared', function (val) {
				vm.results = [];
				vm.menuOpen = false;
				if (!vm.usePill) {
					selectedName = null;
					vm.madeSelection = false;
					$scope.model = [];
				}
			}, $scope);


			function refreshResults() {

				getResults = ssAsync(function* () {
					const queryParams = Object.assign(
						{
							'filter[fullname]': searchTerm,
							include: 'emails,statusHistory',
							limit: vm.queryLimit
						}, (
							vm.statusTypes.length
							? {'filter[status]': vm.statusTypes}
							: {}
						)
					);

					vm.loading = true;
					vm.menuOpen = true;
					vm.results = yield ssEmployeeApi.query(queryParams);

					if ($scope.includeActiveOnlyStatusFilter){
						var org = yield ssOrganizationSvc.getActiveOrg();
						vm.results.forEach((employee) => {
							if (employee._statusHistory != null && employee._statusHistory.length) {
								employee.currentStatus = employee._statusHistory.find(x => ssDateByOrgHlp.isCurrent({ obj: x, fromProp: '_from', toProp: '_to', org }));
								if (employee.currentStatus != null) {
									employee.currentStatus.name = enumEmployeeStatus.byValue(employee.currentStatus._statusId);
								}
							}
						});
					}

					//Remove the current employee from the resultset if we need to
					if ($scope.excludeEmployeesByIds.length) {
						vm.results = vm.results.filter((v) => {
							return $scope.excludeEmployeesByIds.indexOf(v.id) < 0;
						});
					}

					vm.results.forEach((result) => {
						result.primaryEmail = extractPrimary(result._emails);
					});

					vm.loading = false;
				})();
			}
		}
	}

	function extractPrimary(list) {
		return ssArrayHlp.findWhere(list, {'_isPrimary': true}) || list[0];
	}

	return new EmployeeSearch(props, 'employee-search', config);
}

// #-- END Component (super-employee-search) --# //
// #-------------------------------------------# //

angular.module('super-suit-components').directive('superEmployeeSearch', employeeSearch);
