import {Decorator} from 'base-decorator';
// #--------------------------------------# //
// #---- Decorator (ss-window-anchor) ----# //

const DEFAULT_ANCHOR_POINT = 70;
const ANCHOR_HEIGHT_INCREMENT = 10;

/*@ngInject*/
function ssWindowAnchor($injector, $window, rAF, $timeout) {

	const logic = $injector.get('ssWindowAnchorLogic');

	class WindowAnchor extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {

			const domElem = elem[0];
			const scrollElem = attrs.ssWindowAnchor && attrs.ssWindowAnchor.length > 0 ? document.getElementById(attrs.ssWindowAnchor) : $window;
			const anchorPoint = parseInt(attrs.windowAnchorTop, 10) || DEFAULT_ANCHOR_POINT;
			var detachPoint;
			var debounce = null;

			rAF.raw(function () {
				detachPoint = logic.getDetachPoint(domElem, anchorPoint);
				elem.css({
					maxHeight: `calc(100vh - ${anchorPoint + ANCHOR_HEIGHT_INCREMENT}px)`
				});
			});

			function calculateAnchor() {
				const offsetTop = logic.getDetachPointOffset(scrollElem, detachPoint);

				elem.css('top', (offsetTop > 0 ? offsetTop : 0) + 'px');
				if (elem.hasClass('ss-window-anchor') && offsetTop < 0) {
					domElem.scrollY = domElem.scrollTop = 0;
				}
				elem.toggleClass('ss-window-anchor', offsetTop > 0);
				debounce = null;
			}

			angular.element(scrollElem).on('scroll', () => {
				if (debounce !== null) { return; }
				debounce = rAF.raw(calculateAnchor);
			});
		}
	}

	return new WindowAnchor();
}

// #-- END Decorator (ss-window-anchor) --# //
// #--------------------------------------# //

angular.module('super-suit-decorators').directive('ssWindowAnchor', ssWindowAnchor);
