import {
	Enums
}
from 'base-enum';

// #-----------------------# //
// #---- Enums (/race) ----# //

class RaceEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'American Indian or Alaska Native',
			value: 0
		}, {
			name: 'Asian',
			value: 1
		}, {
			name: 'Black or African American',
			value: 2
		}, {
			name: 'Native Hawaiian or Other Pacific Islander',
			value: 3
		}, {
			name: 'White',
			value: 4
		}, {
			name: 'Unknown',
			value: 5
		}, {
			name: 'Two or more races',
			value: 6
		}, {
			name: 'Hispanic or Latino or Spanish Origin of any race',
			value: 7
		}, {
			name: 'Non-Resident Alien (of any race or ethnicity)',
			value: 8
		}];
		super(enums);
	}
}

// #---- End Enums (/race) ----# //
// #---------------------------# //

angular.module('super-suit-enums').factory('enumRace', () => new RaceEnum());
