/*@ngInject*/
function intercept401($q, ssAuthSvc) {
	return {
		responseError: function (res) {
			if (res.config.ignore401) { return res; }
			if (res.status === 401) { ssAuthSvc.authenticate(); }
			return $q.reject(res);
		}
	};
}


angular.module('super-suit').factory('intercept401', intercept401);
