import {Component} from 'base-component';
// #------------------------------------------# //
// #---- Component (super-chart-doughnut) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	data: '=',
	options: '=?',
	highlighted: '=?',
	width: '@',
	height: '@'
};

// directive definition object overrides
const config = {
	// transclude: true
};

/*@ngInject*/
function chartDoughnut(Chart) {

	class ChartDoughnut extends Component {
		/*@ngInject*/
		controller($scope) {
			if ($scope.options == null) { $scope.options = {}; }
			if ($scope.highlighted === undefined) { $scope.highlighted = null; }
		}

		link(scope, elem, attrs) {
			var hSegment;
			var mouseIsOverChart = false;
			const defaults = {
				animation: true,
				animationEasing: 'easeInOutQuart',
				animationSteps: 80
			};
			const ctx = elem[0].getContext('2d');
			const options = Object.assign({}, defaults, scope.options);
			const chart = scope.chart = new Chart(ctx).Doughnut(scope.data, options);

			chart.segments.forEach(function (seg, i) {
				scope.data[i].segment = seg;
				scope.$watch(`data[${i}].value`, function (value, old) {
					const h = scope.highlighted;
					// reset the selected item's highlight
					if (h != null) { h.segment.restore(['fillColor']); }

					// make the data update
					seg.update({value});
					// update the chart
					chart.update();

					// restore selection
					if (h != null) {
						h.segment.update({fillColor: h.segment.highlightColor});
					}
				});
			});

			scope.$watch('highlighted', function (newH, oldH) {
				if (oldH != null) {
					oldH.segment.restore(['fillColor']);
					if (!mouseIsOverChart || newH == null) {
						chart.render(true);
					}
				}
				if (mouseIsOverChart) { return; }
				if (newH == null) { return; }
				newH.segment.update({fillColor: newH.segment.highlightColor});
				chart.render(true);
			});

			elem.on('mouseenter', function (evt) {
				mouseIsOverChart = true;
				trackMouse(evt);
				elem.on('mousemove', trackMouse);
			});
			elem.on('mouseleave', function (evt) {
				mouseIsOverChart = false;
				trackMouse(evt);
				elem.off('mousemove', trackMouse);
			});

			function trackMouse(evt) {
				const currentHighlight = chart.getSegmentsAtEvent(evt);
				if (currentHighlight.length && currentHighlight[0] !== hSegment) {
					hSegment = currentHighlight[0];
					scope.highlighted = scope.data[chart.segments.indexOf(hSegment)];
					scope.$root.safeApply();
				} else if (currentHighlight.length === 0 && hSegment != null) {
					scope.highlighted = null;
					hSegment = null;
					scope.$root.safeApply();
				}
			}
		}
	}


	return new ChartDoughnut(props, 'chart-doughnut', config);
}

// #-- END Component (super-chart-doughnut) --# //
// #------------------------------------------# //

angular.module('super-suit-components').directive('superChartDoughnut', chartDoughnut);
