/*@ngInject*/
function departmentFilter(ssDepartmentSvc) {
	return function(id, prop) {
		// grab the department by id
		var dep = ssDepartmentSvc.getDepartmentById(id);
		// can't find it?
		if (dep == null) { return ''; }
		// did we request a specific property? if not, give all
		if (prop == null) { return dep; }
		// prop exist? no? doh!
		if (dep[`_${prop}`] == null) { return ''; }
		// yay, we find property
		return dep[`_${prop}`];
	};
}

angular.module('super-suit').filter('department', departmentFilter);
