// #-----------------------------------------# //
// #---- Mutator (mutatorSecurityScopes) ----# //

/*@ngInject*/
function securityScopesMutator(enumSecurityGroupScope) {
	return {
		// matchProfile: scope => {
		// 	return profiles => {
		// 		scope.permissionProfile = profiles.find(p => scope._permissionProfileId === p.id);
		// 	};
		// },
		controlDisplay: scope => {
			let location = quantity('location', scope._locationAdministrationContext,
				scope._locationIds.length);
			let department = quantity('dept', scope._departmentAdministrationContext,
				scope._departmentIds.length);
			let jobType = quantity('job type', scope._jobTypeAdministrationContext, scope
				._jobTypeIds.length);
			let employee = quantity('employee', scope._employeeAdministrationContext,
				scope._employeeIds.length);

			return `${location}, ${department}, ${jobType}, ${employee}`;
		}
	};

	function quantity(type, context, count) {
		switch (context) {
			case enumSecurityGroupScope.none:
				return `no ${type}s`;
			case enumSecurityGroupScope.self:
				return `own ${type}`;
			case enumSecurityGroupScope.specific:
				let label = (count === 1) ? type : `${type}s`;
				return `${count} specific ${label}`;
			case enumSecurityGroupScope.all:
				return `any ${type}`;
			case enumSecurityGroupScope.directReports:
				return `direct report${count !== 1 ? 's' : ''}`;
		}

		return '';
	}
}

// #---- End Mutator (mutatorSecurityScopes) ----# //
// #---------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorSecurityScopes', securityScopesMutator);
