export class Enums {
	constructor(enums, defaultName) {

		var item, lcName, lcValue;

		this.enums = enums;

		if (defaultName !== undefined) {
			this.defaultVal = {
				name: defaultName,
				value: -1
			};
		}

		// array of names
		this.aNames = [];
		// array of values
		this.aValues = [];

		// map where the names are the keys
		this.oNames = {};
		// map where the values are the keys
		this.oValues = {};

		this._size = enums.length;

		for (let i = 0; i < this._size; i++) {
			item = enums[i];
			// // lowercase for case insensitive searching
			lcName = (item.name + '').toLowerCase();
			lcValue = (item.value + '').toLowerCase();

			this.aNames.push(item.name);
			this.aValues.push(item.value);

			this.oNames[lcName] = {
				val: item.value,
				index: i
			};

			this.oValues[lcValue] = {
				val: item.name,
				index: i
			};

			this[toTitleCase(item.name)] = item.value;
		}

		function toTitleCase(str) {
			return str.replace(/[^A-Za-z0-9]/g, ' ').replace(/\w\S*/g, (txt, i) => {
				if (i === 0) {
					return txt.toLowerCase();
				}
				return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
			}).replace(/[\s]/g, '');
		}
	}

	/**
	 * Convenience function for byName and byValue to call into
	 */
	by(type, val, defaultVal) {
		var lcVal = (val + '').toLowerCase();
		if (this[type][lcVal] !== undefined) {
			return this[type][lcVal].val;
		}
		if (defaultVal === undefined) {
			return null;
		}
		return defaultVal;
	}

	/**
	 * Returns the value by name
	 */
	byName(val, defaultVal) {
		return this.by('oNames', val, defaultVal);
	}

	/**
	 * Returns the name by value
	 */
	byValue(val, defaultVal) {
		return this.by('oValues', val, defaultVal);
	}

	/**
	 * Convenience function for byName and byValue to call into
	 */
	enumBy(type, val) {
		if (val == null) {
			return this.defaultVal;
		}

		var lcVal = (val + '').toLowerCase();
		if (this[type][lcVal] !== undefined) {
			var i = this[type][lcVal].index;
			return this.enums[i];
		}
		return this.defaultVal;
	}

	/**
	 * Returns the full enum by on name
	 */
	enumByName(val) {
		return this.enumBy('oNames', val);
	}

	/**
	 * Returns the full enum by on value
	 */
	enumByValue(val) {
		return this.enumBy('oValues', val);
	}

	/**
	 * Returns full object the Enums was built from
	 */
	getEnums() {
		return this.enums;
	}

	/**
	 * Returns the array of names
	 */
	names() {
		return this.aNames;
	}

	/**
	 * Returns enums size
	 */
	size() {
		return this._size;
	}

	/**
	 * Returns the array of values
	 */
	values() {
		return this.aValues;
	}

}
