import {Component} from 'base-component';
// #---------------------------------------------# //
// #---- Component (super-multi-filter-list) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	selected: '=',
	label: '@?',
	labelWeight: '@?',
	display: '@',
	isDisabled: '=?',
	defaultSelectedText: '@',
	optionKeyPropName: '@',
	onSearch : '=?',

	// DEPRECATED: options list is constructed by the "multi-filter-list-item-added"
	// event being called by the options that are initialized in the transclude
	// options: '=',

	// DEPRECATED: UX decided to use generic "Selected" verbiage instead of
	// calling out singular or plural versions of the label
	// objectPlural: '@?', // The plural form of the object represented in the dropdown: 'Job Types'
	// objectSingular: '@?',
};

// directive definition object overrides
const config = {
	transclude: true
};

/*@ngInject*/
function multiFilterList(ssCommonControlHlp) {

	class MultiFilterList extends Component {
		/*@ngInject*/
		controller($scope, $attrs) {
			// act as normal if onSearch is not a function.
			if(typeof $scope.onSearch !== 'function'){
				$scope.onSearch = undefined;
			}

			$scope.labelWeight = ssCommonControlHlp.labelWeight($scope.labelWeight, $scope.label);
			const display = $scope.display || '';
			const vm = $scope.vm = {
				label: $scope.label,
				inputId: `multi-filter-${(Math.ceil(Math.random() * 10000000)).toString()}`, // "unique" control identifier
				filterText: '',
				filterResultsLength: 0,
				groups: [],
				options: [],
				showTags: (display.toLowerCase() === 'tags'),
				defaultSelectedText : $scope.defaultSelectedText,
				lazyLoadMode : {
					status : '',
					searchText : '',
					arrRef : []
				}
			};

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				checkAll() {
					$scope.selected = vm.options.filter(o => !o.hide);
				},
				uncheckAll() {
					// reset selected to none, checked state should be synced
					// via the watchCollection on 'selected'
					$scope.selected = [];
				},
				remove(item) {
					const itemIdx = $scope.selected.indexOf(item);
					if (itemIdx === -1) {
						// this shouldn't happen, but if it's not in the selected
						// list, we should at least uncheck it
						item.checked = false;
					} else {
						// found the item to remove, splice it out
						$scope.selected.splice(itemIdx, 1);
					}
				},
				checkFitleredItems() {
					return vm.filterResultsLength === 0 && vm.filterText.length > 0;
				},
				checkLoadedItems() {
					return vm.options.length === 0 && vm.filterText.length > 0;
				}
			};

			$scope.$watch('vm.filterText', val => {
				if($scope.onSearch){
					vm.lazyLoadMode.status = 'loading';
					vm.lazyLoadMode.searchText = vm.filterText;
					vm.lazyLoadMode.arrRef = vm.options;
					$scope.onSearch(vm.lazyLoadMode);
				}

				vm.options.forEach(v => {
					v.hide = true;
				});

				// filter out the options by the text entered
				const visible = vm.options.filter(function (o) {
					return o._name.toLowerCase().includes(val.toLowerCase());
				});

				// those that match the filter, should not be hidden
				visible.forEach(o => o.hide = false);
				// track how many results we have, because if it's 0 we want to
				// show a 'no matches' text
				vm.filterResultsLength = visible.length;
			});

			// sync the "checked" state with the selected array
			$scope.$watchCollection('selected', function (newSelections, prevSelections) {
				if(!newSelections) { return; }
				if (newSelections === prevSelections) { return; }
				newSelections.forEach(function (s, i, a) {
					// if this selected object reference exists in the options, we good.
					if (vm.options.includes(s)) { return; }
					// find option by unique property provided in $scope.optionKeyPropName for unique comparisson
					// find the option by _name by default
					const selectedOption = vm.options.find(o => {
						if($scope.optionKeyPropName && o[$scope.optionKeyPropName] && s[$scope.optionKeyPropName]){
							return o[$scope.optionKeyPropName] === s[$scope.optionKeyPropName];
						} else {
							return o._name === s._name;
						}
					});
					if (selectedOption == null) {
						// if its not on lazyload mode then
						// if not ignore selected array
						if(!$scope.onSearch){
							// couldn't find it? remove it!
							$scope.selected.splice(i, 1);
						}
					} else {
						// found it! replace reference!
						$scope.selected[i] = selectedOption;
					}
				});
				vm.options.forEach(v => v.checked = false);
				newSelections.forEach(v => v.checked = true);
			});

			$scope.$on('multi-filter-list-group-added', (evt, val) => {
				vm.groups.push(val);

				const {vm: {hide = false} = {}} = val;

				// cache first visible group for later comparisons
				if (vm.firstVisibleGroup == null && !hide) {
					vm.firstVisibleGroup = val;
					val.vm.firstVisible = true;
				}

				// if the item is removed, remove it from our list of options.
				evt.targetScope.$on('$destroy', function () {
					vm.groups = vm.groups.filter(v => v !== val);
				});

				// we are done with this event
				evt.stopPropagation();
			});

			$scope.$on('multi-filter-list-group-visibility-changed', (evt, val) => {
				// find first group that is not hidden
				vm.firstVisibleGroup = vm.groups.find(({vm: {hide = false} = {}}) => !hide);

				// update the groups to reflect the visibility change
				vm.groups.forEach(g => {
					g.vm.isFirstVisible = g === vm.firstVisibleGroup;
				});

				// we are done with this event
				evt.stopPropagation();
			});

			$scope.$on('multi-filter-list-item-added', (evt, val) => {
				const {selected} = $scope;
				// create a default pillText value
				if (val.pillText == null) {
					val.pillText = val._name;
				};

				// fuzy find the item in our list of selected items. locate it by
				// unique property provided in the scope as $scope.optionKeyPropName, check for unique comparison if provided, else
				// pillText to ensure uniqueness
				const selectedItem = selected.find(x => {
					if($scope.optionKeyPropName && x[$scope.optionKeyPropName] && val[$scope.optionKeyPropName]){
						return x[$scope.optionKeyPropName] === val[$scope.optionKeyPropName];
					} else {
						return x.pillText === val.pillText;
					}
				});
				const selectedItemIdx = selected.indexOf(selectedItem);

				// we found this item in the selected array, but it's not the
				// same object in memory. Replace the selected item with the
				// correct object reference.
				if (selectedItem && selectedItem !== val) {
					selected[selectedItemIdx] = val;
				}

				// this is checked if we were able to locate a similar object
				// in our selected list.
				val.checked = (selectedItem !== undefined);

				// push the option into our internal list of options
				vm.options.push(val);

				// if the item is removed, remove it from our list of options.
				evt.targetScope.$on('$destroy', function () {
					vm.options = vm.options.filter(v => v !== val);
				});

				// we are done with this event
				evt.stopPropagation();
			});

			$scope.$on('multi-filter-list-item-click', (evt, val) => {
				const valIdx = $scope.selected.indexOf(val);
				// if the item is not selected
				if (valIdx === -1) {
					// select it
					$scope.selected.push(val);
				} else {
					// otherwise remove it
					$scope.selected.splice(valIdx, 1);
				}
			});
		}

		link(scope, elem, attrs) {
			attrs.$observe('label', val => {
				scope.vm.label = val;
			});
		}
	}

	return new MultiFilterList(props, 'multi-filter-list', config);
}

// #-- END Component (super-multi-filter-list) --# //
// #---------------------------------------------# //

angular.module('super-suit-components').directive('superMultiFilterList', multiFilterList);
