/*@ngInject*/
function ssMutator(
	mutatorPhoneNumbers,
	mutatorEmployees,
	mutatorPositions,
	mutatorOrganizations,
	mutatorFormTemplates,
	mutatorIFormLibraryObjects,
	mutatorMessages,
	mutatorLocations,
	mutatorFormInstances,
	mutatorSecurityScopes,
	mutatorSecurityGroups,
	mutatorPaySchedules,
	mutatorAddresses,
	mutatorSupervisors,
	mutatorJobTypeDetails/*mutatorInject*/
) {
	// #-----------------------------# //
	// #---- Utility (ssMutator) ----# //

	// keep the list alphabatized
	return {
		phoneNumbers: mutatorPhoneNumbers,
		employees: mutatorEmployees,
		positions: mutatorPositions,
		organizations: mutatorOrganizations,
		formTemplates: mutatorFormTemplates,
		iFormLibraryObjects: mutatorIFormLibraryObjects,
		messages: mutatorMessages,
		locations: mutatorLocations,
		formInstances: mutatorFormInstances,
		securityScopes: mutatorSecurityScopes,
		securityGroups: mutatorSecurityGroups,
		paySchedules: mutatorPaySchedules,
		addresses: mutatorAddresses,
		supervisors: mutatorSupervisors,
		jobTypeDetails: mutatorJobTypeDetails/*mutatorList*/
	};

	// #-- END Utility (ssMutator) --# //
	// #-----------------------------# //
}

angular.module('super-suit-utilities').factory('ssMutator', ssMutator);
