/*@ngInject*/
function dataImporterApi(ssApiSvc, buildConfig, $http) {
// #------------------------------------------# //
// #---- API Endpoint (ssDataImporterApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.platformImporterApi;

	// header obj
	const headers= {
		'Cache-Control': 'no-cache, no-store',
		'Pragma': 'no-cache'
		};

	// possible relationships
	const rel = [];

	// ATTEMPT Setup
	const attemptUrl = `${baseUrl}/v1/attempts`;
	const attemptGetUrl = (p) => `${attemptUrl}/${p.id}`;
	const attemptModel = 'importAttempt';

	// generate endpoints
	const getAttempt = ssApiSvc.endpoint({url: attemptGetUrl, method: 'GET', headers}, attemptModel, rel);
	const queryAttempts = ssApiSvc.endpoint({ url: attemptUrl, method: 'GET', headers }, attemptModel, rel);

	function startImport(attemptId, processingOptions){
		const config = {
			method: 'POST',
			url: `${attemptUrl}/${attemptId}`,
			data: processingOptions,
			headers
		};
		return $http(config);
	}

	// Attempt status polling
	const attemptStatusUrl = (p) => `${baseUrl}/v1/operations/attemptstatus/${p.id}?dt=${Date.now()}`;
	const pollAttemptStatus = ssApiSvc.endpoint({url: attemptStatusUrl, method: 'GET', headers}, attemptModel, rel);

	// Only used for updating the userConfig currently. msvc will need to change to PATCH anything else
	const updateAttempt = (attemptId, body) => {
		return $http({
			method: 'PATCH',
			url: `${baseUrl}/v1/attempts/${attemptId}`,
			data: body,
			headers
		});
	};

	//JOBS Setup
	const jobUrl = `${baseUrl}/v1/jobs`;
	const jobGetUrl = (p) => `${jobUrl}/${p.id}?dt=${Date.now()}`;
	const jobModel = 'importJob';
	const jobTemplateUrl = `${baseUrl}/v1/jobtemplates`;
	// generate endpoints
	const getJob = ssApiSvc.endpoint({ url: jobGetUrl, method: 'GET', headers }, jobModel, rel);
	const queryJobs = ssApiSvc.endpoint({url: jobUrl, method: 'GET', headers}, jobModel, rel);

	function createImportJob(organizationId, body = {}) {
		const config = {
			method: 'POST',
			url: `${baseUrl}/v1/jobs`,
			params: {organizationId},
			data: body,
			headers
		};
		return $http(config);
	}

	//IMPORT TYPE Setup
	const importTypeUrl = `${baseUrl}/v1/importtypes`;
	const importTypeGetUrl = (p) => `${importTypeUrl}/${p.id}`;
	const importTypeModel = 'importTypeApi';

	// generate endpoints
	const getImportType = ssApiSvc.endpoint({ url: importTypeGetUrl, method: 'GET', headers }, importTypeModel, rel);
	const queryImportTypes = ssApiSvc.endpoint({ url: importTypeUrl, method: 'GET', headers}, importTypeModel, rel);

	// JobTemplate endpoints

	const jobTemplateGetUrl = (p) => `${jobTemplateUrl}/${p.id}`;
	const jobTemplateModel = 'jobTemplateApi';

	const getJobTemplate = ssApiSvc.endpoint({ url: jobTemplateGetUrl, method: 'GET', headers }, jobTemplateModel, rel);
	const queryJobTemplates = ssApiSvc.endpoint({url: jobTemplateUrl, method: 'GET', headers}, jobTemplateModel, rel);

	const createJobTemplate = (body) => {
		return $http({
			method: 'POST',
			url: `${jobTemplateUrl}`,
			data: body,
			headers
		});
	};

	const updateJobTemplate = (jobTemplateId, body) => {
		return $http({
			method: 'PATCH',
			url: `${jobTemplateUrl}/${jobTemplateId}`,
			data: body,
			headers
		});
	};

	const createImportJobFromTemplateId = (jobTemplateId, body) => {
		return $http({
			method: 'POST',
			url: `${jobTemplateUrl}/${jobTemplateId}`,
			data: body,
			headers
		});
	};

	// Possible actions validateSource, pause, unpause, skip, delete
	const jobTemplateActions = (jobTemplateId, action, body) => {
		if(action === 'delete'){
			return $http({
				method: 'DELETE',
				url: `${jobTemplateUrl}/${jobTemplateId}`,
				data: body,
				headers
			});
		}

		return $http({
			method: 'POST',
			url: `${jobTemplateUrl}/${jobTemplateId}/${action}`,
			data: body,
			headers
		});
	};

	// Watchers
	const jobTemplateWatchersUrl = (p) => `${jobTemplateUrl}/${p.jobTemplateId}/watchers`;
	const getJobTemplateWatchers = ssApiSvc.endpoint({ url: jobTemplateWatchersUrl, method: 'GET', headers }, jobTemplateModel, rel);
	const updateJobTemplateWatchers = ssApiSvc.endpoint({ url: jobTemplateWatchersUrl, method: 'POST', headers }, jobTemplateModel, rel);
	const deleteJobTemplateWatchers = ssApiSvc.endpoint({ url: jobTemplateWatchersUrl, method: 'DELETE', headers }, jobTemplateModel, rel);

	// ORGANIZATIONS
	const orgUrl = `${baseUrl}/v1/organizations`;
	const orgGetUrl = (p) => `${orgUrl}/${p.orgId}/sftp`;
	const orgModel = 'organization';

	// generate endpoints
	const getOrganizationSFTP = ssApiSvc.endpoint({url: orgGetUrl, method: 'GET', headers}, orgModel, rel);


	// API endpoint methods
	return {
		getAttempt,
		queryAttempts,
		pollAttemptStatus,
		updateAttempt,
		getJob,
		queryJobs,
		getImportType,
		queryImportTypes,
		startImport,
		createImportJob,
		getJobTemplate,
		queryJobTemplates,
		createJobTemplate,
		updateJobTemplate,
		createImportJobFromTemplateId,
		jobTemplateActions,
		getJobTemplateWatchers,
		updateJobTemplateWatchers,
		deleteJobTemplateWatchers,
		getOrganizationSFTP,
	};

// #-- END API Endpoint (ssDataImporterApi) --# //
// #------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssDataImporterApi', dataImporterApi);
