/*@ngInject*/
function ssAsyncFactory($q, $log) {
	// #---------------------------# //
	// #---- Utility (ssAsync) ----# //

	// the ssAsync method is the main DI payload for this utility
	function ssAsync(generator) {
		// the output of ssAsync function
		return (...args) => new AsyncFunction(generator, args);
	}

	class AsyncFunction {
		constructor(generator, args) {
			const genCompleteDefer = $q.defer();
			const gen = generator.apply(null, args);
			var aborted = false;
			var yieldedValue;

			genCompleteDefer.promise.$abort = abortAsync;
			genNext(gen, null);

			return genCompleteDefer.promise;

			// eslint-disable-next-line no-shadow
			function genNext(gen, lastResult) {
				if (aborted) { genCompleteDefer.reject(Error('async aborted')); return; }
				var yielded = gen.next(lastResult);
				yieldedValue = yielded.value;

				// if we are done, resolve and stop
				if (yielded.done) {
					genCompleteDefer.resolve(yielded.value);
					return;
				}
				// reassign yielded to the value
				if (yielded.value instanceof Array) {
					yielded = $q.all(yielded.value);
				} else {
					yielded = $q.when(yielded.value);
				}

				yielded.then(
					(data) => genNext(gen, data),
					(err) => {
						if (!aborted) {
							genCompleteDefer.reject(err);
							//ssToastHlp.error("foo", {});
							gen.throw(err);
						}
					}
				);
			}

			function abortAsync() {
				callAbort(yieldedValue);
				aborted = true;
			}

			function callAbort(val) {
				if (val instanceof Array) {
					val.forEach(callAbort);
				} else if (val && typeof val.$abort === 'function') {
					val.$abort();
				}
			}
		}
	}

	return ssAsync;

	// #-- END Utility (ssAsync) --# //
	// #---------------------------# //
}

angular.module('super-suit-utilities').factory('ssAsync', ssAsyncFactory);
