/*@ngInject*/
function locationFilter(ssLocationSvc) {
	return function(id, prop) {
		var loc = ssLocationSvc.getLocationById(id);
		if (loc == null) { return ''; }
		if (prop == null) { return loc; }
		if (loc[`_${prop}`] == null) { return ''; }
		return loc[`_${prop}`];
	};
}

angular.module('super-suit').filter('location', locationFilter);
