var authReadyDefer;
const mock = { profile: {} };

/*@ngInject*/
function ssAuthSvc($q, $location, buildConfig, ssActiveUserSvc) {
	// #-----------------------------# //
	// #---- Service (ssAuthSvc) ----# //

	if (authReadyDefer == null) { authReadyDefer = $q.defer(); }

	const token = { access: '', id: '' };
	const ready = authReadyDefer.promise.then(function (authSvc) {
		// proxy requests for the access token through to authSvc directly
		Object.defineProperty(token, 'access', {
			get: () => authSvc.accessToken,
			set(val) { }
		});

		return authSvc;
	});
	const svcAsyncGet = (accessor) => () => ready.then(accessor);

	// External service API
	return {
		ready: (window.jasmine != null ? $q.when(mock) : ready),
		signout: svcAsyncGet(_ => _.signout()),
		profileId: svcAsyncGet(_ => _.profileId),
		isImpersonating: svcAsyncGet(_ => _.isImpersonating),
		isImpersonatingV2: svcAsyncGet(_ => _.isImpersonatingV2),
		impersonate,
		endImpersonate,
		authenticate,
		editSettings,
		token
	};

	function impersonate(destUrl, impersonationId) {
		ready.then(svc => svc.impersonate(destUrl, impersonationId));
	}
	function endImpersonate(returnUrl = buildConfig.idmInternalToolsUrl) {
		ready.then(svc => svc.endImpersonate(returnUrl));
	}
	function authenticate() {
		$q.all({
			svc: ready,
			productId: ssActiveUserSvc.getProductId()
		}).then(function (result) {
			const {svc, productId} = result;
			const baseConfig = { returnUrl: $location.absUrl(), productId };
			svc.authenticate(getAuthConfig(
				baseConfig,
				$location.search()
			));
		});
	}
	function editSettings() {
		ready.then(function (svc) {
			const w = (0, eval)('this'); // eslint-disable-line no-eval
			w.location = svc.accountSettingsUrl(w.location, true);
		});
	}

	// #-- END Service (ssAuthSvc) --# //
	// #-----------------------------# //
}

/*@ngInject*/
function ssAuthConfig(flAuthServiceProvider) {
	const _win = (0, eval)('this'); // eslint-disable-line no-eval
	const buildConfig = _win.buildConfig;
	const {idmGatewayUrl, idmClientId, productLogoutRedirect, productMap} = buildConfig;

	flAuthServiceProvider.identityGatewayBaseUri = idmGatewayUrl;
	flAuthServiceProvider.clientId = idmClientId;

	const protocol = _win.location.protocol;
	const host = _win.location.hostname;

	// Outside of localhost, we don't use ports to determine resources.
	const isLocal = host.startsWith('localhost');
	const port = (isLocal ? `:${_win.location.port}` : ``);

	let productId;
	let logoutRedirectUrl;
	try {
		productId = _win.location.pathname.split('/')[1].split('-')[0].toLowerCase() || 'ec';
	} catch (err) {
		productId = 'ec';
	} finally {
		const redirectMap = productLogoutRedirect;
		if (redirectMap[productId] == null) { productId = 'ec'; }
		logoutRedirectUrl = redirectMap[productId];
	}

	if(productId.toLowerCase() !== 'universal'){
		flAuthServiceProvider.productId = productMap[productId] || productId;
		flAuthServiceProvider.signoutRedirectUri = logoutRedirectUrl;
	}
	flAuthServiceProvider.redirectUri = `${protocol}//${host}${port}/auth_callback`;
	flAuthServiceProvider.silentRenewUri = `${protocol}//${host}${port}/silentRenew.html`;
}

/*@ngInject*/
function ssAuthTrigger($q, $location, flAuthService, ssActiveUserSvc) {
	window.auth = flAuthService;
	if (sessionStorage.getItem('automation-no-auth') !== null) {
		authReadyDefer.resolve({
			profileId: 'mock-id-123',
			isImpersonating: $q.resolve(null),
			accessToken: 'mock-token-123'
		});
		return;
	}
	$q.all({
		svc: flAuthService.ready(),
		productId: ssActiveUserSvc.getProductId()
	}).then(function (result) {
		const {productId} = result;

		if (flAuthService.isAuthenticated) {
			//> log('AUTHENTICATED!')
			if (authReadyDefer == null) { authReadyDefer = $q.defer(); }
			if ($location.search().orgId != null) { $location.search('orgId', null); }
			authReadyDefer.resolve(flAuthService);
		} else {
			//> log('UNAUTHENTICATED!')
			const baseConfig = { returnUrl: $location.absUrl(), productId };
			if(productId.toLowerCase() === 'universal'){
				delete baseConfig.productId;
			}
			flAuthService.authenticate(getAuthConfig(
				baseConfig,
				$location.search()
			));
		}
	});
}

function getAuthConfig(authConfig, qsObj) {
	// if we are on the select route and an orgId is specified, we need
	// to pass it through to idServer so we can check for SSO
	const {orgId} = qsObj;

	const overrideConfig = {};
	if (orgId != null) {
		overrideConfig.organization = orgId;
	}

	return Object.assign({}, authConfig, overrideConfig);
}

angular.module('super-suit-services').factory('ssAuthSvc', ssAuthSvc);
angular.module('super').config(ssAuthConfig).run(ssAuthTrigger);
