const _rAF = rafFunc();

/*@ngInject*/
function rAF($q, $rootScope, $log) {
// #------------------------------------# //
// #---- Utility (ssAnimationFrame) ----# //

	// External utility injectable value
	const rafRunner = function (handler, digestScope) {
		if (handler != null && digestScope == null) {
			$log.error('rAF ==> triggered a full scope digest because no scope was provided');
		}
		var _defer = $q.defer();
		_rAF(function () {
			const hasFuncHandler = (typeof handler === 'function');
			const result = (hasFuncHandler ? handler() : null);

			_defer.resolve(result);
			if (hasFuncHandler && digestScope !== false) {
				if (digestScope == null) {
					$rootScope.$applyAsync();
				} else {
					$rootScope.safeDigest(digestScope);
				}
			}
		});
		return _defer.promise;
	};

	rafRunner.raw = (func) => _rAF(func);

	return rafRunner;

// #-- END Utility (ssAnimationFrame) --# //
// #------------------------------------# //
}

function rafFunc() {
	return window.requestAnimationFrame ||
		window.webkitRequestAnimationFrame ||
		window.mozRequestAnimationFrame ||
		window.oRequestAnimationFrame ||
		window.msRequestAnimationFrame ||
		function (callback) {
			window.setTimeout( callback, 1000 / 60 );
		};
}

angular.module('super-suit-utilities').factory('rAF', rAF);
