// #-------------------------------------# //
// #---- Helper (ssCommonControlHlp) ----# //

/*@ngInject*/
function ssCommonControlHlp($log) { return {

	// label weight value checks
	labelWeight(labelWeight, label) {
		const supportLabelWeights = ['light', 'heavy'];

		if (typeof labelWeight !== 'string') { labelWeight = ''; }
		labelWeight = labelWeight.toLowerCase();
		if(labelWeight.length && !supportLabelWeights.includes(labelWeight)) {
			$log.error(`please use a supported value (${supportLabelWeights.join(', ')}) for "label-weight" attribute on label "${label}"`);
			labelWeight = '';
		}
		return labelWeight;
	}

};}
// #-- END Helper (ssCommonControlHlp) --# //
// #-------------------------------------# //

angular.module('super-suit-helpers').factory('ssCommonControlHlp', ssCommonControlHlp);
