// #--------------------------------# //
// #---- Helper (ssValidateHlp) ----# //

const failClass = 'ss-validate-failing';
const passClass = 'ss-validate-passing';
const fadedClass = 'ss-validate-passing-fade';
const untestedClass = 'ss-validate-untested';

/*@ngInject*/
function ssValidateHlp($timeout, $q, $animate, $rootScope) { return { // eslint-disable-line
	resetValidation(elem) {
		elem.removeClass(failClass);
		clearErrorClasses(elem);
		elem.removeClass(passClass);
		elem.addClass(untestedClass);
		this.clearFadedState(elem);
		this.showErrors(elem, false);
	},

	passValidation(elem, attrs) {
		elem.removeClass(failClass);
		clearErrorClasses(elem);
		elem.addClass(passClass);
		this.setFadedState(elem);
		this.showErrors(elem, false);
	},

	failValidation(elem, attrs, result) {
		var errClasses, errHtml, failMessages;

		elem.addClass(failClass);
		elem.removeClass(passClass);

		failMessages = getFailMessages(result, attrs.ssValidateMessage);

		clearErrorClasses(elem);
		this.clearFadedState(elem);
		errClasses = failMessages.map(err => err.errClass);
		elem.addClass(errClasses.join(' '));
		elem.data('err-classes', errClasses);

		errHtml = failMessages.reduce(function (html, err) {
			return html + `<li data-test="error-message">${err.errMsg}</li>`;
		}, '');

		getErrElem(elem).empty().append(errHtml);
	},

	showErrors(elem, show) {
		if (show && elem[0].getElementsByClassName('ss-validate-failing').length) {
			//> log('aborted for containing failing validation', elem)
			return;
		}
		// default to true
		show = show !== false;

		const errElem = getErrElem(elem);
		if (errElem == null) { return; }

		const addRemove = (show ? 'addClass' : 'removeClass');
		const lastAnim = errElem.data('lastAnim');

		if (lastAnim != null) {
			$animate.cancel(lastAnim);
		}
		errElem.data('lastAnim', $animate[addRemove](errElem, 'show-messages'));
	},

	// manage the faded passing state
	setFadedState(elem) {
		elem.data('validate-fade', $timeout(function () {
			elem.addClass(fadedClass);
		}, 2000));
	},
	clearFadedState(elem) {
		$timeout.cancel(elem.data('validate-fade'));
		elem.removeClass(fadedClass);
	},

	// turn validation results into a promise
	validationPromise(result) {
		if (result == null) { return $q.reject(); }
		if (typeof result.then === 'function') {
			return result;
		} else if (result === true) {
			return $q.resolve();
		} else {
			return $q.reject(result);
		}
	},

	allChildrenAreValid(elStatus) {
		if (elStatus.children.length) {
			return elStatus.children.reduce((prev, cur) => prev && cur.valid === true, true);
		} else {
			return true;
		}
	},

	makeEventEmitter(scope, elem) {
		return function (evt) {
			// if we tabbed away from the field
			// ignore the keyboard event, the blur will still fire
			if ([9, 16].includes(evt.which)) { return; }

			// fire the event for our validator to pickup
			scope.$emit('ss-validate-field-event', elem, evt.type);
		};
	}

};} // eslint-disable-line

function getErrElem(elem) {
	var children = elem.children();
	var i = children.length;

	for (; i-- ;) {
		let child = children.eq(i);
		if (child.hasClass('ss-validate-messages')) {
			return child;
		}
	}

	return null;
}

function getFailMessages(result, inlineMsg = '') {
	var failMessages = [];

	if (inlineMsg.length > 0) {
		failMessages.push({
			errClass: 'err-inline',
			errMsg: inlineMsg
		});
	} else if (angular.isArray(result)) {
		result.forEach(function (msg, idx) {
			if (typeof msg === 'string') {
				failMessages.push({
					errClass: `err-${idx}`,
					errMsg: msg
				});
			} else if (angular.isObject(msg) && msg.errClass != null && msg.errMsg != null) {
				failMessages.push(msg);
			}
		});
	} else if (typeof result === 'string') {
		failMessages.push({errClass: 'err-generic', errMsg: result});
	} else if (angular.isObject(result)) {
		Object.keys(result).forEach(function (k, idx) {
			let val = result[k];
			if (typeof val === 'string') {
				failMessages.push({ errClass: k, errMsg: val });
			}
		});
	} else {
		failMessages.push({
			errClass: 'err-generic',
			errMsg: 'generic invalid input message'
		});
	}

	return failMessages;
}

function clearErrorClasses(elem) {
	var errClasses = elem.data('err-classes');

	if (angular.isArray(errClasses)) {
		errClasses.forEach(err => elem.removeClass(err));
	}
	elem.data('err-classes', null);
}
// #-- END Helper (ssValidateHlp) --# //
// #--------------------------------# //

angular.module('super-suit-helpers').factory('ssValidateHlp', ssValidateHlp);
