import {Component} from 'base-component';
// #--------------------------------------# //
// #---- Component (super-filter-bar) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	search: '=?', // search text value
	typeLabel: '@?',
	typeLabelPlural: '@?',
	totalCount: '=?',
	selectedCount: '=?',
	searchDisabled: '=?',
	searchLabel: '@?',
};

// directive definition object overrides
const config = {
	transclude: {
		actions: '?actions',
		filters: '?filters',
		bulkActions: '?bulkActions'
	}
};

/*@ngInject*/
function filterBar() {

	/**
	 * @class  SuperSuit.FilterBar
	 * @memberOf SuperSuit
	 */
	class FilterBar extends Component {
		/*@ngInject*/
		controller($scope, ssDebounce) {
			// view model for tracking state
			const vm = $scope.vm = {
				classes: '',
				search: '',
				label: $scope.typeLabel,
				searchLabel: $scope.searchLabel != null ? $scope.searchLabel : $scope.typeLabel,
			};

			vm.labelPlural = $scope.typeLabelPlural == null ? vm.label + 's' : $scope.typeLabelPlural;

			vm.pluralize = {
				'1': vm.label,
				'other': vm.labelPlural
			};

			const debounce = ssDebounce(() => {
				$scope.search = vm.search;
			}, 350);

			$scope.$watch('vm.search', debounce);
			$scope.$watch('search', (nv,ov) => {
				vm.search = $scope.search;
			});

			$scope.$watch('selectedCount', (nv, ov) => {
				vm.classes = nv > 0 ? 'show-bulk-actions' : '';
			});

			// behavior that needs to be bound to the view
			const go = $scope.go = {}; // eslint-disable-line no-unused-vars
		}
	}

	return new FilterBar(props, 'filter-bar', config);
}

// #-- END Component (super-filter-bar) --# //
// #--------------------------------------# //

angular.module('super-suit-components').directive('superFilterBar', filterBar);
