/*@ngInject*/
function setupPreloader($q, $window, ssEventSvc, ssFeatureSvc, ssFlagSvc, ssFlagCCSvc, ssActiveUserSvc, ssPermissionsSvc, buildConfig, ssLocationSvc, ssJobTypeSvc, ssDepartmentSvc) {
	$q.all([
		ssFeatureSvc.ready,
		ssFlagSvc.ready,
		ssFlagCCSvc.ready,
		ssActiveUserSvc.ready,
		ssPermissionsSvc.ready,
		ssLocationSvc.ready,
		ssJobTypeSvc.ready,
		ssDepartmentSvc.ready
	]).then(() => ssEventSvc.emit('preloader-complete'));

	ssEventSvc.on('preloader-complete', $window.ss.util.killPreLoader, false);
}

angular.module('super-suit').run(setupPreloader);
