import {Enums} from 'base-enum';

// #----------------------------------# //
// #---- Enums (/highchartsTypes) ----# //

class HighchartsTypesEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'line',
			value: 'line'
		},
		{
			name: 'area',
			value: 'area'
		},
		{
			name: 'column',
			value: 'column'
		},
		{
			name: 'stackedcolumn',
			value: 'stackedcolumn'
		},
		{
			name: 'combination',
			value: 'combination'
		}
		];
		super(enums);
	}
}

// #---- End Enums (/highchartsTypes) ----# //
// #--------------------------------------# //

angular.module('super-suit-enums').factory('enumHighchartsTypes', () => new HighchartsTypesEnum());
