/*@ngInject*/
function ssModalSvc($animate, $compile, $rootScope, $q) {
	// #------------------------------# //
	// #---- Service (ssModalSvc) ----# //

	var template =
		`<div class="modal {type} {size}" ng-keydown="$event.which === 13 && keyPress($event)" data-test="modal" role="dialog" aria-labelledby="ss-modal-title">
					<button type="button" ng-click="cancel()" class="button flat modal-close" ss-icon-before="times" ng-show="!{showSpinner}" data-test="modal-close"></button>
					<div ng-if="{showTitle}" class="title" data-test="modal-title" id="ss-modal-title">{title}</div>
					<div ng-show="{showContent}"  class="content size-{size}" data-test="modal-content">{content}</div>
					<div ng-if="{showSpinner}" class="spinner"><super-loader is-loading="true" role="status"></super-loader></div>
					<div class="suppress-modal-control" ng-show="{showSuppress}">
 						<super-input-checkbox model="" name="suppressModal" ng-click="suppressModal()" label="Don't show again"></super-input-checkbox>
					 </div>
					<div ng-if={showButtons} class="modal-button">
						<button ng-show="{showSecondary}" class="super-button std" type="button" value="Cancel" label="{secondaryText}" ng-click="cancel()" data-test="modal-cancel"><label class="button-control">{secondaryText}</label></button>
						<button ng-show="{showPrimary}" class="super-button {actionType}" type="button" value="Continue" label="{primaryText}" ng-click="cont()" data-test="modal-continue"><label class="button-control">{primaryText}</label></button>
					</div>
				</div>`,
		fade = angular.element(document.getElementById('modal-fade')),
		element = null,
		scope,
		modalResponse,
		container,
		currentDialog = {};

	$rootScope.keyPress = function($e) {
		action($e, currentDialog);
	};

	// External service API
	return {
		showLarge,
		showDestructive,
		showStandard,
		showWait,
		isModalOpen,
		clearDialog
	};

	/**
	 * Close the open dialog
	 */
	function clearDialog() {
		cancel();
	}

	function showDestructive(contentConfig) {
		//Default Destructive Config
		const defaults = {
			title: 'Are you sure you want to do this?',
			content: '',
			primaryText: 'Yes',
			secondaryText: 'No',
			showPrimary: true,
			showSecondary: true,
			showSuppress: false,
			alwaysOnTop: true,
			defaultFocus: ''   // 'close' | 'secondary' | 'primary'
		};

		const protectedConfig = {
			isDestructive: true,
			showButtons: true,
			showSpinner: false
		};

		return show(buildConfig(defaults, contentConfig, protectedConfig));
	}

	function showStandard(contentConfig) {
		//Default Standard Config
		const defaults = {
			title: '',
			content: '',
			primaryText: 'Yes',
			secondaryText: 'No',
			showPrimary: true,
			showSecondary: true,
			showSuppress: false,
			alwaysOnTop: true,
			defaultFocus: ''   // 'close' | 'secondary' | 'primary'
		};

		const protectedConfig = {
			isDestructive: false,
			showButtons: true,
			showSpinner: false
		};

		return show(buildConfig(defaults, contentConfig, protectedConfig));
	}

	function showLarge(contentConfig) {
		//Default Standard Config
		const defaults = {
			title: '',
			content: '',
			showSuppress: false,
			alwaysOnTop: true
		};

		const protectedConfig = {
			isDestructive: false,
			showButtons: false,
			showSpinner: false
		};

		let config = buildConfig(defaults, contentConfig, protectedConfig);

		config.size = 'large';

		return show(config);
	}

	/**
	 * Show a dialog with the finger loader
	 */
	function showWait(contentConfig) {
		//Default Standard Config
		const defaults = {
			title: 'Please wait...',
			size: 'small',
			showSuppress: false,
			alwaysOnTop: true
		};

		const protectedConfig = {
			isDestructive: false,
			showButtons: false,
			showSpinner: true
		};

		let config = buildConfig(defaults, contentConfig, protectedConfig);

		return show(config);
	}

	function buildConfig(def, user, protectedConfig) {
		const config = Object.assign({}, def, user, protectedConfig);

		// if somehow null gets through fix it
		if (typeof config.content !== 'string') {
			config.content = '';
		}

		config.size = config.content === '' ? 'small' : config.size;
		return config;
	}

	function show(config) {
		var temp = template.replace('/{title/}');
		temp = temp.replace('{title}', config.title)
			.replace('{showTitle}', !!config.title)
			.replace('{showContent}', !!config.content)
			.replace('{content}', config.content)
			.replace(/\{size\}/g, config.size)
			.replace('{actionType}', config.isDestructive ? 'neg' : 'pri')
			.replace('{showButtons}', config.showButtons && (config.showPrimary || config.showSecondary))
			.replace('{showPrimary}', config.showPrimary)
			.replace('{showSecondary}', config.showSecondary)
			.replace(/\{showSpinner\}/g, config.showSpinner)
			.replace(/\{secondaryText\}/g, config.secondaryText)
			.replace(/\{primaryText\}/g, config.primaryText)
			.replace('{showSuppress}', config.showSuppress)
			.replace('{type}', config.isDestructive ? 'destructive' : 'standard')
			.replace('{overrideClass}', config.overrideClass ? config.overrideClass : '');

		element = angular.element(temp);
		container = document.body;
		scope = $rootScope.$new();
		modalResponse = $q.defer();

		if (config) {
			for (var prop in config) {
				if (!config.hasOwnProperty(prop)) { continue; }
				scope[prop] = config[prop];
			}
		}

		if (config.alwaysOnTop === true) {
			element.addClass('always-on-top');
			fade.addClass('always-on-top');
		}

		scope.cancel = cancel;
		scope.cont = cont;
		scope.suppressModal = suppressModal;

		$compile(element)(scope);

		fade.addClass('shown');
		$animate.enter(element, container);

		setDefaultFocus(element, config);

		return modalResponse.promise;

	}

	function suppressModal() {
		scope.suppressTheModal = !scope.suppressTheModal;
	}

	function close() {
		fade.removeClass('shown');

		return $animate.leave(element).then(() => {
			scope.$destroy();
			scope = null;
			element.remove();
			element = null;
		});
	}

	function cont() {
		close();

		return modalResponse.resolve(scope);
	}

	function cancel() {
		close();

		return modalResponse.reject(scope);
	}

	function isModalOpen() {
		return !!element;
	}

	function action($event, config) {
		// on negative dialogs you can click the button with the enter key
		if (config.isDestructive) {
			$event.preventDefault();
			$event.stopPropagation();
		} else {
			return cont();
		}
	}

	function setDefaultFocus(modalElem, config) {
		const defaultFocus = config.defaultFocus || '';
		if (defaultFocus.length === 0) {
			return;
		}
		const focusElemSelector = {
			'close': '.modal-close',
			'secondary': '.modal-button button:first-of-type',
			'primary': '.modal-button button:last-of-type'
		};
		const focusElem = modalElem[0].querySelector(focusElemSelector[defaultFocus.toLowerCase()]);
		if (focusElem !== null) {
			focusElem.focus();
		}
	}

	// #-- END Service (ssModalSvc) --# //
	// #------------------------------# //
}

angular.module('super-suit-services').factory('ssModalSvc', ssModalSvc);
