import {
	Enums
}
from 'base-enum';

// #----------------------------# //
// #---- Enums (/usageType) ----# //

class UsageTypeEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Work',
			value: 0
		}, {
			name: 'Personal',
			value: 1
		}, {
			name: 'Other',
			value: 2
		}];
		super(enums);
	}
}

// #---- End Enums (/usageType) ----# //
// #--------------------------------# //

angular.module('super-suit-enums').factory('enumUsageType', () => new UsageTypeEnum());
