/*@ngInject*/
function securityScopeApi(ssApiSvc, buildConfig) {
// #-------------------------------------------# //
// #---- API Endpoint (ssSecurityScopeApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.permissions;
	// const groupBaseUrl = (p) => `${baseUrl}/SecurityGroups/${p.groupId}`;
	const url = (p) => `${baseUrl}/SecurityGroups/${p.groupId}/SecurityScopes/${p.scopeId}??organizationId=:ORGID:`;
	const queryUrl = (p) => `${baseUrl}/SecurityGroups/${p.groupId}/SecurityScopes?organizationId=:ORGID:`;


	// model data type
	const model = 'securityScope';
	// possible relationships
	const rel = [

	];

	// generate default endpoints
	const get = ssApiSvc.endpoint({url, method: 'GET'}, model, rel);
	const query = ssApiSvc.endpoint({url: queryUrl, method: 'GET'}, model, rel);
	const add = ssApiSvc.endpoint({url: queryUrl, method: 'POST'}, model, rel);


	// API endpoint methods
	return {
		get, query, add
	};

// #-- END API Endpoint (ssSecurityScopeApi) --# //
// #-------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssSecurityScopeApi', securityScopeApi);
