/*@ngInject*/
function seiUserAccessApi($http, ssApiSvc, buildConfig) {
// #-------------------------------------------# //
// #---- API Endpoint (ssSeiUserAccessApi) ----# //

	const baseUrl = buildConfig.endpoint.seiUserAccess;
	const configurationPath = 'api/gd/configuration';
	const registerPath = 'api/gd/register';

	function configuration(orgPersonId) {
		return $http.get(`${baseUrl}/${configurationPath}/${orgPersonId}`);
	}

	function register(orgPersonId) {
		return $http.get(`${baseUrl}/${registerPath}/${orgPersonId}`);
	}

	// API endpoint methods
	return {
		configuration,
		register
	};

// #-- END API Endpoint (ssSeiUserAccessApi) --# //
// #-------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssSeiUserAccessApi', seiUserAccessApi);
