/*@ngInject*/
function router($stateProvider, $urlRouterProvider) {

	// base state to allow for preloader
	$stateProvider.state('base', {
		url: '/:module',
		template: '<ui-view></ui-view>',
		resolve: {
			features: /*@ngInject*/ (ssFeatureSvc) => ssFeatureSvc.ready,
			permissions: /*@ngInject*/ (ssPermissionsSvc) => ssPermissionsSvc.ready,
			locations: /*@ngInject*/ (ssLocationSvc) => ssLocationSvc.ready,
			departments: /*@ngInject*/ (ssDepartmentSvc) => ssDepartmentSvc.ready,
			jobTypes: /*@ngInject*/ (ssJobTypeSvc) => ssJobTypeSvc.ready,
		}
	});
}

/*@ngInject*/
function moduleChangeDetection($window, $rootScope, $q, $http, $location, rAF, buildConfig, ssEventSvc, ssFeatureSvc) {

	$rootScope.$on('$destroy', $rootScope.$on('$locationChangeStart', function (ev, toUrl, fromUrl) {
		var toModule = getModuleFromUrl(toUrl);
		var fromModule = getModuleFromUrl(fromUrl);

		if (ev.defaultPrevented) { return; }

		ssEventSvc.emit('location-change-start', ev, toUrl, fromUrl);

		if (toModule !== fromModule) {
			ssEventSvc.emit('ui-module-change-start', toModule, fromModule);
		}
	}));

	$rootScope.$on('$destroy', $rootScope.$on('$locationChangeSuccess', function (ev, toUrl, fromUrl) {
		var toModule = getModuleFromUrl(toUrl);
		var fromModule = getModuleFromUrl(fromUrl);

		$window.ss.events.fire('route-change', toUrl, fromUrl);
		ssEventSvc.emit('location-change-success', ev, toUrl, fromUrl);

		if (toModule !== fromModule) {
			ssEventSvc.emit('ui-module-change-success', toModule, fromModule);
		}
	}));
}

/*@ngInject*/
function syncAppLocationChanges($rootScope, $location, ssEventSvc) {
	// when our location changes we need to trigger a digest on all apps so they
	// can pick up the change. We also want to emit the highlightedMenu event
	// so out navigation will highlight the current route.
	$rootScope.$on('$destroy', $rootScope.$on('$locationChangeSuccess', function (ev, toUrl, fromUrl) {
		ssEventSvc.emit('ss-url-changed', toUrl, fromUrl);
	}));

	ssEventSvc.on('ss-url-changed', function (toUrl, fromUrl) {
		$rootScope.$applyAsync(function () {
			$rootScope.$emit('setHighlightedMenu');
		});
	}, $rootScope);
}

/*@ngInject*/
function handleRedirectionRoutes($rootScope, $log, $state, ssObjectHlp) {
	const {glean} = ssObjectHlp;
	$rootScope.$on('$stateChangeStart', (evt, toState, toParams, fromState, fromParams, options) => {
		// "redirectToState" can be a dotted state path (i.e. base.employee.personal)
		// or it can be an object with attributes for "state" and "params" where the
		// params attr has the stateParams object. by default, the existing stateParams
		// will be passed on to the substate, setting inherit to false in the
		// "redirectToState" config object will prevent this.
		if (toState.redirectToState != null) {
			evt.preventDefault();
			const rts = toState.redirectToState;
			const state = glean(rts, x => rts.state, rts);
			const params = glean(rts, x => rts.params, {});
			const inheritParams = glean(rts, x=>rts.inherit !== false, true);
			if (typeof state !== 'string') {
				$log.error('INVALID ROUTE redirectToState value:', rts);
			}
			const newParams = (inheritParams ? Object.assign(toParams, params) : params);
			$state.transitionTo(state, newParams, {location: 'replace'});
		}

	});
}

function getModuleFromUrl(url) {
	var path = url.substr(url.indexOf(location.host) + location.host.length);
	var match = path.match(/^\/([\w-]*)/i);
	if (match.length === 2) {
		return match[1];
	} else {
		return '';
	}
}

angular.module('super-setup').run(moduleChangeDetection);
angular.module('super-suit').config(router)
	.run(syncAppLocationChanges)
	.run(handleRedirectionRoutes);
