import {
	Enums
}
from 'base-enum';

// #-------------------------# //
// #---- Enums (/gender) ----# //

class GenderEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
				name: 'Male',
				value: 0
			}, {
				name: 'Female',
				value: 1
			},
			{
				name: 'Non-Binary',
				value: 2
			}, {
				name: 'Decline to Answer',
				value: 3
			}
		];
		super(enums);
	}
}

// #---- End Enums (/gender) ----# //
// #-----------------------------# //

angular.module('super-suit-enums').factory('enumGender', () => new GenderEnum());
