import {Component} from 'base-component';
// #----------------------------------# //
// #---- Component (super-select) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=',
	label: '@',
	selected: '=?',
	isDisabled: '=?'
};

// directive definition object overrides
const config = {
	// transclude: true
};

/*@ngInject*/
function select() {

	class Select extends Component {
		/*@ngInject*/
		controller($scope) {
			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				label: $scope.label
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				updateSelect: function(selected){
					$scope.selected = selected;
				}
			};

			if($scope.selected == null || $scope.selected === undefined) {
				$scope.selected = $scope.model.options[0];
				$scope.$watch('selected', val => {
					$scope.model.selected = val;
				});
			}
		}

		link(scope,elem,attrs) {
			//watch the label attribute for changes
			attrs.$observe('label', val => {
				scope.vm.label = val;
			});
		}
	}

	return new Select(props, 'select', config);
}

// #-- END Component (super-select) --# //
// #----------------------------------# //

angular.module('super-suit-components').directive('superSelect', select);
