// #-----------------------------------------# //
// #---- Helper (ssValidationHlp) ----# //

/*@ngInject*/
function ssValidationHlp(ssObjectHlp) {
	const {glean, isObject} = ssObjectHlp;

	// internal helpers

	const always = val => () => val;

	function capitalize(text){
		return text.charAt(0).toUpperCase() + text.substr(1);
	}

	/**
	 * The standard validation interface that always returns true if the test
	 * function passes, otherwise returning the error message. Glean is used
	 * internally to guarantee a measure of type safety.
	 * @private
	 * @param {*} val The value to validate
	 * @param {Function} test The test function which accepts a single argument, and returns a Boolean result
	 * @param {String} err The error message
	 * @return {true|String} This value will always be true, or else the error message
	 */
	function validate(val, test, err) {
		return glean(val, v => test(v) === true || capitalize(err), capitalize(err));
	}

	/**
	 * Compose a new function that runs a group of validation test functions in
	 * order, returning the first non-true result (in the event of a test
	 * failure), or else true (if all tests pass). This is conceptually similar
	 * to Promise.all, which rejects when any one promise is rejected, and
	 * resolves when all promises have been resolved.
	 * @private
	 *
	 * @example
	 * const requiredNum = all(required(), numeric());
	 * requiredNum()
	 * // => 'value is required'
	 * requiredNum('a')
	 * // => 'value must be a number'
	 * requiredNum('123')
	 * // => true
	 *
	 * @param  {...Function} fns - A validation test function with a signature that is consistent with the validation functions exported by this module
	 * @return {Function}        - A new function which, when called with a value, will call each function in fns sequentially with that value
	 */
	function all(...fns) {
		const allValidator = (...params) => {
			for (let i = 0; i < fns.length; i += 1) {
				if (typeof fns[i] !== 'function') {
					return `Invalid validator ${JSON.stringify(fns[i])}`;
				}

				let result = fns[i].apply(this, params);
				if (result === true) { continue; }

				return result;
			}

			return true;
		};

		return allValidator;
	}

	/**
	 * Creates the public interface for a validator factory with the given
	 * options and custom validators, which makes the opts param optional.
	 * @private
	 * @param {Function} validatorCreator The validator creator function around which to wrap the public interface
	 * @returns {Function} The wrapped validator creator function
	 */
	function prepare(validatorFactory) {
		return (opts = {}, customValidators = []) => (
			Array.isArray(opts)
			? validatorFactory({}, opts)
			: validatorFactory(opts, customValidators)
		);
	}

	/**
	 * Creates a coerced validator factory with the given validators, coercion
	 * strategy, and short-circuit test
	 * @private
	 * @param {Function[]|Function} validators A list of base validator functions, or a single validator function
	 * @param {Object} [opts={}] An optional configuration object for specifying variations on the default validation (defaults to an empty object)
	 * @param {Function[]} [customValidators=[]] An optional list of custom validator functions, which will be run after the base validation rules (defaults to an empty list)
	 * @param {Function} [coercionFn=toStr] An optional function by which to coerce the type of the value for all validators (defaults to toStr)
	 * @param {Function} [shortCircuitTest=isEmpty] An optional function by which to coerce the type of the value for all validators (defaults to isEmpty)
	 * @returns {Function}
	 */
	function castValidators(validators, opts = {}, customValidators = [], coercionFn = toStr, shortCircuitTest = isEmpty) {
		if (typeof validators === 'function') { validators = [validators]; }

		const allValidators = validators.concat(customValidators);

		return (
			opts.required === true
			? (val, fieldName = 'value') => required(allValidators).call(null, coercionFn(val), fieldName)
			: (val, fieldName = 'value') => shortCircuitTest(val) || all(...allValidators).call(null, coercionFn(val), fieldName)
		);
	}

	/**
	 * Same strict type checking implementation as used in the Sidekick
	 * @see src/sidekick/util/type-checkers.js
	 * @private
	 * @param {*} val The value to check
	 * @param {string} type The type to strictly check the value against
	 * @return {Boolean}
	 */
	function isOfType(val, type) {
		return Object.prototype.toString
			.call(val)      // => "[object Type]"
			.split(' ')[1]  // => "Type]"
			.slice(0, -1)   // => "Type"
			.toLowerCase() === type.toLowerCase(); // => "type === type"
	}



	// type coercion

	function toNum(val) {
		if (val == null) { return; }

		if (typeof val === 'string') {
			if (val.trim() === '') { return; }
			val = val.replace(/,/g, '');
			return Number(val);
		}

		if (typeof val === 'number') { return val; }

		return NaN;
	}

	function toStr(val) {
		return (val == null) ? '' : val.toString();
	}



	// base validation tests

	function isEmpty(val) {
		if (val == null) { return true; }
		if (typeof val === 'string' && val.trim() === '') { return true; }
		return false;
	}

	function isPopulated(val) {
		return !isEmpty(val);
	}

	function isNumeric(val) {
		return !isNaN(parseFloat(val)) && isFinite(val) || Math.abs(val) === Infinity;
	}

	function isRegExp(val) {
		return isOfType(val, 'regexp');
	}

	function isString(val) {
		return isOfType(val, 'string');
	}

	function isPojo(val) {
		return isOfType(val, 'object');
	}

	/**
	 * Test conformance of an object value with a provided schema of predicate
	 * functions, each of which is mapped to a property of the value to test.
	 * Inspired by lodash.conformsTo, except this supports nested schemae.
	 * @see https://lodash.com/docs/4.17.4#conformsTo
	 * @private
	 *
	 * @example
	 * const myValue = { foo: true, bar: 42, baz: { qux: 'wat' } }
	 * conformsTo(myValue, { foo: x => true });
	 * // true
	 * conformsTo(myValue, { foo: x => true, bar: x => x > 1000 })
	 * // false
	 * conformsTo(myValue, { baz: { qux: x => x != null && x !== '' } })
	 * // true
	 * conformsTo(myValue, { baz: { wat: x => x != null } })
	 * // false
	 *
	 * @param {*} val The value to check
	 * @param {Object} schema An object of predicate functions
	 * @return {Boolean}
	 */
	function conformsTo(val, schema) {
		for (let prop in schema) {
			if (isPojo(schema[prop])) {
				if (!isObject(val[prop])) { return false; }
				if (!conformsTo(val[prop], schema[prop])) { return false; }
			} else if (typeof schema[prop] === 'function') {
				if (val[prop] == null) { return false; }
				if (!schema[prop](val[prop])) { return false; }
			}
		}

		return true;
	}



	// validator wrappers

	/**
	 * Creates a required validator, which tests whether a value is not empty
	 * @protected
	 * @returns {Function} A standard validator wrapper function
	 */
	function requiredValidator() {
		return (val, fieldName = 'value') =>
			validate(val, isPopulated, `${capitalize(fieldName)} is required`);
	}

	/**
	 * Creates a numeric validator, which tests whether a value is a number
	 * @protected
	 * @returns {Function} A standard validator wrapper function
	 */
	function numValidator() {
		return (val, fieldName = 'value') =>
			validate(val, isNumeric, `${capitalize(fieldName)} must be a number`);
	}

	/**
	 * Creates a string validator, which tests whether a value is a string
	 * @protected
	 * @returns {Function} A standard validator wrapper function
	 */
	function strValidator() {
		return (val, fieldName = 'value') =>
			validate(val, isString, `${capitalize(fieldName)} must be a string`);
	}

	/**
	 * Creates an object validator, which tests whether a value is an object
	 * @protected
	 * @returns {Function} A standard validator wrapper function
	 */
	function objValidator() {
		return (val, fieldName = 'value') =>
			validate(val, isPojo, `${capitalize(fieldName)} must be an object`);
	}

	/**
	 * Creates an array validator, which tests whether a value is an array
	 * @protected
	 * @returns {Function} A standard validator wrapper function
	 */
	function arrayValidator() {
		return (val, fieldName = 'value') =>
			validate(val, Array.isArray, `${capitalize(fieldName)} must be an array`);
	}

	/**
	 * Creates an object validator, which tests whether an object conforms to a given schema
	 * @protected
	 * @returns {Function} A standard validator wrapper function
	 */
	function objSchemaValidator({ schema = {} } = {}) {
		return (val, fieldName = 'value') =>
			validate(val, v => conformsTo(v, schema), `${capitalize(fieldName)} is invalid`);
	}

	/**
	 * Creates a string length validator, which tests the min and/or max length
	 * of a string
	 * @protected
	 * @param {Object} opts A configuration object containing minLength and/or maxLength properties
	 * @param {Number} [opts.minLength=0] The lower extent of the string length to test
	 * @param {Number} [opts.maxLength=Infinity] The upper extent of the string length to test
	 * @returns {Function} A standard validator wrapper function
	 */
	function lengthValidator({ minLength = 0, maxLength = Infinity } = {}) {
		if (minLength < 0) { minLength = 0; }

		if (minLength > maxLength) {
			throw new RangeError(`The minLength value (given here as ${minLength}) cannot be greater than the maxLength value (given here as ${maxLength})`);
		}

		switch(true) {
			case minLength === 0 && maxLength === Infinity:
				return always(true);
			case minLength === 0 && maxLength > 0:
				return (val, fieldName = 'value') => validate(val,
					v => v.length <= maxLength,
					isString(val)
					? `${capitalize(fieldName)} must not exceed ${maxLength} character${maxLength === 1 ? '' : 's'} long`
					: `${capitalize(fieldName)} must not exceed ${maxLength} item${maxLength === 1 ? '' : 's'}`
				);
			case minLength > 0 && maxLength === Infinity:
				return (val, fieldName = 'value') => validate(val,
					v => v.length >= minLength,
					isString(val)
					? `${capitalize(fieldName)} must be at least ${minLength} character${minLength === 1 ? '' : 's'} long`
					: `${capitalize(fieldName)} must have at least ${minLength} item${minLength === 1 ? '' : 's'}`
				);
			case minLength > 0 && maxLength > 0:
				return (val, fieldName = 'value') => validate(val,
					v => v.length >= minLength && v.length <= maxLength,
					isString(val)
					? `${capitalize(fieldName)} must be ${minLength === maxLength ? `exactly ${minLength}` : `between ${minLength} and ${maxLength}`} characters long`
					: `${capitalize(fieldName)} must have ${minLength === maxLength ? `exactly ${minLength}` : `between ${minLength} and ${maxLength}`} items`
				);

		}

		return always(true);
	}

	/**
	 * Creates a string length validator, which tests the string against a RegExp pattern
	 * @protected
	 * @param {Object} opts A configuration object containing minLength and/or maxLength properties
	 * @param {RegExp} [opts.re] The RegExp pattern against which to test the string
	 * @returns {Function} A standard validator wrapper function
	 */
	function strPatternValidator({ re } = {}) {
		let {source, flags} = re;
		flags = flags.replace('g', '');
		re = new RegExp(source, flags);

		return (val, fieldName = 'value') =>
			validate(val, v => re.test(v), `Invalid ${fieldName}`);
	}

	/**
	 * Creates a range validator, which itself tests that a number falls within
	 * a range created from a min and max value (inclusive)
	 * @protected
	 * @param {Object} opts A configuration object containing min and max properties
	 * @param {Number} opts.min The lower extent of the range to test
	 * @param {Number} opts.max The upper extent of the range to test
	 * @returns {Function} A standard validator wrapper function
	 */
	function numRangeValidator({ min = -Infinity, max = Infinity } = {}) {
		if (min > max) {
			throw new RangeError(`The min value (given here as ${min}) cannot be greater than the max value (given here as ${max})`);
		}

		switch(true) {
			case min === -Infinity && max === Infinity:
				return always(true);
			case min === -Infinity:
				return (val, fieldName = 'value') => validate(val,
					v => v <= max,
					`${capitalize(fieldName)} must be a number less than or equal to ${max}`
				);
			case max === Infinity:
				return (val, fieldName = 'value') => validate(val,
					v => v >= min,
					`${capitalize(fieldName)} must be a number greater than or equal to ${min}`
				);
		}

		return (val, fieldName = 'value') => validate(val,
			v => v >= min && v <= max,
			`${capitalize(fieldName)} must be a number ${min === max ? `equal to ${min}` : `between ${min} and ${max}`}`
		);
	}



	// public functions

	/**
	 * Creates the public 'required' validator factory with the given options and custom validators
	 * @public
	 * @param {Object} [opts] An optional configuration object for specifying variations on the default validation
	 * @param {Array} [customValidators] An optional list of custom validator functions, which will be run after the base validation rules
	 * @returns {Function}
	 */
	const required = prepare((opts = {}, customValidators = []) => {
		return all(...[requiredValidator()].concat(customValidators));
	});

	/**
	 * Creates the public 'numeric' validator factory with the given options and custom validators
	 * @public
	 * @param {Object} [opts] An optional configuration object for specifying variations on the default validation
	 * @param {Array} [customValidators] An optional list of custom validator functions, which will be run after the base validation rules
	 * @returns {Function}
	 */
	const numeric = prepare((opts = {}, customValidators = []) => {
		const validators = [numValidator()];

		if (isNumeric(opts.min) || isNumeric(opts.max)) {
			validators.push(numRangeValidator(opts));
		}

		return castValidators(validators, opts, customValidators, toNum);
	});

	/**
	 * Creates the public 'string' validator factory with the given options and custom validators
	 * @public
	 * @param {Object} [opts] An optional configuration object for specifying variations on the default validation
	 * @param {Array} [customValidators] An optional list of custom validator functions, which will be run after the base validation rules
	 * @returns {Function}
	 */
	const string = prepare((opts = {}, customValidators = []) => {
		const validators = [strValidator()];

		if (isNumeric(opts.minLength) || isNumeric(opts.maxLength)) {
			validators.push(lengthValidator(opts));
		}

		if (isRegExp(opts.re)) {
			validators.push(strPatternValidator(opts));
		}

		return castValidators(validators, opts, customValidators, toStr);
	});

	/**
	 * Creates the public 'array' validator factory with the given options and custom validators
	 * @public
	 * @param {Object} [opts] An optional configuration object for specifying variations on the default validation
	 * @param {Object} [opts.every] A predicate function that every member of the array must satisfy
	 * @param {Object} [opts.some] A predicate function that at least one member of the array must satisfy
	 * @param {Array} [customValidators] An optional list of custom validator functions, which will be run after the base validation rules
	 * @returns {Function}
	 */
	const array = prepare((opts = {}, customValidators = []) => {
		const validators = [arrayValidator()];

		if (isNumeric(opts.minLength) || isNumeric(opts.maxLength)) {
			validators.push(lengthValidator(opts));
		}

		return castValidators(validators, opts, customValidators, x => x);
	});

	/**
	 * Creates the public 'object' validator factory with the given options and custom validators
	 * @public
	 * @param {Object} [opts] An optional configuration object for specifying variations on the default validation
	 * @param {Object} [opts.schema] A schema of matcher functions mapped by property
	 * @param {Array} [customValidators] An optional list of custom validator functions, which will be run after the base validation rules
	 * @returns {Function}
	 */
	const object = prepare((opts = {}, customValidators = []) => {
		const validators = [objValidator()];

		if (isPojo(opts.schema)) {
			validators.push(objSchemaValidator(opts));
		}

		return castValidators(validators, opts, customValidators, x => x);
	});



	// module exports

	return {

		required,
		numeric,
		string,
		array,
		object,

		// useful preset string validations
		email: prepare((opts = {}, custom = []) => (val, fieldName = 'email') =>
			string(Object.assign({}, {
				re: /^[A-Z0-9."_%'+-]+@[A-Z0-9.-]+\.[A-Z]{2,63}$/i
			}, opts), custom)(val, fieldName)
		),
		zip: prepare((opts = {}, custom = []) => (val, fieldName = 'zip code') =>
			string(Object.assign({}, {
				re: /^\d{5}(-\d{4})?$/
			}, opts), custom)(val, fieldName)
		),
		ssn: prepare((opts = {}, custom = []) => (val, fieldName = 'SSN') =>
			string(Object.assign({}, {
				re: /^(\d{9}|\d{3}-\d{2}-\d{4})$/
			}, opts), custom)(val, fieldName)
		),
		phone: prepare((opts = {}, custom = []) => (val, fieldName = 'phone number') =>
			string(Object.assign({}, {
				re: /^\d{3}-?\d{3}-?\d{4}$/
			}, opts), custom)(val, fieldName)
		),
		url: prepare((opts = {}, custom = []) => (val, fieldName = 'url') =>
			string(Object.assign({}, {
				re: /^((cc|http|https):\/\/|w{3}\.)(([a-zA-Z0-9\-]+\.)+[a-zA-Z]{2,63}(:[0-9]{4})?(\/|\/[\w#!:.?+=&%@!\-\/]*)*)$/
			}, opts), custom)(val, fieldName)
		),

	};
}
// #-- END Helper (ssValidationHlp) --# //
// #-----------------------------------------# //

angular.module('super-suit-helpers').factory('ssValidationHlp', ssValidationHlp);
