import {Enums} from 'base-enum';

// #----------------------------------# //
// #---- Enums (/medicalCategory) ----# //

class MedicalCategoryEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'General Health',
			value: 0,
			properties: {
				permission: 'filesMedicalGeneralHealth',
				description: 'Fitness for Duty Examination, Drug Test Results, Freedom from Communicable Diseases'
			}
		}, {
			name: 'Medical Leave',
			value: 1,
			properties: {
				permission: 'filesMedicalLeave',
				description: 'Leave Requests, FMLA Designation Notices, Medical Certifications'
			}
		}, {
			name: 'Medical Benefits',
			value: 2,
			properties: {
				permission: 'filesMedicalBenefits',
				description: 'Benefits Enrollment, Wellness Plan Agreements, COBRA Notices'
			}
		}, {
			name: 'Workers\' Compensation',
			value: 3,
			properties: {
				permission: 'filesMedicalWorkersComp',
				description: 'Claims, Return to Work Reports, Reasonable Accommodation Requests'
			}
		}, {
			name: 'Other Documents',
			value: 4,
			properties: {
				permission: 'filesMedicalOther'
			}
		}];
		super(enums);
	}
}

// #---- End Enums (/medicalCategory) ----# //
// #--------------------------------------# //

angular.module('super-suit-enums').factory('enumMedicalCategory', () => new MedicalCategoryEnum());

