/*@ngInject*/
function ssDepartmentSvc(ssAsync, ssDepartmentsApi, ssActiveUserSvc, ssServiceLogicHlp, ssConfigurationHlp, ssStorage) {
// #-----------------------------------# //
// #---- Service (ssDepartmentSvc) ----# //

	// list of IDM AppIds that use the microservice
	const usingDepartmentMsvc = ssServiceLogicHlp.productIdList([
		'EC'
	]);

	var departments = [];

	const ready = (ssAsync(function* () {
		try {
			const orgId = yield ssActiveUserSvc.getOrgId();
			if (orgId == null) { return []; }

			// resolve with nothing if this product doesn't use the departmentSvc
			const pId = yield ssActiveUserSvc.getProductId();
			if (!usingDepartmentMsvc.includes(pId)) { return []; }

			departments = ssStorage.load(`${orgId}_departments`);

			if (departments == null) {
				departments = (yield ssDepartmentsApi.query({
					limit: ssConfigurationHlp.getLimit('departments')
				})).map(function (dep) {
					const obj = dep.$serialize().data.attributes;
					Object.keys(obj).forEach(function (attr) {
						obj[`_${attr}`] = obj[attr];
						delete obj[attr];
					});

					return Object.assign({id: dep.id}, obj);
				});
				ssStorage.save(`${orgId}_departments`, departments);
			}

			return departments;
		} catch (e) {
			/* eslint-disable no-console */
			if (console && console.error) {
				console.error('ERROR', e);
			}
			/* eslint-enable no-console */
		}
	})());


	// External service API
	return {
		getDepartmentById,
		getAll,
		ready
	};

	function getDepartmentById(id) {
		return departments.filter(d => d.id === id)[0];
	}

	function getAll() {
		return departments;
	}

// #-- END Service (ssDepartmentSvc) --# //
// #-----------------------------------# //
}

angular.module('super-suit-services').factory('ssDepartmentSvc', ssDepartmentSvc);
