/*@ngInject*/
function plmEemApi($http, $q, buildConfig, ssActiveUserSvc) {
// #------------------------------------# //
// #---- API Endpoint (ssPlmEemApi) ----# //
	// base endpoint url
	const baseUrl = buildConfig.endpoint.plmEem;

	/**
	 * @description Obtain a list of all active districts from PLM-EEM.  This endpoint requires the IdmAdministrator Role.
	 */
	function getDistrictList () {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/District/menu_list`
		});
	}

	/**
	 * @description Obtain a list of all PLM-EEM externalIds for a given districtId.  This endpoint requires the IdmAdministrator Role.
	 */
	function getDistrictUserIdentifiers(districtId, userIdentifierType){
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/District/${districtId}/useridentifiers?identifiertype=${userIdentifierType}`
		});
	}

	function migrateFcpContentToLcr(districtId, testMode){
		return $http({
			method: 'POST',
			url: `${baseUrl}/v1/ContentMigration/migratecontentfromfcptolcr/${districtId}?testmode=${testMode}`
		});
	}

	function putFileInS3Bucket(file){
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/aws/presignedartifactimports3url?fileType=${file.type}&fileKey=${file.name}`
		}).then(
			function (response){
				var defer = $q.defer();
				$http({
					method: 'PUT',
					url: response.data,
					noAuth: true,
					noFlUser: true,
					headers: {
						'content-type': file.type
					},
					uploadEventHandlers: {
						progress: function(e) {
							defer.notify(e.loaded * 100 / e.total);
						}
					},
					data: file
				})
				.then(defer.resolve.bind(defer), defer.reject.bind(defer));
				return defer.promise;
			}
		);
	}

	function getBuildings() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Building')
            }));
	}

	function getDepartments() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Department')
            }));
	}

	function getGrades() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Grade')
            }));
	}

	function getCourseCodes() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'CourseCode')
            }));
	}

	function getCategories() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Category')
            }));
	}

	function getProviders() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Provider')
            }));
	}

	function getPurposes() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Purpose')
            }));
	}

	function getActivityFormats() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'ActivityFormat')
            }));
	}

	function getPrograms() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Program')
            }));
	}

	function getRegistrationForms() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'RegistrationForms')
            }));
	}

	function getObjectives() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Objective')
            }));
	}

	function getStandards() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Standard')
            }));
	}

	function getEvalForms() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'EvaluationForm')
            }));
	}

	function getEvents() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Event')
            }));
	}

	function getPdeCategories() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'PdeCategory')
            }));
	}

	function getLearningPlansByActivity(activityId) {
		if (!activityId) {
			return $q.reject('activityId is empty');
		}

        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, `activity/${activityId}/learningplans`)
            }));
	}

	function searchUsers(searchFilter, pageNumber, pageSize) {
		if (!searchFilter) {
			return $q.reject('searchFilter is empty');
		}

		return ssActiveUserSvc
			.getOrgId()
			.then(orgId => $http({
				method: 'POST',
				url: getApiOrgUrl(baseUrl, orgId, `user/search?pageNumber=${pageNumber}&pageSize=${pageSize}`),
				data: searchFilter
			}));
	}

	function searchDistricts(searchFilter, pageNumber, pageSize){
		if (!searchFilter) {
			return $q.reject('searchFilter is empty');
		}
		
		return $http({
			method: 'POST',
			url: `${baseUrl}/v1/District/search?pageNumber=${pageNumber}&pageSize=${pageSize}`,
			data: searchFilter
		});
	}


	/**
	 * @description Checks if user has the Add/Edit User rights in PLM
	 */
	function getUserRightCheck(adminRightId) {
		if (!adminRightId) {
			return $q.reject('adminRightId is empty');
		}

        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, `user/right/check/${adminRightId}`)
            }));
	}

	function getActivity(id) {
		if (!id) {
			return $q.reject('id is empty');
		}

        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, `activity/${id}`)
            }));
	}

	function getGroups() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'Group')
            }));
	}

	function getUserInformationForm() {
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, 'UserInformationForm')
            }));
	}

	function getActivityForm(formId = null, activityId = null) {
		const query = (activityId !== null) ? `activityId=${activityId}` : `formId=${formId}`;
        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
                url: getApiOrgUrl(baseUrl, orgId, `ActivityForm?${query}`)
            }));
	}

	function saveUser(user) {
		if (!user) {
			return $q.reject('user data is empty');
		}

        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'POST',
				url: getApiOrgUrl(baseUrl, orgId, 'User'),
				data: user
            }));
	}

	function getUser(orgPersonId) {
		if (!orgPersonId) {
			return $q.reject('orgPersonId is empty');
		}

        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'GET',
				url: getApiOrgUrl(baseUrl, orgId, `User/${orgPersonId}`)
            }));
	}

	function patchUser(orgPersonId, patchData) {
		if (!orgPersonId) {
			return $q.reject('orgPersonId is empty');
		}

		if (!patchData) {
			return $q.reject('patchData is empty');
		}

        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'PATCH',
				url: getApiOrgUrl(baseUrl, orgId, `User/${orgPersonId}`),
				data: patchData
            }));
	}

	function postActivity(activity) {
		if (!activity) {
			return $q.reject('activity is empty');
		}

        return ssActiveUserSvc
            .getOrgId()
            .then(orgId => $http({
                method: 'POST',
				url: getApiOrgUrl(baseUrl, orgId, `Activity`),
				data: activity
            }));
	}

	function patchActivity(activityId, patchData) {
		if (!activityId) {
			return $q.reject('activityId is empty');
		}

		if (!patchData) {
			return $q.reject('patchData is empty');
		}

		return ssActiveUserSvc
			.getOrgId()
			.then(orgId => $http({
				method: 'PATCH',
				url: getApiOrgUrl(baseUrl, orgId, `Activity/${activityId}`),
				data: patchData
			}));
	}

	function postContent(content){
		return ssActiveUserSvc
			.getOrgId()
			.then(orgId => $http({
				method: 'POST',
				url: getApiOrgUrl(baseUrl, orgId, 'content'),
				data: content
			}));
	}

	function patchContent(id, patchData){
		if (!id) {
			return $q.reject('contentId is empty');
		}

		if (!patchData) {
			return $q.reject('patchData is empty');
		}

		return ssActiveUserSvc
			.getOrgId()
			.then(orgId => $http({
				method: 'PATCH',
				url: getApiOrgUrl(baseUrl, orgId, `content/${id}`),
				data: patchData
			}));
	}

	function searchContent(contentNumbers){
		return ssActiveUserSvc
			.getOrgId()
			.then(orgId => $http({
				method: 'POST',
				url: getApiOrgUrl(baseUrl, orgId, 'content/search'),
				data: contentNumbers
			}));
	}

	function getProductAuth() {
        return $http({
			method: 'GET',
			url: `${baseUrl}/v1/config/productauth`
        });
	}

	function getApiOrgUrl(url, orgId, path) {
		return `${url}/v1/org/${orgId}/${path}`;
	}

	// API endpoint methods
	return {
		getDistrictList,
		getBuildings,
		getDepartments,
		getGrades,
		getCategories,
		getCourseCodes,
		getProviders,
		getGroups,
		getPurposes,
		getActivityFormats,
		getPrograms,
		getRegistrationForms,
		getUserInformationForm,
		getActivityForm,
		postActivity,
		patchActivity,
		getDistrictUserIdentifiers,
		putFileInS3Bucket,
		getUserRightCheck,
		saveUser,
		getUser,
		patchUser,
		getProductAuth,
		migrateFcpContentToLcr,
		getActivity,
		getStandards,
		getObjectives,
		getEvalForms,
		getEvents,
		getPdeCategories,
		getLearningPlansByActivity,
		searchUsers,
		searchDistricts,
		searchContent,
		patchContent,
		postContent
	};

// #-- END API Endpoint (ssPlmEemApi) --# //
// #------------------------------------# //
}

angular.module('super-suit-apis').factory('ssPlmEemApi', plmEemApi);
