/*@ngInject*/
function employeeApi($http, $window, $q, ssAuthSvc, ssApiSvc, ssApiResponseHlp, ssActiveUserSvc, buildConfig, ssCredentialsApi, ssEffectiveDateHlp, ssObjectHlp) {
	// #--------------------------------------# //
	// #---- API Endpoint (ssEmployeeApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.employees;
	const convertToValidatingSave = ssEffectiveDateHlp.convertToValidatingSave;

	// EMPLOYEE ENDPOINTS
	const url = (p) => `${baseUrl}/employees/${p.id}`;
	const addFileToEmployeeUrl = (p) => url(p) + '/files';
	const queryUrl = `${baseUrl}/organizations/:ORGID:/employees`;
	const queryByIdsUrl = `${baseUrl}/employees/ids?organizationId=:ORGID:`;
	const credentialsUrl = (p) => `${baseUrl}/employees/${p.flId}/credentials`;
	const employeeAccess = () => `${baseUrl}/organizations/:ORGID:/employeeaccess`;
	const searchUrl = `${baseUrl}/organizations/:ORGID:/employees/search`;

	const upsertOrgPersonUrl = (p) => {
		return `${baseUrl}/employees/${p.employeeId}/UpsertOrgPerson`;
	};

	// FILE ENDPOINTS
	const baseFileRecordUrl = (p) => {
		return `${baseUrl}/employees/${p.id}/fileRecords?organizationId=:ORGID:`;
	};
	const getUrlUrl = (p) => {
		return `${baseUrl}/employees/${p.id}/fileRecords/${p.fileId}/url?organizationId=:ORGID:`;
	};
	const uploadFileUrl = (p) => {
		return `${baseUrl}/employees/${p.id}/fileRecords/${p.fileId}`;
	};

	/*** DEPRECATED ***/
	/*** Can remove after bulk invite features are release ***/
	const productAccessUrl = p => `${baseUrl}/employees/${p.id}/productAccess?disabled=${p.disabled}`;

	/*** DEPRECATED ***/
	/*** Can remove after bulk invite features are release ***/
	const invitationUrl = p => `${baseUrl}/employees/${p.id}/invitation`;

	// product access urls
	const userAccessUrl = `${baseUrl}/employees/userstatus`;
	const revokeUrl = p => `${baseUrl}/employees/${p.id}/productAccess`;
	const moveToEmpRecordUrl = p => `${baseUrl}/employees/${p.employeeId}/formInstances`;
	const userAccountCredentialsUrl = (p) => `${baseUrl}/credentials/${p.flId}`;
	const createFromOrgPersonIdUrl = (orgPersonId) => `${baseUrl}/employees/fromOrgPerson/${orgPersonId}`;

	const employeeExportUrl = `${baseUrl}/employees/ids/csv`;

	// model data type
	const model = 'employees';
	// possible relationships
	const rel = [
		'phoneNumbers',
		'addresses',
		'emails',
		'positions',
		'statusHistory',
		'emergencyContacts',
		'jobTypes',
		'assignments',
		'locations',
		'employmentHistory',
		'endorsements',
		'paySchedules',
		'departments',
		'files',
		'formInstances',
		'supervisors'
	];

	// generate default endpoints
	const get = convertToValidatingSave(ssApiSvc.endpoint({ url, method: 'GET' }, model, rel));
	const query = convertToValidatingSave(ssApiSvc.endpoint({ url: queryUrl, method: 'GET' }, model, rel));
	const add = convertToValidatingSave(ssApiSvc.endpoint({ url: queryUrl, method: 'POST' }, model, rel));
	const search = convertToValidatingSave(ssApiSvc.endpoint({ url: searchUrl, method: 'POST' }, model, rel));

	const queryByIds = convertToValidatingSave(ssApiSvc.endpoint({url: queryByIdsUrl, method: 'POST'}, model, rel));

	const upsertOrgPerson = ssApiSvc.endpoint({url: upsertOrgPersonUrl, method: 'PATCH' }, model, rel);

	/**
	 * Gets the templates
	 * @param {*} params
	 */
	function getSsn(employeeId) {
		return api({ method: 'GET', url: url({ id: employeeId }), params: { fields: 'sSN', sensitiveFields: 'ssn' } }).then(res => {
			return ssObjectHlp.glean(res, _ => _.data.data.attributes.sSN, '');
		});
	}

	// Application Access without permission checks
	const getEmployeeAccess = ssApiSvc.endpoint({ url: employeeAccess, method: 'GET' }, model, rel);

	// The credentialsApi is for getting creds at the org level, but we need the model and rel info for managing employee level selections
	const getCredentials = ssApiSvc.endpoint({ url: credentialsUrl, method: 'GET' }, ssCredentialsApi.config.model, ssCredentialsApi.config.rel);

	// ** file urls **
	//	add file to employee
	const getFile = convertToValidatingSave(ssApiSvc.endpoint({ url, method: 'GET' }, model, rel));
	const getFileUrlById = convertToValidatingSave(ssApiSvc.endpoint({ url: getUrlUrl, method: 'GET' }, model, rel));
	const preAddFile = convertToValidatingSave(ssApiSvc.endpoint({ url: baseFileRecordUrl, method: 'POST' }, model, rel));
	const addFileId = convertToValidatingSave(ssApiSvc.endpoint({ url: addFileToEmployeeUrl, method: 'POST' }, model, rel));
	const queryFile = convertToValidatingSave(ssApiSvc.endpoint({ url: queryByIdsUrl, method: 'POST' }, model, rel));
	//	add file to employee record
	const addFileToRecord = convertToValidatingSave(ssApiSvc.endpoint({ url: moveToEmpRecordUrl, method: 'POST' }, model, rel));
	//	Create Employee From OrgPersonId (acts as select (GET) on subsequent calls
	const createFromOrgPersonId = ssApiSvc.endpoint({ url: createFromOrgPersonIdUrl, method: 'POST' }, model, rel);

	//  export employee directory
	const exportEmployee = (body) => {
		let config = {
			url: `${queryUrl}/csv`,
			method: 'POST',
			data: body
		};
		return api(config);
	};

	// START product access endpoints
	// ******************************
	const getUserAccountCredentials = ssApiSvc.endpoint({ url: userAccountCredentialsUrl, method: 'GET' }, model, rel);
	const exportEmployeesById = convertToValidatingSave(ssApiSvc.endpoint({ url: employeeExportUrl, method: 'POST' }, model, rel));

	const invite = (a, b) => {
		if (angular.isArray(a)) {
			let invitations = a;

			return $http({
				url: `${baseUrl}/employees/invitations`,
				method: 'POST',
				data: {
					data: {
						attributes: invitations
					}
				}
			});
		} else {
			/*** DEPRECATED ***/
			/*** Can remove after bulk invite features are release ***/
			let id = a;
			let email = b;

			const loc = $window.location;
			return $http({
				url: invitationUrl({ id }),
				method: 'POST',
				data: {
					data: {
						attributes: {
							email: email,
							returnUrl: `${loc.protocol}//${loc.hostname}/${buildConfig.defaultModule}/`
						}
					}
				}
			});
		}
	};

	const revoke = id => {
		return $http({
			url: revokeUrl({ id }),
			method: 'DELETE'
		});
	};

	/*** DEPRECATED ***/
	/*** Can remove after bulk invite features are release ***/
	const patchSingle = data => {
		return $http({
			url: productAccessUrl(data),
			method: 'PATCH'
		});
	};

	const patch = users => {
		return $http({
			url: userAccessUrl,
			method: 'PATCH',
			data: {
				data: {
					attributes: users
				}
			}
		});
	};

	/*** DEPRECATED ***/
	/*** Can remove after bulk invite features are release ***/
	const suspend = id => {
		return patchSingle({
			id,
			disabled: true
		});
	};

	const deactivate = ids => {
		let users = ids.map(id => {
			return {
				id: id,
				enabled: false
			};
		});

		return patch(users);
	};

	const activate = (a) => {
		if (angular.isArray(a)) {
			let ids = a;

			let users = ids.map(id => {
				return {
					id: id,
					enabled: true
				};
			});

			return patch(users);
		} else {
			/*** DEPRECATED ***/
			/*** Can remove after bulk invite features are release ***/
			let id = a;

			return patchSingle({
				id,
				disabled: false
			});
		}
	};

	const productAccessStatus = () => {
		let config = {
			url: `${baseUrl}/organizations/${ssActiveUserSvc.getOrgIdSync()}/productaccess`,
			method: 'GET',
			params: {
				'productId': 'EC',
				limit: 999999
			}
		};

		return $http(config);
	};
	// ****************************
	// END product access endpoints

	// API endpoint methods
	return {
		get, query, add, search, queryByIds, getSsn, invite, suspend, deactivate, activate, revoke, productAccessStatus, getUserAccountCredentials,
		getFile, queryFile, addFileId, getFileUrlById, uploadFileUrl, preAddFile, getCredentials, addFileToRecord, exportEmployee,
		getEmployeeAccess, exportEmployeesById, createFromOrgPersonId, upsertOrgPerson
	};

	/**
	 * Base method all calls go through.
	 * @param {*} config
	 */
	function api(config) {
		var returnPromise = $q.when(ssAuthSvc.ready);

		// if the url uses the orgId, wait for it to be ready
		if (usesOrgId(config.url)) {
			returnPromise = returnPromise.then(() => ssActiveUserSvc.ready);
		}

		returnPromise = returnPromise.then(function () {
			const orgId = ssActiveUserSvc.getOrgIdSync();
			const urlNeedsOrgId = config.url.includes(':ORGID:');

			// if this url needs an orgId replacement, and we don't have an org context... abort!
			if (urlNeedsOrgId && orgId == null) { return; }
			// if this url needs an orgId, replace it
			if (urlNeedsOrgId) {
				config.url = config.url.replace(/:ORGID:/ig, orgId);
			}

			return $http(config)
				.then(res => (res.status >= 400 && res.status < 500 ? $q.reject(res) : res))
				.catch(res => {
					if (res.status === -1) { return; } // timeout or cancelled
					ssApiResponseHlp.handle4XX(res);
				});
		});

		return returnPromise;
	}

	/**
	 *
	 * @param {*} url
	 */
	function usesOrgId(url) {
		const str = (typeof url === 'function' ? url({}, {}) : url);
		if (typeof str === 'string') {
			return str.toLowerCase().includes(':orgid:');
		}
		return false;
	}

	// #-- END API Endpoint (ssEmployeeApi) --# //
	// #--------------------------------------# //
}

angular.module('super-suit-apis').factory('ssEmployeeApi', employeeApi);
