// #----------------------------# //
// #---- Helper (ssMaskHlp) ----# //

// https://www.npmjs.com/package/ng-mask

const masks = {};

/*@ngInject*/
function ssMaskHlp() {

	// External service API
	return {
		getMaskedValue, getOptions
	};

	function getMaskedValue(name, value) {
		if (masks[name] == null) { return value; }
		return masks[name].svc.getViewValue(value).withDivisors();
	}

	function getOptions(name) {
		if (masks[name] == null) { return null; }
		return masks[name].options;
	}
}
// #-- END Helper (ssMaskHlp) --# //
// #----------------------------# //

/*@ngInject*/
function maskDataHookup(MaskService) {
	/**
	 * Phone mask
	 */
	register('phone', {
		mask: '(999) 999-9999',
		clean: 'true',
		restrict: 'reject',
	});

	/**
	 * SSN mask
	 */
	register('ssn', {
		mask: '999-99-9999',
		clean: 'true',
		restrict: 'reject',
	});

	/**
	 * Register a new mask
	 *
	 * All boolean values are passed in as strings
	 * @param {*} options
	 * @param {string} options.mask -
	 * @param {number} options.repeat - repeat mask expression n times
	 * @param {string} options.clean - clean model value - without divisors 'true' or 'false'
	 * @param {string} options.limit - limit length based on mask length
	 * @param {string} options.restrict - how to act with a wrong value
	 * @param {string} options.validate - set validity mask
	 * @param {*} options.model - default model value
	 * @param {*} options.value - default input value
	 */
	function register(name, options) {
		const svc = MaskService.create();
		return svc.generateRegex(options).then(() => {
			masks[name] = { svc, options };
		});
	}
}

angular.module('super-suit').run(maskDataHookup);
angular.module('super-suit-helpers').factory('ssMaskHlp', ssMaskHlp);
