// #------------------------------# //
// #---- Helper (ssObjectHlp) ----# //
// Array
const {isArray} = Array;
// Object or Array
const isMutable = o => (o !== null && typeof o === 'object');
// Object NOT an Array
const isObject = o => (isMutable(o) && !isArray(o));

/*@ngInject*/
function ssObjectHlp() { return {

	// extend an object with properties from another object only when they
	// don't already exist on the target object
	passiveExtend(target, source) {
		Object.keys(source).forEach(function(key) {
			if (typeof target[key] === 'undefined') {
				target[key] = source[key];
			}
		});

		return target;
	},

	// generic type helpers
	isArray, isMutable, isObject,

	// basic shallow copy
	copy,

	// copy all nested objects and arrays as well
	deepCopy,

	// freeze a an object in place so it cannot be changed
	// all references to this object will be frozen
	deepFreeze,

	// update values in target with corresponding values in source only if
	// both objects have that same property
	update,

	/**
	 * glean - Access nested paths in an object whos structure we are uncertain of.
	 * Unholy marriage of Facebook's existential function and lodash get()
	 * https://facebook.github.io/react-native/blog/2017/03/13/idx-the-existential-function.html
	 * https://lodash.com/docs#get
	 *
	 * @param  {Object} source            the source object we are operating on
	 * @param  {Function} accessor        attepts to access the property desired
	 * @param  {Any} defaultValue = null  value to return if the accessor throws
	 * @return {Any}
	 */
	glean(source, accessor, defaultValue = null) {
		try {
			const val = accessor(source);
			if (typeof val === 'undefined') { return defaultValue; }
			return val;
		} catch(err) {
			return defaultValue;
		}
	}

};}

function deepCopy(src, stripInternal = true) {
	const dest = copy(src, stripInternal);

	// Retrieve the property names defined on src
	var propNames = Object.getOwnPropertyNames(dest);

	for (let name of propNames) {
		const propVal = dest[name];
		// Recurse copy of propVal if it is an Object or Array
		if (isMutable(propVal)) {
			dest[name] = deepCopy(propVal, stripInternal);
		}
	}

	return dest;
}

function copy(src, stripInternal = true) {
	const dest = (isArray(src) ? [] : {});
	Object.assign(dest, src);

	if (stripInternal) {
		const props = Object.getOwnPropertyNames(dest);
		var i = props.length;
		for (; i-- ;) {
			if (props[i].startsWith('$')) {
				delete dest[props[i]];
			}
		}
	}

	return dest;
}

function deepFreeze(src) {
	// Retrieve the property names defined on src
	var propNames = Object.getOwnPropertyNames(src);

	// Freeze properties before freezing self
	for (let name of propNames) {
		const propVal = src[name];
		// Freeze propVal if it is an object
		if (isMutable(propVal)) {
			src[name] = deepFreeze(propVal);
		}
	}

	// Freeze self (no-op if already frozen)
	return Object.freeze(src);
}

function update(target, source) {
	Object.keys(target).forEach(function (key) {
		if (source[key] == null) { return; }
		if (isObject(source[key]) && isObject(target[key])) {
			update(target[key], source[key]);
			return;
		}
		if (!isMutable(source[key])) {
			target[key] = source[key];
		}
		// Arrays are intentionally not handled here
		// because we can't reliably merge 2 arrays together
	});

	return target;
}


// #-- END Helper (ssObjectHlp) --# //
// #------------------------------# //

angular.module('super-suit-helpers').factory('ssObjectHlp', ssObjectHlp);
