/* globals sidekick */

/*@ngInject*/
function hookupIdm($location, ssAsync, ssAuthSvc, ssOrganizationSvc, buildConfig, ssEventSvc, ssAppConfigSvc, ssActiveUserSvc, $window, ssStorage) {
	const {store, events} = sidekick;
	const setupIdm = ssAsync(function*(product) {

		// If url contains universal, redirect to use the last stored productId; If none, use EC if user has access, otherwise use whatever is first in access list
		store.onValueOnce('idm-data', (idmData) => {
			const pId = idmData.productId;
			if(pId.toLowerCase() === 'universal'){
				let storedProductId = ssStorage.load('productId');
				if (!storedProductId) {
					if (idmData.access.find((org) => org.ProductId.toLowerCase() === 'ec') != null) {
						storedProductId = 'ec';
					} else {
						storedProductId = idmData.access[0].ProductId;
					}
				}

				let foundProductId = Object.keys(buildConfig.productMap) // Reverse map the buildConfig.productMap
					.find( (prodProductId) => (buildConfig.productMap[prodProductId].toLowerCase() === storedProductId.toLowerCase()));
				if(foundProductId == null) {
					foundProductId = storedProductId;
				}
				const newUrl = `/${foundProductId.toLowerCase()}${$window.location.href.slice($window.location.origin.length+10)}`;
				window.location.replace(newUrl);
			} else{
				ssStorage.save('productId', pId, {
					expires: null,
					persistent: true
				});
			}
		});


		/////
		// set the initial IDM data so the sidekick can handle it
		//
		const {currentOrg, orgId} = $location.search();
		var flId = yield ssAuthSvc.profileId();

		// internal product test
		const flInternalProducts = store.get('fl-internal-product-ids') || [];
		const isInternalProduct = flInternalProducts.includes(product.toLowerCase());

		// if we are impersonation a user, change the flId used to init the
		// sidekick to the impersonated user's flId. Also, don't allow
		// impersonation for internal products like internaltools
		const impersonationId = yield ssAuthSvc.isImpersonating();
		if (impersonationId != null && !isInternalProduct) {
			flId = impersonationId;
			events.emit('header.impersonation', true);
			events.on('header.end-impersonation', ssAuthSvc.endImpersonate, {terminal: true});
		}

		// handle impersonation v2
		const isImpersonatingV2 = yield ssAuthSvc.isImpersonatingV2();

		store.set('idm', {
			idGatewayUrl: buildConfig.idmGatewayUrl,
			getAccessToken: () => ssAuthSvc.token.access,
			flId, product, currentOrg: currentOrg || orgId, isImpersonatingV2
		});

		// if the page was loaded via the idm internal tools, remove the org querystring
		if (currentOrg) { $location.search('currentOrg', null); }

		///////
		// once the orgSvc gets the full org object for the current orgSvc
		// put that data back into the sidekick for rendering
		//
		const org = yield ssOrganizationSvc.getActiveOrg();

		// if this ui-module uses an external sidekick script, defer to that.
		ssAppConfigSvc.getAsync('init').catch(() => store.set('current-org', {
			id: org.id,
			name: org._name,
			phone: org.formattedPhone,
			email: org._email,
			fax: org.formattedFax
		}));

	});

	events.on('idm.no-access', user => ssEventSvc.emit('error-no-access', user));
	ssActiveUserSvc.getProductId().then(setupIdm);
}

angular.module('super').run(hookupIdm);
