/*@ngInject*/
function permissionProfilesApi(ssApiSvc, buildConfig) {
// #------------------------------------------------# //
// #---- API Endpoint (ssPermissionProfilesApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.permissions;
	const url = `${baseUrl}/PermissionProfiles`;
	const duplicateUrl = (p) => `${baseUrl}/PermissionProfiles/${p.id}/duplicate`;
	const queryUrl = `${url}?organizationId=:ORGID:`;
	const searchUrl = `${url}/Search?organizationId=:ORGID:`;
	// model data type
	const model = 'permissionProfiles';
	// possible relationships
	const rel = [
		'permissionSets'
	];

	// generate default endpoints

	const get = ssApiSvc.endpoint({url, method: 'GET'}, model, rel);
	const query = ssApiSvc.endpoint({url: queryUrl, method: 'GET'}, model, rel);
	const add = ssApiSvc.endpoint({url: queryUrl, method: 'POST'}, model, rel);
	const duplicate = ssApiSvc.endpoint({url: duplicateUrl, method: 'POST'}, model, rel);
	const search = ssApiSvc.endpoint({ url: searchUrl, method: 'GET' }, model, rel);

	// API endpoint methods
	return {
		get, query, add, duplicate, search
	};

// #-- END API Endpoint (ssPermissionProfilesApi) --# //
// #------------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssPermissionProfilesApi', permissionProfilesApi);
