// #-----------------------------# //
// #---- Helper (ssToastHlp) ----# //

/**
 * @module SuperSuit.ToastHelper
 * @memberOf SuperSuit
 */

var toastrInst;

/*@ngInject*/
function ssToastHlp(toastr, toastrConfig, $rootScope, $templateCache) {
	/////
	// prevent multiple toastr instances
	//
	if (toastrInst == null) { toastrInst = toastr; }

	const configOptions = {
		error: {
			preventOpenDuplicates: true,
			timeOut: 15000,
			extendedTimeout: 10000
		},
		success: {
			preventOpenDuplicates: true,
			timeOut: 10000,
			extendedTimeout: 10000
		},
		warn: {
			preventOpenDuplicates: true,
			timeOut: 10000,
			extendedTimeOut: 10000
		},
		info: {
			preventOpenDuplicates: true,
			timeOut: 3000,
			extendedTimeOut: 3000
		},
		notification: {
			iconClass: 'toast-notification',
			allowHtml: true
		}
	};

	const configDefaults = {
		progressBar: true,
		tapToDismiss: true,
		closeButton: true
	};

	Object.keys(configOptions).forEach(k => Object.assign(configOptions[k], configDefaults));

	const messagingDefaults = {
		successTitle: 'Success',
		successMessage: 'Saved successfully!',
		failureTitle: 'Failure',
		failureMessage: 'Action failed',
		connectionFailed: 'Could not contact service.',
		infoTitle: 'Did you know?',
		warnTitle: 'Warning'
	};

	// Set the global defaults for toasts.
	toastrConfig.preventOpenDuplicates = true;

	/**
	 * Template file for standard toasts.
	 */
	$templateCache.put('directives/toast/toast.html',
		'<div class="{{toastClass}} {{toastType}}" ng-click="tapToast()" role="alert">' +
		'<div ng-switch on="allowHtml">' +
		'<div ng-switch-default ng-if="title" class="{{titleClass}}" aria-label="{{title}}">{{title}}</div>' +
		'<div ng-switch-default class="{{messageClass}}" aria-label="{{message}}">{{message}}</div>' +
		'<div ng-switch-when="true" ng-if="title" class="{{titleClass}}" ng-bind-html="title"></div>' +
		'<div ng-switch-when="true" class="{{messageClass}}" ng-bind-html="message"></div>' +
		'</div>'
	);

	/**
	 * Template file for standard progresss bars
	 */
	$templateCache.put('directives/progressbar/progressbar.html',
		'<div>' +
			'<progress-bar ng-if="progressBar"></progress-bar>' +
		'</div>'
	);

	/**
	 * Gets all of the errors from the response.
	 * Does not take into account delimited messages. for POST March
	 * @private
	 * @param {Object} error The response from the AJAX call
	 * @param {Object} error.data The data from the response
	 * @param {String} error.data.message Another place the server sends errors to ¯\_(ツ)_/¯
	 * @param {Object} error.data.errors The error from the server response
	 * @param {String} error.data.errors.Title The delimited string containing all of the error responses from the AJAX call
	 */
	function grabAjaxMessages(error) {
		// what? no error object!? default!
		if (error == null) { return [messagingDefaults.failureMessage]; }

		// no data, no internet...
		if (error.data == null) { return [messagingDefaults.connectionFailed]; }

		// Yes, server errors are buried in this hierarchy then concatenated in the title
		if (error.data.errors && error.data.errors.Title) {
			// server generated message collection
			return [error.data.errors.Title];
		} else if (error.data.message) {
			// generic http status message
			return [error.data.message];
		}

		// default message if we were unable to parse
		return [messagingDefaults.failureMessage];
	}

	/**
	 * Grabs errors/messages from the caller and parses them into a format tha can be used by the notifier.
	 * If nothing can be found, just return the default error messaging.
	 * @private
	 * @param messages The list of errors to process and toast
	 * @returns Array An array of errors to toast
	 */
	function grabMessages(messages) {
		if (typeof messages === 'string') { return [messages]; }
		if (messages instanceof Array) { return messages; }
		//> log('Sorry, I can\'t find the messages you wanted me to display. Try string arrays! I like them. Here is what you sent: ', errors)
		return [messagingDefaults.failureMessage];
	}

	return {

		/**
		 * Specialized endpoint for toasting to AJAX errors (ssApi and ssJsonApi) since the repsonse object generally
		 * has as special format and hierarchy.
		 * @instance
		 *
		 * @param {(Array|String)} errors - The server response that contains messages about what happened.
		 * @param {String} [title=default failure title] - The title for the toast notification
		 */
		errorAjax: function(errors, title) {
			title = title || messagingDefaults.failureTitle;
			// ensure that errors is an array of strings
			grabAjaxMessages(errors).forEach(function(error) {
				//> log('configOptions.error', configOptions.error)
				toastrInst.error(error, title, configOptions.error);
			});
		},

		/**
		 * Show a toast message with the error formatting.
		 * @instance
		 * @param {(Array|String)} errors - An array of errors to display
		 * @param {String} [title=default failure title] - The title for the toast notification
		 */
		error: function(errors, title) {
			title = title || messagingDefaults.failureTitle;
			// ensure that errors is an array of strings
			grabMessages(errors).forEach(function(error) {
				//> log('configOptions.error', configOptions.error)
				toastrInst.error(error, title, configOptions.error);
			});
		},

		/**
		 * Show a toast message with the warn formatting.
		 * @instance
		 * @param {(Array|String)} errors - The errors that we are going to toast.
		 * @param {String} [title=default warn title] - The title for the toast notification
		 */
		warn: function(errors, title) {
			title = title || messagingDefaults.warnTitle;

			grabMessages(errors).forEach(function(error) {
				toastrInst.warning(error, title, configOptions.warn);
			});
		},

		/**
		 * Show a toast message with the information formatting.
		 * @instance
		 * @param {(Array|String)} errors - An array of errors to display
		 * @param {String} [title] - The title for the toast notification
		 */
		info: function(messages, title) {
			grabMessages(messages).forEach(function(msg) {
				toastrInst.info(msg, title, configOptions.info);
			});
		},

		/**
		 * Show an info toast message for the notifications center.
		 * @instance
		 * @param {(Array|String)} messages - An array of messages to display
		 * @param {String} [title] - The title for the toast notification
		 */
		notification: function(message, title) {
			toastrInst.info(message, title, configOptions.notification);
		},

		/**
		 * Show a toast message with the success formatting.
		 * @instance
		 * @param {(Array|String)} errors - An array of errors to display
		 * @param {String} [title=default success title] - The title for the toast notification
		 * @param {Boolean} persist Sets the toast to disappear or fade away
		 */
		success: function(messages, title, persist) {
			// ensure that messages is an array of strings
			title = title || messagingDefaults.successTitle;
			if (typeof messages === typeof '') { messages = [messages]; }

			var opts = (persist === true) ? { timeOut: 0, extendedTimeOut: 0 } : {};
			opts = Object.assign({}, configOptions.success, opts);

			messages.forEach(function(msg) {
				toastrInst.success(msg, title, opts);
			});
		},
		/**
		 * Clear any open toast notifications.
		 * @instance
		 */
		clear: function() {
			toastrInst.clear();
		}
	};
}
// #-- END Helper (ssToastHlp) --# //
// #-----------------------------# //

angular.module('super-suit-helpers').factory('ssToastHlp', ssToastHlp);
