function ssWindowAnchorLogic() {
	return {
		/**
		 * Given an element and an anchor point (px), return the intended detach point based on the element's position relative to the viewport
		 * @param {Object} element DOM element within a scrolling context
		 * @param {Number} anchorPoint Pixel value
		 * @return {Number}
		 */
		getDetachPoint(element, anchorPoint) {
			const intendedDetachPoint = Math.floor(element.getBoundingClientRect().top) - anchorPoint;
			return intendedDetachPoint <= anchorPoint ? anchorPoint : intendedDetachPoint;
		},

		/**
		 * Given an element and a detach point (px), return the offset based on the element's scrollY or scrollTop value
		 * @param {Object} scrollElement DOM element that provides a scrolling context
		 * @param {Number} detachPoint Pixel value
		 * @return {Number}
		 */
		getDetachPointOffset(scrollElement, detachPoint) {
			const scrollPos = scrollElement.scrollY || scrollElement.scrollTop || 0;
			return scrollPos - detachPoint;
		}
	};
}

angular.module('super-suit-decorators').factory('ssWindowAnchorLogic', ssWindowAnchorLogic);
