/*@ngInject*/
function ssOrgDataSvc(ssActiveUserSvc, ssDeprecated) {
// #--------------------------------# //
// #---- Service (ssOrgDataSvc) ----# //

	var _orgId;
	ssActiveUserSvc.getOrgId().then(oId => _orgId = oId);

	const orgData = {
		orgReady: ssActiveUserSvc.ready.then(
			ssDeprecated('ssOrgDataSvc.orgReady', 'ssActiveUserSvc.ready', noop => noop)
		)
	};

	Object.defineProperty(orgData, 'orgId', {
		get: ssDeprecated('ssOrgDataSvc.orgId', 'ssActiveUserSvc.getOrgIdSync()', () => _orgId)
	});

	// External service API
	return orgData;


// #-- END Service (ssOrgDataSvc) --# //
// #--------------------------------# //
}

angular.module('super-suit-services').factory('ssOrgDataSvc', ssOrgDataSvc);
