import {
	Decorator
}
from 'base-decorator';
// #-------------------------------# //
// #---- Decorator (ss-toggle) ----# //

/*@ngInject*/
function ssToggle($document) {

	class Toggle extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {
			const activeClass = 'ss-toggle-open';
			const inActiveClass = 'ss-toggle-closed';
			const activeIcon = 'fa-caret-down';
			const inActiveIcon = 'fa-caret-right';
			const target = attrs.ssToggle || 'dropdown';
			var ddElem = getDdElem();

			elem.addClass(inActiveIcon);
			elem.addClass(inActiveClass);
			elem.addClass('ss-toggle');

			if (ddElem.length) {
				ddElem.addClass(inActiveClass);
				ddElem.addClass('ss-toggle-target');
			}

			if (attrs.ssToggleStartExpanded != null && attrs.ssToggleStartExpanded !== 'false') {
				toggleState();
			}

			elem.on('click', function(e) {
				if (angular.element(e.target.parentElement).hasClass('is-disabled')) {
					return;
				}

				toggleState();
			});

			function toggleState(toActive) {
				if (toActive == null) {
					toActive = elem.hasClass(inActiveClass);
				}
				if (!ddElem.length) {
					ddElem = getDdElem();
					if (!ddElem.length) {
						return;
					}
				}

				const oldClass = toActive ? inActiveClass : activeClass;
				const newClass = toActive ? activeClass : inActiveClass;

				elem.addClass(toActive ? activeIcon : inActiveIcon);
				elem.removeClass(toActive ? inActiveIcon : activeIcon);

				elem.addClass(newClass);
				ddElem.addClass(newClass);

				elem.removeClass(oldClass);
				ddElem.removeClass(oldClass);
			}

			function getDdElem() {
				if (ddElem != null && ddElem.length) {
					return ddElem;
				}
				var dd = elem[0].getElementsByClassName(target);
				if (dd.length === 0) {
					dd = elem.parent()[0].getElementsByClassName(target);
				}
				dd = angular.element(dd);
				if (dd.length === 0) {
					return dd;
				}

				dd.addClass(inActiveClass);
				return dd;
			}
		}
	}

	return new Toggle();
}

// #-- END Decorator (ss-toggle) --# //
// #-------------------------------# //

angular.module('super-suit-decorators').directive('ssToggle', ssToggle);
