/*@ngInject*/
function interceptAuth(ssAuthSvc) {
	function addAuthToken(config) {
		if (config.noAuth) { return config; }
		if (ssAuthSvc.token.access && ssAuthSvc.token.access.length > 0) {
			config.headers.Authorization = `Bearer ${ssAuthSvc.token.access}`;
		}
		return config;
	}

	return {
		request: addAuthToken
	};
}

angular.module('super-suit').factory('interceptAuth', interceptAuth);
