const models = [];
var firstLoad = true;

/*@ngInject*/
function ssSaveInterrupterSvc($rootScope, $state, $window, $location, ssModalSvc, ssEventSvc) {
	// #----------------------------------------# //
	// #---- Service (ssSaveInterrupterSvc) ----# //

	load();

	if (firstLoad) {
		firstLoad = false;
		loadOnce();
	}

	// External service API
	return { clear, isDirty, register, showModal, unregister };

	/**
	 * Sets up these event listeners tied to the scope of which app started it
	 */
	function load() {
		// listens for UI Module internal state changes
		$rootScope.$on('$destroy', $rootScope.$on('$stateChangeStart', (event, toState, toParams) => {
			if (!isDirty($state.href(toState, toParams))) { return; }
			// prevent going to new state
			event.preventDefault();
			showModal(() => {
				$state.go(toState, toParams);
			});
		}));

		// listens for cross UI Module changes
		ssEventSvc.on('location-change-start', (event, toUrl) => {
			if (!isDirty(toUrl) || event.defaultPrevented) { return; }
			// prevent going to new state
			event.preventDefault();
			showModal(() => {
				$window.location = toUrl;
			});
		}, $rootScope);

		// clear any current registers if we successfully change state
		$rootScope.$on('$destroy', $rootScope.$on('$stateChangeSuccess', (event, toState, toParams) => {
			if (!isDirty($state.href(toState, toParams))) { return; }
			clear();
		}));

		// clear any current registers if we successfully change location
		$rootScope.$on('$destroy', $rootScope.$on('$locationChangeSuccess', (event, toUrl) => {
			if (!isDirty(toUrl)) { return; }
			clear();
		}));

		// listens for cross UI Module changes
		ssEventSvc.on('location-change-success', (event, toUrl) => {
			if (!isDirty(toUrl)) { return; }
			clear();
		}, $rootScope);
	}

	/**
	 * Sets up these event listeners once
	 */
	function loadOnce() {
		// listens to browser events (reload, back, etc)
		$window.addEventListener('beforeunload', e => {
			if (!isDirty()) { return; }
			clear();
			// message doesn't matter; browsers don't support custom messages.
			var msg = 'You have unsaved changes. Are you sure you want to leave?';
			e.returnValue = msg; // Gecko, Trident, Chrome 34+
			return msg; // Gecko, WebKit, Chrome <34
		});
	}

	/**
	 * Clears the models reference
	 */
	function clear() { models.length = 0; }

	/**
	 * Add a model to the models array with an optional list of field names / relations to look for.
	 *
	 * @param {Object} mod
	 * @param {String[]} [fields=[]]
	 * @param {String} [url=$location.url()]
	 *
	 * @return {Boolean} Returns true if the model was added. False if the model was already present.
	 */
	function register(mod, {fields = [], url = $location.url()} = {}) { // jshint ignore:line
		var index;
		if (mod.id && mod.id.length) {
			index = models.find(m => m.mod.id === mod.id);
			// if found, return
			if (index != null) { return false; }
		} else {
			index = models.findIndex(model => model === mod);
			// if found, return
			if (index >= 0) { return false; }
		}

		// if we're here, add to the array
		models.push({mod, fields, url}); // jshint ignore:line

		return true;
	}

	/**
	 * Removes a model from the models array
	 *
	 * @param {Object} mod
	 *
	 * @return {Boolean} Returns true if the model was removed. False if the model was not found.
	 */
	function unregister(mod) {
		var index = -1;
		if (mod.id && mod.id.length) {
			index = models.find(m => m.mod.id === mod.id);
			// if not found, return
			if (index == null) { return false; }
		} else {
			index = models.findIndex(model => model === mod);
			// if not found, return
			if (index < 0) { return false; }
		}
		models.splice(index, 1);
		return true;
	}

	/**
	 * Determines if the the models are dirty. Dirty is defined as being a new
	 * model (does not have $isDirty) or JsonApiModel.$isDirty /
	 * JsonApiCollection.$isDirty returns true.
	 *
	 * @return {Boolean} Returns true if dirty, false if not.
	 */
	function isDirty(toUrl = '') {

		// no models so we're clean
		if (models.length === 0) { return false; }

		var model;

		// are any models dirty?
		for (var i = 0; i < models.length; i++) {
			model = models[i].mod;

			if (toUrl.startsWith($location.protocol())) {
				// 8 is https:// length
				toUrl = toUrl.substring(toUrl.indexOf('/', 8));
			}

			if (toUrl.startsWith(models[i].url)) { continue; }

			// it is not a JsonApiModel/JsonApiCollection object or is dirty
			if (model.$isDirty == null || model.$isDirty({fields: models[i].fields})) {
				// returns true if we found a dirty model; don't need to continue checking
				return true;
			}
		}

		// we're clean
		return false;
	}

	/**
	 * Show Unsaved Changes modal
	 *
	 * @param {Function} [primary=function() {}]
	 */
	function showModal(primary = () => {}) {
		ssModalSvc.showDestructive({
			title: 'You have unsaved changes. Are you sure you want to leave?',
			primaryText: 'Leave Without Saving',
			secondaryText: 'Return to Page',
			alwaysOnTop: true
		}).then(() => {
			// primary
			clear();
			if (typeof primary === 'function') { primary(); }
		}, () => {
			// secondary, do nothing
		});
	}

	// #-- END Service (ssSaveInterrupterSvc) --# //
	// #----------------------------------------# //
}

angular.module('super-suit-services').factory('ssSaveInterrupterSvc', ssSaveInterrupterSvc);
