import {Component} from 'base-component';

// #----------------------------------# //
// #---- Component (super-loader) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	isLoading: '=',
	statusText: '=?',
	coverColor: '@',
	fill: '@'
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function loader(ssElemHlp) {

	class Loader extends Component {
		/*@ngInject*/
		controller($scope, rAF) {
			// view model for tracking state
			const vm = $scope.vm = {
				loading: false,
				hidden: false,
				coverStyles: {},
				fill: false
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				click(evt) {}
			};

			$scope.$watch('isLoading', newVal => {
				rAF(() => vm.loading = newVal, $scope);
				vm.hidden = !newVal;
			});
		}

		link(scope, elem, attrs) {
			scope.vm.fill = attrs.fill != null;

			if (scope.coverColor != null) {
				scope.vm.coverStyles['background-color'] = scope.coverColor;
			} else {
				scope.vm.coverStyles['background-color'] = ssElemHlp.getBackgroundColor(elem);
			}
		}
	}

	return new Loader(props, 'loader', config);
}

// #-- END Component (super-loader) --# //
// #----------------------------------# //

angular.module('super-suit-components').directive('superLoader', loader);
