import {Enums} from 'base-enum';

// #------------------------------------# //
// #---- Enums (/personnelCategory) ----# //

class PersonnelCategoryEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Pre-Employment',
			value: 0,
			properties: {
				permission: 'filesPersonnelPreEmployment',
				description: 'Examples: Applications, resumes, references, verifications of prior experience, background checks, disability status, equal employment opportunity, reporting, etc.'
			}
		}, {
			name: 'Employment Authorization',
			value: 1,
			properties: {
				permission: 'filesPersonnelEmploymentAuth',
				description: 'I-9 Employment Eligibility Verification Form, E-Verify Confirmation (SSN), Driver’s License, Social Security Card'
			}
		}, {
			name: 'Contracts & Notices',
			value: 2,
			properties: {
				permission: 'filesPersonnelContracts',
				description: 'Job Offers/Letters of Intent, Recommendation to Hire Form, Temporary Agreements, Supplemental Pay/Extra-Curricular Pay Agreements, Contract Adjustments'
			}
		}, {
			name: 'Evaluations',
			value: 9,
			properties: {
				permission: 'filesPersonnelEvaluations',
				description: 'Walk-Throughs, Formative Assessments, Summative Assessments, Evaluation Artifacts, etc.'
			}
		}, {
			name: 'Payroll',
			value: 3,
			properties: {
				permission: 'filesPersonnelPayroll',
				description: 'Federal and State W-4, Direct Deposit, Pay Schedule Agreement'
			}
		}, {
			name: 'Employment / Status Change',
			value: 4,
			properties: {
				permission: 'filesPersonnelEmploymentStatus',
				description: 'Transfers, Promotions, Demotions, Formal Separation, Notices, Status Change Forms'
			}

		}, {
			name: 'Policy Acknowledgements',
			value: 5,
			properties: {
				permission: 'filesPersonnelPolicy',
				description: 'Job Descriptions, IT Network Usage, Policy Agreement, Compliance, Training Verifications'
			}
		}, {
			name: 'Transcripts / Credentials',
			value: 6,
			properties: {
				permission: 'filesPersonnelTranscripts',
				description: 'Certifications, Licenses, Endorsements, Transcripts'
			}
		}, {
			name: 'Discipline',
			value: 7,
			properties: {
				permission: 'filesPersonnelDiscipline',
				description: 'Letters of Reprimand, Hearing Notices, Investigations, Suspensions, Terminations/Non-Renewal Notices, Formal Separation Agreements'
			}
		}, {
			name: 'Other Documents',
			value: 8,
			properties: {
				permission: 'filesPersonnelOther'
			}
		}];
		super(enums);
	}
}

// #---- End Enums (/personnelCategory) ----# //
// #----------------------------------------# //

angular.module('super-suit-enums').factory('enumPersonnelCategory', () => new PersonnelCategoryEnum());
