const localCache = {};
const cacheTypes = {};
const keyPrefix = 'SUPER';

// do an initial spin of localStorage and sessionStorage to load
// their values into the localCache object for fast access
for (let i = (Modernizr.localstorage ? window.localStorage.length : 0); i-- ;) {
	let k = window.localStorage.key(i);
	if (!k.startsWith(`${keyPrefix}_`)) { continue; }
	cacheTypes[k] = 'localStorage';
	localCache[k] = window.localStorage.getItem(k);
}
// spin over sessionStorage
for (let i = (Modernizr.sessionstorage ? window.sessionStorage.length : 0); i-- ;) {
	let k = window.sessionStorage.key(i);
	if (!k.startsWith(`${keyPrefix}_`)) { continue; }
	cacheTypes[k] = 'sessionStorage';
	localCache[k] = window.sessionStorage.getItem(k);
}

/*@ngInject*/
function ssStorage($log, ssJsonApi, ssActiveUserSvc) {
// #-----------------------------# //
// #---- Utility (ssStorage) ----# //
	const MAX_DATASET_SIZE_BYTES = 1024 * 2000; // ~kinda sorta 2MB

	var activeProfileId;
	ssActiveUserSvc.getProfileId().then(pId => activeProfileId = pId);

	function saveToStorage(key, data, options = {}) {
		const persistent = options.persistent === true;	// should this data span mutliple sessions
		const noExpire = options.expires === null;		// should this data not expire

		// minutes until persistent data expires (defaults to 24 hours)
		const expires = (typeof options.expires === 'number') ? options.expires : 1440;

		// if persistent is requested but not supported
		// then fallback to sessionStorage
		let storeType = (persistent && Modernizr.localstorage) ? 'localStorage' : 'sessionStorage';

		// I can haz key?
		if (typeof key !== 'string') { $log.error('ssStorage.save => key is required'); return; }
		// I can haz datas?
		if (data == null) { $log.error('ssStorage.save => data is required'); return; }
		if (isJsonApi(data)) { $log.error('ssStorage.save => JsonApi data not supported', key); return; }

		// prefix the key
		key = `${getPrefix()}_${key}`;

		// wrap and serialize the data with meta-data
		data = JSON.stringify({
			expires: (noExpire ? null : new Date((new Date()).getTime() + (expires * 60000))),
			data: data
		});

		// once a key is set with data, the storage type should
		// not be changed. this would cause sync problems
		if (cacheTypes[key] && cacheTypes[key] !== storeType) {
			$log.warn('ssStorage.save => type for ' + key + ' can not change once set');
			storeType = cacheTypes[key];
		}

		// cache the data before we save it
		localCache[key] = data;

		// We are only caching data if it is less than the predifined max size
		if (byteLength(data) < MAX_DATASET_SIZE_BYTES) {
			if (Modernizr[storeType.toLowerCase()]) {
				// set storage type
				cacheTypes[key] = storeType;
				// send to storage
				window[storeType].setItem(key, data);
			}
		}

		return JSON.parse(data).data;
	}

	function loadFromStorage(key) {
		const now = (new Date()).getTime();
		let expires = null;
		let metaWrapper;

		if (typeof key !== 'string') { $log.error('ssStorage.load => storage key is required'); }

		// prefix the key
		key = `${getPrefix()}_${key}`;

		metaWrapper = JSON.parse(
			localCache[key] ||						// check local cache
			window.sessionStorage.getItem(key) ||	// check sessionStorage
			window.localStorage.getItem(key) ||		// check localStorage
			'null'									// fallback to null
		);

		if (metaWrapper && metaWrapper.expires != null) {
			expires = (new Date(metaWrapper.expires)).getTime();
		}

		// check if this has expired yet
		if (expires && now >= expires) {
			clearFromStorage(key);
			// tell the caller that we didn't find anything
			return null;
		}

		// parse the data and return
		return metaWrapper && metaWrapper.data || null;
	}

	function clearFromStorage(key) {
		// prefix the key if the prefixed version was not passed
		if (!key.startsWith(`${keyPrefix}_`)) {
			if (localCache[`${getPrefix()}_${key}`] != null) {
				key = `${getPrefix()}_${key}`;
			} else if (localCache[`${keyPrefix}_${key}`] != null) {
				key = `${keyPrefix}_${key}`;
			}
		}

		// delete from localCache
		delete localCache[key];
		delete cacheTypes[key];

		// remove from all storage
		window.localStorage.removeItem(key);
		window.sessionStorage.removeItem(key);
	}

	function clearAllFromStorage() {
		var idx, key;

		idx = Modernizr.localstorage && window.localStorage.length || 0;
		for (; idx-- ;) {
			key = window.localStorage.key(idx);
			if (key.startsWith(`${keyPrefix}_`)) { clearFromStorage(key); }
		}

		idx = Modernizr.sessionstorage && window.sessionStorage.length || 0;
		for (; idx-- ;) {
			key = window.sessionStorage.key(idx);
			if (key.startsWith(`${keyPrefix}_`)) { clearFromStorage(key); }
		}
	}

	function isJsonApi(obj) {
		return (
			obj instanceof ssJsonApi.Model
		) || (
			obj.$$constructor &&
			obj.$$constructor instanceof ssJsonApi.Collection
		);
	}

	function getPrefix() {
		var prefix = keyPrefix;
		if (activeProfileId != null) { prefix += `_${activeProfileId}`; }
		return prefix;
	}

	/**
	 * A rough approximation of how big some object is in memory.  Doesn't need to be super
	 * accurate, just enough to apply some rough logic based on the size of something.
	 * Use case: Getting all JobTypes returns a 20MB list.  This is too large for session
	 * 	storage.  We can calculate the size and make a determination about where to put
	 * 	the data for reuse.
	 * From:
	 * https://stackoverflow.com/questions/5515869/string-length-in-bytes-in-javascript
	 * @param {string} str The string that needs its size calculated
	 */
	function byteLength(str) {
		// returns the byte length of an utf8 string
		if (str == null) {
			return 0;
		}
		var s = str.length;
		for (var i = str.length - 1; i >= 0; i--) {
			var code = str.charCodeAt(i);
			if (code > 0x7f && code <= 0x7ff) {
				s++;
			} else if (code > 0x7ff && code <= 0xffff) {
				s += 2;
			};
			if (code >= 0xDC00 && code <= 0xDFFF) {
				i--;
			} //trail surrogate
		}
		return s;
	}

	return {
		// exposedMethodName: internalMethodName
		load: loadFromStorage,
		save: saveToStorage,
		clear: clearFromStorage,
		clearAll: clearAllFromStorage
	};

// #-- END Utility (ssStorage) --# //
// #-----------------------------# //
}

angular.module('super-suit-utilities').factory('ssStorage', ssStorage);
