// #----------------------------------------------# //
// #---- Mutator (mutatorIFormLibraryObjects) ----# //

/*@ngInject*/
function iFormLibraryObjectsMutator(ssMutatorDataHlp) {
	return {
		'!statusObject': function(obj) {
			return ssMutatorDataHlp.getStatus(obj);
		},
		'status': function(obj) {
			if (obj.statusObject == null) {return;}
			return obj.statusObject.status;
		},
		'statusLabel': function(obj) {
			if (obj.statusObject == null) {return;}
			return obj.statusObject.statusLabel;
		}
	};
}

// #---- End Mutator (mutatorIFormLibraryObjects) ----# //
// #--------------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorIFormLibraryObjects', iFormLibraryObjectsMutator);
