// #------------------------------------# //
// #---- Helper (ssFluidImportsHlp) ----# //

/*@ngInject*/
function ssFluidImportsHlp($window, $log, ssStringHlp) {

	return {
		hookupFluidComponents
	};

	function hookupFluidComponents(ngModule, cmptMap) {
		Object.entries(cmptMap).forEach(([name, def]) => {
			switch (def.type) {
				case 'svelte3':
					hookupFluidComponentsSvelte3(ngModule, name, def.constructor);
					break;
				default:
					$log.error(`Unknown Fluid Import Type: "${def.type}"`);
					break;
			}
		});
	}

	function hookupFluidComponentsSvelte3(ngModule, name, construct) {
		const {toCamelCase, toDashCase} = ssStringHlp;
		const tmplVariation = name => `tmpl${name[0].toUpperCase()}${name.slice(1)}`;
		const isOnAttr = x => (/^on[A-Z]/).test(toCamelCase(x));

		// init an blank component to inspect its props
		const {props: propList} = (new construct({target: $window.document.createElement('span')})).$$;
		// user prop list to generate ng component bindings
		const bindings = propList.reduce((acc, n) => {
			acc[n] = '<';
			acc[tmplVariation(n)] = '@';
			return acc;
		}, {});
		const resolveData = data => propList.reduce((acc, prop) => {
			const value = (
				data[prop] !== undefined // check if normal prop exists
				? data[prop] // prefer normal prop if available
				: data[tmplVariation(prop)] // fallback to tmplVariation
			);

			// only use value if there is one
			if (value !== undefined) { acc[prop] = value; }
			return acc;
		}, {});

		ngModule.component(toCamelCase(name), {
			bindings,
			template: '',
			controller: ['$scope', '$element', '$attrs', function ($s, $e, $a) {
				const target = $e[0];
				const props = resolveData(this);
				const cmptInst = new construct({ target, props });
				const applyHandler = (applyFn, attr) => evtData => {
					applyFn.call($s, () => $s.$parent.$eval($a[attr], {$event: evtData}));
					if (!['onFocus', 'onBlur'].includes(attr)) {
						$s.$emit('ss-validate-field-event', $e, 'update');
					}
				};

				// listen for blur and focus events for ss-validate hooks
				cmptInst.$on('blur', () => $s.$emit('ss-validate-field-event', $e, 'blur'));
				cmptInst.$on('focus', () => $s.$emit('ss-validate-field-event', $e, 'focus'));

				Object.keys($a).filter(isOnAttr).forEach(a => {
					cmptInst.$on(toDashCase(a.slice(2)), applyHandler($s.$applyAsync, a));
				});

				this.$onChanges = function (changes) {
					const changesObj = Object.keys(changes)
						.filter(k => !changes[k].isFirstChange())
						.reduce((acc, k) => (acc[k] = changes[k].currentValue, acc), {});
					cmptInst.$set(resolveData(changesObj));
				};

				this.$onDestroy = () => cmptInst.$destroy();
			}]
		});
	}
}
// #-- END Helper (ssFluidImportsHlp) --# //
// #------------------------------------# //

angular.module('super-suit-helpers').factory('ssFluidImportsHlp', ssFluidImportsHlp);
