import {
	Enums
}
from 'base-enum';

// #---------------------------------# //
// #---- Enums (/flInviteStatus) ----# //

class FlInviteStatusEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'No Access',
			value: 0
		}, {
			name: 'Sent',
			value: 1
		}, {
			name: 'Active',
			value: 2
		}, {
			name: 'Inactive',
			value: 3
		}];
		super(enums);
	}
}

// #---- End Enums (/flInviteStatus) ----# //
// #-------------------------------------# //

angular.module('super-suit-enums').factory('enumFlInviteStatus', () => new FlInviteStatusEnum());
