// #------------------------------------# //
// #---- Mutator ({ camelCase name }}) ----# //

/*@ngInject*/
function positionsMutator($filter) { return {

	'location': function (pos) {
		var locationId = pos._locationId || null;
		if (locationId == null) { locationId = pos._location && pos._location.id || null; }
		if (locationId == null) { return; }
		return $filter('location')(locationId) || null;
	},

	'department': function (pos) {
		var departmentId = pos._departmentId || null;
		if (departmentId == null) { departmentId = pos._department && pos._department.id || null; }
		if (departmentId == null) { return; }
		return $filter('department')(departmentId) || null;
	},

	'jobType': function (pos) {
		var jobTypeId = pos._jobTypeId || null;
		if (jobTypeId == null) { jobTypeId = pos._jobType && pos._jobType.id || null; }
		if (jobTypeId == null) { return; }
		return $filter('jobType')(jobTypeId) || null;
	}

};}

// #---- End Mutator ({ camelCase name }}) ----# //
// #----------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorPositions', positionsMutator);
