import {Enums} from 'base-enum';

// #----------------------------# //
// #---- Enums (/emailType) ----# //

class EmailTypeEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Work',
			value: 0
		}, {
			name: 'Personal',
			value: 1
		}, {
			name: 'Other',
			value: 2
		}];
		super(enums);
	}
}

// #---- End Enums (/emailType) ----# //
// #--------------------------------# //

angular.module('super-suit-enums').factory('enumEmailType', () => new EmailTypeEnum());
