// #-----------------------------------# //
// #---- Helper (ssApiResponseHlp) ----# //

/*@ngInject*/
function ssApiResponseHlp(ssObjectHlp, ssToastHlp) {
	const DEFAULT_ERROR = 'Something went wrong. Please try again later.';
	const DEFAULT_VALIDATION = 'There was an error with one or more of the items you entered. Please double check your responses and try again.';
	const DEFAULT_ERROR_TITLE = 'System Error';

	return {
		_defaults: {
			DEFAULT_ERROR,
			DEFAULT_ERROR_TITLE,
			DEFAULT_VALIDATION,
		},

		handle4XX(res) {
			if (![417, 422].includes(res.status)) {
				ssToastHlp.errorAjax(res);
			}

			if (res.status === 417) {
				// We haz an error, and we will show the default error message.
				ssToastHlp.error(DEFAULT_ERROR, DEFAULT_ERROR_TITLE);
			} else if (res.status === 422) {
				// pass through the message because we know it is supposed to be a validation message,
				//	but only if we have a valid object to pull out the message
				// first check camelCase property; then check check TitleCase property; SVC team is aiming to not use TitleCase anymore.
				const errorTitle = ssObjectHlp.glean(res, _ => _.data.errors.title || _.data.errors.Title, false);
				if (errorTitle) {
					// Pass through the validation message to the client as something on the server
					//	has failed a validation check and we ASSUME that the server's message is safe for the user.
					ssToastHlp.error(errorTitle);
				} else {
					// we don't have the properties on the response object we are expecting, just give the default.
					ssToastHlp.error(DEFAULT_VALIDATION, DEFAULT_ERROR_TITLE);
				}
			}
		}
	};
}
// #-- END Helper (ssApiResponseHlp) --# //
// #-----------------------------------# //

angular.module('super-suit-helpers').factory('ssApiResponseHlp', ssApiResponseHlp);
