// #---------------------------------------# //
// #---- Mutator (mutatorPaySchedules) ----# //

/*@ngInject*/
function paySchedulesMutator(ssDateHlp) { return {

	// finds the default pay schedule on a job type and identifies it
	'isDefault': function (schedule) {
		return ssDateHlp.isForeverPast(schedule._fromDateUtc);
	}

};}

// #---- End Mutator (mutatorPaySchedules) ----# //
// #-------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorPaySchedules', paySchedulesMutator);
