import {Component} from 'base-component';
// #-----------------------------------------# //
// #---- Component (super-tab-list-item) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	state: '@',
	url: '@',
	label: '@',
	subLabel: '@?',
	params: '=?',
	paramsIgnoredForActive: '=?',
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function tabListItem() {

	class TabListItem extends Component {

		/*@ngInject*/
		controller($scope, $state, $stateParams, $element, ssPermissionsSvc) {

			// view model for tracking state
			const vm = $scope.vm = {
				params: Object.assign({}, $stateParams, $scope.params || {}),
				label: $scope.label,
				subLabel: $scope.subLabel || ''
			};

			// destroy the scope if this tab is not visible to the user
			let notPermittedRoute = $scope.state && !ssPermissionsSvc.isPermittedRoute($state.href($scope.state, vm.params));
			let notPermittedUrl = $scope.url && !ssPermissionsSvc.isPermittedRoute($scope.url);
			if (notPermittedRoute || notPermittedUrl) {
				$scope.$destroy();
			}

			// behavior that needs to be bound to the view
			const go = $scope.go = {
				testActiveState() {

					// first test if the state name is the current state name
					if ($state.is($scope.state)) {
						// if so then test params removing ignored

						let testParams = Object.assign({}, vm.params);

						if (angular.isArray($scope.paramsIgnoredForActive)) {
							$scope.paramsIgnoredForActive.forEach(p => {
								delete testParams[p];
							});
						}
						return $state.includes($scope.state, testParams);
					} else if ($scope.url != null) {
						return $scope.url.startsWith($state.href($state.current.name));
					}

					// otherwise test if state is child of link
					return $state.includes($scope.state);
				}
			};

			$scope.$onMany = (events, fn) => {
				events.forEach((event) => {
					$scope.$on(event, fn);
				});
			};

			$element.removeClass('active');
			$element.attr('aria-selected', false);
			if (go.testActiveState()) {
				$element.addClass('active');
				$element.attr('aria-selected', true);
			}

			$scope.$onMany(['$stateChangeSuccess', '$locationChangeSuccess'], (event, toState, toParams, fromState, fromParams) => {
				$element.removeClass('active');
				$element.attr('aria-selected', false);
				if (go.testActiveState()) {
					$element.addClass('active');
					$element.attr('aria-selected', true);
				}
			});


		}
	}

	return new TabListItem(props, 'tab-list-item', config);
}

// #-- END Component (super-tab-list-item) --# //
// #-----------------------------------------# //

angular.module('super-suit-components').directive('superTabListItem', tabListItem);
