import {Decorator} from 'base-decorator';
// #----------------------------------------------# //
// #---- Decorator (ss-multi-item-truncation) ----# //

/*@ngInject*/
function ssMultiItemTruncation() {

	class MultiItemTruncation extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {

			const parentElem = elem[0];
			const parentElemWidth = parentElem.offsetWidth;
			const children = angular.element(elem).children();
			const idealRatio = 100 / children.length;
			const itemMargin = 0.5;
			const defaultCss = {
				"display": 'inline-block',
				"vertical-align": "text-bottom",
			}

			// set the initial CSS props for each element
			Array.from(children).forEach((child, idx) => {
				// set margin on every item but the first
				if (idx !== 0) {
					Object.assign(defaultCss, {
						"margin-left": `${itemMargin}em`
					});
				}
				angular.element(child).css(defaultCss);
			});

			// run after the digest cycle and rendering have completed
			setTimeout(() => {
				// get the total width of all items
				const totalChildrenWidth = Array.from(children).reduce((acc, cur) => {
					return acc + cur.offsetWidth;
				}, 0);

				// If the parent element is wider than the total width of the children, do nothing
				if (parentElemWidth >= totalChildrenWidth) {
					return;
				}

				// convert the excess width to a percent
				const excessPercent = ((totalChildrenWidth - parentElemWidth) / parentElemWidth) * 100;
				// Adjust and set the width for any items that exceed the ideal width given the number of items
				Array.from(children).filter((child) => {
					return ((child.offsetWidth / parentElemWidth) * 100) > idealRatio;
				}).forEach((item, idx, arr) => {
					const widthPercentage = ((item.offsetWidth / parentElemWidth) * 100);
					const adjustedWidth = widthPercentage - (excessPercent / arr.length);
					angular.element(item).css({
						"white-space": "nowrap",
						"overflow-x": "hidden",
						"-o-text-overflow": "ellipsis",
						"text-overflow": "ellipsis",
						'width': `calc(${adjustedWidth}% - ${itemMargin * (children.length - 1)}em)`
					});
				});
			}, 0);

		}
	}

	return new MultiItemTruncation();
}

// #-- END Decorator (ss-multi-item-truncation) --# //
// #----------------------------------------------# //

angular.module('super-suit-decorators').directive('ssMultiItemTruncation', ssMultiItemTruncation);
