import {Enums} from 'base-enum';

// #-----------------------------------# //
// #---- Enums (/payRateFrequency) ----# //

class PayRateFrequencyEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Annually',
			value: 0
		}, {
			name: 'Hourly',
			value: 1
		}];
		super(enums);
	}
}

// #---- End Enums (/payRateFrequency) ----# //
// #---------------------------------------# //

angular.module('super-suit-enums').factory('enumPayRateFrequency', () => new PayRateFrequencyEnum());
