/*@ngInject*/
function statusApi(ssApiSvc, buildConfig, ssEffectiveDateHlp) {
// #------------------------------------# //
// #---- API Endpoint (ssStatusApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.formTemplates;
	const convertToValidatingSave = ssEffectiveDateHlp.convertToValidatingSave;
	const url = (p) => `${baseUrl}/formtemplates/${p.id}/statushistory`;
	// model data type
	const model = 'statusHistories';
	// possible relationships
	const rel = [
	];

	const update = convertToValidatingSave(ssApiSvc.endpoint({ url, method: 'POST'}, model, rel));

	// API endpoint methods
	return {
		update
	};

// #-- END API Endpoint (ssStatusApi) --# //
// #------------------------------------# //
}

angular.module('super-suit-apis').factory('ssStatusApi', statusApi);
