import {Component} from 'base-component';
// #--------------------------------------------# //
// #---- Component (super-select-list-item) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	value: '=',
	label: '=',
	isDisabled: '=?',
	type: '@?'
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function selectListItem() {

	class SelectListItem extends Component {
		/*@ngInject*/
		controller($scope, $attrs) {
			const vm = $scope.vm = {};
			// these values for the 'type' attr are excluded from click functionality
			const excludedTypes = ['heading'];

			$scope.$watch('isDisabled', nv => {
				vm.disabledClass = nv === true ? 'disabled' : '';
			});

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				click($event) {
					if ($scope.isDisabled || excludedTypes.includes($attrs.type)) { 
						$event.stopPropagation();
						$event.preventDefault();
						return;
					}
					$scope.$emit('select-list-item-click', $scope.value, $scope.label);
				}
			};

			switch($attrs.type) {
				case 'heading':
					break;
				default: 
					$scope.$emit('select-list-item-init', $scope.value, $scope.label);
					break;
			}
		}

		link(scope, element, attrs) {

			switch(attrs.type) {
				case 'heading':
					scope.$applyAsync(() => {
						element.addClass('heading');
					});
					break;
				default: 
					break;
			}

		}
	}

	return new SelectListItem(props, 'select-list-item', config);
}

// #-- END Component (super-select-list-item) --# //
// #--------------------------------------------# //

angular.module('super-suit-components').directive('superSelectListItem', selectListItem);
