// #--------------------------------# //
// #---- Helper (ssEmployeeHlp) ----# //
//

/*@ngInject*/
function ssEmployeeHlp(ssEmployeeApi, $filter, ssEffectiveDateHlp, ssDateHlp,
								ssArrayHlp, ssDeprecated) {

	const _INCLUDES = 'addresses,assignments,positions,paySchedules,jobTypes';

	function getEmployee(id, asOf = null, includes = null, modelStateUtc = null){
		return ssEmployeeApi.get({
			id: id,
			asOfUtc: asOf,
			include: includes == null ? _INCLUDES : includes,
			modelStateUtc: modelStateUtc
		});
	}

	function getCredentials(id){
		return ssEmployeeApi.getCredentials({
			flId: id,
			include: 'endorsements'
		});
	}


	return {
		getCredentials,
		getEmployee
	};
}
// #-- END Helper (ssEmployeeHlp) --# //
// #--------------------------------# //

angular.module('super-suit-helpers').factory('ssEmployeeHlp', ssEmployeeHlp);
