// #----------------------------------------# //
// #---- Mutator (mutatorOrganizations) ----# //

/*@ngInject*/
function organizationsMutator(ssMutatorDataHlp) { return {

	'formattedPhone': function(org) {
		const propertyName = '_phone';
		if (!org.hasOwnProperty(propertyName)) { return ''; }
		if (!org[propertyName]) { return ''; }

		return ssMutatorDataHlp.formatPhone(org[propertyName]);
	},

	'formattedFax': function(org) {
		const propertyName = '_fax';
		if (!org.hasOwnProperty(propertyName)) { return ''; }
		if (!org[propertyName]) { return ''; }

		return ssMutatorDataHlp.formatPhone(org[propertyName]);
	}

};}

// #---- End Mutator (mutatorOrganizations) ----# //
// #--------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorOrganizations', organizationsMutator);
