// #----------------------------# //
// #---- Helper (ssMathHlp) ----# //

/*@ngInject*/
function ssMathHlp() {
	return {
		average,
		sum
	};

	function average(arrayOfNumbers = []){
		if(!isArrayOfNumbers(arrayOfNumbers)){
			throw new TypeError('ssMathHlp.average expected an array of numbers');
		}
		return sum(arrayOfNumbers) / arrayOfNumbers.length;
	}

	function sum(arrayOfNumbers = []){
		if(!isArrayOfNumbers(arrayOfNumbers)) {
			throw new TypeError('ssMathHlp.sum expected an array of numbers');
		}
		return arrayOfNumbers.reduce((accumulatedTotal, currentNumber) => (accumulatedTotal + currentNumber));
	}
}

function isArrayOfNumbers(numbers) {
	return Array.isArray(numbers) && numbers.every(Number.isFinite);
}
// #-- END Helper (ssMathHlp) --# //
// #----------------------------# //

angular.module('super-suit-helpers').factory('ssMathHlp', ssMathHlp);
