import {Decorator} from 'base-decorator';
// #----------------------------------------# //
// #---- Decorator (ss-permitted-route) ----# //

const config = {
	priority: 1500, // run before everything
	terminal: true  // prevent all other directives from running
};

/*@ngInject*/
function ssPermittedRoute($compile, ssPermissionsSvc) {

	class PermittedRoute extends Decorator {
		link(scope, elem, attrs) {
			if (ssPermissionsSvc.isNotPermittedRoute(attrs.ssPermittedRoute)) {
				return elem.remove();
			}

			// we passed the permissions check remove the terminal
			// decorator and re-compile so this can be seen
			elem.removeAttr('ss-permitted-route');
			elem.replaceWith($compile(elem[0].outerHTML)(scope));
		}
	}

	return new PermittedRoute(config);
}

// #-- END Decorator (ss-permitted-route) --# //
// #----------------------------------------# //

angular.module('super-suit-decorators').directive('ssPermittedRoute', ssPermittedRoute);
