
//@ts-check

// #---------------------------------------# //
// #---- Helper (ssDashboardColorsHlp) ----# //
const $$smallArrayOfColorOptions = [
    {
        normal: '#2E7AB8',
        darker: '#205586',
        lighter: '#54A2DE'
    },{
        normal: '#6BCCB3',
        darker: '#52A38F',
        lighter: '#85DEC8'
    },{
        normal: '#7E4082',
        darker: '#402B56',
        lighter: '#A05DA6'
    },{
        normal: '#DE8D37',
        darker: '#C26B13',
        lighter: '#EDB06B'
    },{
        normal: '#A6B0B9',
        darker: '#87939E',
        lighter: '#C0C8CF'
    },{
        normal: '#DEB743',
        darker: '#BF942E',
        lighter: '#F6DC6B'
    },{
        normal: '#E56A54',
        darker: '#C44C31',
        lighter: '#F09078'
    },{
        normal: '#C67DCC',
        darker: '#A05DA6',
        lighter: '#DF9EE5'
    },{
        normal: '#2E7362',
        darker: '#1D5447',
        lighter: '#52A38F'
    },{
        normal: '#E05AA2',
        darker: '#BA4182',
        lighter: '#ED82BB'
    },
];

// Loop the colors once so that the front end will have many colors to choose from
const $$arrayOfColorOptions = [].concat($$smallArrayOfColorOptions,$$smallArrayOfColorOptions);

const lightnessTypes = ['normal', 'darker', 'lighter'];

/*@ngInject*/
function ssDashboardColorsHlp(ssObjectHlp) {

	const thePatternLookup = createLookupObject();

	return {
		getOptions,
		getPatternedOptions,
		getAllPatternDefs,
		createLookupObject,
		lightnessTypes
	};

	function getOptions(){
		// Return a copy to prevent modification post function call
		return ssObjectHlp.deepCopy($$arrayOfColorOptions, true);
	}

	function createLookupObject(){
		const arrayOfTexturedColorOptions=[];

		getOptions().forEach( (colorObj, colorIndex) => {
			const newColorObj = {};

			Object.keys(colorObj).forEach( colorLightness => {
				const patternId = `fl-stripe-pattern-${colorIndex}-${colorLightness}`;
				newColorObj[colorLightness] ={
					id: patternId,
					path: {
						d: 'M 0 0 L 10 10 M 9 -1 L 11 1 M -1 9 L 1 11',
						stroke: colorObj[colorLightness],
						strokeWidth: 4
					},
					url: `url(${window.location.href}#${patternId})`
				};
			});
			arrayOfTexturedColorOptions.push(newColorObj);
		});
		return arrayOfTexturedColorOptions;
	}

	/**
	* Returns array of objects with ids and paths (for highchartconfig.defs.patterns)
	**/
	function getAllPatternDefs() {
		const defPatterns= [];

		thePatternLookup.forEach( (colorObj, colorIndex) => {
			lightnessTypes.forEach( lightness => {
				defPatterns.push({
					id: colorObj[lightness].id,
					path: colorObj[lightness].path
				});
			});
		});

		return defPatterns;
	}


	/**
	 * Return an array that looks similar to getOptions, but instead of colors hexidecimals, the lightnesses map to urls of ids
	 */
	function getPatternedOptions(){
		const newArray= [];

		thePatternLookup.forEach( (colorObj, colorIndex) => {
			const newObj = {};
			lightnessTypes.forEach( lightness => {
				newObj[lightness]= `url(${window.location.href}#${colorObj[lightness].id})`;
			});
			newArray.push(newObj);
		});
		return newArray;
	}
};

// #-- END Helper (ssDashboardColorsHlp) --# //
// #---------------------------------------# //

angular.module('super-suit-helpers').factory('ssDashboardColorsHlp', ssDashboardColorsHlp);
