import {Decorator} from 'base-decorator';
// #-----------------------------# //
// #---- Decorator (ss-icon) ----# //

class Icon extends Decorator {
	// linking function for decorator directive
	link(scope, elem, attrs) {
		var iconState = {};

		// we only need to attach observers once per element.
		// but this directive could be fired multiple times.
		if (elem.data('iconsApplied')) { return; }
		elem.data('iconsApplied', true);

		// setup the initial state so we know what icon to remove if one changes
		iconState.ssIcon = attrs.ssIcon && `fa-${attrs.ssIcon}-before`;
		iconState.ssIconBefore = attrs.ssIconBefore && `fa-${attrs.ssIconBefore}-before`;
		iconState.ssIconAfter = attrs.ssIconAfter && `fa-${attrs.ssIconAfter}-after`;

		// check for icon before
		Object.keys(iconState).forEach(k => {
			if (attrs[k] != null) {
				attrs.$observe(k, v => {
					let suffix = k.toLowerCase().endsWith('after') ? 'after' : 'before';
					let newIcon = (v.length > 0 ? `fa-${v}-${suffix}` : '');

					elem.removeClass(iconState[k]);
					elem.addClass(newIcon);
					iconState[k] = newIcon;
				});
			}
		});
	}
}

// #-- END Decorator (ss-icon) --# //
// #-----------------------------# //

angular.module('super-suit-decorators').directive('ssIcon', () => new Icon());
angular.module('super-suit-decorators').directive('ssIconBefore', () => new Icon());
angular.module('super-suit-decorators').directive('ssIconAfter', () => new Icon());
