/*@ngInject*/
function accountApi($http, buildConfig) {
// #---------------------------------------# //
// #---- API Endpoint (ssPositionsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.productAccess;
	const url = `${baseUrl}/Password`;

	const forgotPassword = (flid) => {
		let config = {
			method: 'POST',
			url: `${url}/Forgot`
		};

		if (flid) {
			config.data = { Flid: flid };
		}

		return $http(config);
	};

	// API endpoint methods
	return {
		forgotPassword,
	};

// #-- END API Endpoint (ssPositionsApi) --# //
// #---------------------------------------# //
}

angular.module('super-suit-apis').factory('ssAccountApi', accountApi);
