/*@ngInject*/
function ssApiSvc($q, $http, $log, ssAuthSvc, JsonApiDataStore, ssJsonApi, ssActiveUserSvc, ssToastHlp, ssObjectHlp, ssApiResponseHlp) {
	// #----------------------------# //
	// #---- Service (ssApiSvc) ----# //
	const privateAttrs = ['ownerid', 'ownerversionnumber'];

	// External Service API
	return { endpoint, defaultEndpoints };

	// creates a single API endpoint function
	function endpoint(endpointCfg, model, allRelTypes = []) {
		const allRelTypesLower = allRelTypes.map(rel => rel.toLowerCase());
		const initialCfg = angular.copy(endpointCfg);

		// check the initial config object
		if (initialCfg.method == null) { throw new Error('Http method missing for API endpoint config'); }
		if (initialCfg.url == null) { throw new Error('URL missing for API endpoint config'); }

		initialCfg.params = (initialCfg.params != null) ? initialCfg.params : {};

		return function (params, data, internalErrorHandling = true, configOverride = {}) {
			const config = {};
			const cfg = angular.copy(initialCfg);
			const schema = { model };
			var returnPromise = $q.when(ssAuthSvc.ready);
			var abort = $q.defer();
			config.data = data;

			// if the url uses the orgId, wait for it to be ready
			if (usesOrgId(cfg.url)) {
				returnPromise = returnPromise.then(() => ssActiveUserSvc.ready);
			}
			// resolve dynamic config values
			for (let key of Object.keys(cfg)) {
				config[key] = resolveConfig(key, cfg, params, data);
			}

			// allow the api caller to override config values (example: request headers)
			// this does not preempt work that is done by interceptors
			Object.assign(config, configOverride);

			// if data exists, serializing into json api
			if (config.data != null) {
				let newModel = new JsonApiDataStore.Model(model);

				Object.keys(config.data).forEach((k) => {
					let key = k;
					if (k.startsWith('_')) {
						key = k.substr(1);
					}
					newModel.setAttribute(key, config.data[k]);
				});
				config.data = newModel.serialize();
			}

			// merge params with prefilled params
			if (params != null) { Object.assign(config.params, params); }
			// add the abort promise
			if (config.timeout == null) { config.timeout = abort.promise; }
			// fill the schema path with the resolved url
			schema.path = config.url;

			// filter includes by available relationships
			schema.relationships = [];
			if (config.params.include) {
				schema.relationships = config.params.include.split(',');
				let includeCount = schema.relationships.length;

				schema.relationships = schema.relationships
					.filter(rel => allRelTypesLower.includes(rel.toLowerCase()))
					.map(rel => allRelTypes[allRelTypesLower.indexOf(rel.toLowerCase())]);

				if (schema.relationships.length !== includeCount) {
					$log.warn(
						'invalid include requested: ' +
						config.params.include.split(',')
							.filter(rel => !allRelTypesLower.includes(rel.toLowerCase()))
							.join(',')
						);
				}
				config.params.include = schema.relationships.join(',');
			}

			// always include version related fields if we filter by fields
			if (config.params.fields) {
				config.params.fields.split(',')
					.filter(f => privateAttrs.includes(f.toLowerCase()))
					.join(',');

				config.params.fields += ',' + privateAttrs.join(',');
			}

			returnPromise = returnPromise.then(function () {
				const orgId = ssActiveUserSvc.getOrgIdSync();
				const urlNeedsOrgId = config.url.includes(':ORGID:');

				// if this url needs an orgId replacement, and we don't have an org context... abort!
				if (urlNeedsOrgId && orgId == null) { return; }
				// if this url needs an orgId, replace it
				if (urlNeedsOrgId) {
					config.url = config.url.replace(/:ORGID:/ig, orgId);
				}
				// update schema path to make sure it's correct
				schema.path = config.url;

				return $http(config)
					.then(res => (res.status >= 400 && res.status < 500 ? $q.reject(res) : res))
					.then(res => jsonApiData(res.data, res.headers, schema))
					.catch(res => {
						if (res.status === -1) { return; } // timeout or cancelled
						if (internalErrorHandling) {
							ssApiResponseHlp.handle4XX(res);
						} else {
							throw res;
						}
					});
			});

			returnPromise.$abort = abort.resolve;

			return returnPromise;
		};
	}

	function usesOrgId(url) {
		const str = (typeof url === 'function' ? url({}, {}) : url);
		if (typeof str === 'string') {
			return str.toLowerCase().includes(':orgid:');
		}
		return false;
	}

	// creates the 4 main crud API endpoint functions from a common config
	function defaultEndpoints(cfg, model, rel) {
		if (cfg.url == null || typeof cfg.url !== 'string') {
			throw new Error('Invalid url for ssApiSvc.defaultEndpoints, expected a string');
		}

		let baseUrl = cfg.url;
		let urlAndId = { url: (p) => `${baseUrl}/${p.id}` };
		return {
			get: endpoint(Object.assign({}, cfg, { method: 'GET' }, urlAndId), model, rel),
			query: endpoint(Object.assign({}, cfg, { method: 'GET' }), model, rel),
			add: endpoint(Object.assign({}, cfg, { method: 'POST' }), model, rel)
		};
	}

	function resolveConfig(key, cfg, params, data) {
		var val = cfg[key];
		if (typeof cfg[key] === 'function') {
			try {
				val = cfg[key](params, data);
				if (key === 'url') {
					let valArr = val.split('/');

					// make sure we are checking queryString params if they exist
					if (valArr[valArr.length - 1].includes('?')) {
						let qsArr = valArr[valArr.length - 1].split('?')[1].split('&');
						valArr[valArr.length - 1] = valArr[valArr.length - 1].split('?')[0];
						valArr = valArr.concat(qsArr.map(p => decodeURIComponent(p.split('=')[1])));
					}
					Object.keys(params).forEach(k => {
						if (valArr.includes(params[k])) {
							delete params[k];
						}
					});
				}
			} catch (err) {
				if (params == null) {
					$log.warn('Parameters not provided to API service');
				}
				if (data == null && ['PUT', 'POST'].includes(cfg.method)) {
					$log.warn(`${cfg.method} data not provided to API service`);
				}
				throw err;
			}
		}

		return val;
	}

	/**
	 * Returns the JsonApiObject or JsonApiCollection depending on the base type
	 * @param {object} res The returned object from the api call
	 * @param {function} headers The headers function that returns response headers
	 * @param {schema} string The type of model we are returning
	 */
	function jsonApiData(res, headers, schema) {
		var obj;

		const metaMap = new Map();

		if (res == null) {return;}
		if (!res.data) { return; }

		if (angular.isArray(res.data)) {
			res.data.forEach(item => {
				metaMap.set(item.id, item.meta);
			});
		} else {
			metaMap.set(res.data.id, res.meta || res.data.meta || null);
		}

		const store = JsonApiDataStore.store.sync(res);

		if (angular.isArray(store)) {
			store._meta = res.meta || null;
			store.forEach(item => {
				item._meta = metaMap.get(item.id);
			});

			obj = new ssJsonApi.Collection(store, schema);
		} else {
			store._meta = metaMap.get(store.id);
			obj = new ssJsonApi.Model(store, schema);
		}

		obj.$headers = headers();

		return obj;
	}


	// #-- END Service (ssApiSvc) --# //
	// #----------------------------# //
}

angular.module('super-suit-services').factory('ssApiSvc', ssApiSvc);
