/*@ngInject*/
function insightsRecruitApi(ssApiSvc, buildConfig, ssStringHlp, ssEventSvc) {
	// #---------------------------------------------# //
	// #---- API Endpoint (ssInsightsRecruitApi) ----# //

	// base endpoint url
	const baseUrl = `${buildConfig.endpoint.insightsRecruit}/kpi`;
	// model data type
	const model = 'InsightResults';
	// possible relationships
	const rel = [];

	const kpiList = [
		'applicants_by_referral_source',
		'applicants_by_years_of_experience_and_subject',
		'applicants_by_years_of_experience',
		'average_days_to_fill_by_subject',
		'average_days_to_fill',
		'average_days_to_fill_by_subject_full_year',
		'average_number_of_applications_per_open_job_posting_by_subject_detail',
		'average_number_of_applications_per_open_job_posting_by_subject',
		'average_number_of_applications_per_open_job_posting',
		'hired_applicants_by_referral_source',
		'jobs_filled_by_subject_per_district_year',
		'jobs_posted_and_filled_per_district',
		'jobs_posted_by_subject_per_district_month',
		'jobs_posted_by_subject_per_district_year'
	];

	// API endpoint methods
	return kpiList.reduce(function (api, kpi) {
		const method = 'POST';
		const url = `${baseUrl}/${kpi}?organizationId=:ORGID:`;
		const name = ssStringHlp.toCamelCase(kpi);
		const endpoint = ssApiSvc.endpoint({url, method}, model, rel);
		api[name] = parameters => endpoint({}, {parameters}).then(function (res) {
			ssEventSvc.emit('insight-results', name, res);
			return res;
		});
		return api;
	}, {});

	// #-- END API Endpoint (ssInsightsRecruitApi) --# //
	// #---------------------------------------------# //
}

angular.module('super-suit').factory('ssInsightsRecruitApi', insightsRecruitApi);
