// #----------------------------------------# //
// #---- Mutator (mutatorFormInstances) ----# //

/*@ngInject*/
function formInstancesMutator(moment, enumStepAction) { return {

	// set up some properties for use in our view
	'name': function (inst) {
		if (inst._formTemplate == null) { return; }
		return inst._formTemplate.name;
	},

	// is it part of a packet?
	'isInPacket': function(inst) {
		if (inst._packetInstance == null) { return; }
		if (inst._packetInstance.instanceId == null) { return; }
		return true;
	},

	// is it past the due date for the form?
	'daysTillDue': function(inst) {
		if (inst._dueDateUtc == null) { return Infinity; }

		var dueDate = inst._dueDateUtc;
		var start = moment(Date.now());
		var end = moment(dueDate);

		return difference(start, end);
	},

	// is it past the due date for the form?
	'isOverdue': function(inst) {
		return inst.daysTillDue < 0;
	},

	'currentStep': function (inst) {
		if (inst._steps == null) { return; }
		if (inst._steps.length === 0) { return; }
		return inst._steps.find(step => step._formTemplateStepId === inst._currentStepId);
	}

};}

function difference(start, end) {
	var diff = end.diff(start, 'days', true);
	return Math.ceil(diff);
}

// #---- End Mutator (mutatorFormInstances) ----# //
// #--------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorFormInstances', formInstancesMutator);
