// #-------------------------------------# //
// #---- Helper (ssEffectiveDateHlp) ----# //

/*@ngInject*/
function ssEffectiveDateHlp(moment, ssDateHlp, ssToastHlp, $log, ssDeprecated) {

	function convertToValidatingSave(jsonApiEndpointFunction){
		if(typeof(jsonApiEndpointFunction) !== 'function'){
			throw new TypeError('convertToValidatingSave requires jsonApiEndpointFunction to be a function');
		}
		const originalJsonEndpointFunction = jsonApiEndpointFunction;

		function newEndpointFunction(...args){
			return originalJsonEndpointFunction(...args)
				.then(anObjectWithSaveFunctionsOnIt => {
					return convertEndpointResultToAValidatingSave(anObjectWithSaveFunctionsOnIt);
				});
		}

		return newEndpointFunction;
	}

	function sessionStorageSaysToDisableThis(){
		const sessionStorageKey = 'disable-json-api-pre-save';
		var sessionStorageValue = window.sessionStorage.getItem(sessionStorageKey);
		// Short-circuit if not set
		if(sessionStorageValue == null){
			return false;
		}
		// Deal with real values now
		if(sessionStorageValue === true || sessionStorageValue === 'true'){
			return true;
		} else if(sessionStorageValue === false || sessionStorageValue === 'false'){
			return false;
		} else {
			$log.warn('Whatever you tried to set ' + sessionStorageKey + ' to was not a boolean. It was: ' + sessionStorageValue);
			return false;
		}
	}

	function convertEndpointResultToAValidatingSave(anObjectWithSaveFunctionsOnIt){
		var prodUrlRegex = /.*frontlineeducation.com.*/;
		const saveFuncName = '$save';

		if(
			(window && window.location && prodUrlRegex.test(window.location.href)) ||
			(sessionStorageSaysToDisableThis())
		){
			// Then pass-through without any validation since hopefully we'll have caught any issues in the lower environments through manual and automated testing
			return anObjectWithSaveFunctionsOnIt;
		} else {
			// Replace it with a pre-validation function that will help QA and us to test for places we missed effective dating corrections
			recursivelyReplaceSaveMethodsOnObject(anObjectWithSaveFunctionsOnIt);
			return anObjectWithSaveFunctionsOnIt;
		}

		function recursivelyReplaceSaveMethodsOnObject(anObj, levelsOfRecursionSoFar = -1){
			levelsOfRecursionSoFar++;
			if(anObj == null){
				// then there is nothing to correct the $save functions on, so just return it as is (i.e. pass-through)
				return anObj;
			} else if(Array.isArray(anObj)){
				anObj.forEach(anElement => {
					recursivelyReplaceSaveMethodsOnObject(anElement, levelsOfRecursionSoFar);
				});
			} else if(typeof(anObj) === 'object' && anObj != null) {
				const theOriginal$SaveFunction = anObj[saveFuncName];
				if(theOriginal$SaveFunction != null){
					mutateToHaveCustomSave(anObj);
				}
				Object.keys(anObj).forEach(aKey => {
					if(typeof(anObj[aKey]) === 'object'){
						recursivelyReplaceSaveMethodsOnObject(anObj[aKey], levelsOfRecursionSoFar);
					}
				});
			}
		}

		function mutateToHaveCustomSave(objThatHasASaveFunc){
			const original$saveFunction = objThatHasASaveFunc.$save;
			objThatHasASaveFunc.$save = $saveButWithValidation;

			function $saveButWithValidation(...args){
				const theObject = this;
				const msInADay = 86399999;
				const msBetweenTwoDays = msInADay + 1;

				function expectedRemainderWithRespectToDST({startMoment, endMoment}){
					const msInAnHour = 3600000;
					let expectedRemainder = msInADay;
					function isDST(aMoment){
						// UTC does not observe DST, so we have to convert to a timezone that actually observes Daylight savings time first
						return aMoment.tz('America/New_York').isDST();
					}
					if(isDST(startMoment) === isDST(endMoment)){
						expectedRemainder = msInADay;
					} else if(isDST(startMoment) && !isDST(endMoment)){
						expectedRemainder = (msInAnHour - 1);
					} else {
						expectedRemainder = msInADay - msInAnHour;
					}
					return expectedRemainder;
				}

				const startDateKeySynonyms = ['_from', '_startDateUtc', '_startDate'];
				const endDateKeySynonyms = ['_to', '_endDateUtc', '_endDate'];

				function pluckTheFirstExistingValue(objectToSearch, arrayOfKeyNames){
					// Try and get them whatever their names might be
					//		Note: I could use Object.keys() however some of these objects are huge, and the following is Big-O(1) from within the loop
					for (let i = 0; i < arrayOfKeyNames.length; i++) {
						const aKey = arrayOfKeyNames[i];
						if(objectToSearch[aKey] != null){
							return objectToSearch[aKey];
						}
					}
					return undefined;
				}

				function getValidityErrorForAnEffectiveItem(anObj, objectsName){
					const startDateValue = pluckTheFirstExistingValue(anObj, startDateKeySynonyms);
					const endDateValue = pluckTheFirstExistingValue(anObj, endDateKeySynonyms);

					if(startDateValue == null || endDateValue == null){
						// Since we can't validate this object (due to it not being a ranged object), we will consider it valid and move on
						return;
					} else {
						// Do the real validation

						// Is it a valid time?
						const startMoment = moment.utc(startDateValue);
						if(!startMoment.isValid()){
							return `${objectsName}'s start date was not a valid time`;
						}
						const endMoment = moment.utc(endDateValue);
						if(!endMoment.isValid()){
							return `${objectsName}'s end date was not a valid time`;
						}
						if(endMoment.isBefore(startMoment)){
							return `${objectsName}'s end date must be after it's start date`;
						}
						if(ssDateHlp.isForeverPast(startMoment)){
							return; // Because we can't do a valid duration check since foreverPast doesn't end on a relative day boundary
						}
						if(ssDateHlp.isForeverFuture(endMoment)){
							return; // Because we can't do a valid duration check since foreverFuture doesn't end on a relative day boundary
						}
						if(startMoment.format('mm:ss') !== '00:00'){
							return `${objectsName}'s start date is not on an hour mark, which means that it is not the start of a day for any timeZone. Please use the dateByOrg helper to prevent bad data from getting to the server.`;
						}
						if(endMoment.format('mm:ss') !== '59:59'){
							return `${objectsName}'s end date is not 59 min and 59 seconds, which means that it is not the end of a day for any timeZone. Please use the dateByOrg helper to prevent bad data from getting to the server.`;
						}

						const msDelta = endMoment.diff(startMoment, 'ms');
						// Is the remainder exactly the length of a day?
						const msRemainder = msDelta % msBetweenTwoDays;
						if(msRemainder === expectedRemainderWithRespectToDST({startMoment, endMoment})){
							return; // Success!
						} else {
							return `The range of this effective item (named: ${objectsName}) is not day-bound. You are likely forgetting to use endOf(\'day\') or to use the dateByOrg helper`;
						}
					}
				}

				function validateRecursively(anObj, objectsName, levelsOfRecursionSoFar = -1){
					levelsOfRecursionSoFar++;
					let errorOutputArray = [];
					// console.info('About to validate ' + objectsName + ' at levelsOfRecursionSoFar: ' + levelsOfRecursionSoFar);
					if(Array.isArray(anObj)){
						anObj.forEach((anElem, index) => {
							errorOutputArray = errorOutputArray.concat(validateRecursively(anElem, `${objectsName}[${index}]`, levelsOfRecursionSoFar));
						});
					}
					else if(typeof(anObj) === 'object' && anObj != null){
						errorOutputArray.push(getValidityErrorForAnEffectiveItem(anObj, objectsName));

						// Also do it's keys too
						Object.keys(anObj).forEach(aKey => {
							if(aKey !== '$permitted'){
								errorOutputArray = errorOutputArray.concat(validateRecursively(anObj[aKey], aKey, levelsOfRecursionSoFar));
							}
						});
					} else {
						// this is neither an object or an array, so there is no way to validate its effectivity
					}
					// Get rid of the empty string cases since those are the success cases
					return errorOutputArray.filter(x => !!x);
				}

				const errorArray = validateRecursively(theObject, '(the base object with the $save on it)');
				if(errorArray.length > 0){
					// Show the developer the first one
					ssToastHlp.warn(errorArray[0]);
					// Console log all of the errors so developers can diagnose the issues
					$log.error(errorArray.join('\nNEXT ERROR: '));
				}
				// Call the save no matter what since we can't impede a developer's ability to make progress on any unrelated PRs.
				return original$saveFunction.call(objThatHasASaveFunc, ...args);
			}
		}
	}

	return {
		convertToValidatingSave: convertToValidatingSave
	};
}
// #-- END Helper (ssEffectiveDateHlp) --# //
// #-------------------------------------# //

angular.module('super-suit-helpers').factory('ssEffectiveDateHlp', ssEffectiveDateHlp);
