// Subset of Lodash. We want to be careful about the size of SuperSuit
import groupBy from 'lodash-es/groupBy';
import filter from 'lodash-es/filter';
import matches from 'lodash-es/matches';
const _ = { groupBy, filter, matches };

// #-----------------------------# //
// #---- Helper (ssArrayHlp) ----# //

/*@ngInject*/
function ssArrayHlp() {

	return {
		where,
		findWhere,
		sortByObjectKey,
		unique,
		groupBy,
	};


	/**
	 * filters an array of objects to a subset that matches the parameters
	 * and values defined in the keyValuesItMustHave object
	 *
	 * @param {object[]} arrayOfObj - the array that you want to search for similar objects
	 * @param {object} keyValuesItMustHave - the attributes and values of this object will be used to filter objects out of the arrayOfObj
	 * @returns {object[]} - the objects from arrayOfObj that contain the key/value pairs from keyValuesItMustHave
	 */
	function where(arrayOfObj, keyValuesItMustHave) {
		// Sends the callback of the object filter to the matches
		// Note: matches is the curried/partially-applied version of lodash's isMatch
		return _.filter(arrayOfObj, _.matches(keyValuesItMustHave));
	}

	/**
	 * same as ssArrayHlp.where, but it returns the first matching object
	 *
	 * @param {object[]} arrayOfObj - the array that you want to search for similar objects
	 * @param {object} keyValuesItMustHave - the attributes and values of this object will be used to filter objects out of the arrayOfObj
	 * @returns {object} - the first object from arrayOfObj that contain the key/value pairs from keyValuesItMustHave
	 */
	function findWhere(arrayOfObj, keyValuesItMustHave) {
		return arrayOfObj.find(_.matches(keyValuesItMustHave));
	}

	function sortByObjectKey(arr, key, reverse = false) {
		var r = (reverse ? -1 : 1);
		return arr.sort(function(a, b) {
			var x = a[key]; var y = b[key];

			if (x < y) { return -1 * r; }
			if (x > y) { return 1 * r; }
			return 0;
		});
	}

	/**
	 * @param {Array|Object} collection The collection to iterate over.
	 * @returns {Object} Returns the deduplicated array.
	 */
	function unique(collection) {
		return collection.filter((elem, pos, arr) => arr.indexOf(elem) === pos);
	}

	/**
	 *
	 * @param {Array|Object} collection The collection to iterate over.
	 * @param {Function} [iteratee=_.identity] The iteratee to transform keys.
	 * @returns {Object} Returns the composed aggregate object.
	 */
	function groupBy(collection, iteratee) {
		// delegate to lodash groupBy
		// https://lodash.com/docs/#groupBy
		return _.groupBy(collection, iteratee);
	}
}

// #-- END Helper (ssArrayHlp) --# //
// #-----------------------------# //
angular.module('super-suit-helpers').factory('ssArrayHlp', ssArrayHlp);
