/*@ngInject*/
function interceptAddVersionNumber(ssAuthSvc) {
	function addVersionNumber(config) {
		const _d = config.data;
		if (_d && _d.data && _d.data.attributes && _d.data.attributes.ownerVersionNumber != null) {
			config.params = config.params || {};
			config.params.version = _d.data.attributes.ownerVersionNumber;
			delete _d.data.attributes.ownerId;
			delete _d.data.attributes.ownerVersionNumber;
		}

		return config;
	}

	return {
		request: addVersionNumber
	};
}

angular.module('super-suit').factory('interceptAddVersionNumber', interceptAddVersionNumber);
