/* globals sidekick */

/*@ngInject*/
function ssFlagSvc($log, $q) {
// #-----------------------------# //
// #---- Service (ssFlagSvc) ----# //

	var sidekickFeatureFlags = {};
	var ready = $q.defer(); //promise


	sidekick.events.on('feature-flags-ready', (sff) => {
		sidekickFeatureFlags = sff;
		ready.resolve();
	}, {greedy: true});


	return {
		ready: ready.promise,   // service ready promise
		/*
		* @params Params are key (required) and value (optional) to compare against
		* @return If value of feature flag key in LD for this user matched value. If no value, defaults to checking if true
		*/
		checkState: (key, value) => {
			return sidekickFeatureFlags.checkState(key, value);
		}
	};

// #-- END Service (ssFlagSvc) --# //
// #-----------------------------# //
}

angular.module('super-suit-services').factory('ssFlagSvc', ssFlagSvc);

