const untestedClass = 'ss-validate-form-untested';
const failingClass = 'ss-validate-form-failing';
const passingClass = 'ss-validate-form-passing';
const asyncClass = 'ss-validate-form-async';

/*@ngInject*/
function ssValidateForm($q, rAF, ssAsync) {
	return {
		restrict: 'A',
		scope: false,
		priority: 0,

		/*@ngInject*/
		controller($scope, $element, ssElemHlp) {
			const $validate = $scope.$validate = {
				isValid: null,
				validationHandlers: [],
				resetHandlers: [],
				reset() {
					this.resetHandlers.forEach(reset => reset());
				}
			};

			$scope.$on('ss-validate-handler', function (evt, validate, reset, formElem) {
				evt.stopPropagation();
				if (ssElemHlp.isMyChild($element, formElem)) {
					const tScope = evt.targetScope;
					const onDestroy = (handler) => tScope.$on('$destroy', handler);
					const testExpr = formElem.attr('ss-validate') || 'true';
					const testFunc = ($eventType) => tScope.$eval(testExpr, {$eventType});

					$validate.validationHandlers.push(validate);
					$validate.resetHandlers.push(reset);

					// when the field's scope is destroyed, clean up the validation
					// handler functions that we are tracking locally.
					onDestroy(() => {
						$validate.validationHandlers = $validate.validationHandlers.filter(x => x !== validate);
						$validate.resetHandlers = $validate.resetHandlers.filter(x => x !== reset);
					});

					// This sets up a listener on the form that checks if a change to
					// one field is causing validation in another part of the page
					// to flip from failing to passing. If this happens, we will
					// reset the state of that field to clear the failure message.
					onDestroy($scope.$on('ss-validate-field-event', function (emitted, el, eventType) {
						if (formElem[0] === el[0]) { return; }
						if (['focus', 'blur'].includes(eventType)) { return; }

						// if the field's status has changed from passing to failing
						// or vice versa, we will clear the field status. It will
						// be checked again when the form is submitted.
						const isPassing = testFunc(eventType) === true;
						const shouldReset = (
							isPassing && formElem.hasClass('ss-validate-failing')
							|| !isPassing && formElem.hasClass('ss-validate-passing')
						);
						if (shouldReset) { reset(); }
					}));
				}
			});

			$scope.$on('ss-validate-reset', function(evt) {
				evt.stopPropagation();
				$scope.$validate.reset();
			});
		},

		link(scope, elem, attrs) {
			elem.addClass(untestedClass);

			scope.$on('ss-validate-field-event', function (emitted, formElem, eventType) {
				if (['focus', 'blur'].includes(eventType)) { return; }
				elem.removeClass(`${passingClass} ${failingClass}`);
				elem.addClass(untestedClass);
				scope.$validate.isValid = null;
			});

			elem.on('submit', ssAsync(function* (evt) {
				try {
					// remove the untested class
					if (elem.hasClass(untestedClass)) { elem.removeClass(untestedClass); }
					// set the "in progress" class
					elem.addClass(asyncClass);

					// if we know the form is valid, don't stop the form
					// from performing the submit operation
					if (scope.$validate.isValid === true) { return; }
					// prevent submit operation
					evt.stopImmediatePropagation();

					elem.removeClass(`${passingClass} ${failingClass}`);

					// wait for the validation to run
					// throws error if not passing validation
					yield runAllValidation(evt.type);

					// passing
					scope.$validate.isValid = true;
					elem.removeClass(failingClass);
					elem.addClass(passingClass);

					// resubmit form
					rAF(() => elem.triggerHandler('submit'), false);
				} catch (err) {
					// failing
					scope.$validate.isValid = false;
					elem.removeClass(passingClass);
					elem.addClass(failingClass);
				} finally {
					elem.removeClass(asyncClass);
				}
			}));

			var runAllValidation = ssAsync(function* (eventType) {
				const runValidator = v => v(elem, eventType);
				var prevValidatorCount;
				var validatorCount = 0;
				var promiseList = [];

				while (prevValidatorCount !== validatorCount) {
					try {
						prevValidatorCount = validatorCount;
						promiseList = scope.$validate.validationHandlers
							.map(runValidator)
							.filter(p => p != null);

						validatorCount = promiseList.length;
						// run validators, but suppress errors until the validator
						// count stabilizes
						yield $q.all(promiseList).catch((err) => {});
					} catch(err) { continue; }
				}

				// run the final list of validators, allowing errors to bubble up
				// if there are still failing validator functions
				return yield promiseList;
			});

		}
	};
}

angular.module('super-suit-decorators').directive('ssValidateForm', ssValidateForm);
