// #-----------------------------------------# //
// #---- Mutator (mutatorJobTypeDetails) ----# //

/*@ngInject*/
function jobTypeDetailsMutator() { return {

	// sets obj.example to obj.name.toUpperCase()
	'isCurrent': function (jobTypeDetail) {
		var today = moment();
		var fromDate = moment(jobTypeDetail._fromDateUtc);
		var toDate = moment(jobTypeDetail._toDateUtc);
		return fromDate.isSameOrBefore(today) && toDate.isAfter(today);
	},
	'isFuture': function(jobTypeDetail){
		var today = moment();
		var fromDate = moment(jobTypeDetail._fromDateUtc);
		var toDate = moment(jobTypeDetail._toDateUtc);
		return (fromDate.isSameOrAfter(today) && toDate.isAfter(today));
	},
	'isPast': function(jobTypeDetail){
		var today = moment();
		var toDate = moment(jobTypeDetail._toDateUtc);
		var fromDate = moment(jobTypeDetail._fromDateUtc);
		return fromDate.isBefore(today) && toDate.isSameOrBefore(today);
	}

};}

// #---- End Mutator (mutatorJobTypeDetails) ----# //
// #---------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorJobTypeDetails', jobTypeDetailsMutator);
