// #----------------------------------------# //
// #---- Mutator (mutatorFormTemplates) ----# //

/*@ngInject*/
function formTemplatesMutator(ssMutatorDataHlp) {
	return {
		'!statusObject': function(obj) {
			return ssMutatorDataHlp.getStatus(obj);
		},
		'status': function(obj) {
			if (obj.statusObject == null) {return;}
			return obj.statusObject.status;
		},
		'statusLabel': function(obj) {
			if (obj.statusObject == null) {return;}
			return obj.statusObject.statusLabel;
		}
	};
}

// #---- End Mutator (mutatorFormTemplates) ----# //
// #--------------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorFormTemplates', formTemplatesMutator);
