
///////
// ready is out here so that it exists outside of the context of any
// given angular application (super or fl-app)
//
var ready;

/*@ngInject*/
function ssFeatureSvc($window, $rootScope, $http, $q, $log, buildConfig, ssOrganizationSvc) {
// #--------------------------------# //
// #---- Service (ssFeatureSvc) ----# //

	var feature = {};
	const localFeatureFlags = JSON.parse(($window.sessionStorage.getItem('feature-flags') || '{}'));

	/////
	// if we don't have a ready promise, go make the http request
	//
	if (ready == null) {
		ready = $q.all([
			$http.get(`${buildConfig.featureFlagsUrl}?cb=${Date.now()}`),
			ssOrganizationSvc.getActiveOrg()
		]).then(([featureData, orgData]) => {
			// TODO: remove this once new User Access page is turned on for all organizations
			if (isTrueValue(featureData.data.featureEnableAppAccessLinkBacks) && isTrueValue(orgData._hasLegacyUserAccessPage)) {
				featureData.data.featureEnableAppAccessLinkBacks = false;
			}
			return Object.assign({}, featureData.data, localFeatureFlags);
		});
	}

	/////
	// when the feature flag data is ready, build the feature object from it
	//
	ready.then(function (featureFlags) {
		// attach the feature object to $root for convenience
		$rootScope.feature = feature;

		// creates an object where all the values are static
		Object.keys(featureFlags).forEach(function (key) {
			Object.defineProperty(feature, key, {
				enumerable: true,
				value: isTrueValue(featureFlags[key])
			});
		});
	});

	// not the hardware store
	function isTrueValue(val) {
		if (val === true) { return true; }
		if (typeof val === 'string' && val.toLowerCase() === 'true') { return true; }
		return false;
	}

	return {
		feature, // all features as keys, value is bool
		ready,   // service ready promise
		isOn,    // check if a feature is on
		isOff    // do I really need to explain this?
	};

	/**
	 * checks the feature object for a specific feature flag
	 *
	 * @param {string} key - the name of the feature flag
	 * @returns {boolean}
	 */
	function isOn(key) {
		if (feature[key] == null) {
			$log.warn('feature flag', key, 'does not exist!');
			return false;
		} else {
			return !!feature[key];
		}
	}

	/**
	 * checks if the feature flag is currently off
	 *
	 * @param {string} key - the name of the feature flag
	 * @returns {boolean}
	 */
	function isOff(key) {
		return !isOn(key);
	}

// #-- END Service (ssFeatureSvc) --# //
// #--------------------------------# //
}

angular.module('super-suit-services').factory('ssFeatureSvc', ssFeatureSvc);
