import {
	Enums
}
from 'base-enum';

// #-----------------------------# //
// #---- Enums (/stepAction) ----# //

class StepActionEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Form Entry',
			value: 0
		}, {
			name: 'Review Form',
			value: 1
		}, {
			name: 'Review & Approve (Or Deny)',
			value: 2
		}, {
			name: 'Form Completed',
			value: 3
		}, {
			name: 'Employee Profile Review',
			value: 4
		}];
		super(enums);
	}
}

// #---- End Enums (/stepAction) ----# //
// #---------------------------------# //

angular.module('super-suit-enums').factory('enumStepAction', () => new StepActionEnum());
