import {Decorator} from 'base-decorator';
// #---------------------------------------# //
// #---- Decorator (ss-a11y-vert-list) ----# //

/*@ngInject*/
function ssA11yVertList() {

	class A11yVertList extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {
			const menuItemSelector = attrs.ssA11yVertList || '.select-menu-list-item';
			bindMenuKeys(elem, menuItemSelector);
		}
	}

	function bindMenuKeys(el, menuItemSelector) {
		el.on('keydown', evt => {
			const focusedEl = document.activeElement;
			const menuItems = getMenuItems(el, menuItemSelector);
			if (menuItems.some(e => e.attr('tabindex') == null)) {
				menuItems.forEach(ensureTabIndex);
			}
			const menuMaxIdx = menuItems.length - 1;
			const selectedIdx = menuItems.findIndex(i => i[0] === focusedEl);

			let nextIdx = selectedIdx === -1 ? 0 : selectedIdx + 1;
			if (nextIdx > menuMaxIdx) { nextIdx = 0; }
			let prevIdx = selectedIdx === -1 ? menuMaxIdx : selectedIdx - 1;
			if (prevIdx < 0) { prevIdx = menuMaxIdx; }

			const nextEl = menuItems[nextIdx];
			const prevEl = menuItems[prevIdx];

			switch(evt.key) {
				case 'ArrowDown':
					focusEl(nextEl);
					cancelEvt(evt);
					break;
				case 'ArrowUp':
					focusEl(prevEl);
					cancelEvt(evt);
					break;
				case 'Enter': case ' ':
					if (selectedIdx === -1) { return; }
					menuItems[selectedIdx][0].click();
					cancelEvt(evt);
					break;
				default: break;
			}
		});
		el.on('keyup', evt => {
			switch(evt.key) {
				case 'ArrowDown':
				case 'ArrowUp':
					cancelEvt(evt);
					break;
				default: break;
			}
		});
	}

	function ensureTabIndex(el) {
		const notNativelyTabable = (
			el[0].tabIndex === -1
			&& el.attr('tabindex') == null
		);
		if (notNativelyTabable) {
			el.attr('tabindex', '-1');
		} else {
			el.attr('tabindex', el[0].tabIndex);
		}
	}
	function focusEl(el) {
		ensureTabIndex(el);
		el[0].focus();
	}

	function getMenuItems(el, menuItemSelector) {
		return Array.from(el[0].querySelectorAll(menuItemSelector))
			.map(e => angular.element(e));
	}

	function cancelEvt(evt) {
		evt.stopPropagation();
		evt.preventDefault();
	}

	return new A11yVertList();
}

// #-- END Decorator (ss-a11y-vert-list) --# //
// #---------------------------------------# //

angular.module('super-suit-decorators').directive('ssA11yVertList', ssA11yVertList);
