import {Component} from 'base-component';
// #-------------------------------------# //
// #---- Component (super-grid-cell) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {

};

// directive definition object overrides
const config = {
	transclude: true
};

/*@ngInject*/
function gridCell(rAF) {

	class GridCell extends Component {
		/*@ngInject*/
		controller($scope) {
			const gridData = $scope.gridData = $scope.$root.utils.findInParentScope('gridData', $scope); // eslint-disable-line no-unused-vars
		}

		link(scope, elem, attrs) {
			const cellSizeCache = scope.gridData.cellSizeCache;
			const cellTemplateKey = elem[0].outerHTML;
			if (cellSizeCache.has(cellTemplateKey)) {
				elem[0].style.width = cellSizeCache.get(cellTemplateKey);
			} else {
				// rAF will allow all cells to be added to the dom before they try
				// to get their sizes from the columnScopes. This allows cells to be
				// ng-repeated over, defined explicitly, or both in a single row
				rAF.raw(function () {
					const colIndex = Array.from(elem.parent().children()).indexOf(elem[0]);
					const columnScopes = scope.gridData.columnScopes;
					elem[0].style.width = columnScopes[colIndex].vm.styles.width;
					cellSizeCache.set(cellTemplateKey, elem[0].style.width);
				});
			}
		}
	}

	return new GridCell(props, 'grid-cell', config);
}

// #-- END Component (super-grid-cell) --# //
// #-------------------------------------# //

angular.module('super-suit-components').directive('superGridCell', gridCell);
