import {Component} from 'base-component';
// #------------------------------------------# //
// #---- Component (super-input-textarea) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=',
	isDisabled: '=?',
	label: '@',
	labelWeight: '@?',
	id: '@?',
	placeholder: '@',
	name: '@',
	required: '=',
	initialHeight: '@?',
	editMode: '=?'
};

// directive definition object overrides
const config = {};
const DEFAULT_HEIGHT = 50;
const HEIGHT_THRESH = 200;

/*@ngInject*/
function inputTextarea($timeout, ssCommonControlHlp, $log) {

	class InputTextarea extends Component {

		/*@ngInject*/
		controller($scope) {
			$scope.labelWeight = ssCommonControlHlp.labelWeight($scope.labelWeight, $scope.label);
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				label: $scope.label,
				editMode: $scope.editMode == null || $scope.editMode === true, // strict equality to not bind it to the $scope
			};
		}

		link(scope, elem, attrs) {

			var control = elem.find('textarea');

			attrs.$observe('label', val => {
				scope.vm.label = val;
			});

			attrs.$observe('initialHeight', val => {
				if(!val || parseInt(val, 10) < DEFAULT_HEIGHT) {
					$log.warn(`Default textarea height will be set to ${DEFAULT_HEIGHT}px`);
				};
			});

			if (scope.placeholder != null) {
				control.attr('placeholder', scope.placeholder);
			}

			if (scope.name != null) {
				control.attr('name', scope.name);
			}

			if (scope.id != null) {
				control.attr('id', scope.id);
			}

			function checkInitialHeight(val) {
				let defaultHeight = DEFAULT_HEIGHT;
				if(val <= defaultHeight) {
					return defaultHeight;
				} else {
					return val;
				}
			}

		//	Check the 'initialHeight' prop to see if it exists, then parse out & check the the numeric value against the default, or set it to the default
			var initialHeight = scope.initialHeight && scope.initialHeight !== '' ? checkInitialHeight(parseInt(scope.initialHeight,10)) : DEFAULT_HEIGHT;

			// Resize the textarea on change
			var resize = function() {
				if(scope.vm.editMode) {
					if (control[0].scrollHeight <= HEIGHT_THRESH) {
						control[0].style.height = `${initialHeight}px`;
						control[0].style.height = `${control[0].scrollHeight}px`;
					} else {
						control[0].style.height = `${HEIGHT_THRESH}px`;
					}
				} else {
					control[0].style.height = control[0].scrollHeight <= initialHeight ? `${initialHeight}px` : `${control[0].scrollHeight}px`;
				}

			};

			control.on('input change', resize);
			$timeout(resize, 0);

		}
	}

	return new InputTextarea(props, 'input-textarea', config);
}

// #-- END Component (super-input-textarea) --# //
// #------------------------------------------# //

angular.module('super-suit-components').directive('superInputTextarea', inputTextarea);
