// #-----------------------------------# //
// #---- Helper (ssMutatorDataHlp) ----# //

/*@ngInject*/
function ssMutatorDataHlp(enumFormTemplateStatus) { return {

	formatPhone (num) {
		var country; // eslint-disable-line no-unused-vars
		var city, prefix, number;

		//clean it up.
		var value = num.replace(/[^\d]/g, '');

		if (value.length === 11) { value = value.slice(1); }
		if (value.length === 10) {
			country = 1;
			city = value.slice(0, 3);
			prefix = value.slice(3,6);
			number = value.slice(6);

			return city + '-' + prefix + '-' + number;
		} else {
			return value;
		}
	},

	getStatus(obj) {
		var now = new Date(),
			status,
			statusLabel;

		if (obj._statusId == null) {
			statusLabel = enumFormTemplateStatus.byValue(0);
		} else {
			statusLabel = enumFormTemplateStatus.byValue(obj._statusId);
		}

		status = statusLabel.toLowerCase();

		if (obj._statusHistory) {

			for (let i = 0; i < obj._statusHistory.length; i++) {
				let from = new Date(obj._statusHistory[i]._from);
				let statusId = obj._statusHistory[i]._statusId;

				if (now < from && statusLabel === 'Inactive') {
					if (statusId === enumFormTemplateStatus.active) {
						statusLabel = `Inactive until ${from.toLocaleDateString()}`;
					}
				}
			}

		}

		return { status, statusLabel };
	}

};}
// #-- END Helper (ssMutatorDataHlp) --# //
// #-----------------------------------# //

angular.module('super-suit-helpers').factory('ssMutatorDataHlp', ssMutatorDataHlp);
