const loadedModules = [];

/*@ngInject*/
function bootstrapLogic(
	$q, $log, $window, ssActiveUserSvc, ssObjectHlp, ssEventSvc, ssAppConfigSvc
) {
	const preloaded = $q.defer();
	const flApp = angular.element(document.getElementById('fl-app'));
	const {glean} = ssObjectHlp;
	let currentMod = null;

	// resolve the reloader defer so that the module bootstrap process can continue
	ssEventSvc.on('preloader-complete', preloaded.resolve, false);

	const setupApp = mod => {
		$log.info('SETUP APP', mod);

		const nonModuleRoutes = ['select'];
		if (!mod.isValid && !nonModuleRoutes.includes(mod.urlValue)) {
			$window.ss.events.fire('page-error-404');
			return;
		}

		// setup the markup for bootstrapping
		flApp.empty();
		flApp.append(`<div id="fl-app-${mod.bootstrapName}"><ui-view></ui-view></div>`);
		bootstrapModule(mod);
	};

	$window.ss.events.on('ui-module-deactivate', teardownApp);
	$window.ss.events.on('ui-module-activate', setupApp, { greedy: true });

	function teardownApp(mod) {
		//> log('tearing down ui-module and clearing appConfig', mod.bootstrapName)
		ssAppConfigSvc.clear();
		angular.element(document.getElementById(`fl-app-${mod.bootstrapName}`))
			.injector()
			.get('$rootScope')
			.$destroy();

		flApp.empty();
	}

	function bootstrapModule(mod) {
		preloaded.promise.then(function () {
			const userCtx = ssActiveUserSvc.getUserContextSync();
			const haveOrgCtx = glean(userCtx, _=>!!_.OrganizationId, false);
			const isInternalApp = glean(userCtx, _=>_.Internal === true, false);
			if (haveOrgCtx || isInternalApp) {
				//> log('bootstrapping', (isInternalApp ? 'INTERNAL' : '') ,'ui-module', mod.bootstrapName)
				angular.bootstrap(document.getElementById(`fl-app-${mod.bootstrapName}`), [mod.bootstrapName]);
				currentMod = mod;
			} else {
				// if we do not have an org context and this product is not an
				// internal frontline app, don't bootstrap a ui-module because
				// the users needs to first pick an org/app context
				//> log('aborting the ui-module bootstrap')
			}
			//> groupEnd('setup app')
		});
	}

	$window.registerUiModule = registerUiModule;
	function registerUiModule(moduleBootstrapName) {
		loadedModules.push(moduleBootstrapName);
		if (currentMod && moduleBootstrapName === currentMod.bootstrapName) {
			bootstrapModule(moduleBootstrapName);
		}
	}
}


/*@ngInject*/
function routerConfig ($locationProvider) { $locationProvider.html5Mode(true); }
angular.module('super').run(bootstrapLogic).config(routerConfig);
