import {Component} from 'base-component';
// #-------------------------------# //
// #---- Component (super-tab) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	tabId: '@',
	label: '@',
	subLabel: '@?'
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function tab() {

	class Tab extends Component {
		/*@ngInject*/
		controller($scope, $log, $element) {
			// view model for tracking state
			const vm = $scope.vm = {
				active: false,
				tabId: $scope.tabId,
				label: $scope.label,
				subLabel: $scope.subLabel || '',
				visible: true,
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				activate() {
					$scope.$emit('ss-tab-activate', vm.tabId);
				}
			};

			$scope.$emit('ss-tab-init', vm.tabId, id => vm.active = (vm.tabId === id));
		}

		link(scope, elem, attrs) {
			scope.$watch('vm.active', function (nv, ov) {
				if (nv === ov) { return; }
				if (nv && !ov) {
					elem[0].focus();
				}
			});
		}
	}

	return new Tab(props, 'tab', config);
}

// #-- END Component (super-tab) --# //
// #-------------------------------# //

angular.module('super-suit-components').directive('superTab', tab);
