// #-----------------------------# //
// #---- Decorator (ss-href) ----# //
/*@ngInject*/
function ssHref() {
	return {
		restrict: 'A',
		link(scope, elem, attrs) {
			var modName = scope.routeData.module,
				href = attrs.ssHref;

			if (!href.startsWith('/')) {
				href = '/' + href;
			}
			elem.attr('href', `/${modName}${href}`);
        }
	};

}
// #-- END Decorator (ss-href) --# //
// #-----------------------------# //
angular.module('super-suit-decorators').directive('ssHref', ssHref);
