(function () {
    'use strict';
    
    var flAuthModule = angular.module('flAuth');

    flAuthModule.run(['flAuthService', 'flAuthSessionService', function (flAuthService, flAuthSessionService) {
        flAuthSessionService.startListening();
    }]);

    function flAuthSession($window, $log, flAuthService, flAuthSessionSettings) {
        var config = flAuthSessionSettings;
        var enabled = angular.isDefined(config.autoRenewEnabled) ? config.autoRenewEnabled : true;
        var events = config.renewEvents || ['click', 'touchstart', 'keyup'];
        var renewCallback = config.renewCallback;
        var renewBufferSeconds = config.renewBufferSeconds || 300;
        var renewBufferMilliseconds = renewBufferSeconds * 1000;
        
        var lastRenewTime;

        function startListening() {
            if (!enabled) {
                return;
            }
            
            renewSession();
            
            events.map(function (event) {
                $window.addEventListener(event, function (e) {
                    renewSession();
                });
            });
        }
        
        function renewSession() {
            var timeElapsedMilliseconds = new Date() - lastRenewTime;

            if (angular.isUndefined(lastRenewTime) || (timeElapsedMilliseconds > renewBufferMilliseconds)) {
                flAuthService.renewSession()
                    .then(function (session) {
                        if (renewCallback && typeof renewCallback === 'function') {
                            renewCallback(session);
                        }
                    })
                    .catch(function (error) {
                        $log.error("Could not auto renew the session.", error);
                    });

                lastRenewTime = new Date();
            }
        }
        
        return {
            startListening: startListening
        };
    }

    flAuthModule.provider('flAuthSessionService', function () {
        var _autoRenewEnabled;
        Object.defineProperty(this, 'autoRenewEnabled', {
            get: function () {
                return _autoRenewEnabled;
            },
            set: function (value) {
                _autoRenewEnabled = value;
            }
        });
        var _renewEvents;
        Object.defineProperty(this, 'renewEvents', {
            get: function () {
                return _renewEvents;
            },
            set: function (value) {
                _renewEvents = value;
            }
        });
        var _renewCallback;
        Object.defineProperty(this, 'renewCallback', {
            get: function () {
                return _renewCallback;
            },
            set: function (value) {
                _renewCallback = value;
            }
        });
        var _renewBufferSeconds;
        Object.defineProperty(this, 'renewBufferSeconds', {
            get: function () {
                return _renewBufferSeconds;
            },
            set: function (value) {
                _renewBufferSeconds = value;
            }
        });
        
        // Obtained from http://stackoverflow.com/a/5639455/1072030 
        function readCookie(name, c, C, i) {
            var cookies;

            c = document.cookie.split('; ');
            cookies = {};

            for (i = c.length - 1; i >= 0; i--) {
                C = c[i].split('=');
                cookies[C[0]] = C[1];
            }

            return cookies[name];
        }
        
        function getConfigOverrides() {
            // Would love to use angular's $Cookie service, but this project isn't setup to use bower dependencies.
            var overridesAsString = readCookie('Config-Overrides');

            if (angular.isUndefined(overridesAsString)) {
                return;
            }

            overridesAsString = decodeURIComponent(overridesAsString);

            return JSON.parse(overridesAsString);
        }
        
        function applyConfigOverrides($log) {
            try {
                var overrides = getConfigOverrides();
                
                if (angular.isUndefined(overrides)) {
                    return;
                }
                
                if (angular.isDefined(overrides.flAuthSessionRenewBufferSeconds)) {
                    _renewBufferSeconds = parseInt(overrides.flAuthSessionRenewBufferSeconds);
                }
            } catch (error) {
                $log.error("An error occurred trying to parse flAuth config overrides", error);
            }
        }
        
        this.$get = ['$window', '$document', '$log', 'flAuthService', function ($window, $document, $log, flAuthService) {
            applyConfigOverrides($log);
            
            var flAuthSessionSettings = {
                autoRenewEnabled: _autoRenewEnabled,
                renewEvents: _renewEvents,
                renewCallback: _renewCallback,
                renewBufferSeconds: _renewBufferSeconds
            };
            return new flAuthSession($window, $log, flAuthService, flAuthSessionSettings);
        }];
    });
})();