/*@ngInject*/
function ssJobTypeSvc($log, $q, ssAsync, ssJobTypesApi, ssActiveUserSvc, ssListSvc, ssServiceLogicHlp, ssObjectHlp, ssConfigurationHlp, ssStorage, ssFlagCCSvc) {
	// #--------------------------------# //
	// #---- Service (ssJobTypeSvc) ----# //

	// list of IDM AppIds that use the microservice
	const usingJobTypeMsvc = ssServiceLogicHlp.productIdList([
		'EC',
		'absmgmt',
		'PD',
		'RH'
	]);

	const { deepCopy } = ssObjectHlp;
	var jobTypes;

	const ready = (ssAsync(function*() {
		let deferred = $q.defer();

		try {
			const orgId = yield ssActiveUserSvc.getOrgId();
			if (orgId == null) { return []; }

			// resolve with nothing if this product doesn't use the jobTypeSvc
			const pId = yield ssActiveUserSvc.getProductId();
			if (!usingJobTypeMsvc.includes(pId)) { return []; }

			ssFlagCCSvc.ready.then(() => {
				return ssFlagCCSvc.checkState('fc_job_type_cache');
			}).then((enabled) => {
				if(enabled) {
					return deferred.resolve(loadJobTypesFull(orgId));
				}
				return deferred.resolve(loadJobTypesIds(orgId));
			});
		} catch (err) {
			$log.error('jobType service =>', err);
			deferred.reject(err);
		}

		return deferred.promise;
	})());

	// External service API
	return {
		getJobTypeById,
		getAll,
		ready
	};

	/*
	 * Used to load a lighter jobTypes collection (primarily `id` attr). Orgs that have a very large amount of jobTypes likely use this version
	*/
	function loadJobTypesIds(orgId) {
		return ssAsync(function*() {
			jobTypes = ssStorage.load(`${orgId}_jobTypes`);

			if (jobTypes == null) {
				jobTypes = (yield ssJobTypesApi.query({
					limit: ssConfigurationHlp.getLimit('jobTypes'),
					fields: 'name'
				}));

				let filteredJobTypes = jobTypes.map(jobType => {
					const obj = jobType.$serialize().data.attributes;

					Object.keys(obj).forEach(function(attr) {
						obj[`_${attr}`] = obj[attr];
						delete obj[attr];
					});

					return Object.assign({ id: jobType.id }, obj);
				});

				ssStorage.save(`${orgId}_jobTypes`, filteredJobTypes);

				return filteredJobTypes;
			} else {
				return jobTypes;
			}
		})();
	}

	/*
	 * Used to load a collection of full jobType objs. Backwards compatible with original version that uses session storage cache
	*/
	function loadJobTypesFull(orgId) {
		return ssAsync(function*() {
			const employmentGroups = yield ssListSvc.getByExternalId('jobtype.employmentgroups');
			jobTypes = ssStorage.load(`${orgId}_jobTypes`);

			if (jobTypes == null) {
				jobTypes = (yield ssJobTypesApi.query({
					include: 'paySchedules,jobTypeDetails',
					limit: ssConfigurationHlp.getLimit('jobTypes')
				}));

				let filteredJobTypes = jobTypes.filter(j => j._bargainingUnit != null)
				.filter(j => j._currentJobTypeDetail != null)
				.filter(j => j._paySchedules.length > 0)
				.map(jobType => {
					const obj = jobType.$serialize().data.attributes;

					Object.keys(obj).forEach(function(attr) {
						obj[`_${attr}`] = obj[attr];
						delete obj[attr];
					});

					//Find current pay schedule
					let now = new Date();
					const pS = jobType._paySchedules.find(pS => {
						let fromDate = new Date(pS._fromDateUtc);
						let toDate = new Date(pS._toDateUtc);

						return (now > fromDate && now < toDate);
					});

					if (pS == null) {
						obj.paySchedule = {};
					} else {
						obj.paySchedule = Object.assign({ id: pS.id },
							pS.$serialize().data.attributes
						);
					}

					obj.employmentGroup = employmentGroups.find(g => {
						return g.value === jobType._bargainingUnit.id;
					});

					// add job type details and pay schedules
					if (jobType._jobTypeDetails != null) { obj._jobTypeDetails = jobType._jobTypeDetails; }
					if (jobType._paySchedules != null) { obj._paySchedules = jobType._paySchedules; }

					return Object.assign({ id: jobType.id }, obj);
				});

				if (filteredJobTypes.length === 0) {
					filteredJobTypes = jobTypes.map(jobType => {
						const obj = jobType.$serialize().data.attributes;

						Object.keys(obj).forEach(function(attr) {
							obj[`_${attr}`] = obj[attr];
							delete obj[attr];
						});

						return Object.assign({ id: jobType.id }, obj);
					});
				}

				ssStorage.save(`${orgId}_jobTypes`, filteredJobTypes);

				return filteredJobTypes;
			} else {
				return jobTypes;
			}
		})();
	}

	function getJobTypeById(id) {
		return deepCopy(jobTypes.find(l => l.id === id));
	}

	function getAll() {
		return deepCopy(jobTypes);
	}

	// #-- END Service (ssJobTypeSvc) --# //
	// #--------------------------------# //
}

angular.module('super-suit-services').factory('ssJobTypeSvc', ssJobTypeSvc);
