
/*@ngInject*/
function ssProductAccessSvc($log, ssActiveUserSvc, ssDeprecated) {
// #--------------------------------------# //
// #---- Service (ssProductAccessSvc) ----# //

	const getMyProductRecords = ssDeprecated(
		'ssProductAccessSvc.getMyProductRecords()',
		'ssActiveUserSvc.getAccessRecords()',
		() => ssActiveUserSvc.getAccessRecords()
	);
	const getActiveUserId = ssDeprecated(
		'ssProductAccessSvc.getActiveUserId()',
		'ssActiveUserSvc.getIdentityByType(\'employee\')',
		() => ssActiveUserSvc.getIdentityByType('employee')
	);

	// External service API
	return {
		getActiveUserId,
		getMyProductRecords,
		ready: ssActiveUserSvc.ready
	};

// #-- END Service (ssProductAccessSvc) --# //
// #--------------------------------------# //
}

angular.module('super-suit-services').factory('ssProductAccessSvc', ssProductAccessSvc);
