/*@ngInject*/
function credentialsApi($http, $q, ssApiSvc, ssAuthSvc, buildConfig, ssDeprecated) {
	// #-----------------------------------------# //
	// #---- API Endpoint (ssCredentialsApi) ----# //

	const baseEmployeeUrl = buildConfig.endpoint.employees;
	const baseCredentialUrl = buildConfig.endpoint.credentials;
	const baseUrl = buildConfig.endpoint.new_credentials;

	/* OLD CREDENTIAL  */

	const oldUrl = `${baseCredentialUrl}/credentials`;
	const model = 'credentials';
	// possible relationships
	const rel = [
		'endorsements'
	];

	// Expose the underlying config because the employeeApi needs this to manage creds/endorsements internally
	var config = {
		model: model,
		rel: rel
	};

	const getCredentials = ssDeprecated(
		'ssCredentialsApi.getCredentials',
		'ssCredentialsApi. {New Method yet to be created}',
		ssApiSvc.endpoint({ url: oldUrl, method: 'GET' }, model, rel)
	);

	/* END REGION  */

	// base endpoint url

	const baseFileRecordUrl = ({ employeeId }) => `${baseEmployeeUrl}/employees/${employeeId}/fileRecords?organizationId=:ORGID:`;
	const uploadFileUrl = ({ employeeId, fileId }) => `${baseEmployeeUrl}/employees/${employeeId}/fileRecords/${fileId}`;
	const employeeCredentialUrl = ({ employeeId, credentialId }) => `${baseEmployeeUrl}/employees/${employeeId}/employeeCredentials/${credentialId || ''}`;
	const employeeCredentialFileUrl = ({ employeeId, credentialId, fileId = '' }) => `${baseEmployeeUrl}/employees/${employeeId}/employeeCredentials/${credentialId}/files/${fileId || ''}`;
	const issuerByIdUrl = (issuerId) => `${baseUrl}/Issuers/${issuerId}`;
	const credentialByIdUrl = (credentialId) => `${baseUrl}/credentials/${credentialId}`;

	const credentialsUrl = `${baseUrl}/Credentials`;
	const credentialTypesUrl = `${baseUrl}/CredentialTypes`;
	const issuersUrl = `${baseUrl}/Issuers`;
	const subjectsUrl = `${baseUrl}/Subjects`;

	// model data type
	const employeeModel = 'employees';
	const employeeCredentialModel = 'publicEmployeeCredentialV1';
	const credentialModel = 'credential';
	const credentialTypeModel = 'credentialtype';
	const issuerModel = 'issuer';
	const subjectModel = 'subject';

	const getEmployeeCredential = ssApiSvc.endpoint({ url: employeeCredentialUrl, method: 'GET' }, employeeCredentialModel, []);
	const queryEmployeeCredentials = ssApiSvc.endpoint({ url: employeeCredentialUrl, method: 'GET' }, employeeCredentialModel, []);
	const addEmployeeCredential = ssApiSvc.endpoint({ url: employeeCredentialUrl, method: 'POST' }, employeeCredentialModel, []);
	const patchEmployeeCredential = ssApiSvc.endpoint({ url: employeeCredentialUrl, method: 'PATCH' }, employeeCredentialModel, []);

	const addFileRecord = ssApiSvc.endpoint({ url: baseFileRecordUrl, method: 'POST' }, employeeModel, []);
	const uploadFileRecord = (employeeId, fileId, file) => {
		// debugger;
		var fd = new FormData();
		fd.append('file', file); // If/when we support uploading multiple files, can use extra param and if statement
		return $http.post(`${uploadFileUrl({ employeeId, fileId })}?version=0`, fd, {
			headers: {
				'Content-Type': undefined,
				'Authorization': `Bearer ${ssAuthSvc.token.access}`
			}
		}).then(function(response) {
			if (response.status > 299 || response.status < 200) {
				return $q.reject(response);
			}
			if (!response.data) {
				return $q.reject(Error('response did not contain any data'));
			}
			return response.data;
		});
	};
	const associateFile = ssApiSvc.endpoint({ url: employeeCredentialFileUrl, method: 'POST' }, employeeCredentialModel, []);
	const deleteFile = ssApiSvc.endpoint({ url: employeeCredentialFileUrl, method: 'DELETE' }, employeeCredentialModel, []);

	const queryCredentials = ssApiSvc.endpoint({ url: credentialsUrl, method: 'GET' }, credentialModel, []);
	const queryCredentialTypes = ssApiSvc.endpoint({ url: credentialTypesUrl, method: 'GET' }, credentialTypeModel, []);
	const queryIssuers = ssApiSvc.endpoint({ url: issuersUrl, method: 'GET' }, issuerModel, []);
	const querySubjects = ssApiSvc.endpoint({ url: subjectsUrl, method: 'GET' }, subjectModel, []);

	const addIssuer = ssApiSvc.endpoint({ url: issuersUrl, method: 'POST' }, issuerModel, []);
	const patchIssuer = ssApiSvc.endpoint({ url: issuersUrl, method: 'PATCH' }, issuerModel, []);
	const deleteIssuer = ssApiSvc.endpoint({ url: issuersUrl, method: 'DELETE' }, issuerModel, []);

	const addCredential = ssApiSvc.endpoint({ url: credentialsUrl, method: 'POST' }, credentialModel, []);
	const patchCredential = ssApiSvc.endpoint({ url: credentialsUrl, method: 'PATCH' }, credentialModel, []);
	const deleteCredential = ssApiSvc.endpoint({ url: credentialsUrl, method: 'DELETE' }, credentialModel, []);

	const addSubject = ssApiSvc.endpoint({ url: subjectsUrl, method: 'POST' }, subjectModel, []);
	const patchSubject = ssApiSvc.endpoint({ url: subjectsUrl, method: 'PATCH' }, subjectModel, []);
	const deleteSubject = ssApiSvc.endpoint({ url: subjectsUrl, method: 'DELETE' }, subjectModel, []);

	const queryIssuerById = ssApiSvc.endpoint({ url: issuerByIdUrl, method: 'GET' }, issuerModel, []);
	const queryCredentialById = ssApiSvc.endpoint({ url: credentialByIdUrl, method: 'GET' }, credentialModel, []);

	const downloadSubjectsCsv = (issuerId) => {
		let urlConfig = {
			method: 'GET',
			url: `${baseUrl}/subjects/csv?` + encodeURIComponent('filter[issuerid]') + `=${issuerId}`
		};
		return $http(urlConfig);
	};

	const downloadCredentialsCsv = (issuerId) => {
		let urlConfig = {
			method: 'GET',
			url: `${baseUrl}/credentials/csv?` + encodeURIComponent('filter[issuerid]') + `=${issuerId}`
		};
		return $http(urlConfig);
	};

	/**
	 * Makes a call to the Employee Credentials Report endpoint, and returns the data as a CSC
	 * @param {string} filter The filter (pre-encoded) that will be applied to the report endpoint to get a result set as CSV
	 */
	const downloadEmployeeCredentialsReportCsv = (filter) => {
		let urlConfig = {
			method: 'GET',
			url: `${baseEmployeeUrl}/employeeCredentials/csv?${filter}`
		};
		return $http(urlConfig);
	};

	// API endpoint methods
	return {
		addFileRecord,
		uploadFileRecord,
		associateFile,
		deleteFile,
		employeeCredentialFileUrl,
		getEmployeeCredential,
		queryEmployeeCredentials,
		addEmployeeCredential,
		patchEmployeeCredential,
		queryCredentials,
		queryCredentialTypes,
		queryIssuers,
		querySubjects,
		getCredentials,
		config,
		addIssuer,
		patchIssuer,
		deleteIssuer,
		addCredential,
		patchCredential,
		deleteCredential,
		queryIssuerById,
		queryCredentialById,
		addSubject,
		patchSubject,
		deleteSubject,
		downloadCredentialsCsv,
		downloadSubjectsCsv,
		downloadEmployeeCredentialsReportCsv
	};

	// #-- END API Endpoint (ssCredentialsApi) --# //
	// #-----------------------------------------# //
}

angular.module('super-suit-apis').factory('ssCredentialsApi', credentialsApi);
