import {
	Enums
}
from 'base-enum';

// #------------------------------------# //
// #---- Enums (/relationshipTypes) ----# //

class RelationshipTypesEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Spouse',
			value: 0
		}, {
			name: 'Parent',
			value: 1
		}, {
			name: 'Friend',
			value: 2
		}, {
			name: 'Other',
			value: 3
		}];
		super(enums);
	}
}

// #---- End Enums (/relationshipTypes) ----# //
// #----------------------------------------# //

angular.module('super-suit-enums').factory('enumRelationshipTypes', () => new RelationshipTypesEnum());
