/*@ngInject*/
function microcredentialsApi($http, buildConfig) {
	// #---------------------------------------------# //
	// #---- API Endpoint (ssMicrocredentialsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.microcredentials;

	const removeEmptyProperty = query => {
		return prop => {
			return query[ prop ] || typeof query[ prop ] === 'number';
		};
	};

	const buildQuery = query => {
		return key => {
			return key + '=' + query[ key ];
		};
	};

	const appendQuery = query => {
		return '?' + Object.keys( query || {} )
			.filter( removeEmptyProperty( query ) )
			.map( buildQuery( query )  )
			.join( '&' );
	};

	const getMicrocredential = microcredentialId => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/microcredentials/${microcredentialId}`,
		});
	};

	const getMicrocredentials = () => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/microcredentials`,
		});
	};

	const getEvidenceUploadUrl = (microcredentialId, microcredentialRequirementId) => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/microcredentials/${microcredentialId}/evidence-requirements/${microcredentialRequirementId}/evidence/attachment-upload-url`,
		});
	};

	const getAssessments = ( query ) => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/assessments` + appendQuery( query ),
		});
	};

	const getAssessment = ( userId, evidenceRequirementId ) => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/assessments/users/${userId}/evidence-requirements/${evidenceRequirementId}`
		});
	};

	const getAssessmentsSubmitters = ( query ) => {
		return $http({
			method: 'GET',
			url: `${baseUrl}/v1/assessments/users` + appendQuery( query ),
		});
	};

	// API endpoint methods
	return {
		getEvidenceUploadUrl, // Get a signed POST url and required form fields for uploading a file for evidence
		getMicrocredential, // Get a microcredential
		getMicrocredentials, // Get all microcredentials
		getAssessments, // Get list of assessments
		getAssessment, // Get user specific assessment
		getAssessmentsSubmitters // Get list of users who has assessments
	};

	// #-- END API Endpoint (ssResourceLibraryApi) --# //
	// #---------------------------------------------# //
}

angular
	.module('super-suit-apis')
	.factory('ssMicrocredentialsApi', microcredentialsApi);
