import {
	Enums
}
from 'base-enum';

// #--------------------------------# //
// #---- Enums (/maritalStatus) ----# //

class MaritalStatusEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Single',
			value: 0
		}, {
			name: 'Married',
			value: 1
		}, {
			name: 'Separated',
			value: 2
		}, {
			name: 'Divorced',
			value: 3
		}, {
			name: 'Widowed',
			value: 4
		}, {
			name: 'Civil Union',
			value: 5
		}];
		super(enums);
	}
}

// #---- End Enums (/maritalStatus) ----# //
// #------------------------------------# //

angular.module('super-suit-enums').factory('enumMaritalStatus', () => new MaritalStatusEnum());
