// #--------------------------------------# //
// #---- Mutator (mutatorSupervisors) ----# //

/*@ngInject*/
function supervisorsMutator(ssDateHlp) { return {

	// sets obj.example to obj.name.toUpperCase()
	'isCurrent': function (supervisor) {
		var today = moment();
		var fromDate = moment(supervisor._from);
		var toDate = moment(supervisor._to);
		return fromDate.isSameOrBefore(today) && toDate.isAfter(today);
	},
	'isFuture': function(supervisor){
		var today = moment();
		var fromDate = moment(supervisor._from);
		var toDate = moment(supervisor._to);
		return (fromDate.isSameOrAfter(today) && toDate.isAfter(today));
	},
	'isPast': function(supervisor){
		var today = moment();
		var toDate = moment(supervisor._to);
		var fromDate = moment(supervisor._from);
		return fromDate.isBefore(today) && toDate.isSameOrBefore(today);
	}
};}

// #---- End Mutator (mutatorSupervisors) ----# //
// #------------------------------------------# //

angular.module('super-suit').factory('mutatorSupervisors', supervisorsMutator);
