import { Component } from 'base-component';
// #---------------------------------------# //
// #---- Component (super-file-upload) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	label: '@?',
	required: '=',
	model: '=',
	isDisabled: '=?',
	editMode: '=?',
	preventMultipleUploads: '=?',
	filteredFileTypes: '='
};

// directive definition object overrides
const config = {
};

/*@ngInject*/
function fileUpload(ssValidateHlp){

	class FileUpload extends Component {

		/*@ngInject*/
		controller($scope, $stateParams, $window, $element, ssAuthSvc, filenameHlp, FileUploader, ssFormTemplatesApi, ssAsync, ssFileApi, ssToastHlp){
			// view model for tracking state
			const MAX_FILESIZE = 52428800;
			const MAX_FILESIZE_ERROR_MSG = 'The file upload size cannot exceed 50MB. Please upload a smaller file.';
			const FILE_UPLOAD_ERROR_TITLE = 'File Upload Failed';
			const FILE_UPLOAD_ERROR_MSG = 'The selected file did not upload correctly. Please try again.';
			const FILE_EXISTS_ERROR_MSG = 'You have already uploaded a file by that name.';

			if ($scope.model == null) { $scope.model = []; }
			if ($scope.filteredFileTypes == null) { $scope.filteredFileTypes = []; }

			const vm = $scope.vm = {
				files: $scope.model || [],
				isDisabled: !$scope.isDisabled && $stateParams.editMode ? $stateParams.editMode.toLowerCase() === 'read-only' : $scope.isDisabled,
				editMode: $scope.editMode == null || $scope.editMode === true, // strict equality to not bind it to the $scope
			};

			// reference isn't found without the setTimeout
			setTimeout(() => { $scope.fileInput = $element.find('input'); }, 0);

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				getIcon(file) {
					return filenameHlp.getIcon(filenameHlp.getExtension(file.filename));
				},

				download(id) {
					vm.loading = true;
					vm.loadingStatus = 'downloading file...';
					ssFormTemplatesApi.getFileUrl({
						id: id
					}).then(success => {
						vm.loading = false;
						vm.loadingStatus = '';
						$window.open(success._string, '_blank');
					}).catch((err) => {
						vm.loading = false;
						vm.loadingStatus = '';
						ssToastHlp.error('There was an error downloading the file', 'Error');
						//>log('Error getting file', e);
					});
				},

				deleteFile(fileItem) {
					$scope.statusElem.text('');
					vm.loading = true;
					vm.loadingStatus = 'deleting file...';
					ssAsync(function* () {
						try {
							let idx = vm.files.indexOf(fileItem);
							if (idx > -1) {
								vm.files.splice(idx, 1);
							}
							yield ssFileApi.removeFile(fileItem.id);
							$scope.statusElem.text('the file has been successfully deleted');
							$scope.$emit('ss-validate-reset');
						} catch (err) {
							//> error('problem deleting file', err)
							$scope.statusElem.text('the file was not successfully deleted');
						} finally {
							vm.loading = false;
							vm.loadingStatus = '';
							// rebind the viewModel to the form instance step
							$scope.model = vm.files;
						}
					})();
				},

				shouldShowUpload(){
					if ($scope.preventMultipleUploads && vm.files.length >= 1) {
						return false;
					}else {
						return true;
					}
				}
			};

			// Instantiate the Angular file uploader
			var uploader = $scope.uploader = new FileUploader({
				withCredentials: false,
				headers: {
					Authorization: `Bearer ${ssAuthSvc.token.access}`
				}
			});

			// Prevents non-image type files from being uploaded.
			if ($scope.filteredFileTypes.length){
				uploader.filters.push({
					name: 'imageFilter',
					fn: function(item, options) {
						var type = item.type.slice(item.type.lastIndexOf('/') + 1);
						if ($scope.filteredFileTypes.indexOf(type) !== -1){
							return true;
						}else {
							ssToastHlp.error(`Invalid file type. Please use one of the following: ${$scope.filteredFileTypes.join(', ')}`);
							return false;
						}
					}
				});
			}

			uploader.onAfterAddingFile = function (fileItem) {

				$scope.statusElem.text('');

			//	check to see if the user has already uploaded a file with the same name
				if(vm.files.findIndex((file) => {
					return fileItem.file.name === file.filename;
				}) >= 0 ) {
					ssToastHlp.error(FILE_EXISTS_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
					uploader.clearQueue();
					return;
				}

			//	check the file size
				if (fileItem._file.size == null && fileItem._file.size >= 0 && fileItem._file.size >= MAX_FILESIZE) {
					ssToastHlp.error(MAX_FILESIZE_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
					uploader.clearQueue();
					return;
				}

				// create file id
				ssFormTemplatesApi.attachFile({}, {
					filename: fileItem.file.name,
					fileType: 2
				}, null, false).then(file => {
					vm.loading = true;
					vm.loadingStatus = 'uploading file...';
					vm.file = { filename: file._filename, id: file.id, isSuccess: true };
					fileItem.url = ssFormTemplatesApi.getUploadFileUrl({ id: vm.file.id });
					fileItem.upload();
				}).catch((err) => {
					if (err.status === 422){
						ssToastHlp.error(err.data.errors.Title, FILE_UPLOAD_ERROR_TITLE);
					}
					vm.loading = false;
					vm.loadingStatus = '';
					$scope.fileInput.val('');
					uploader.clearQueue();
				});

			};

			uploader.onAfterAddingAll = function (addedFileItems) {
				$scope.$emit('ss-validate-field-event', $element, 'change');
			};

			uploader.onBeforeUploadItem = function (item) {
			};

			uploader.onProgressAll = function (progress) {
			};

			uploader.onErrorItem = function (fileItem, response, status, headers) {
				vm.loading = false;
				vm.loadingStatus = '';
				// clear the file input
				$scope.fileInput.val('');
				ssToastHlp.error(FILE_UPLOAD_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
			};

			uploader.onCancelItem = function (fileItem, response, status, headers) {
				// user cancelled file upload
				vm.loading = false;
				vm.loadingStatus = '';
				// clear the file input
				$scope.fileInput.val('');
				ssToastHlp.info('File upload cancelled.');
			};

			uploader.onSuccessItem = function (fileItem, response, status, headers) {
				// Something happened
				if (!fileItem.isSuccess) {
					ssToastHlp.error(FILE_UPLOAD_ERROR_MSG, FILE_UPLOAD_ERROR_TITLE);
					(ssAsync(function* () {
						vm.loading = true;
						try {
							yield ssFileApi.remove(fileItem, null, false);
						} catch (err) {
							//> error('problem deleting file', err)
						} finally {
							vm.loading = false;
							vm.loadingStatus = '';
						}
					}))();
					return;
				}

				// file upload successful
				vm.loading = false;
				vm.loadingStatus = '';
				vm.files.push(vm.file);
				$scope.statusElem.text('the file has been uploaded successfully');
				$scope.model = [...$scope.model, vm.file];
			};

			uploader.onCompleteItem = function (fileItem, response, status, headers) {
			};

			uploader.onCompleteAll = function () {
				vm.loading = false;
				vm.loadingStatus = '';

				// clear the file input
				$scope.fileInput.val('');

				//Reset the file uploader state
				if (vm.files.length) { uploader.queue = []; }

				$scope.$emit('ss-validate-reset');
			};
		}

		link(scope, elem, attrs) {

			//	get a reference to the status element thats used to append A11Y messaging
			scope.statusElem = angular.element(elem[0].querySelector('.attachment-status-alert'));

			var input = elem.find('input');

			// if the input is not visible, this is not editable
			// and validation events should not be triggered
			if (input.length === 0) { return; }

			input.on('focus', ssValidateHlp.makeEventEmitter(scope, input));
			input.on('click', () => {
				ssValidateHlp.resetValidation(elem);
			});
			input.on('blur', ssValidateHlp.makeEventEmitter(scope, input));
		}
	}

	return new FileUpload(props, 'file-upload', config);
}

// #-- END Component (super-file-upload) --# //
// #---------------------------------------# //

angular.module('super-suit-components').directive('superFileUpload', fileUpload);
