import {Component} from 'base-component';
// #---------------------------------------------------# //
// #---- Component (super-multi-filter-list-group) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	label: '@'
};

// directive definition object overrides
const config = {
	transclude: true
};

/*@ngInject*/
function multiFilterListGroup() {

	class MultiFilterListGroup extends Component {
		/*@ngInject*/
		controller($scope) {
			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				isFirstVisible: false,
				hide: false,
				options: [],
				label: $scope.label
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = {}; // eslint-disable-line no-unused-vars

			function shouldHide() {
				return vm.options.every(({hide = false} = {}) => hide);
			}

			$scope.$on('multi-filter-list-item-added', (evt, val) => {
				// push the option into our internal list of options
				vm.options.push(val);

				// if the item is removed, remove it from our list of options.
				evt.targetScope.$on('$destroy', function () {
					vm.options = vm.options.filter(v => v !== val);
				});

				// do not call evt.stopPropagation() since we want this event
				// to bubble up to multi-filter-list
			});

			// hide the group altogether if all of its options are hidden (due to filtering)
			$scope.$watch(shouldHide, (nVal, oVal) => {
				if (nVal === oVal) { return; }

				vm.hide = nVal;
				$scope.$emit('multi-filter-list-group-visibility-changed', $scope);
			});

			$scope.$emit('multi-filter-list-group-added', $scope);
		}
	}

	return new MultiFilterListGroup(props, 'multi-filter-list-group', config);
}

// #-- END Component (super-multi-filter-list-group) --# //
// #---------------------------------------------------# //

angular.module('super-suit-components').directive('superMultiFilterListGroup', multiFilterListGroup);
