/*@ngInject*/
function ssDebounce($timeout) {
// #------------------------------# //
// #---- Utility (ssDebounce) ----# //

	// External utility injectable value
	// eslint-disable-next-line no-shadow
	return function ssDebounce(func, wait, immediate, invokeApply) {
		var timeout, args, context, result;

		function debounce() {
			context = this;
			args = arguments;

			var later = function () {
				timeout = null;
				if (!immediate) {
					result = func.apply(context, args);
				}
			};

			var callNow = immediate && !timeout;
			if (timeout) {
				$timeout.cancel(timeout);
			}

			timeout = $timeout(later, wait, invokeApply);

			if (callNow) {
				result = func.apply(context, args);
			}

			return result;
		}

		debounce.cancel = function () {
			$timeout.cancel(timeout);
			timeout = null;
		};

		return debounce;
	};

// #-- END Utility (ssDebounce) --# //
// #------------------------------# //
}

angular.module('super-suit-utilities').factory('ssDebounce', ssDebounce);
