import {Component} from 'base-component';
// #--------------------------------# //
// #---- Component (super-grid) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	selectable: '=?', // should this grid have selectable rows?
	onSelectAll: '&', // action to perform when the user chooses to "Select All"
	onSelectNone: '&', // action to perform when the user chooses to "Select None"
	sortable: '=?', // reference to the data array that backs this grid (so it can be sorted by the grid)
	defaultSort: '@', // default sorting value
	defaultSortDirection: '@', // sort direction ("asc" or "desc")
	defaultRowHeight: '@', // height of each row in pixels (default 38)
	onSortChange: '&', // action to perform when the user chooses to change the sorting
	hoverable: '=?',
	disableSelectAll: '@' // don't show the "Select All/Select None" button
};

// directive definition object overrides
const config = {
	transclude: {
		header: 'gridHeader',
		rows: 'gridRows'
	},
};

/*@ngInject*/
function grid(ssDebounce) {

	class Grid extends Component {

		/*@ngInject*/
		controller($scope, $log, $parse, $attrs, ssArrayHlp, rAF) {
			/////
			// set defaults for un-required values
			//
			if ($scope.defaultSortDirection == null) { $scope.defaultSortDirection = 'asc'; }
			if ($scope.defaultRowHeight == null) { $scope.defaultRowHeight = 38; }

			/////
			// Setup variables used throughout this component
			//
			const sortExpressions = new Map();
			const rows = [];
			// var checkingInView = false;

			// grid level vm for storing state that the cells, rows,
			// and columns need access to
			const gridData = $scope.gridData = {
				sortBy: $scope.defaultSort,
				sortDirection: $scope.defaultSortDirection,
				selectable: $scope.selectable,
				hoverable: $scope.hoverable,
				defaultRowHeight: $scope.defaultRowHeight,
				columnScopes: [],
				cellSizeCache: new Map(),
				// debounceCheckInView: function () {
				// 	if (checkingInView) { return; }
				// 	checkingInView = true;

				// 	rAF.raw(function () {
				// 		checkInView();
				// 		checkingInView = false;
				// 	});
				// }
			};

			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				// disableSelectAll is false by default, true if present (unless
				// specifically set to "false")
				disableSelectAll: (
					$scope.disableSelectAll != null
					&& $scope.disableSelectAll.toLowerCase() !== 'false'
				),
				rowCount: 0
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				selectAll() {
					for (let r of rows) { r.vm.selected = true; }
					$scope.onSelectAll({rows: rows});
				},
				selectNone() {
					for (let r of rows) { r.vm.selected = false; }
					$scope.selectable.length = 0;
					$scope.onSelectNone();
				}
			};

			$scope.$watchCollection('selectable', function(newRows, oldRows) {
				if (newRows === oldRows) {return;}

				// check for rows that used to be selected, but now are not
				oldRows.filter(oldRow => !newRows.includes(oldRow)).forEach(function (removed) {
					const rowToRemove = rows.find(row => row.selectedValue === removed);
					if (rowToRemove == null) { return; }
					rowToRemove.vm.selected = false;
				});

				// check for rows that used to not be selected, but now are
				newRows.filter(newRow => !oldRows.includes(newRow)).forEach(function (added) {
					const rowToAdd = rows.find(row => row.selectedValue === added);
					if (rowToAdd == null) { return; }
					rowToAdd.vm.selected = true;
				});
			});

			/////
			// Setup watchers and event listeners for scope data
			//

			// watch the sort data. When it changes, generate the row sortValue
			// from the sort expression and sort on it
			$scope.$watchGroup(['gridData.sortBy', 'gridData.sortDirection', 'sortable', 'sortable.length'],
				function (nSort, oSort) {
					const [sortBy, sortDirection, sortable] = nSort;
					const [oldSortBy, oldSortDirection] = oSort;

					// no sort data, don't sort!
					if (sortBy == null) { return; }
					if (sortBy === '') { return; }

					const sortChanged = sortBy !== oldSortBy || sortDirection !== oldSortDirection;

					// fire onSortChange handler if available
					if (sortChanged && $attrs.onSortChange != null) {
						$scope.onSortChange({sortBy, sortDirection});
					}

					// no sort data, don't sort!
					if (sortable == null) { return; }

					// perform default in-memory sort
					localSort({sortBy, sortDirection, sortable});

					// gridData.debounceCheckInView();
				}
			);

			// when a column is clicked, this event will be fired by the column
			// we need to update the grid's sort data which will fire a re-sort
			$scope.$on('super-grid-column-sort', function (evt) {
				evt.stopPropagation(); // capture this event
				const col = evt.targetScope;
				const sortBy = col.sortBy;
				const defaultSortDirection = col.defaultSortDirection;

				if (sortBy === gridData.sortBy) {
					gridData.sortDirection = (gridData.sortDirection === 'asc' ? 'desc' : 'asc' );
				} else {
					gridData.sortBy = sortBy;
					gridData.sortDirection = defaultSortDirection;
				}
			});

			// when a column initially instantiates, handle it
			$scope.$on('super-grid-column-init', function (evt, columnIndex) {
				evt.stopPropagation(); // capture this event
				const colScope = evt.targetScope;

				// push the new column onto our array for tracking
				gridData.columnScopes.splice(columnIndex, 0, colScope);

				// check the columnScopes for the number of column scopes with a'sortBy' value, and if there are more 1, then allow the 'sorted' class to be added to the columns.
				gridData.addSortClass = gridData.columnScopes.filter((col) => {
					return col.sortBy != null;
				}).length > 1;

				// pull the column out if the scope is torn down
				colScope.$on('$destroy', function () {
					const removeIdx = gridData.columnScopes.indexOf(colScope);
					gridData.columnScopes.splice(removeIdx, 1);
				});

				// recalc our column widths
				calculateColumnWidths();
			});

			// when a row first instantiates, handle it
			$scope.$on('super-grid-row-init', function (evt) {
				evt.stopPropagation(); // capture this event
				const row = evt.targetScope;

				if ($scope.selectable != null) {
					const dupIdx = rows.findIndex(r => r.selectedValue === row.selectedValue);

					if (dupIdx >= 0) {
						rows.splice(dupIdx, 1);
					}
				}

				vm.rowCount += 1;
				row.$on('$destroy', function () {
					vm.rowCount -= 1;
					if ($scope.selectable != null) {
						let removeIdx = $scope.selectable.indexOf(row.selectedValue);
						if (removeIdx >= 0) {
							$scope.selectable.splice(removeIdx, 1);
						}
					}
					// gridData.debounceCheckInView();
				});

				rows.push(row);
				// gridData.debounceCheckInView();

				// if this row starts out as selected, then select it
				if ($scope.selectable != null && $scope.selectable.includes(row.selectedValue)) {
					row.vm.selected = true;
				}
			});

			// a row was selection has changed
			$scope.$on('super-grid-row-selection', function (evt) {
				evt.stopPropagation(); // capture this event

				const row = evt.targetScope;

				if (!row.vm.selected && $scope.selectable.length === 0) { return; }

				if (row.vm.selected) {
					if ($scope.selectable.includes(row.selectedValue)) { return; }
					$scope.selectable.push(row.selectedValue);


				} else {
					let removeIdx = $scope.selectable.indexOf(row.selectedValue);
					if (removeIdx >= 0) {
						$scope.selectable.splice(removeIdx, 1);
					} else {
						$log.warn('super-grid :: could not find the row\'s selected-value =>', row.selectedValue);
					}
				}
			});

			/////
			// helper functions
			//

			// function checkInView() {
			// 	angular.element(window).triggerHandler('checkInView');
			// 	$scope.$root.safeDigest($scope);
			// }

			function localSort({ sortBy, sortDirection, sortable } = {}) {
				if (!sortExpressions.has(sortBy)) {
					sortExpressions.set(sortBy, $parse(sortBy));
				}
				const sortFunc = sortExpressions.get(sortBy);
				const reverseSort = (sortDirection === 'desc');

				for(let row of $scope.sortable) {
					const rawVal = sortFunc(row);
					row.$$sortValue = (rawVal == null ? '' : rawVal.toString().toLowerCase());
				}
				ssArrayHlp.sortByObjectKey($scope.sortable, '$$sortValue', reverseSort);
			}

			// calculate all column widths.
			function calculateColumnWidths() {
				var numOfStaticColumns = 0;
				var numOfDynamicColumns = 0;
				var numOfFillerColumns = 0;
				var percentTotalWidth = 0;
				var staticTotalWidth = 0;
				var staticAmtReducer = 0;
				var fillerColumnWidth;

				gridData.columnScopes.forEach(function (col) {
					if (col.width == null || col.width === '') {
						numOfFillerColumns++;
					} else if (col.width.endsWith('px')) {
						numOfStaticColumns++;
						staticTotalWidth += parseFloat(col.width);
					} else if (col.width.endsWith('%')) {
						numOfDynamicColumns++;
						percentTotalWidth += parseFloat(col.width);
					}
				});

				if (numOfFillerColumns > 0) {
					fillerColumnWidth = (100 - percentTotalWidth) / numOfFillerColumns;
				}

				if (numOfStaticColumns > 0) {
					staticAmtReducer = staticTotalWidth / (numOfDynamicColumns + numOfFillerColumns);
				}

				gridData.columnScopes.forEach(function (col) {
					if (col.width == null || col.width === '') {
						col.vm.styles.width = `calc(${fillerColumnWidth}% - ${staticAmtReducer}px)`;
					} else if (col.width.endsWith('px')) {
						col.vm.styles.width = col.width;
					} else if (col.width.endsWith('%')) {
						col.vm.styles.width = `calc(${col.width} - ${staticAmtReducer}px)`;
					}
				});
			}
		}
	}

	return new Grid(props, 'grid', config);
}

// #-- END Component (super-grid) --# //
// #--------------------------------# //

angular.module('super-suit-components').directive('superGrid', grid);
