import {Decorator} from 'base-decorator';
// #---------------------------------# //
// #---- Decorator (ss-validate) ----# //


/*@ngInject*/
function ssValidate($timeout, $q, rAF, ssValidateHlp, ssElemHlp) {

	const untestedClass = 'ss-validate-untested';
	const statusMap = new WeakMap();

	class Validate extends Decorator {
		compile() {return { pre, post };}
	}

	function pre(scope, elem, attrs) {
		// setup status map to handle
		setStatus(elem, { valid: null, children: [] });
	}

	function post(scope, elem, attrs) {
		const elStatus = statusMap.get(elem[0]);
		const expr = attrs.ssValidate;
		// const
		var shortDebounce = 100;
		var longDebounce = 700;
		var asyncValidate = false;
		var currentValidatePromise = $q.resolve();
		var validateTimer = $q.resolve();
		var hasFocus = false;
		var showErrorsTimer;
		var untested = true;

		elem.addClass(untestedClass);
		elem.append('<ul class="ss-validate-messages" aria-live="polite" data-test="error-messages"></ul>');

		scope.$on('ss-validate-field-event', formFieldEventFired);
		scope.$emit('ss-validate-handler', goValidate, resetValidation, elem);

		/**
		 * Resets the state of validation to "untested"
		 */
		function resetValidation() {
			// cancel the focusEventDebounced if it's currently active
			// otherwise errors will show up for a field that has been reset
			if (showErrorsTimer != null && showErrorsTimer.$$state.status === 0) {
				$timeout.cancel(showErrorsTimer);
			}
			elStatus.valid = null;
			untested = true;
			ssValidateHlp.resetValidation(elem);
		}

		/**
		 *  Handles a form event regarding a modification to a field being watched; 'click', 'blur', etc
		 *
		 * @param {any} emitted Event metadata
		 * @param {jQueryLite} formElem The parent form
		 * @param {string} eventType The type of event that fired
		 * @returns void
		 */
		function formFieldEventFired (emitted, formElem, eventType) {
			const focusEvent = ['focus', 'blur'].includes(eventType);

			// Note: we are testing both if we're validating a child AND the element itself because, currently,
			//	the DigSignature widget changes its state in order to be valid, and we emit the entire widget on 'ss-validate-field-event'
			//	so we need to be able to validate the whole widget sometimes.
			if (ssElemHlp.isMyChild(elem, formElem) || elem === formElem) {
				if (!ssValidateHlp.allChildrenAreValid(elStatus)) {
					ssValidateHlp.showErrors(elem, false);
					return;
				}
				if (focusEvent) {
					$timeout.cancel(showErrorsTimer);
					showErrorsTimer = $timeout(focusEventDebounced, 200);
				} else if (!untested) {
					if (eventType === 'keyup' && !asyncValidate) { ssValidateHlp.clearFadedState(elem); }
					$timeout.cancel(validateTimer);
					validateTimer = $timeout(function () {
						ssValidateHlp.clearFadedState(elem);
						goValidate.call(null, formElem, eventType);
					}, (asyncValidate ? longDebounce : shortDebounce));
				}
			}

			function focusEventDebounced() {
				const validateDebounceWaiting = validateTimer.$$state.status === 0;
				const asyncValidationRunning = currentValidatePromise.$$state.status !== 2;
				const waitForValidation = validateDebounceWaiting || asyncValidationRunning;
				// track focus state
				hasFocus = eventType === 'focus';

				if (untested && !hasFocus) {
					untested = false;
					elem.removeClass(untestedClass);
					goValidate.call(null, formElem, eventType);
				} else {
					if (waitForValidation) { return; }
					ssValidateHlp.showErrors(elem, eventType === 'blur');
				}
			}
		}

		/**
		 * Validates a form element according to the expression passed in to the validator.
		 *
		 * @param {any} formElem The parent form
		 * @param {any} eventType The event that was raised that go us to this method
		 * @returns {promise} describing the result of a validation test
		 */
		function goValidate(formElem, eventType) {
			var vPromise, vResult;

			if (!ssValidateHlp.allChildrenAreValid(elStatus)) { return; }

			// if we have previously run validation and the user tries to
			// submit, return the results of our last validation attempt
			if (!untested && eventType === 'submit') { return currentValidatePromise; }
			if (untested) { elem.removeClass(untestedClass); untested = false; }

			// run the validation expression
			vResult = scope.$eval(expr, {$eventType: eventType});

			// turn the result into a promise that we can wait for
			// currentValidatePromise is a second reference to this promise so once it's
			// resolved we can verify that this is still the most recent validation attempt
			vPromise = currentValidatePromise = ssValidateHlp.validationPromise(vResult);
			if (vPromise.$$state.status === 0) {
				// this promise didn't resolve immediately add the async class
				elem.addClass('ss-validate-async');
				// and let the rest of the code know that we are async now
				asyncValidate = true;
			}

			vPromise.then(
				// passing
				function () {
					// if another validation attempt has been fired this one is no
					// longer applicable, bail out
					if (vPromise !== currentValidatePromise) { return; }

					if (!elStatus.valid) {
						scope.$emit('ss-validate-field-event', formElem, eventType);
						elStatus.valid = true;
					}
					ssValidateHlp.passValidation(elem, attrs);
				},
				// failing
				function (failResult) {
					// if another validation attempt has been fired this one is no
					// longer applicable, bail out
					if (vPromise !== currentValidatePromise) { return; }

					elStatus.valid = false;
					ssValidateHlp.failValidation(elem, attrs, failResult);
					rAF(function () {
						if (!hasFocus) {
							ssValidateHlp.showErrors(elem, !hasFocus);
						}
					}, scope);
				}
			).finally(function () {
				// if another validation attempt has been fired this one is no
				// longer applicable, bail out
				if (vPromise !== currentValidatePromise) { return; }
				elem.removeClass('ss-validate-async');
			});

			return vPromise;
		}
	}

	function setStatus(elem, statusObj, isChild = false) {
		// abort when we reach the body
		if (!elem[0] || elem[0].tagName === 'BODY') { return; }

		if (!isChild) {
			// this is the initial ss-validate element apply here
			statusMap.set(elem[0], statusObj);
		} else if (statusMap.has(elem[0])) {
			// this is a parent of this validator, add to children
			statusMap.get(elem[0]).children.push(statusObj);
		}

		// propagate up the dom tree
		setStatus(elem.parent(), statusObj, true);
	}

	return new Validate();
}

// #-- END Decorator (ss-validate) --# //
// #---------------------------------# //

angular.module('super-suit-decorators').directive('ssValidate', ssValidate);
