const _hlp = {
	// #--------------------------------# //
	// #---- Helper (ssFilenameHlp) ----# //

	/**
	 * var - Grabs the extension of the filename
	 *
	 * @param  {string} filename The name of the file to parse
	 * @return {string}          The extension of the file or ''
	 */
	getExtension(filename) {
		if (!filename) { return ''; }
		if (filename.indexOf('.') < 0) {
			return '';
		}

		var extension = filename.substr(filename.lastIndexOf('.') + 1, 10).toLowerCase();
		return extension;
	},

	/* getValidExtensions - returns the list of supported file extensions */
	getValidExtensions() {
		return ['pdf', 'doc', 'docx', 'csv', 'xls', 'xlsx', 'jpg', 'jpeg', 'bmp', 'png', 'jp2', 'gif', 'wmf', 'ppt', 'pptx', 'txt', 'tif', 'tiff'];
	},

	/**
	 * getFilename - Gets the name portion of a filename
	 *
	 * @param  {string} filename The name of the file to parse
	 * @return {string}
	 */
	getFilename(filename) {
		if (!filename) { return ''; }
		if (filename.indexOf('.') < 0) {
			return filename; //no extension. Probably an error state here.
		}

		return filename.substr(0, filename.lastIndexOf('.'));
	},

	/**
	 * Generates the Frontline ss-icon name for the FontAwesome icon name
	 */
	getIcon(extension) {
		var ret = 'text';

		switch (extension.toLowerCase()) {
			case 'pdf':
				ret = 'pdf';
				break;
			case 'doc':
			case 'docx':
				ret = 'word';
				break;
			case 'csv':
			case 'xls':
			case 'xlsx':
				ret = 'excel';
				break;
			case 'jpg':
			case 'jpeg':
			case 'bmp':
			case 'png':
			case 'jp2':
			case 'gif':
			case 'wmf':
				ret = 'photo';
				break;
			case 'ppt':
			case 'pptx':
				ret = 'powerpoint';
				break;
			default:
				ret = 'text';
				break;
		}

		return `file-${ret}-o`;
	}


	// #-- END Helper (ssFilenameHlp) --# //
	// #--------------------------------# //
};

/**
 * A helper to parse a file and return an extension or a name.
 */
function ssFilenameHlp() { return _hlp; }
// Keep filenameHlp for backward compatibility but adding ssFilenameHlp to follow 'ss' prefix convention
angular.module('super-suit-helpers').factory('ssFilenameHlp', ssFilenameHlp).factory('filenameHlp', ssFilenameHlp);
