/*@ngInject*/
function ssInteractionApi($http, buildConfig) {
	// #---------------------------------------------# //
	// #---- API Endpoint (ssMicrocredentialsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.interaction;

	const createExperience = payload => {
		return $http({
			method: 'POST',
			url: `${baseUrl}/v1/experiences`,
      data: payload
    });
	};

	// API endpoint methods
	return {
		createExperience // create an experience entry
	};

	// #-- END API Endpoint (ssResourceLibraryApi) --# //
	// #---------------------------------------------# //
}

angular
	.module('super-suit-apis')
	.factory('ssInteractionApi', ssInteractionApi);
