/*@ngInject*/
function setup403route($stateProvider) {
	$stateProvider.state('403', { templateUrl: '_error-403.html' });
}

/*@ngInject*/
function setupRoutePermissions($rootScope, $state, ssPermissionsSvc, ssEventSvc) {
	$rootScope.$on('$stateChangeStart', function(evt, toState, toParams){
		if (ssPermissionsSvc.ready.$$state.status === 0) {
			// if the permissions service is not ready yet, wait for it
			//> log('ROUTE: async permissions check', toState)
			ssPermissionsSvc.ready.then(() => checkRoutePermission());
		} else {
			// permissions service is ready, immidiately check the route
			//> log('ROUTE: permissions check', toState)
			checkRoutePermission(evt.preventDefault);
		}

		// use the permissions service to see if we are allowed to view this route
		function checkRoutePermission(onRejectRoute = () => {}) {
			// isPermittedRoute accepts a url, so construct one from the state
			// that we are currently trying to transition to
			const stateUrl = $state.href(toState, toParams);

			// no url, don't check permissions. We are either going to an abstract
			// route, or a route that exists in another ui-module. That ui-module
			// will handle the permission check when it bootstraps
			if (stateUrl == null) { return; }

			if (!ssPermissionsSvc.isPermittedRoute(stateUrl)) {
				// if this is an immidiate check (line 14), then we need to
				// cancel the route transition that is currently in progress
				onRejectRoute();

				// show the 403 route without changing the url
				//> log('rejecting route', stateUrl)
				$state.transitionTo('403');

				// remove the preloader if it's currently showing
				ssEventSvc.emit('preloader-complete');
			}
		}
	});
}

angular.module('super-suit').config(setup403route).run(setupRoutePermissions);
