(function () {
    "use strict";

    var flAuthModule = angular.module('flAuth', []);

    function isAbsolute(url) {
        return url.indexOf('http://') == 0 || url.indexOf('https://') == 0;
    }

    function flAuthService($window, $location, $q, $http, $log, flAuthSettings, callbackParams) {
        var that = this;
        if (!flAuthSettings) {
            throw new Error("flAuthSettings is required");
        }
        if (!flAuthSettings.clientId) {
            throw new Error("clientId is required");
        }
        if (!flAuthSettings.identityGatewayBaseUri) {
            throw new Error("identityGatewayBaseUri is required");
        }
        if (!flAuthSettings.signoutRedirectUri) {
            throw new Error("signoutRedirectUri is required");
        }
        if (!flAuthSettings.redirectUri) {
            throw new Error("redirectUri is required");
        }
        this._flAuthSettings = flAuthSettings;

        this._userInfoApi = this._flAuthSettings.identityGatewayBaseUri + "api/account/userinfo";

        //Generate session id used in the client persist and request state keys (for multiple browser tab issues)
        var sessionId = '';
        var sessionKey = that._flAuthSettings.clientId + '.sessionId';
        sessionId = $window.sessionStorage.getItem(sessionKey);
        if (!sessionId) {
            sessionId = (new Date()).getTime() + '-' + Math.random();
            $window.sessionStorage.setItem(sessionKey, sessionId);
        }

        this._window = $window;
        this._oidcDiscoveryUri = this._flAuthSettings.identityGatewayBaseUri + ".well-known/openid-configuration";
        this._flConfigDiscoveryUri = this._flAuthSettings.identityGatewayBaseUri + "api/configs/fl-configuration";
        this._$q = $q;
        this._$http = $http;
        this._$http.cache = false;
        this._deferred = $q.defer();
        this._userInfoDeferred = $q.defer();
        this._location = $location;
        this._store = $window.localStorage;
        this._clientPersistKey = this._flAuthSettings.clientId + '.TokenManager.client.' + sessionId;

        Object.defineProperty(this, 'isAuthenticated', {
            get: function () {
                return this._tokenManager.id_token !== undefined;
            }
        });

        Object.defineProperty(this, 'accessToken', {
            get: function () {
                return this._tokenManager.access_token || null;
            }
        });

        Object.defineProperty(this, 'isImpersonatingV2', {
            get: function () {
                try {
                    return this._tokenManager._token.profile.impersonator_id && true;
                } catch (err) {}
                return false;
            }
        });

        Object.defineProperty(this, 'isImpersonating', {
            get: function () {
                if (this._tokenManager.profile) {
                    return this._tokenManager.profile.acting_as_id;
                }
                return null;
            }
        });

        Object.defineProperty(this, 'profileId', {
            get: function () {
                if (this._tokenManager.profile) {
                    return this._tokenManager.profile.sub;
                }
                return null;
            }
        });

        function getOidcTokenManagerSettings() {
            var silentRenew = (that._flAuthSettings.silentRenewUri);
            var tokenManagerSettings = {
                client_id: that._flAuthSettings.clientId,
                authority: that._flAuthSettings.identityGatewayBaseUri,
                response_type: "id_token token",
                redirect_uri: that._flAuthSettings.redirectUri,
                post_logout_redirect_uri: that._flAuthSettings.signoutRedirectUri,
                scope: "openid impersonation flapi.all fl.session flapi.introspection",
                persistKey: that._flAuthSettings.clientId + ".TokenManager.token",
                request_state_key: that._flAuthSettings.clientId + ".OidcClient.request_state." + sessionId,
                clientPersistKey: that._clientPersistKey,
                silent_renew: silentRenew,
                silent_redirect_uri: that._flAuthSettings.silentRenewUri,
                load_user_profile: false
            };

            return tokenManagerSettings;
        }

        function getTokenManagerAsync() {
            var tokenManagerSettings = getOidcTokenManagerSettings();

            var config = {
                responseType: "text"
            };

            var tokenManager = new OidcTokenManager(tokenManagerSettings, config);

            if (callbackParams) {
                var handleState = function(state) {
                    if (state) {
                        if (isAbsolute(state.data)) {
                            that._window.location.replace(state.data);
                        }
                        else {
                            that._location.url(state.data).replace();
                        }
                    }

                    return tokenManager;
                }

                return tokenManager.processTokenCallbackAsync(callbackParams).then(handleState);
            } else {
                return $q.when(tokenManager); // resolves immediately
            }
        }

        function getProfileAsync(profileId) {
            var defer = $q.defer();
            var config = {};
            config.withCredentials = true;
            config.headers = {
                Authorization: "Bearer " + that.accessToken
            };
            var profileIdParam = "?profileId=" + profileId;
            that._$http.get(that._userInfoApi + profileIdParam, config)
                .then(function (json)
                {
                    defer.resolve(json.data.model);
                    return json;
                }, function ()
                {
                    defer.reject();
                });
            return defer.promise;
        }

        function getSessionAsync()
        {
            return $q(function(resolve, reject) {
                var accessToken = that.accessToken;
                // If no access token - we assume you have been logged out
                // return a null session
                if (!accessToken)
                {
                    that._callSessionExpired();
                    return resolve(null);
                }
                var config = {};
                config.headers = {
                    Authorization: "Bearer " + accessToken
                };
                that._$http.get(that._checkSessionUri, config)
                    .then(function (response)
                    {
                        // Check to ensure we have a success code - if not then assume session expired
                        // This is in case application has interceptors that do not throw error
                        if (response.status !== 200)
                        {
                            that._callSessionExpired();
                            resolve(null);
                        }
                        resolve(response.data);
                    }).catch(function (response)
                    {
                        // Doing this for any bad response status code - not just 404.
                        // Should we handle other error codes differently and set the check session again?
                        that._callSessionExpired();
                        resolve(null);
                    });
            });
        }

        Object.defineProperty(this, 'profile', {
            get: function ()
            {
                if (this._tokenManager.profile) {
                    return getProfileAsync(this._tokenManager.profile.sub);
                } else
                {
                    return $q.reject();
                }
            }
        });

        Object.defineProperty(this, 'actingAsProfile', {
            get: function() {
                if (this._tokenManager.profile && this.isImpersonating) {
                    return getProfileAsync(this._tokenManager.profile.acting_as_id);
                } else {
                    return $q.reject();
                }
            }
        });

        /* Following Code is for checking for expiring session and logouts  */
        // CheckSession Expire
        this._checkSessionUri = this._flAuthSettings.identityGatewayBaseUri + "sessions";
        this._sessionCallbacks = {
            expiredCallbacks: [],
            logoutCallbacks: [],
            expiringCallbacks: []
        }

        function configureCheckSession()
        {
            var handle = null;
            var ExpiringNotificationSeconds = 300;

            function cancelCheckSession() {
                if (handle){
                    $window.clearTimeout(handle);
                    handle = null;
                }
            }

            function scheduleCheckSession(duration) {
                cancelCheckSession();
                handle = $window.setTimeout(checkSession, duration * 1000);
            }

            function checkSession() {
                getSessionAsync()
                    .then(function (session)
                    {
                        if (session)
                        {
                            var secondsLeft = session.ExpireSeconds;
                            var duration = secondsLeft - ExpiringNotificationSeconds;
                            // If less than the notification period - then modify next duration check and call expiring function
                            if (secondsLeft <= ExpiringNotificationSeconds)
                            {
                                duration = secondsLeft + 1;
                                that._callSessionExpiring(secondsLeft);
                            }
                            scheduleCheckSession(duration);
                        }
                        return;
                     }).catch(function ()
                     {
                        return;
                     });
            }
            // Call these when session expires or logsout
            that.addOnSessionExpired(cancelCheckSession);
            that.addOnSessionLogout(cancelCheckSession);
            // Start the check session polls - wait 1 second for load activity to finish
            scheduleCheckSession(1);
        }

        // Used for detetcing signout on another tab for same session
        function loadiFrameAsync() {
            return $q(function (resolve, reject) {
                // add iframe to monitor session state at id gateway
                var iframe = document.createElement('iframe');
                iframe.style.display = "none";
                iframe.src = that._flAuthSettings.identityGatewayBaseUri + 'connect/checksession';
                iframe.id = 'checkSession';
                iframe.onload = function () {

                    var timer = window.setInterval(checkSessionState, 2000);

                    function checkSessionState() {
                        iframe.contentWindow.postMessage(that._flAuthSettings.clientId + " " + that._tokenManager.session_state, that._flAuthSettings.identityGatewayBaseUri);
                    }

                    function cancelCheck(){
                        window.clearInterval(timer);
                    }

                    function onMessageReceived(e) {
                        var identityGatewayHost = that._flAuthSettings.identityGatewayBaseUri.substr(0, that._flAuthSettings.identityGatewayBaseUri.indexOf("/", that._flAuthSettings.identityGatewayBaseUri.indexOf("//") + 2));
                        if (e.origin.toLowerCase() === identityGatewayHost.toLowerCase()) {
                            if (e.data === "error" || e.data === "changed") {
                                cancelCheck();
                            }
                            if (e.data === "changed") {
                                that._callSessionLoggedOut();
                            }
                        }

                        resolve();
                    }

                    // Call these when session expires or logsout
                    that.addOnSessionExpired(cancelCheck);
                    that.addOnSessionLogout(cancelCheck);

                    window.addEventListener("message", onMessageReceived);
                    checkSessionState();
                };
                document.body.appendChild(iframe);
            });
        }
        /*  End Code for Session Expire and Logout   */

        function markReady() {
            that._deferred.resolve();
        }

        function reportProblems (fault) {
            $log.error(String(fault));
        }

		getTokenManagerAsync()
            .then(function(tokenManager) {
                that._tokenManager = tokenManager;
                that.addOnSessionExpired(tokenManager.removeToken.bind(tokenManager));
                that.addOnSessionLogout(tokenManager.removeToken.bind(tokenManager));
                configureCheckSession();
                return loadiFrameAsync();
            })
            .then(function() {
                // Check session on load in case existing access token is valid
                // but session has expired.  This will remove access token if necessary
                return getSessionAsync();
            })
            .then(function() {
                markReady();
            })
            .catch(reportProblems);

    }

       //  Callback Functions
    flAuthService.prototype.addOnSessionExpired = function (cb) {
        this._sessionCallbacks.expiredCallbacks.push(cb);
    }

    flAuthService.prototype.addOnSessionLogout = function (cb) {
        this._sessionCallbacks.logoutCallbacks.push(cb);
    }

    flAuthService.prototype.addOnSessionExpiring = function (cb) {
        this._sessionCallbacks.expiringCallbacks.push(cb);
    }

    flAuthService.prototype._callSessionExpired = function () {
        this._sessionCallbacks.expiredCallbacks.forEach(function (cb) {
            cb();
        })
    }

    flAuthService.prototype._callSessionLoggedOut = function () {
        this._sessionCallbacks.logoutCallbacks.forEach(function (cb) {
            cb();
        })
    }

    flAuthService.prototype._callSessionExpiring = function (remainingTime) {
        this._sessionCallbacks.expiringCallbacks.forEach(function (cb) {
            cb(remainingTime);
        })
    }

    flAuthService.prototype.getFlConfigAsync = function() {
        // I'm so sorry. This is a hack. But it works. I feel so ashamed.
        // https://github.com/angular/angular.js/issues/1586
        var uri = this._flConfigDiscoveryUri + '?t=' + new Date().getTime();
        return this._$http.get(uri).then(function(response) {
            var flConfig = response.data.model;
            if (flConfig) {
            return flConfig;
            } else {
                throw (new Error("FL config appears to be empty"));
            }
        });
    };

    flAuthService.prototype.authenticate = function (params) {
        params = params || {};
        params.impersonationId = null;
        params.stopImpersonation = false;
        this._redirectToAuthorize(params);
    };

    flAuthService.prototype.impersonate = function (returnUrl, impersonationId) {
        var params = {
            returnUrl: returnUrl,
            forceLogin: true,
            showRegister: false,
            impersonationId: impersonationId,
            stopImpersonation: false,
            productId: null
        };
        this._redirectToAuthorize(params);
    };

    flAuthService.prototype.endImpersonate = function (returnUrl) {
        var that = this;
        this.getFlConfigAsync()
            .then(function(config){

                var params = {
                    returnUrl: returnUrl || config.defaultEndImpersonateRedirectUri,
                    forceLogin: true,
                    showRegister: false,
                    impersonationId: null,
                    stopImpersonation: true,
                    productId: null
                }

                that._redirectToAuthorize(params);
            });
    };

    flAuthService.prototype._redirectToAuthorize = function (params) {
        this._tokenManager._settings.acr_values = "";
        if (params.forceLogin) {
            this._tokenManager._settings.prompt = "login";
        }
        if (params.showRegister) {
            this._tokenManager._settings.acr_values += " showRegister:true";
        }
        if (params.stopImpersonation) {
            this._tokenManager._settings.acr_values += " stopImpersonate";
        } else if (params.impersonationId) {
            this._tokenManager._settings.acr_values += (" impersonate:" + params.impersonationId);
        }
        if (params.productId) {
            this._tokenManager._settings.acr_values += (" productId:" + params.productId);
        }
        if (params.inviteToken) {
            this._tokenManager._settings.acr_values += (" inviteToken:" + params.inviteToken);
        }
        if (params.organization) {
            this._tokenManager._settings.acr_values += (" orgId:" + encodeURIComponent(params.organization));
        }

        this._tokenManager.redirectForToken(params.returnUrl);
    };

    flAuthService.prototype.signout = function (clientState) {
        this._tokenManager.redirectForLogout(clientState);
    };

    flAuthService.prototype.accountSettingsUrl = function(returnUrl, supportImpersonation) {
        if (!returnUrl) {
            throw new Error('returnUrl is required');
        }

        var params = ["returnUrl=" + encodeURIComponent(returnUrl),
            "idToken=" + this._tokenManager.id_token,
            "signoutUrl=" + encodeURIComponent(this._flAuthSettings.signoutRedirectUri)];
        if (this._flAuthSettings.productId.length > 0) {
            params.push("productId=" + this._flAuthSettings.productId);
        } else {
            params.push("clientId=" + this._flAuthSettings.clientId);
        }
        if (supportImpersonation === true)
            params.push("supportImpersonation=true");
        var url = this._flAuthSettings.identityGatewayBaseUri + "ui/#/settings/profile?" + params.join("&");
        return url;
    }

    flAuthService.prototype.processLogoutCallback = function(callbackState) {
        var key = this._clientPersistKey;
        var clientData = this._tokenManager._settings.store.getItem(key);
        clientData = JSON.parse(clientData);

        if (clientData && clientData.state === callbackState) {
            this._tokenManager._settings.store.removeItem(key);
            return clientData.data;
        }
    };

    flAuthService.prototype.ready = function() {
        return this._deferred.promise;
    };

    flAuthService.prototype.renewSession = function () {
        var that = this;

        return that.ready()
            .then(function () {
				if (that.isAuthenticated) {
					var url = that._flAuthSettings.identityGatewayBaseUri + 'sessions/renew';

					var config = {
						withCredentials: true,
						headers: {
							'Authorization': 'Bearer ' + that.accessToken
						}
					}
					return that._$http.post(url, {}, config);
				}
            });
    };

    flAuthModule.provider('flAuthService', function flAuthServiceProvider() {
        var _clientId = '';
        var _productId = '';
        var _identityGatewayBaseUri = '';
        var _silentRenewUri = '';
        var _redirectUri = '';
        var _signoutRedirectUri = '';
        Object.defineProperty(this, 'clientId', {
            get: function() {
                return _clientId;
            },
            set: function(value) {
                _clientId = value;
            }
        });
        Object.defineProperty(this, 'productId', {
            get: function() {
                return _productId;
            },
            set: function(value) {
                _productId = value;
            }
        });
        Object.defineProperty(this, 'identityGatewayBaseUri', {
            get: function() {
                return _identityGatewayBaseUri;
            },
            set: function(value) {
                _identityGatewayBaseUri = value;
            }
        });
        Object.defineProperty(this, 'silentRenewUri', {
            get: function () {
                return _silentRenewUri;
            },
            set: function (value) {
                _silentRenewUri = value;
            }
        });
        Object.defineProperty(this, 'signoutRedirectUri', {
            get: function () {
                return _signoutRedirectUri;
            },
            set: function (value) {
                _signoutRedirectUri = value;
            }
        });
        Object.defineProperty(this, 'redirectUri', {
            get: function () {
                return _redirectUri;
            },
            set: function (value) {
                _redirectUri = value;
            }
        });

        this.$get = ['$location', '$q', '$http', '$log', '$window', function ($location, $q, $http, $log, $window) {
            var flAuthSettings = {
                clientId: _clientId,
                productId: _productId,
                identityGatewayBaseUri: _identityGatewayBaseUri,
                silentRenewUri: _silentRenewUri,
                redirectUri: _redirectUri,
                signoutRedirectUri: _signoutRedirectUri
            };

            var params;
            if ($location.absUrl().indexOf(_redirectUri) >= 0) {
                params = $location.absUrl().replace(_redirectUri, '');
            }

            if (params) {
                return new flAuthService($window, $location, $q, $http, $log, flAuthSettings, params);
            } else {
                return new flAuthService($window, $location, $q, $http, $log, flAuthSettings);
            }
        }];
    });

})();
