/*@ngInject*/
function registerActionBundles(ssPermissionsSvc, ssEnum, ssActionBundlesLogic) {
	const {isPermitted, isNotPermitted} = ssPermissionsSvc; // eslint-disable-line no-unused-vars
	const logic = ssActionBundlesLogic;

	ssPermissionsSvc.addPermittedActionBundle('ViewOthers', function () {
		const scEnum = ssEnum.securityGroupScope;
		const securityScopes = ssPermissionsSvc.securityScopes();
		return logic.isViewOthers({ scEnum, securityScopes });
	}, true); // this bundle is persistant
}

angular.module('super').run(registerActionBundles);
