import {Enums} from 'base-enum';

// #-------------------------------------# //
// #---- Enums (/locationCategories) ----# //

class LocationCategoriesEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Preschool',
			value: 0
		}, {
			name: 'Elementary School',
			value: 1
		}, {
			name: 'Middle School',
			value: 2
		}, {
			name: 'Secondary School',
			value: 3
		}, {
			name: 'Adult Education',
			value: 4
		}, {
			name: 'Administrative',
			value: 5
		}, {
			name: 'Other',
			value: 6
		}];
		super(enums);
	}
}

// #---- End Enums (/locationCategories) ----# //
// #-----------------------------------------# //

angular.module('super-suit-enums').factory('enumLocationCategories', () => new LocationCategoriesEnum());
