/*@ngInject*/
function insightsAbsenceApi(ssApiSvc, buildConfig, ssStringHlp, ssEventSvc) {
	// #---------------------------------------------# //
	// #---- API Endpoint (ssInsightsAbsenceApi) ----# //

	// base endpoint url
	const baseUrl = `${buildConfig.endpoint.insightsAbsence}/kpi`;
	// model data type
	const model = 'InsightResults';
	// possible relationships
	const rel = [];

	const kpiList = [
		'absences_reason',                     // absencesReason(requestData)
		'absences_dayofweek',                  // absencesDayofweek(requestData)
		'absences_avg_days_per_employee',      // absencesAvgDaysPerEmployee(requestData)
		'employee_to_substitute_ratio',        // employeeToSubstituteRatio(requestData)
		'fillrate_dayofweek',                  // fillrateDayofweek(requestData)
		'fillrate_leadtime',                   // fillrateLeadtime(requestData)
		'subs_avg_days_worked',                // subsAvgDaysWorked(requestData)
		'subs_percent_nonworking_by_fillrate', // subsPercentNonworkingByFillrate(requestData)
		'subs_percent_nonworking'              // subsPercentNonworking(requestData)
	];

	// API endpoint methods
	return kpiList.reduce(function (api, kpi) {
		const method = 'POST';
		const url = `${baseUrl}/${kpi}?organizationId=:ORGID:`;
		const name = ssStringHlp.toCamelCase(kpi);
		const endpoint = ssApiSvc.endpoint({url, method}, model, rel);
		api[name] = parameters => endpoint({}, {parameters}).then(function (res) {
			ssEventSvc.emit('insight-results', name, res);
			return res;
		});
		return api;
	}, {});

	// #-- END API Endpoint (ssInsightsAbsenceApi) --# //
	// #---------------------------------------------# //
}

angular.module('super-suit').factory('ssInsightsAbsenceApi', insightsAbsenceApi);
