/*@ngInject*/
function fileApi($http, ssApiSvc, buildConfig) {
	// #----------------------------------# //
	// #---- API Endpoint (ssFileApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.files;
	const url = `${baseUrl}/files`;
	const getUrlUrl = (p) => `/employees/${p.id}/fileRecords/${p.fileId}/url`;

	// Add &simulateFailure=true to force a 417 on file upload to test upload errors
	const removeFileUrl = (p) => `${url}/${p.fileId}?version=1`;
	// model data type
	const model = 'files';
	// possible relationships
	const rel = [

	];

	const queryUrl = `${baseUrl}/organizations/:ORGID:/files`;
	const queryByIdsUrl = `${baseUrl}/files/ids?organizationId=:ORGID:`;


	// generate default endpoints
	const get = ssApiSvc.endpoint({ url, method: 'GET' }, model, rel);
	const getUrlById = ssApiSvc.endpoint({ url: getUrlUrl, method: 'GET' }, model, rel);
	const preAdd = ssApiSvc.endpoint({ url: queryUrl, method: 'POST' }, model, rel);
	const add = ssApiSvc.endpoint({ url: queryUrl, method: 'POST' }, model, rel);
	const query = ssApiSvc.endpoint({ url: queryByIdsUrl, method: 'POST' }, model, rel);

	const removeFile = fileId => {
		return $http({
			url: removeFileUrl({ fileId }),
			method: 'DELETE'
		});
	};

	// API endpoint methods
	return {
		get, query, add, getUrlById, preAdd, removeFile
	};

	// #-- END API Endpoint (ssFileApi) --# //
	// #----------------------------------# //
}

angular.module('super-suit-apis').factory('ssFileApi', fileApi);
