import productIdConfigMap from './_product-mappings';

const configCache = {};
var loadedConfigSet;
var config = {};

/*@ngInject*/
function ssAppConfigSvc($http, $q, ssEventSvc, ssUiModuleHlp) {
// #----------------------------------# //
// #---- Service (ssAppConfigSvc) ----# //

	// External service API
	return {
		load,
		loadUniversal,
		get, getAsync,
		set,
		clear: flushConfig
	};

	function loadUniversal() {
		const modName = ssUiModuleHlp.getCurrentModuleName();
		const productId = modName.split('-')[0];
		const productFileName = productIdConfigMap[productId] || productId.toLowerCase();

		return load(productFileName);
	}

	function load(configSet) {
		var _d = $q.defer();

		// first load? track what config set we are using
		if (loadedConfigSet == null) { loadedConfigSet = configSet; }

		// when the config set changes, we must reload the page to allow custom
		// sidekick integrations for other apps using ui-modules
		if (loadedConfigSet !== configSet) { window.location.reload(); }

		if (configCache[configSet] != null) {
			_d.resolve(
				fillConfig(configCache[configSet])
			);
		} else {
			const min5 = (5 * 60 * 1000);
			const cacheBuster = Math.ceil(Date.now() / min5) * min5;
			$http.get(`/app-configs/${configSet}.json?cb=${cacheBuster}`)
				.then((res) => fillConfig(res.data))
				.then((jsonData) => configCache[configSet] = jsonData)
				.then(_d.resolve);
		}

		_d.promise.then(data => ssEventSvc.emit('app-config.load', data));
		return _d.promise;
	}

	function get(area) {
		if (config[area] == null) { config[area] = {}; }
		return config[area];
	}

	function getAsync(area) {
		const _d = $q.defer();
		if (config[area] == null || JSON.stringify(config[area]) === '{}') {
			ssEventSvc.on(`app-config.set.${area}`, _d.resolve, false);
			ssEventSvc.on('app-config.load', (cfg) => {
				if (cfg[area] == null) { _d.reject(); }
			}, false);
		} else {
			_d.resolve(config[area]);
		}
		return _d.promise;
	}

	function set(area, obj) {
		if (config[area] == null) { config[area] = {}; }
		clearObject(config[area]);
		Object.keys(obj).forEach(function (v) {
			config[area][v] = obj[v];
		});
		ssEventSvc.emit(`app-config.set.${area}`, obj);
	}

	function clearObject(obj) {
		Object.keys(obj).forEach((v) => delete obj[v]);
	}

	function flushConfig() {
		Object.keys(config).forEach(function(area) {
			clearObject(config[area]);
		});
	}

	function fillConfig(setData) {
		flushConfig();

		if (typeof setData === 'string') { setData = JSON.parse(setData); }

		Object.keys(setData).forEach(function(area) {
			set(area, setData[area]);
		});

		return setData;
	}

// #-- END Service (ssAppConfigSvc) --# //
// #----------------------------------# //
}

angular.module('super-suit-services').factory('ssAppConfigSvc', ssAppConfigSvc);

/////
// example usage
//

// load configs from app-config file
// ssAppConfigSvc.load('employee-center');

// get a specific config from the file
// ssAppConfigSvc.get('left-nav');

// get a specific config from the file via async
// ssAppConfigSvc.getAsync('left-nav').then(doThings);

// overwrite a specific config with a custom value
// ssAppConfigSvc.set('left-nav', {});
