/*@ngInject*/
function initExternalSidekick(ssAppConfigSvc, buildConfig, $log) {
	ssAppConfigSvc.getAsync('init').then(handleInit);
	function handleInit(init) {
		const ignoreRemoteSideNavOption = 'automation-ignore-remote-side-nav';
		if(sessionStorage.getItem(ignoreRemoteSideNavOption) === 'true'){
			$log.info('WARNING: You have sessionStorage.' + ignoreRemoteSideNavOption + ' configured, so that means that the external sidenav will not be loading.');
			return;
		}

		// just make sure that we have a reasonable value here
		if (typeof init.script !== 'string') { return; }

		const initScriptUrl = buildConfig.sidekickExternal[init.script];

		// we found a value here right? ...right?
		if (typeof initScriptUrl !== 'string') { return; }

		// bootstrap in the init script for execution
		const initScriptEl = document.createElement('script');
		initScriptEl.setAttribute('src', initScriptUrl);
		angular.element(document.head).append(initScriptEl);
	}
}

angular.module('super').run(initExternalSidekick);
