import {Component} from 'base-component';
// #------------------------------------# //
// #---- Component (super-tab-menu) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	tabs: '=',
	activeTabId: '='
};

// directive definition object overrides
const config = {
	// transclude: true
};

/*@ngInject*/
function tabMenu() {

	class TabMenu extends Component {
		/*@ngInject*/
		controller($scope) {
			if ($scope.tabs == null) { $scope.tabs = []; }
			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
			};

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				activateTab(tabId) {
					$scope.$emit('ss-tab-activate', tabId);
				},
				checkIfActive() {
					return $scope.tabs.some(t => t.tabId === $scope.activeTabId);
				},
				calcMenuLabel() {
					if (go.checkIfActive()) {
						return $scope.tabs.find(t => t.tabId === $scope.activeTabId).label;
					} else {
						return `${$scope.tabs.length} more`;
					}
				}
			};
		}

		link(scope, elem, attrs) {
			scope.$watch('activeTabId', function (nv, ov) {
				if (nv === ov) { return; }
				if (scope.tabs.some(t => t.tabId === nv)) {
					if (elem.hasClass('ss-menu-open')) {
						elem[0].querySelector(`.tab-menu-${nv}`).focus();
					} else {
						elem[0].focus();
					}
				}
			});

			let lastKey = null;
			elem.on('click', () => lastKey = null);
			elem.on('keydown', evt => {
				const keys = ['ArrowDown', 'ArrowUp', ' ', 'Enter'];
				if (keys.includes(evt.key)) {
					lastKey = evt.key;
				}
			});

			scope.go.handleMenuOpen = function () {
				const menuItems = Array.from(elem[0].querySelectorAll('.select-menu-list-item'));
				const selectedItem = elem[0].querySelector('.active-menu-item');
				if (lastKey === 'ArrowDown') {
					menuItems[0].focus();
				} else if (lastKey === 'ArrowUp') {
					menuItems.pop().focus();
				} else if (selectedItem != null) {
					selectedItem.focus();
				}
			};
		}
	}

	return new TabMenu(props, 'tab-menu', config);
}

// #-- END Component (super-tab-menu) --# //
// #------------------------------------# //

angular.module('super-suit-components').directive('superTabMenu', tabMenu);
