import {Decorator} from 'base-decorator';
// #----------------------------------# //
// #---- Decorator (ss-bind-html) ----# //

/*@ngInject*/
function ssBindHtml($compile) {

	var templateCache = new Map();

	class BindHtml extends Decorator {

		link(scope, elem, attrs) {
			if (attrs.ssBindHtmlNoWatch == null) {
				scope.$watch(attrs.ssBindHtml, compileTemplate);
			}

			function compileTemplate() {
				var template = scope.$eval(attrs.ssBindHtml);

				if (template == null) { return; }
				if (template.indexOf('<') !== 0) { template = '<span>' + template + '</span>'; }
				if (!templateCache.has(template)) {
					templateCache.set(template, $compile(template));
				}

				templateCache.get(template)(scope, function (clonedElem) {
					elem.empty();
					elem.append(clonedElem);
				});
			}
		}
	}


	return new BindHtml();
}

// #-- END Decorator (ss-bind-html) --# //
// #----------------------------------# //

angular.module('super-suit-decorators').directive('ssBindHtml', ssBindHtml);
