/*@ngInject*/
function insightsOrgStatsApi(ssApiSvc, buildConfig) {
// #----------------------------------------------# //
// #---- API Endpoint (ssInsightsOrgStatsApi) ----# //

	// base endpoint url
	const absenceBaseUrl = buildConfig.endpoint.insightsAbsence;
	const absenceUrl = `${absenceBaseUrl}/orgstats/:ORGID:`;

	const recruitBaseUrl = buildConfig.endpoint.insightsRecruit;
	const recruitUrl = `${recruitBaseUrl}/orgstats/:ORGID:`;

	// model data type
	const model = 'OrganizationStatistics';
	// possible relationships
	const rel = [];

	// generate default endpoints

	// Have to leave Absence as get to be backwards compatible, but also creating a duplicate with absence in name to eventually phase in
	const get = ssApiSvc.endpoint({ url: absenceUrl, method: 'GET' }, model, rel);
	const getAbsence = ssApiSvc.endpoint({ url: absenceUrl, method: 'GET' }, model, rel);


	const getRecruit =ssApiSvc.endpoint({ url: recruitUrl, method: 'GET' }, model, rel);

	// API endpoint methods
	return {
		get,
		getAbsence,
		getRecruit
	};

// #-- END API Endpoint (ssInsightsOrgStatsApi) --# //
// #----------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssInsightsOrgStatsApi', insightsOrgStatsApi);
