var notifications, stats, userKey, notificationCollection, notificationsDefer, statsDefer, scopes;

/*@ngInject*/
function ssNotificationsSvc($rootScope, $q, $timeout, ssAppConfigSvc, ssEventSvc, ssAsync, ssNotificationsApi, ssActiveUserSvc, ssToastHlp) {
// #--------------------------------------# //
// #---- Service (ssNotificationsSvc) ----# //

	if (notificationsDefer == null) { notificationsDefer = $q.defer(); }
	if (statsDefer == null) { statsDefer = $q.defer(); }
	if (stats == null) { stats = {}; }
	if (scopes == null) { scopes = []; }

	scopes.push($rootScope);

	if (notifications == null) {
		notifications = [];
		ssAppConfigSvc.getAsync('notifications').then(updateNotifications);
	}

	// External service API
	return {
		getNotifications: () => notificationsDefer.promise,
		getStats: () => statsDefer.promise,
		markAsRead, remove
	};

	function markAsRead(id) {
		var notification = notificationCollection.find(n => n.id === id);
		return (ssAsync(function* () {
			if (notification.isRead) { return; }
			notification._acknowledgeAtUtc = new Date();

			yield notification.$save();

			updateNotificationData();
		})());
	}

	function remove(id) {
		var notification = notificationCollection.find(n => n.id === id);
		return (ssAsync(function* () {
			try {
				notification.deleted = true;
				yield notification.$remove();
			} catch (err) {
				notification.deleted = false;
			} finally {
				updateNotificationData();
			}
		})());
	}

	function updateNotificationData() {
		// clear current list of notifications
		notifications.length = 0;
		// reload the newly downloaded notification list
		notificationCollection.map(n => Object.assign({}, n)).forEach(n => notifications.push(n));

		stats.total = notifications.length;
		stats.unread = notifications.filter(n => !n.isRead).length;
		stats.read = stats.total - stats.unread;

		scopes = scopes.filter(scope => scope.$root != null);
		scopes.forEach(scope => scope.$root.safeApply());
	}

	function updateNotifications() {
		(ssAsync(function* () {
			var currentList;
			try {
				// get user if we don't have
				if (userKey == null) {
					userKey = yield ssActiveUserSvc.getIdentityByType('employee');
				} else {
					currentList = notifications.map(n => n.id);
				}

				// if we have no user, don't try to fetch their notifications. duh!
				if (userKey == null) { return; }

				var res;
				try {
					res = yield ssNotificationsApi.query({employeeId: userKey});
				} catch(err) {
					// if the call fails, bail out
					return;
				}
				// if the response is empty, bail out
				if (res == null) { return; }

				notificationCollection = res;

				updateNotificationData();

				// toasts for new notifications
				if (currentList != null) {
					notifications
						.filter(n => !currentList.includes(n.id))
						.forEach(function (n) {
							ssToastHlp.notification(n.shortContent, n._subject);
						});

				} else {
					notificationsDefer.resolve(notifications);
					statsDefer.resolve(stats);
				}

				ssEventSvc.emit('notifications.updated', notifications, stats);
			} finally {
				$timeout(updateNotifications, 60000);
			}
		})());
	}

// #-- END Service (ssNotificationsSvc) --# //
// #--------------------------------------# //
}

angular.module('super-suit-services').factory('ssNotificationsSvc', ssNotificationsSvc);
