import {Component} from 'base-component';
// #--------------------------------------# //
// #---- Component (super-input-text) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=',
	label: '@',
	labelWeight: '@?',
	isDisabled: '=?',
	name: '@',
	inputId: '@',
	value: '=?',
	required: '=?',
	debounce: '@',
	type: '@?',
	size: '@?',
	placeholder: '@',
	readonly: '@?',
	defaultValue: '=?',
	permissions: '=?',
	editMode: '=?',
	hasFocus: '=?',
	mask: '@?',
	step: '@?', // used for type=number
};

// directive definition object overrides
const config = {
	transclude: true,
	templateUrl: undefined
};

/*@ngInject*/
function inputText(ssTempCache, ssElemHlp, ssEventSvc, ssCommonControlHlp, ssMaskHlp, ssStringHlp) {

	const baseTemplate = ssTempCache('components/super-input-text/input-text.html');

	class InputText extends Component {

		template(tElem, tAttrs) {
			const _template = angular.element(baseTemplate);
			let inputSpans = _template.find('span');
			let iconElems = Array.from(inputSpans).filter(f => angular.element(f).hasClass('input'));

			if (tAttrs.ssIcon || tAttrs.ssIconBefore) {
				for(let i = 0, len = iconElems.length; i < len; i++) {
					iconElems[i].setAttribute('ss-Icon-Before', tAttrs.ssIcon || tAttrs.ssIconBefore);
				}
				ssElemHlp.removeAttr(tElem, tAttrs, ['ssIcon', 'ssIconBefore']);
			}

			if (tAttrs.ssIconAfter) {
				for(let i = 0, len = iconElems.length; i < len; i++) {
					iconElems[i].setAttribute('ss-Icon-After', tAttrs.ssIconAfter);
				}
				ssElemHlp.removeAttr(tElem, tAttrs, 'ssIconAfter');
			}

			if (tAttrs.mask) {
				let maskOptions = ssMaskHlp.getOptions(tAttrs.mask);
				if (maskOptions != null) {
					// only not redacted inputs
					let notRedactedSpans = Array.from(inputSpans).filter(element => {
						let classes = element.className.split(' ');
						return classes.includes('input') && !classes.includes('.is-disabled');
					});
					notRedactedSpans.forEach(e => {
						let input = e.getElementsByTagName('input')[0];
						let keys = Object.keys(maskOptions);
						keys.forEach(key => {
							input.setAttribute(key, maskOptions[key]);
						});
					});
				}
			}

			return _template[0].outerHTML;
		}

		/*@ngInject*/
		controller($scope, $log, $element, $attrs, $stateParams, rAF, enumPropertyAction) {
			// prevent use of type="date" because it's not supported by IE9, and
			// super-input-datepicker is designed to handle this use case
			if ($scope.type === 'date') {
				$log.error('super-input-text => type="date" is not supported, please use super-input-datepicker instead.');
				return;
			}

			$scope.labelWeight = ssCommonControlHlp.labelWeight($scope.labelWeight, $scope.label);

			if ($scope.hasFocus == null) {
				$scope.hasFocus = false;
			}

			// created to allow assigning to model because of ng-if scope
			const cmpt = $scope.cmpt = $scope;

			const vm = $scope.vm = {
				modelopts: {},
				show: false,
				id: `input-${(Math.ceil(Math.random() * 10000000)).toString()}`
			};

			// run on load as well as watch; Needs to finish before other watches start
			loadEditModeAndPermissions([$scope.editMode, $scope.permissions]);

			// set the passed in 'debounce' value on scope
			if ($scope.debounce != null) {
				vm.modelopts.debounce = parseInt($scope.debounce, 10);
			}

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				// clears the input
				clearEntry: function () {
					$scope.model = '';
					$scope.hasFocus = true;
					$scope.$input.triggerHandler('keyup');
					ssEventSvc.emit('entry-cleared');
				},
				toggleView() {
					vm.show = !vm.show;
				},
				override() {
					cmpt.model = $scope.defaultValue;
					rAF.raw(() => $scope.$input[0].select());
				},
				getMaskedModel(model) {
					// no mask, just return the model
					if ($scope.mask == null) { return model; }
					return ssMaskHlp.getMaskedValue($scope.mask, model);
				},
			};

			$scope.$watchGroup(['editMode', 'permissions'], loadEditModeAndPermissions);

			$scope.$watch('vm.show', () => {
				//emitted inside $watch to make sure occurs after digest cycle
				$scope.$emit('show-hide-toggle', {
					show: vm.show,
					redacted: vm.permissions.redacted,
					editMode: vm.editMode
				});
			});

			// displays the clear input control if there's a value
			$scope.$watch('model', val => {
				vm.showClear = (val) && !$scope.readonly;
			});

			function loadEditModeAndPermissions(nv) {
				// index matches expression
				const editMode = nv[0];
				const permissions = nv[1];

				const editModeInferred = editMode == null;
				if (editModeInferred) {
					vm.editMode = $stateParams.action && $stateParams.action.endsWith('edit');
				} else {
					vm.editMode = editMode === true; // strict equality to not bind it to the $scope
				}

				if (permissions == null) {
					if ($attrs.permissions === undefined) {
						// if the component wasn't even passed a permissions object assume
						// edit privileges so that we actually show the expected input
						vm.permissions = { view: true, edit: true, redacted: false, raw: 3 };
						if (editModeInferred) { vm.editMode = true; }
					} else {
						// if we expect permissions on the component, but we were
						// passed an empty permissions object, deny edit privileges
						vm.permissions = { view: true, edit: false, redacted: false, raw: 1 };
						if (editModeInferred) { vm.editMode = false; }
					}
				} else {
					vm.permissions = angular.extend({}, permissions);
				}

				if (!vm.permissions.edit) { vm.editMode = false; }
				if (vm.permissions.raw === enumPropertyAction.notVisible) {
					$element.remove();
				}
			}
		}

		link(scope, elem, attrs, ctrl, $transclude) {
			let tcContent;
			let phText = '';

			scope.$watch('model', m => elem.toggleClass('model-is-empty', !m));

			// default binding for input type
			if (!attrs.type) { attrs.type = 'text'; }

			// move the transcluded content to the placeholder attribute of the input
			$transclude(function (clone, scope) {
				tcContent = clone;
				phText = clone.text().trim();
			});
			scope.placeholder = phText;
			tcContent.remove();

			scope.$applyAsync(() => {
				let inputEl = scope.$input = elem.find('input');

				//add the readonly attribute
				if (attrs.readonly != null) {
					inputEl.attr('readonly', 'readonly');
					inputEl.attr('aria-readonly', 'true');
				}

				// add the step attribute
				if (attrs.type === 'number' && attrs.step != null) {
					inputEl.attr('step', attrs.step);
				}

				// pass attributes through to the form element
				if (attrs.name != null) { inputEl.attr('name', attrs.name); }

				if (attrs.inputId) {
					attrs.$observe('inputId', val => {
						scope.vm.id = ssStringHlp.toDashCase(val);
					});
				}

				if (!attrs.label && attrs.ariaLabel) {
					attrs.$observe('ariaLabel', val => {
						inputEl.attr('aria-label', attrs.ariaLabel);
					});
					elem.removeAttr('aria-label');
				}

				// if we have a default value and the user clears sets the
				// value of the input to nothing (""), set it to null so the
				// default is used instead
				if (scope.defaultValue != null) {
					inputEl.on('blur', function () {
						scope.$applyAsync(() => scope.model = scope.model || null);
					});
				}
			});

			//watch the label attribute for changes
			attrs.$observe('label', v => scope.label = v);
			attrs.$observe('type', v => scope.type = v);
		}
	}

	return new InputText(props, 'input-text', config);
}

// #-- END Component (super-input-text) --# //
// #--------------------------------------# //

angular.module('super-suit-components').directive('superInputText', inputText);
