import {
	Enums
}
from 'base-enum';

// #---------------------------------# //
// #---- Enums (/employeeStatus) ----# //

class EmployeeStatusEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Inactive',
			value: 0
		}, {
			name: 'Active',
			value: 1
		}, {
			name: 'Terminated',
			value: 2
		}, {
			name: 'Archived',
			value: 3
		}, {
			name: 'No Status',
			value: 4
		}];
		super(enums);
	}
}

// #---- End Enums (/employeeStatus) ----# //
// #-------------------------------------# //

angular.module('super-suit-enums').factory('enumEmployeeStatus', () => new EmployeeStatusEnum());
