// #---------------------------------# //
// #---- Helper (ssValidatorHlp) ----# //

/*@ngInject*/
function ssValidatorHlp(ssDeprecated) {

	function capitalize(text){
		return text.charAt(0).toUpperCase() + text.substr(1);
	}

	const helper = {

		required: ssDeprecated('ssValidatorHlp.required', 'ssValidationHlp.required()', (val, fieldName) => {
			var re = /[^\s]+/g;
			if (val != null && typeof val !== 'string') { val = val.toString(); }
			if (re.test(val) && typeof val === 'string' && val.length > 0) { return true; }
			if (fieldName != null) { return `${capitalize(fieldName)} is required`; }
			return 'This field is required';
		}),

		email: ssDeprecated('ssValidatorHlp.email', 'ssValidationHlp.email()', (val, fieldName = 'email') => {
			var re = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,63}$/i;
			if (typeof val === 'string' && val.length === 0 || val == null) { return true; }
			if (re.test(val)) { return true; }
			return `Invalid ${fieldName}`;
		}),

		zip: ssDeprecated('ssValidatorHlp.zip', 'ssValidationHlp.zip()', (val, fieldName = 'zip code') => {
			var re = /^\d{5}(-\d{4})?$/i;
			if (typeof val === 'string' && val.length === 0 || val == null) { return true; }
			if (typeof val !== 'string') { val = val.toString(); }
			if (re.test(val)) { return true; }
			return `Invalid ${fieldName}`;
		}),

		ssn: ssDeprecated('ssValidatorHlp.ssn', 'ssValidationHlp.ssn()', (val, fieldName = 'SSN') => {
			if (val == null) { return true; }
			var ssnRegEx = /^\d{3}-?\d{2}-?\d{4}$/;
			var redactedRegEx = /^\*{6,8}$/g;

			if (redactedRegEx.test(val)) { return true; }

			if (typeof val === 'string' && val.length === 0 || val == null) { return true; }
			if (ssnRegEx.test(val)) { return true; }
			return `Invalid ${fieldName}`;
		}),

		phone: ssDeprecated('ssValidatorHlp.phone', 'ssValidationHlp.phone()', (val, fieldName = 'phone number') => {
			if (val != null && typeof val !== 'string') { val = val.toString(); }
			if (typeof val === 'string' && val.length === 0 || val == null) { return true; }
			var re = /^\d{3}-?\d{3}-?\d{4}$/;
			if (re.test(val)) { return true; }
			return `Invalid ${fieldName}`;
		}),

		url: ssDeprecated('ssValidatorHlp.url', 'ssValidationHlp.url()', (val, fieldName = 'url') => {
			var re = /^((cc|http|https):\/\/|w{3}\.)(([a-zA-Z0-9\-]+\.)+[a-zA-Z]{2,3}(:[0-9]{4})?(\/|\/[\w#!:.?+=&%@!\-\/]*)*)$/;
			if (typeof val === 'string' && val.length > 0 && re.test(val)) { return true; }
			return `Invalid ${fieldName}`;
		}),

		num: ssDeprecated('ssValidatorHlp.num', 'ssValidationHlp.numeric()', (val, fieldName = 'value', checkZero = false) => {
			var re;
			// null and empty are ok, this validator does not require a value
			if (val == null || val === '') { return true; }
			// if val is not a string, change it to one so we can test with RegExp
			if (val != null && typeof val !== 'string') { val = val.toString(); }
			// remove commas... because we don't care
			val = val.replace(/,/g, '');
			// digits and periods are the only valid input
			// known limitation: this validator will accept multiple periods,
			// so 456.154.233 would still be valid

			if(checkZero) {
				if(Number(val) > 0) {return true;}
				return `${capitalize(fieldName)} must be a number greater than 0`;
			} else {
				re = /^[\d\.]+$/g;
				if (re.test(val)) { return true; }
				return `${capitalize(fieldName)} must be a number`;
			}
		}),

		requiredEmail: ssDeprecated('ssValidatorHlp.requiredEmail', 'ssValidationHlp.email({ required: true })', (val, fieldName = 'email') => {
			var err;

			err = helper.required(val, fieldName);
			if (err !== true) { return err; }

			err = helper.email(val, fieldName);
			if (err !== true) { return err; }

			return true;
		}),

		requiredSSN: ssDeprecated('ssValidatorHlp.requiredSSN', 'ssValidationHlp.ssn({ required: true })', (val, fieldName = 'SSN') => {
			var err;

			err = helper.required(val, fieldName);
			if (err !== true) { return err; }

			err = helper.ssn(val, fieldName);
			if (err !== true) { return err; }

			return true;
		}),

		requiredNum: ssDeprecated('ssValidatorHlp.requiredNum', 'ssValidationHlp.numeric({ required: true })', (val, fieldName = 'value') => {
			var err;

			err = helper.required(val, fieldName);
			if (err !== true) { return err; }

			err = helper.num(val, fieldName);
			if (err !== true) { return err; }

			return true;
		}),

		requiredNumNotZero: ssDeprecated('ssValidatorHlp.requiredNumNotZero', 'ssValidationHlp.numeric({ required: true, min: 1 })', (val, fieldName = 'value') => {
			var err;

			err = helper.required(val, fieldName);
			if (err !== true) { return err; }

			err = helper.num(val, fieldName, true);
			if (err !== true) { return err; }

			return true;
		}),

		requiredNumZeroOrGreater: ssDeprecated('ssValidatorHlp.requiredNumZeroOrGreater', 'ssValidationHlp.numeric({ required: true, min: 0 })', (val, fieldName = 'value') => {
			var err;

			err = helper.required(val, fieldName);
			if (err !== true) { return err; }

			if(val < 0){
				return `${capitalize(fieldName)} must be 0 or greater.`;
			}

			return true;
		}),

		requiredUrl: ssDeprecated('ssValidatorHlp.requiredUrl', 'ssValidationHlp.url({ required: true })', (val, fieldName = 'url') => {
			var err;
			err = helper.required(val, fieldName);
			if (err !== true) { return err; }

			err = helper.url(val, fieldName);
			if (err !== true) { return err; }

			return true;
		}),

		externalId: ssDeprecated('ssValidatorHlp.externalId', 'ssValidationHlp.string({ max: 100 })', (val, fieldName = 'External ID') => {
			// No value is ok in my book.
			if (typeof val === 'string' && val.length === 0 || val == null) { return true; }

			// Is it the right length?
			if (val.length > 100) {
				return `${capitalize(fieldName)} must be 100 characters or less.`;
			} else {
				return true;
			}
		}),

		requiredDigitalSignature: ssDeprecated('ssValidatorHlp.requiredDigitalSignature', 'ssValidationHlp.object({ required: true, schema: { isSigned: x => x === true, name: x => x != null && x.trim() !== "" } })', (val, fieldName = 'Digital Signature') => {
			// Ensure that the signature has some length besides whitespace
			if (val.isSigned && val.name.trim().length !== 0) {
				return true;
			}
			return `${capitalize(fieldName)} is required.`;
		}),

		requiredFileUpload: ssDeprecated('ssValidatorHlp.requiredFileUpload', 'ssValidationHlp.string({ required: true })', (val, fieldName = 'File Upload') => {
			if (val.length > 0) { return true; }
			return `${capitalize(fieldName)} is required.`;
		})

	};
	return helper;
}
// #-- END Helper (ssValidatorHlp) --# //
// #---------------------------------# //

angular.module('super-suit-helpers').factory('ssValidatorHlp', ssValidatorHlp);
