import {Component} from 'base-component';
// #----------------------------------# //
// #---- Component (super-button) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = false;
const config = { templateUrl: undefined };

/*@ngInject*/
function superButton(ssTempCache, ssElemHlp, $document) {
	const baseTemplate = ssTempCache('components/super-button/button.html');

	class Button extends Component {
		link(scope, element, attrs) {
			const menu = element[0].querySelector('.select-menu');
			if (menu == null) { return; }
		}

		template(tElem, tAttrs) {
			// the main template file
			var _template = angular.element(baseTemplate);

			// the children of tElem are the menu items
			const children = tElem.children();
			// reference to the label element for later
			const labelEl = _template.find('label');
			//const menuEl = _template.find('div');
			// placeholder for dropdown content
			let content = '';

			// apply the button type/size className
			_template.addClass(getTypeFromAttrs(tAttrs));
			_template.addClass(getSizeFromAttrs(tAttrs));

			// pass the label template/text through to the label element
			labelEl.text(tAttrs.label || '');

			// default all buttons to a type of button
			_template.attr('type', tAttrs.type || 'button');
			if (tAttrs.value == null) {
				_template.attr('value', tAttrs.label || '');
			}

			if(tAttrs.ariaLabel){
				_template.attr('aria-label', tAttrs.ariaLabel);
				ssElemHlp.removeAttr(tElem, tAttrs, ['ariaLabel']);
			}

			if(tAttrs.ssPending){
				_template.attr('ss-pending', tAttrs.ssPending);
				ssElemHlp.removeAttr(tElem, tAttrs, ['ssPending']);
			}

			_template.attr('ng-disabled', tAttrs.isDisabled);

			// transfer icons decorators to label element
			if (tAttrs.ssIcon || tAttrs.ssIconBefore) {
				labelEl.attr('ss-icon-before', tAttrs.ssIcon || tAttrs.ssIconBefore);
				ssElemHlp.removeAttr(tElem, tAttrs, ['ssIcon', 'ssIconBefore']);
			}
			if (tAttrs.ssIconAfter) {
				labelEl.attr('ss-icon-after', tAttrs.ssIconAfter);
				ssElemHlp.removeAttr(tElem, tAttrs, 'ssIconAfter');
			}

			// if there's no content, we don't need to go any further
			if (children.length === 0) { return _template[0].outerHTML; }

			/////
			// process the children and construct menu
			//

			// process the child elements for dropdown content
			for (let i = children.length; i--;) {
				const c = children.eq(i);
				const noChild = !c.children().length;
				const instructListItem = '<li ng-click="$event.stopPropagation()" class="super-button-instruction"';

				let cTemplate = c[0].outerHTML;
				cTemplate = cTemplate.replace(/<(\/?)item/gi, '<$1li');
				cTemplate = cTemplate.replace(
					/<(\/?)instruction/gi,
					(m, s) => (s ? '</li' : instructListItem)
				);
				if (!(/^<li/i).test(cTemplate)) { cTemplate = `<li>${cTemplate}</li>`; }
				// convert our new template to an element
				cTemplate = angular.element(cTemplate);
				cTemplate.addClass('select-menu-list-item');
				// if it doesn't have children, apply the correct padding to the li
				if (noChild) { cTemplate.addClass('item'); }
				c.replaceWith(cTemplate);
			}

			// add the dropdown content
			content = tElem.html();
			if (content.length > 0) {
				// if we have content, apply the ss-menu decorator to hide/show it
				_template.attr('ss-menu', 'select-menu');
				_template.attr('hide-on-select', '');
			}

			// return the wrapped version of the super button template that contains
			// the menu that is not INSIDE the button (works in firefox)
			return `
				<div class="super-button-container" ss-a11y-vert-list=".select-menu-list-item[ng-click]">
					${_template[0].outerHTML}
					<div class="select-menu"><ul class="select-menu-list">${content}</ul></div>
				</div>
			`;
		}

	}

	function getTypeFromAttrs(attrs) {
		/* eslint-disable brace-style */
		// check for the different type attributes
		if (attrs.primary != null || attrs.pri != null) { return 'pri'; }
		else if (attrs.negative != null || attrs.neg != null) { return 'neg'; }
		else if (attrs.positive != null || attrs.pos != null) { return 'pos'; }
		else if (attrs.standard != null || attrs.std != null) { return 'std'; }
		else if (attrs.simple != null || attrs.smp != null) { return 'smp'; } // flat with grey text
		else if (attrs.flat != null) { return 'flat'; } // flat with inheritted text color
		else if (attrs.link != null) { return 'link'; } // like a link
		/* eslint-enable brace-style */
		return 'std';
	}

	function getSizeFromAttrs(attrs) {
		/* eslint-disable brace-style */
		// check for the different size attributes
		if (attrs.small != null || attrs.sm != null) { return 'sm'; }
		else if (attrs.large != null || attrs.lg != null) { return 'lg'; }
		/* eslint-enable brace-style */

		return '';
	}

	return new Button(props, 'button', config);
}

// #-- END Component (super-button) --# //
// #----------------------------------# //

angular.module('super-suit-components').directive('superButton', superButton);
