import { Enums } from 'base-enum';

// #---------------------------------# //
// #---- Enums (/propertyAction) ----# //

class PropertyActionEnum extends Enums {
	/*@ngInject*/
	constructor() {
		// NOTE: the enums defined by the server are the binary numbers.
		// The rest defined are the combinations of those numbers for use on the front end.
		// We do send and receive the numbers as defined by here. If we send 5, we will get 5 back.
		var enums = [{
			name: 'Not Visible',
			value: 0
		}, {
			name: 'View',
			value: 1
		}, {
			//NOTE: Server does bitwise addition on 0x01 and 0x02, will never send 0x02
			name: 'Edit',
			value: 3
		}, {
			//NOTE: User sees existence of field, but not value. Service passes '******'
			name: 'Redacted', //Visible as '******'
			value: 4
		}, {
			name: 'Redacted - View', // Visible but option to view
			value: 5
		}, {
			//NOTE: Server does bitwise addition on 0x01, 0x02, and 0x04, will never send 0x06
			name: 'Redacted - Edit', // Visible but option to edit
			value: 7
		}];
		super(enums);
	}
}

// #---- End Enums (/propertyAction) ----# //
// #-------------------------------------# //

angular.module('super-suit-enums').factory('enumPropertyAction', () => new PropertyActionEnum());
