/* globals sidekick */

/*@ngInject*/
function hookupUserInfo(ssAuthSvc) {
	sidekick.events.on('header.user-info.account-settings', ssAuthSvc.editSettings, {terminal: true});
	sidekick.events.on('session.logout', ssAuthSvc.signout, {terminal: true});
}

angular.module('super').run(hookupUserInfo);
