// #------------------------------------# //
// #---- Mutator (mutatorLocations) ----# //

/*@ngInject*/
function locationsMutator(ssMutatorDataHlp) { return {

	// sets obj.example to obj.name.toUpperCase()
	'formattedPhone': function (obj) {
		const propertyName = '_phone';
		if (!obj.hasOwnProperty(propertyName)) { return ''; }
		if (!obj[propertyName]) { return ''; }

		return ssMutatorDataHlp.formatPhone(obj[propertyName]);
	},

};}

// #---- End Mutator (mutatorLocations) ----# //
// #----------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorLocations', locationsMutator);
