/*@ngInject*/
function buildConfig($window, ssObjectHlp) {
// #---------------------------------# //
// #---- Utility (buildConfig) ----# //

	const { update } = ssObjectHlp;
	const localBuildConfig = JSON.parse(($window.sessionStorage.getItem('env-vars') || '{}'));
	update($window.buildConfig, localBuildConfig);
	return $window.buildConfig;

// #-- END Utility (buildConfig) --# //
// #---------------------------------# //
}

angular.module('super-suit-utilities').factory('buildConfig', buildConfig);
