/*@ngInject*/
function protectedContextApi(ssApiSvc, buildConfig) {
// #----------------------------------------------# //
// #---- API Endpoint (ssProtectedContextApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.permissions;
	const url = `${baseUrl}/ProtectedContextRegistrations`;
	const queryUrl = `${url}?organizationId=:ORGID:`;
	// model data type
	const model = 'protectedContextRegistrations';
	// possible relationships
	const rel = [
	];

	// generate default endpoints
	const get = ssApiSvc.endpoint({url, method: 'GET'}, model, rel);
	const query = ssApiSvc.endpoint({url: queryUrl, method: 'GET'}, model, rel);
	const add = ssApiSvc.endpoint({url: queryUrl, method: 'POST'}, model, rel);

	// API endpoint methods
	return {
		get, query, add
	};

// #-- END API Endpoint (ssProtectedContextApi) --# //
// #----------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssProtectedContextApi', protectedContextApi);
