/*@ngInject*/
function ssProfileCompletenessSvc($q, enumUsageType, ssEffectiveDateHlp) {
	// #--------------------------------------------# //
	// #---- Service (ssProfileCompletenessSvc) ----# //

	// basic completeness testing methods
	const stringIncomplete = (val) => typeof val !== 'string' || val.length === 0;
	const numberIncomplete = (val) => typeof val !== 'number';
	const basicIncomplete = (val) => val == null;


	// single source of truth for all fields that make a profile complete and
	// how we test whether or not they are complete
	var fieldsList = [];
	var ready = $q.resolve().then(function() {
		fieldsList = [{
			name: '_firstName',
			group: 'personal',
			label: 'First name',
			incomplete: stringIncomplete
		}, {
			name: '_lastName',
			group: 'personal',
			label: 'last name',
			incomplete: stringIncomplete
		}, {
			name: '_birthDateUtc',
			group: 'personal',
			label: 'Birth Date',
			incomplete: basicIncomplete
		}, {
			name: '_sSN',
			group: 'personal',
			label: 'Social Security Number',
			incomplete: stringIncomplete
		}, {
			name: '_maritalStatus',
			group: 'personal',
			label: 'Marital Status',
			incomplete: numberIncomplete
		}, {
			name: '_gender',
			group: 'personal',
			label: 'Gender',
			incomplete: numberIncomplete
		}, {
			name: '_ethnicity',
			group: 'personal',
			label: 'Ethnicity',
			incomplete: numberIncomplete
		}, {
			name: '_race',
			group: 'personal',
			label: 'Race',
			incomplete: numberIncomplete
		}, {
			name: '_addresses',
			group: 'personal',
			label: 'Home Address',
			incomplete: addressIncomplete
		}, {
			name: '_emails',
			group: 'personal',
			label: 'Work/Personal Email Address',
			incomplete: emailIncomplete
		}, {
			name: '_phoneNumbers',
			group: 'personal',
			label: 'Personal/Other Phone #',
			incomplete: phoneIncomplete
		}, {
			name: '_hireDate',
			group: 'employment',
			label: 'Hire Date',
			incomplete: basicIncomplete
		}, {
			name: '_assignments',
			group: 'employment',
			label: 'Primary Assignment',
			incomplete: assignmentIncomplete
		}, {
			name: '_emergencyContacts',
			group: 'emergency',
			label: 'Primary Emergency Contact',
			incomplete: emergencyIncomplete
		}, {
			name: '_statusHistory',
			group: 'employment',
			label: 'Employment Status',
			incomplete: statusIncomplete
		}];
	});



	// External service API
	return {
		ready,
		resolve,
		getIncompleteList
	};

	/**
	 * Run the employee object through the checker and map out what is complete and what isnt.
	 *
	 * @param {object} emp An employee record that needs to display its completeness to the user.
	 */
	function resolve(emp) {
		return {
			// Tabs are generally checkers for a physical tab on the ui
			tabsIncomplete: {
				employee: fieldsList.filter(f => f.group === 'personal')
					.some(f => f.incomplete(emp[f.name])),

				employment: fieldsList.filter(f => f.group === 'employment')
					.some(f => f.incomplete(emp[f.name])),

				emergency: fieldsList.filter(f => f.group === 'emergency')
					.some(f => f.incomplete(emp[f.name])),
			}
		};
	}

	/**
	 * Returns a string array that is the list of sections that need to be completed for a complete profile
	 *
	 * @returns {Array} string[] List of areas to complete
	 */
	function getIncompleteList(emp) {
		return fieldsList
			.filter(f => f.incomplete(emp[f.name]))
			.map(f => f.label);
	}

	/**
	 * Checks the list of emergency contacts to ensure the the primary contact is present.
	 */
	function emergencyIncomplete(contacts) {
		if (contacts == null) {
			return true;
		}
		var primaryContact = contacts.find((c) => c._isPrimary);

		return primaryContact == null ||
			stringIncomplete(primaryContact._primaryPhone) ||
			stringIncomplete(primaryContact._alternatePhone) ||
			stringIncomplete(primaryContact._contactName) ||
			primaryContact._relationshipType === -1;
	}
	/**
	 * Checks the address to ensure that it is valid
	 * @return True if the address is not present or incomplete
	 */
	function addressIncomplete(address) {
		if (address == null) {
			return true;
		}
		if (!address) {
			return true;
		}
		if (angular.isArray(address) && address.length === 0) {
			return true;
		}

		address = address.filter(x => x.isCurrent)[0];

		if (address == null) {
			return true;
		}
		return !(address._street1 && address._city && address._state && address._zip);
	}

	/**
	 * Checks to see if any emails in the list have an email and are of type Personal or Work.
	 *
	 * @return True if the check fails. False if we do have a match.
	 */
	function emailIncomplete(emails) {
		if (emails == null || emails.length === 0) {
			return true;
		}

		return emails.filter(e => e._emailAddress != null && e._emailAddress.length > 0 &&
			(e._emailType === enumUsageType.work || e._emailType === enumUsageType.personal)
		).length === 0;
	}

	/**
	 * Checks to see if any phones in the list have a number and are of type Personal or Other.
	 *
	 * @return True if the check fails. False if we do have a match.
	 */
	function phoneIncomplete(phones) {
		if (phones == null || phones.length === 0) {
			return true;
		}

		return phones.filter(p => p._number != null && p._number.length > 0 &&
			(p._usageType === enumUsageType.other || p._usageType === enumUsageType.personal)
		).length === 0;
	}

	/**
	 * Checks to see if there is a valid assignment on the employee object
	 *
	 * @param {any} assignments - the assignments on an employee object
	 * @returns {bool} true if check fails, false if we have valid assignments
	 */
	function assignmentIncomplete(assignments) {
		return (assignments == null || assignments.length === 0);
	}

	/**
	 * Checks to see if there is a current status on the employee object
	 *
	 * @param {any} statuses - the statusHistory on an employee object
	 * @returns {bool} true if check fails, false if we have valid assignments
	 */
	function statusIncomplete(statusHistory) {
		if (statusHistory == null || statusHistory.length === 0) { return true; }
		const currentStatus = statusHistory.filter(x => ssEffectiveDateHlp.isCurrent({ obj: x, fromProp: '_from', toProp: '_to' }))[0];

		return currentStatus == null;
	}

	// #-- END Service (ssProfileCompletenessSvc) --# //
	// #--------------------------------------------# //
}

angular.module('super-suit-services').factory('ssProfileCompletenessSvc', ssProfileCompletenessSvc);
