/*@ngInject*/
function securityGroupsApi($http, $q, ssApiSvc, buildConfig, ssActiveUserSvc, ssAuthSvc, ssApiResponseHlp) {
// #--------------------------------------------# //
// #---- API Endpoint (ssSecurityGroupsApi) ----# //

    // base endpoint url
    const baseUrl = buildConfig.endpoint.permissions;
    const url = `${baseUrl}/SecurityGroups`;
    const queryUrl = `${url}?organizationId=:ORGID:`;
    const getUrl = (p) => `${url}/{p.id}`;
    const securityGroupsByEmployeeIdUrl = (p) => `${baseUrl}/SecurityGroups/Employee/${p.id}?organizationId=:ORGID:`;
    const getSecurityGroupsByPermissionIdsUrl = `${url}/permissionIds?organizationId=:ORGID:`;

    // model data type
    const model = 'securityGroups';
    // possible relationships
	const rel = [
        'securityScopes'
    ];

    // generate default endpoints

    const get = ssApiSvc.endpoint({url: getUrl, method: 'GET'}, model, rel);
    const query = ssApiSvc.endpoint({url: queryUrl, method: 'GET'}, model, rel);
    const add = ssApiSvc.endpoint({url: queryUrl, method: 'POST'}, model, rel);
    const getSecurityGroupsByEmployeeId = ssApiSvc.endpoint({url: securityGroupsByEmployeeIdUrl, method: 'GET'}, model, rel);
    const getSecurityGroupsByPermissionIds = (params, body) => {
		const config = {
			url: getSecurityGroupsByPermissionIdsUrl,
			method: 'POST',
			params,
			data: {
				data: body
			}
		};
		return api(config).then(res => {
			return res.data;
		});;
	};
	function api(config) {
		let returnPromise = $q.when(ssAuthSvc.ready);

		// if the url uses the orgId, wait for it to be ready
		returnPromise = returnPromise.then(() => ssActiveUserSvc.ready);

		returnPromise = returnPromise.then(function () {
			const orgId = ssActiveUserSvc.getOrgIdSync();
			const urlNeedsOrgId = config.url.includes(':ORGID:');

			// if this url needs an orgId replacement, and we don't have an org context... abort!
			if (urlNeedsOrgId && orgId == null) { return; }
			// if this url needs an orgId, replace it
			if (urlNeedsOrgId) {
				config.url = config.url.replace(/:ORGID:/ig, orgId);
			}

			return $http(config)
				.then(res => (res.status >= 400 && res.status < 500 ? $q.reject(res) : res))
				.catch(res => {
					if (res.status === -1) { return; } // timeout or cancelled
					ssApiResponseHlp.handle4XX(res);
				});
		});
		return returnPromise;
	}

	// API endpoint methods
	return {
		get, query, add, getSecurityGroupsByEmployeeId, getSecurityGroupsByPermissionIds
	};

// #-- END API Endpoint (ssSecurityGroupsApi) --# //
// #--------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssSecurityGroupsApi', securityGroupsApi);
