/* globals sidekick */
var currentEmployeeDefer;
const {store} = sidekick;

// list of products that are using common employee
var usingEmployeeMsvc = [
	'EC'
];

/*@ngInject*/
function ssActiveUserSvc($q, $location, ssEventSvc, ssDeprecated, ssServiceLogicHlp) {
// #-----------------------------------# //
// #---- Service (ssActiveUserSvc) ----# //

	const userDataDeferred = $q.defer();
	if (currentEmployeeDefer == null) { currentEmployeeDefer = $q.defer(); }
	store.onValueOnce('idm-data', userDataDeferred.resolve);

	var idmData, employee;
	const ready = userDataDeferred.promise.then(res => idmData = res);
	const employeeReady = currentEmployeeDefer.promise.then(res => employee = res);

	// find productId via url data
	var productId;
	const productIdPromise = findProductId().then(ssServiceLogicHlp.productIdEnvMap);
	productIdPromise.then(pId => productId = pId);

	const pIdList = ssServiceLogicHlp.productIdList(usingEmployeeMsvc);

	// External service API
	return {
		ready,

		// get data async
		employeeReady,
		getIdentityByType,
		getIdentitiesByType: type => ready.then(d => identitiesByType(d.identities, type)),
		getUserKey: ssDeprecated(
			'ssActiveUserSvc.getUserKey()',
			'ssActiveUserSvc.getIdentityByType(\'employee\')',
			() => getIdentityByType('employee')
		),
		getOrgId: () => ready.then(d => d.userContext.OrganizationId),
		getProductId: () => productIdPromise,
		getProfileId: () => ready.then(d => d.user.profileId),
		getUserContext: () => ready.then(d => d.userContext),
		getAccessRecords: () => ready.then(d => d.access),
		getUserRoles: () => ready.then(d => d.roles),
		getEmployee: () => employeeReady,

		// get data SYNC
		getIdentityByTypeSync: type => findIdentityByType(idmData.identities, type),
		getIdentitiesByTypeSync: type => identitiesByType(idmData.identities, type),
		getUserKeySync: ssDeprecated(
			'ssActiveUserSvc.getUserKeySync()',
			'ssActiveUserSvc.getIdentityByTypeSync(\'employee\')',
			() => findIdentityByType(idmData.identities, 'employee')
		),
		getOrgIdSync: () => idmData.userContext.OrganizationId,
		getProductIdSync: () => productId,
		getProfileIdSync: () => idmData.user.profileId,
		getUserContextSync: () => idmData.userContext,
		getAccessRecordsSync: () => idmData.access,
		getUserRolesSync: () => idmData.roles,
		getEmployeeSync: () => employee,

		// logic checks
		productUsesCommonEmployee: (pId) => pIdList.includes(pId.toUpperCase()),

		// DEPRECATED
		// left in place for backward compatibility
		get: ssDeprecated(
			'ssActiveUserSvc.get()',
			'ssActiveUserSvc.getEmployee()',
			() => employeeReady
		),
	};

	// get first id that matches type
	function getIdentityByType(type) {
		return ready.then(d => findIdentityByType(d.identities, type));
	}

	function findProductId() {
		const uiModMatch = $location.path().match(/^\/([\w-]*)/i);
		const uiModuleName = (uiModMatch.length === 2 ? uiModMatch[1] : '').toUpperCase();
		const productDefer = $q.defer();
		const productPromise = productDefer.promise.then(function (moduleName) {
			const moduleNameSplit = moduleName.split('-');
			return (moduleNameSplit.length > 1 ? moduleNameSplit[0].toUpperCase() : '');
		});

		// if this is the auth_callback url, don't resolve the ui-module name yet
		if (!uiModuleName || uiModuleName.toLowerCase() === 'auth_callback') {
			ssEventSvc.on('ui-module-change-start', productDefer.resolve, false);
		} else {
			productDefer.resolve(uiModuleName);
		}

		return productPromise;
	}

// #-- END Service (ssActiveUserSvc) --# //
// #-----------------------------------# //
}

function findIdentityByType(idList, type) {
	if (!Array.isArray(idList)) { return undefined; }
	const idRecord = idList.find(id => id.Type === type);
	return idRecord && idRecord.Id || undefined;
}

function identitiesByType(idList, type) {
	if (!Array.isArray(idList)) { return undefined; }
	const idRecords = idList.filter(id => id.Type === type);
	return idRecords.map(rec => rec.Id);
}

/*@ngInject*/
function employeeDataHookup($q, ssEmployeeApi, ssServiceLogicHlp) {
	const pIdList = ssServiceLogicHlp.productIdList(usingEmployeeMsvc);

	if (currentEmployeeDefer == null) { currentEmployeeDefer = $q.defer(); }
	store.onValueOnce('idm-data', function (idmData) {
		const employeeId = findIdentityByType(idmData.identities, 'employee');
		const noEmployeeId = idmData.identities == null || employeeId == null;
		const notPlatformEmployee = !pIdList.includes(idmData.productId);
		if (noEmployeeId || notPlatformEmployee) {
			currentEmployeeDefer.reject({});
		} else {
			ssEmployeeApi.get({id: employeeId}).then(currentEmployeeDefer.resolve);
		}
	});
}

angular.module('super-suit-services').factory('ssActiveUserSvc', ssActiveUserSvc);

angular.module('super-suit').run(employeeDataHookup);
