/*@ngInject*/
function ssEnum(
	enumEthnicity,
	enumGender,
	enumLicenseTypes,
	enumMaritalStatus,
	enumPhoneTypes,
	enumRace,
	enumRelationshipTypes,
	enumSalutation,
	enumStates,
	enumSuffix,
	enumUsageType,
	enumStepAction,
	enumStepEventType,
	enumStepEventAction,
	enumStepParticipantType,
	enumEmployeeStatus,
	enumFormStatus,
	enumFormTemplateStatus,
	enumFieldWidths,
	enumFieldHeights,
	enumFlInviteStatus,
	enumSecurityGroupScope,
	enumLocationCategories,
	enumJobTypeCategories,
	enumPropertyAction,
	enumPersonnelCategory,
	enumMedicalCategory,
	enumSalaryType,
	enumRetirementSystem,
	enumEmergencyContactRelationship,
	enumFormPacketCategory,
	enumPayRateFrequency,
	enumFormAdministratorType,
	enumAddressType,
	enumHighchartsTypes,
	enumEmailType,
	enumSignatureDisplayMethod/*enumInject*/
) {
	// #---------------------------# //
	// #---- Utility (ssEnums) ----# //

	// keep the list alphabatized
	return {
		ethnicity: enumEthnicity,
		gender: enumGender,
		licenseTypes: enumLicenseTypes,
		maritalStatus: enumMaritalStatus,
		phoneTypes: enumPhoneTypes,
		race: enumRace,
		relationshipTypes: enumRelationshipTypes,
		salutation: enumSalutation,
		states: enumStates,
		suffix: enumSuffix,
		usageType: enumUsageType,
		stepAction: enumStepAction,
		stepEventType: enumStepEventType,
		stepEventAction: enumStepEventAction,
		stepParticipantType: enumStepParticipantType,
		employeeStatus: enumEmployeeStatus,
		formStatus: enumFormStatus,
		formTemplateStatus: enumFormTemplateStatus,
		fieldWidths: enumFieldWidths,
		fieldHeights: enumFieldHeights,
		flInviteStatus: enumFlInviteStatus,
		securityGroupScope: enumSecurityGroupScope,
		locationCategories: enumLocationCategories,
		jobTypeCategories: enumJobTypeCategories,
		propertyAction: enumPropertyAction,
		personnelCategory: enumPersonnelCategory,
		medicalCategory: enumMedicalCategory,
		salaryType: enumSalaryType,
		retirementSystem: enumRetirementSystem,
		emergencyContactRelationship: enumEmergencyContactRelationship,
		formPacketCategory: enumFormPacketCategory,
		payRateFrequency: enumPayRateFrequency,
		formAdministratorType: enumFormAdministratorType,
		addressType: enumAddressType,
		highchartsTypes: enumHighchartsTypes,
		emailType: enumEmailType,
		signatureDisplayMethod: enumSignatureDisplayMethod/*enumList*/
	};

	// #-- END Utility (ssEnums) --# //
	// #---------------------------# //
}

angular.module('super-suit-utilities').factory('ssEnum', ssEnum);
