///////
// ready is out here so that it exists outside of the context of any
// given angular application (super or fl-app)
//
var ready;
const actionData = [];
const persistantBundles = [];
const routeData = [];

/*@ngInject*/
function ssPermissionsSvc($window, $rootScope, $q, $http, $log, ssAsync, buildConfig,
	ssObjectHlp, ssActiveUserSvc, ssStringHlp, ssSecurityGroupsApi, ssFeatureSvc) {
// #------------------------------------# //
// #---- Service (ssPermissionsSvc) ----# //

	const {glean} = ssObjectHlp;
	const permissionsUrl = buildConfig.endpoint.permissions;
	const {aesopApi} = buildConfig.endpoint;
	const permittedActions = {};
	const permittedActionsNoCase = {}; // for internal (case insensitive) lookups only
	const permissionExemptPaths = ['/auth_callback', '', '/select'];
	let securityGroups = [];
	let securityScopes = [];
	/////
	// if we don't have a ready promise, go make the http requests
	//
	if (ready == null) {
		const loadRouteData = ssAsync(function* (manifest) {
			const scriptTarget = angular.element(document.head);

			yield Object.keys(manifest).map(modName => {
				const d = $q.defer();
				const modPath = manifest[modName].path;
				const overrideUrl = localStorage.getItem(`assets.uiModules.${modName}`);
				const routeDataPath = overrideUrl != null
					? `${overrideUrl}/route-data.js`
					: modPath.routeData;
				const routeScript = document.createElement('script');
				routeScript.setAttribute('type', 'text\/javascript');
				routeScript.onerror = d.resolve;
				routeScript.onload = d.resolve;
				routeScript.setAttribute('src', routeDataPath);
				scriptTarget.append(routeScript);
				return d.promise;
			});
		});

		ready = (ssAsync(function* () {
			const [userId, orgId] = yield [
				ssActiveUserSvc.getIdentityByType('employee'),
				ssActiveUserSvc.getOrgId()
			];
			yield ssFeatureSvc.ready;
			const userPermissionsUrl = `${permissionsUrl}/ActionPermissions?organizationId=${orgId}`;
			const aesopApiUrl = `${aesopApi}/v1.0/Organizations/${orgId}/FrontlineInstitute/User/Permissions`;

			// permissions microservice endpoint check
			const permReq = orgId != null ? $http.get(userPermissionsUrl) : null;

			// aesop institute permissions endpoint check
			const hasOrgId = orgId != null;
			const {identities = []} = sidekick.store.get('idm-data');
			const hasTaIdentity = identities.some(i => i.Type.startsWith('abs_time_orguser'));
			const shouldCheckTaInstitute = hasOrgId && hasTaIdentity;
			const aesopReq = (shouldCheckTaInstitute ? $http.get(aesopApiUrl, {ignore401: true}).catch(r => ({})) : null);


			let apiCalls = [permReq, aesopReq];
			if(userId != null) {
				const securityGroupsReq = ssSecurityGroupsApi.getSecurityGroupsByEmployeeId({ id: userId , include: 'securityScopes'});
				apiCalls.push(securityGroupsReq);
			}else{
				securityGroups = null;
				securityScopes = null;
			}
			// make the permissions calls
			const [permRes, aesopRes, securityGroupsRes] = yield apiCalls;
			if(securityGroupsRes) {
				securityGroups = ssObjectHlp.deepFreeze(distillSecurityGroups(securityGroupsRes));
				securityScopes = ssObjectHlp.deepFreeze(distillSecurityScopes(securityGroupsRes.filter(g => g._securityScopes.length).reduce(function(prev, curr){ return prev.concat(curr._securityScopes); },[])));
			}


			// get the permissions object from the response
			const permObjArr = glean(permRes, _=>_.data.data.attributes.objects, []);
			if (Array.isArray(permObjArr) && permObjArr.length > 0) {
				permObjArr.forEach(p => actionData.push(p));
			}

			// copy the roles data from the active user
			const roles = ssActiveUserSvc.getUserRolesSync().slice();

			// add a permissions group that displays the roles as
			// a set of permissions (example: idmAdministrator)
			actionData.push({
				name: 'ActiveUser',
				type: 'activeUser',
				id: 'IDM-ACTIVE-USER-ROLES-AND-DATA',
				permittedActions: roles
			});

			if (glean(aesopRes, _=>_.data.data.attributes.allow === true, false)) {
				actionData.push({
					name: 'absmgmt',
					type: 'absmgmt',
					id: 'ABSMGMT-ACCESS-PERMISSIONS',
					permittedActions: [
						'viewInstituteReport'
					]
				});
			}

			// add activeUser.userKey so we can hide feature if this
			// active user doesn't have a userkey in the system
			// this should only happen when a Frontline internal user
			// visits the site.
			if (userId != null) { roles.push('userKey'); }

			const {manifest} = $window.ss.env;
			return yield loadRouteData(manifest);
		})());
	}

	/////
	// when the permission data is ready, build the actions object from it
	//
	const permReady = ready.then(function () {
		// mutate the array of objects into a single object that
		// represents the permitted actions
		const actions = actionData.reduce(function (out, action) {
			const actionName = action.type;
			const actionList = action.permittedActions.map(a => ssStringHlp.toCamelCase(a));

			// create group object and populate it
			out[actionName] = {};
			for (let verb of actionList) {
				out[actionName][verb] = true;
			}

			return out;
		}, {});

		// attach the permitted object to $root for convenience
		$rootScope.isPermitted = permittedActions;

		// creates an object where all the values are static
		Object.keys(actions).forEach(function (groupName) {
			Object.defineProperty(permittedActions, groupName, {enumerable: true, value: {}});
			permittedActionsNoCase[groupName.toLowerCase()] = {};

			Object.keys(actions[groupName]).forEach(function (key) {
				Object.defineProperty(permittedActions[groupName], key, { enumerable: true, value: true });
				permittedActionsNoCase[groupName.toLowerCase()][key.toLowerCase()] = true;
			});
		});

		// setup action group container
		Object.defineProperty(permittedActions, 'bundle', { enumerable: true, value: {} });
		Object.defineProperty(permittedActionsNoCase, 'bundle', { enumerable: true, value: {} });

		// register any persistant bundles with this new permitted object
		// this allows the super-suit to declare bundles that aren't torn down
		persistantBundles.forEach(b => addPermittedActionBundle(b.bundleName, b.isPermittedCheck));
	});

	// External service API
	const permissionSvc = {
		permittedActions,		 // all permitted actions as keys, value is bool
		ready: permReady,		 // service ready promise
		addPermittedActionBundle, // create a logical grouping of permitted actions
		isPermittedAction,		// check if an action is permitted
		isNotPermittedAction,	 // do I really need to explain this?
		isPermittedRoute,		 // is the user allowed to visit this url?
		isNotPermittedRoute,	  // is the user not allowed to visit this url?,
		securityGroups: () => securityGroups,				//All the security groups logged in user belongs to
		securityScopes: () => securityScopes			// flatten list of Security Scopes in user security group contains
	};
	return permissionSvc;

	/**
	 * addPermittedActionBundle - add a single value that represents a group of
	 * permissions or other logic so that it can be easily evaluated in to UI
	 *
	 * @param  {String} bundleName - the name of the bundle property
	 * @param  {Function} isPermittedCheck - handler for the bundle. Should
	 * return boolean, receives ssPermissionSvc as the first param.
	 * @return {Boolean} - whether this group is permitted
	 */
	function addPermittedActionBundle(bundleName, isPermittedCheck, persistant = false) {
		var cached;
		ready.then(function () {
			if (permittedActions.bundle.hasOwnProperty(bundleName)) {
				$log.error('failed to add the permittedActionBundle', bundleName, '. It was already defined!');
				return;
			}

			if (persistant) {
				persistantBundles.push({bundleName, isPermittedCheck});
			}

			// register the bundle
			Object.defineProperty(permittedActions.bundle, bundleName, {
				enumerable: true,
				get: () => (cached == null ? cached = isPermittedCheck() : cached),
				set: () => {}
			});
			Object.defineProperty(permittedActionsNoCase.bundle, bundleName.toLowerCase(), {
				enumerable: true,
				get: () => (cached == null ? cached = isPermittedCheck() : cached),
				set: () => { }
			});
		});
	}

	/**
	 * isPermittedAction - checks the permitted object for a specific action
	 *
	 * @param {string} groupName - the name of the action group
	 * @param {string} actionName - the name of the action
	 * @returns {boolean}
	 */
	function isPermittedAction(groupName, actionName) {
		groupName = ssStringHlp.toCamelCase(groupName).toLowerCase();
		actionName = ssStringHlp.toCamelCase(actionName).toLowerCase();

		if (permittedActionsNoCase[groupName] == null) { return false; }
		return permittedActionsNoCase[groupName][actionName] === true;
	}

	/**
	 * isNotPermittedAction - checks if the action is NOT permitted
	 *
	 * @param {string} groupName - the name of the action group
	 * @param {string} actionName - the name of the action
	 * @returns {boolean}
	 */
	function isNotPermittedAction(groupName, actionName) {
		return !isPermittedAction(groupName, actionName);
	}

	function isPermittedRoute(url, {skipInherit = false} = {}) {
		//> groupCollapsed('%cssPermissionsSvc.isPermittedRoute => ' + url, 'color: lightgrey;')
		const ret = (() => {
			// strip any empty params and trailing slashes
			var queryParams = {};

			const isExternal = RegExp.prototype.test.bind((/^https?:\/\//));
			if (isExternal(url)) {
				//> log('route is external, assuming we have permission')
				return true;
			}

			url = '/' + url.split('/').filter(p => !!p.length).join('/');
			if (url.includes('?')) {
				let queryString = url.split('?')[1];
				url = url.split('?')[0];
				queryParams = getQueryStringObj(queryString);
			}

			// remove any trailing slashes
			const exemptCheckUrl = (url.endsWith('/') ? url.slice(0, -1) : url);
			// allow the path if it's permission exempt
			if (permissionExemptPaths.includes(exemptCheckUrl)) {
				//> log('route is a permission exempt Url, assuming we have permission')
				return true;
			}

			var state = routeData.find(r => {
				return r.matchesUrl(url, queryParams);
			});

			// couldn't find state,
			if (state == null) {
				//> warn('state not found')
				return false;
			}

			//> log('state data: ', state)
			if (state.$permissions === 'inherit') {
				// if we are skipping inherited permissions, then these are the
				// same as a null permissions function (true)
				if (skipInherit) { return true; }
				//> log('checking inherited permissions')
				const skip = {skipInherit: true};
				return url // => "/this/that/other"
					.split('/') // => ["", "this", "that", "other"]
					.slice(1, -1) // => [this", "that"]
					.map((v, i, a) => `/${a.slice(0, i+1).join('/')}`) // => ["/this", "/this/that"]
					.every(route => isPermittedRoute(route, skip)); // check the recontructed url => true/false
			}
			// if no $permissions function is specified, allow the route
			if (state.$permissions == null) {
				//> log('no $permissions function exists, assuming we have permission')
				return true;
			}
			if (typeof state.$permissions !== 'function') {
				$log.error('$permissions must be a function');
				return false;
			}
			const permissionsResult = state.$permissions(permissionSvc);
			//> log('$permissions function', state.$permissions)
			//> log('$permissions result', permissionsResult)
			return permissionsResult;
		})();
		//> groupEnd('%cssPermissionsSvc.isPermittedRoute => ' + url, 'color: lightgrey;')
		return ret;
	}

	function isNotPermittedRoute(url) {
		return !isPermittedRoute(url);
	}

	function getQueryStringObj(qs) {
		var vars = qs.split('&');

		return vars.reduce((prev, varItem) => {
			let pair = varItem.split('=');
			prev[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1]);

			return prev;
		}, {});
	}

	function distillSecurityGroups(groups){
		return groups.map(g => {
			return {
				id: g._groupId,
				name: g._groupName,
				autoCreated:
				g._autoCreated,
				employeeIds: g._employeeIds,
				securityScopes: g._securityScopes.length ? distillSecurityScopes(g._securityScopes) : []
			};
		});
	}
	function distillSecurityScopes(scopes){
		if(!scopes.length) { return []; }
		return scopes.map(s => {
			return {
				id: s.id,
				autoCreated: s._autoCreated,
				isViewSelf: s._isViewSelf,
				locked: s._locked,
				departmentAdministrationContext: s._departmentAdministrationContext,
				departmentIds: s._departmentIds,
				employeeAdministrationContext: s._employeeAdministrationContext,
				employeeIds:s._employeeIds,
				jobTypeAdministrationContext: s._jobTypeAdministrationContext,
				jobTypeIds: s._jobTypeIds,
				locationAdministrationContext: s._locationAdministrationContext,
				locationIds: s._locationIds,
			};
		});
	}

// #-- END Service (ssPermissionsSvc) --# //
// #------------------------------------# //
}

/*@ngInject*/
function routeDataRegistration($window, $urlMatcherFactory, ssAsync, buildConfig, ssUiModuleHlp) {
	// global function for registering route data. this will be run by the ui-module
	// route-data.js scripts that send in all the ui-module route data so we know
	// what routes are allowed for this user without needing to load the ui-module
	// codebase into memory.
	$window.registerRouteData = ssAsync(function* (moduleName, routes) {
		const {manifest} = $window.ss.env; // TODO.... how to handle this?
		const mod = ssUiModuleHlp.getDetails(moduleName, manifest);
		const states = routes.reduce(function (s, r) {
			s[r.stateName] = r;
			return s;
		}, {});

		routes.sort(function (a, b) {
			if (a.stateName < b.stateName) { return -1; }
			if (a.stateName > b.stateName) { return 1; }
			return 0;
		});

		routes.forEach(function(r) {
			if (r.url == null) { return; }
			if (r.stateName == null) { return; }

			if (r.stateName.includes('.')) {
				let statePrefix = r.stateName.substr(0, r.stateName.lastIndexOf('.'));
				r.urlMatchPattern = `${states[statePrefix].urlMatchPattern}${r.url}`;
			} else {
				if (mod.isUniversal) {
					const universalModBaseRoute = moduleName.split('-').slice(1).join('-');
					r.urlMatchPattern = `/{productId:\\w+}-${universalModBaseRoute}${r.url}`;
				} else {
					r.urlMatchPattern = `/${moduleName}${r.url}`;
				}
			}
			if (r.urlMatchPattern.endsWith('/')) {
				r.urlMatchPattern = r.urlMatchPattern.substr(0, r.urlMatchPattern.length - 1);
			}
			r.urlMatcher = $urlMatcherFactory.compile(r.urlMatchPattern);
			r.matchesUrl = function (url) {
				if (url == null) { return false; }
				return r.urlMatcher.exec(url) != null;
			};

			routeData.push(r);
		});
	});
}

angular.module('super-suit-services').factory('ssPermissionsSvc', ssPermissionsSvc);
angular.module('super').run(routeDataRegistration);
