/*@ngInject*/
function rootUtils($rootScope) {
	$rootScope.utils = {
		findInParentScope(key, scope) {
			if (key in scope) {
				return scope[key];
			} else if (scope.$parent != null) {
				return $rootScope.utils.findInParentScope(key, scope.$parent);
			} else {
				return null;
			}
		}
	};
}

angular.module('super-suit').run(rootUtils);
