/*@ngInject*/
function ssDeprecated($log) {
// #--------------------------------# //
// #---- Utility (ssDeprecated) ----# //

	// External utility injectable value
	return function DEPRECATED(oldCodeName, newCodeName, func) {
		return function (...args) {
			$log.error(`DEPRECATED! ${oldCodeName} has been deprecated, use ${newCodeName} instead`);
			return func.apply(null, args);
		};
	};

// #-- END Utility (ssDeprecated) --# //
// #--------------------------------# //
}

angular.module('super-suit-utilities').factory('ssDeprecated', ssDeprecated);
