
/*@ngInject*/
function ssOrganizationSvc(ssAsync, ssObjectHlp, ssActiveUserSvc, ssOrganizationApi) {
// #-------------------------------------# //
// #---- Service (ssOrganizationSvc) ----# //

	var id, orgData;
	const {deepFreeze, deepCopy} = ssObjectHlp;

	// External service API
	return {
		ready: ssActiveUserSvc.ready,
		getActiveOrg: ssAsync(function* () {
			if (id == null) {
				id = yield ssActiveUserSvc.getOrgId();
			}
			if (id == null) { return {}; }
			if (orgData == null) {
				orgData = deepFreeze(deepCopy(yield ssOrganizationApi.get({id, include: 'applications'})));
			}
			return orgData;
		})
	};

// #-- END Service (ssOrganizationSvc) --# //
// #-------------------------------------# //
}

angular.module('super-suit-services').factory('ssOrganizationSvc', ssOrganizationSvc);
