/*@ngInject*/
function positionsApi(ssApiSvc, buildConfig, ssEffectiveDateHlp) {
// #---------------------------------------# //
// #---- API Endpoint (ssPositionsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.employees;
	const convertToValidatingSave = ssEffectiveDateHlp.convertToValidatingSave;

	const forEmpUrl = (p) => `${baseUrl}/employees/${p.id}/positions?organizationId=:ORGID:`;
	const queryUrl = `${baseUrl}/Positions`;

	// model data type
	const model = 'positions';

	// possible relationships.
	const rel = ['assignments'];

	// generate default endpoints
	const getForEmployee = convertToValidatingSave(ssApiSvc.endpoint({url: forEmpUrl, method: 'GET'}, model, rel));
	const query = convertToValidatingSave(ssApiSvc.endpoint({url: queryUrl, method: 'GET'}, model, rel));

	// API endpoint methods
	return {
		getForEmployee,
		query
	};

// #-- END API Endpoint (ssPositionsApi) --# //
// #---------------------------------------# //
}

angular.module('super-suit-apis').factory('ssPositionsApi', positionsApi);
