/*@ngInject*/
function organizationApi($http, ssApiSvc, buildConfig) {
	// #------------------------------------------# //
	// #---- API Endpoint (ssOrganizationApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.organizations;
	const url = `${baseUrl}/organizations`;

	// model data type
	const model = 'organizations';

	// possible relationships
	const rel = ['applications'];

	const get = ssApiSvc.endpoint({
		url: (p) => `${url}/${p.id}`,
		method: 'GET',
		cache: true
	}, model, rel);

	const query = ssApiSvc.endpoint({ url, method: 'GET' }, model, rel);
	const add = ssApiSvc.endpoint({ url, method: 'POST' }, model, rel);

	const searchOrgs = (params={}) => {
		let productAccessOrgsUrl = `${buildConfig.endpoint.productAccess}/organizations`;

		params.productId = 'EC';
		if (params.limit == null) { params.limit = 50; }

		let config = {
			url: productAccessOrgsUrl,
			method: 'GET',
			data: {},
			params
		};

		return $http(config);
	};

	// API endpoint methods
	return {
		get, query, add, searchOrgs
	};

	// #-- END API Endpoint (ssOrganizationApi) --# //
	// #------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssOrganizationApi', organizationApi);
