/*@ngInject*/
function listApi(ssApiSvc, buildConfig) {
	// #----------------------------------# //
	// #---- API Endpoint (ssListApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.lists;
	const url = `${baseUrl}/organizations/:ORGID:/lists`;
	const listItemsUrl = p => `${baseUrl}/lists/${p.id}/items`;

	// model data type
	const model = 'listHeaders';

	// possible relationships
	const rel = [
		'Items'
	];

	const query = ssApiSvc.endpoint({ url: url, method: 'GET' }, model, rel);
	const getListItems = ssApiSvc.endpoint({ url: listItemsUrl, method: 'GET' }, model, rel);

	// API endpoint methods
	return {
		query,
		getListItems
	};

	// #-- END API Endpoint (ssListApi) --# //
	// #----------------------------------# //
}

angular.module('super-suit-apis').factory('ssListApi', listApi);
