/*@ngInject*/
function jobTypeFilter(ssJobTypeSvc) {
	return function(id, prop) {
		// grab the jobType by id
		var jT = ssJobTypeSvc.getJobTypeById(id);
		// can't find it?
		if (jT == null) { return ''; }
		// did we request a specific property? if not, give all
		if (prop == null) { return jT; }
		// prop exist? no? doh!
		if (jT[`_${prop}`] == null) { return ''; }
		// yay, we find property
		return jT[`_${prop}`];
	};
}

angular.module('super-suit').filter('jobType', jobTypeFilter);
