// #------------------------------------# //
// #---- Mutator (mutatorAddresses) ----# //

/*@ngInject*/
function addressesMutator(ssDateHlp, moment) { return {

	// sets obj.example to obj.name.toUpperCase()
	'isCurrent': function (address) {
			var today = moment();
			var fromDate = moment(address._from);
			var toDate = moment(address._to);
			return fromDate.isSameOrBefore(today) && toDate.isAfter(today);
	},
	'isPast': function(address){
		var today = moment();
		var toDate = moment(address._to);
		var fromDate = moment(address._from);
		return fromDate.isBefore(today) && toDate.isSameOrBefore(today);
	},
	'isFuture': function(address){
		var today = moment();
		var fromDate = moment(address._from);
		var toDate = moment(address._to);
		return (fromDate.isSameOrAfter(today) && ssDateHlp.isForeverFuture(toDate));
	}

};}

// #---- End Mutator (mutatorAddresses) ----# //
// #----------------------------------------# //

angular.module('super-suit-mutators').factory('mutatorAddresses', addressesMutator);
