import {Decorator} from 'base-decorator';
// #-----------------------------# //
// #---- Decorator (ss-menu) ----# //

/*@ngInject*/
function ssMenu ($document, ssElemHlp, rAF) {

	class Menu extends Decorator {
		// linking function for decorator directive
		link(scope, elem, attrs) {
			const activeClass = 'ss-menu-open';
			const inActiveClass = 'ss-menu-closed';
			const target = attrs.ssMenu || 'dropdown';
			const hideOnSelect = attrs.hideOnSelect != null;
			const hideOnBlur = attrs.hideOnBlur != null;
			var ssMenuOnClose = () => {}; // starts as noop
			var ssMenuOnOpen = () => {}; // starts as noop
			var ddElem = getDdElem();
			var lock = false;

			// if we have an on-closed attribute passed, setup execution
			if (typeof attrs.ssMenuOnClose === 'string') {
				ssMenuOnClose = () => {
					scope.$eval(attrs.ssMenuOnClose);
					scope.$root.safeApply();
				};
			}
			// if we have an on-opened attribute passed, setup execution
			if (typeof attrs.ssMenuOnOpen === 'string') {
				ssMenuOnOpen = () => {
					scope.$eval(attrs.ssMenuOnOpen);
					scope.$root.safeApply();
				};
			}

			const elemNotNativelyTabable = (
				elem[0].tabIndex === -1
				&& elem.attr('tabindex') == null
			);
			if (elemNotNativelyTabable) { elem.attr('tabindex', '-1'); }

			scope.$applyAsync(() => {
				elem.addClass(inActiveClass);
			});

			if (ddElem.length) { ddElem.addClass(inActiveClass); }

			function hitMenuControl(e) {
				if (angular.element(e.target.parentElement).hasClass('is-disabled')) { return; }
				lock = true;
				toggleState();
			}
			elem.on('click', hitMenuControl);
			elem.on('keydown', e => {
				if (!['Enter', ' ', 'ArrowDown', 'ArrowUp'].includes(e.key)) { return; }
				// stop the event from scrolling the view
				e.preventDefault();
				// handle the menu state change
				hitMenuControl(e);
			});

			$document.on('click', closeMenu);
			scope.$on('$destroy', () => $document.off('click', closeMenu));

			// allow other components to close all menus
			scope.$on('$destroy', scope.$root.$on('ss-menu-close', closeMenu));

			function toggleState(toActive) {
				if (toActive === false && elem.hasClass(inActiveClass)) { return; }

				if (toActive == null) { toActive = elem.hasClass(inActiveClass); }
				const oldClass = (toActive ? inActiveClass : activeClass);
				const newClass = (toActive ? activeClass : inActiveClass);
				if (!ddElem.length) {
					ddElem = getDdElem();
					if (!ddElem.length) { return; }
				}

				elem.addClass(newClass);
				ddElem.addClass(newClass);
				elem.removeClass(oldClass);
				ddElem.removeClass(oldClass);

				if (toActive) {
					let outOfVp = ssElemHlp.isOutOfViewport(ddElem);
					if (outOfVp.right) { ddElem.addClass('ss-menu-vp-right'); }
					if (outOfVp.bottom) { ddElem.addClass('ss-menu-vp-bottom'); }
				} else {
					ddElem.removeClass('ss-menu-vp-right');
					ddElem.removeClass('ss-menu-vp-bottom');
				}

				scope.$emit(newClass, elem, ddElem);

				const handleEscape = evt => {
					switch(evt.key) {
						case 'Tab':
							if (!hideOnBlur) { break; }
							// if we ARE supposed to hide on blur, fall through
						case 'Escape':
							toggleState(false);
							ddElem.off('keydown', handleEscape);
						default: break;
					}
				};

				if (toActive) {
					ddElem[0].focus();
					ddElem.on('keydown', handleEscape);
					rAF(ssMenuOnOpen, false);
				} else {
					elem[0].focus();
					ddElem.off('keydown', handleEscape);
					rAF(ssMenuOnClose, false);
				}
			}

			function closeMenu() {
				if (!lock) { toggleState(false); }
				setTimeout(() => { lock = false; }, 0);
			}

			function getDdElem() {
				if (ddElem != null && ddElem.length) { return ddElem; }
				var dd = elem[0].getElementsByClassName(target);
				if (dd.length === 0) {
					dd = elem.parent()[0].getElementsByClassName(target);
				}
				dd = angular.element(dd);
				if (dd.length === 0) { return dd; }

				dd.addClass(inActiveClass);
				dd.attr('tabindex', '-1');
				addDdClick(dd);
				return dd;
			}

			function addDdClick(dd) {
				// if we hide-on-select we need to bail out here
				// the click event will get picked up by the document
				// click handler and close the menu
				if (hideOnSelect) { return; }

				// when we DON'T hide-on-select, we should block the click
				// event from reaching the document, so the menu stays open
				dd.on('click', function (e) { e.stopImmediatePropagation(); });
			}
		}
	}

	return new Menu();
}

// #-- END Decorator (ss-menu) --# //
// #-----------------------------# //

angular.module('super-suit-decorators').directive('ssMenu', ssMenu);
