// #-----------------------------# //
// #---- Helper (ssConfigurationHlp) ----# //

/**
 * *******************************************************************************************
 * Frontline Central - You can't inject dep-sets into an Angular SVC. SO it must be promoted
 * *******************************************************************************************
 * Configuration Helper is the the single place to manage configurations for FC lists
 *  and other items.	We have places all over the site where we have one off limits.
 * 	Let's	just put them all here.  Maybe someday we'll have a configuration mSvc!
 */
/*@ngInject*/
function ssConfigurationHlp($log) {
	const allTheThings = 99999999;
	const frontlineCentralDataLimits = {
		locations: 999999,
		departments: 999999,
		jobTypes: 999999,
		statusReasons: 500,
		employmentGroups: 500,
		positions: 500,
		permissions: 50, // MAX IS 50 for permissions PLAT-4804
		permissionGroups: 999999,
		recentlySent: 50,
		standardDataPage: 50
	};

	return {

		/**
		 * Returns the configured max limit for the requested list name or the max if none is provided.
		 * @param {string} name the name of the limit to get or <none> to get the "max"
		 */
		getLimit(name) {
			let ret = allTheThings;

			if (frontlineCentralDataLimits.hasOwnProperty(name)) {
				ret = frontlineCentralDataLimits[name];
			} else {
				$log.warn('Returned default limit since no limit found for: ', name);
			}

			return ret;
		}

	};
}
// #-- END Helper (ssConfigurationHlp) --# //
// #-----------------------------# //

angular.module('super-suit-helpers').factory('ssConfigurationHlp', ssConfigurationHlp);
