import {
	Enums
}
from 'base-enum';

// #-----------------------------# //
// #---- Enums (/salutation) ----# //

class SalutationEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'None',
			value: 0
		}, {
			name: 'Mr.',
			value: 1
		}, {
			name: 'Ms.',
			value: 2
		}, {
			name: 'Mrs.',
			value: 3
		}, {
			name: 'Miss',
			value: 4
		}, {
			name : 'Dr.',
			value: 5
		}, {
			name: 'Prof.',
			value: 6
		}, {
			name : 'Rev.',
			value: 7
		}];
		super(enums);
	}
}

// #---- End Enums (/salutation) ----# //
// #---------------------------------# //

angular.module('super-suit-enums').factory('enumSalutation', () => new SalutationEnum());
