import {Decorator} from 'base-decorator';
// #----------------------------------------# //
// #---- Decorator (ss-hide-when-empty) ----# //

/*@ngInject*/
function ssHideWhenEmpty() {

	class HideWhenEmpty extends Decorator {

		link(scope, elem, attrs) {
			const spaceRegex = /\s/g;
			const hideClass = 'hide';

			// put in applyAsync to allow children to finish processing their templates
			scope.$applyAsync(() => {
				if (isEmpty(elem)) {
					hide();
					return;
				}

				let children = elem.children();

				let hidden = [...children].filter(child => {
					if (child.style.display === 'none') {return true;}

					child = angular.element(child);
					if (child.hasClass(hideClass)) {return true;}

					return isEmpty(child);
				});

				if (hidden.length === children.length) {
					hide();
				}
			});

			function isEmpty(e) {
				let children = e.children();
				if (children.length) {return false;}
				return e.text().replace(spaceRegex, '').length === 0;
			}

			function hide() {
				elem.remove();
			}
		}
	}

	return new HideWhenEmpty();
}

// #-- END Decorator (ss-hide-when-empty) --# //
// #----------------------------------------# //

angular.module('super-suit-decorators').directive('ssHideWhenEmpty', ssHideWhenEmpty);
