// Route Directive
/*@ngInject*/
function route($compile, $stateParams, ssStringHlp) {

	function folderToPathData(folder) {
		var pathKey = folder.substr(1).replace(/\//g, '-'),
			viewName = folder.substr(folder.lastIndexOf('/') + 1);

		return {pathKey, viewName};
	}

	return {
		restrict: 'E',
		priority: 999,
		scope: true,
		replace: true,
		template(tElem, tAttrs) {
			let {pathKey} = folderToPathData(tAttrs.folder),
				segments = tElem.find('segment'),
				i = segments.length;

			// replace segment placeholders with their directive counterpart
			for (; i-- ;) {
				const seg = segments.eq(i);
				const segName = seg.attr('name');
				const segTag = ssStringHlp.toDashCase(`segment ${pathKey} ${segName}`);
				const attrHtml = Array.from(seg[0].attributes)
					.filter(a => a.name !== 'name')
					.map(a => `${a.name}="${a.value}"`)
					.join(' ');

				seg.replaceWith(`<${segTag} folder="${tAttrs.folder}" ${attrHtml}></${segTag}>`);
			}

			return `<section folder="${tAttrs.folder}" class="route-${pathKey}">${tElem.html()}</section>`;
		},
		/*@ngInject*/
		controller($scope) {
			$scope.routeData = { module: $stateParams.module };
		},
		link: {
			pre(scope, elem, attrs) {
				Object.assign(scope.routeData, folderToPathData(attrs.folder));
			}
		},
	};

}

angular.module('super-suit').directive('route', route);
