/*@ngInject*/
function usersApi($http, $httpParamSerializer, buildConfig) {
// #---------------------------------------# //
// #---- API Endpoint (ssPositionsApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.idmGatewayUrl;
	const url = `${baseUrl}userservice/v0/api/users`;

	// Get users
	const getUsers = (params) => {
		let config = {
			method: 'GET',
			url: `${url}`,
		};

		if (params) {
			config.params = params;
		}

		return $http(config);
	};

	// Get user
	const getUser = (type, id) => {
		let config = {
			method: 'GET',
			url: `${url}/${type}/${id}/`,
		};

		return $http(config);
	};

	// Get user by ID
	const getUserById = (id, params) => {
		let config = {
			method: 'GET',
			url: `${url}/id/${id}`,
		};

		if (params) {
			config.params = params;
		}

		return $http(config);
	};

	// Send Invitation
	const sendInvitationToUser = (userId, productsToAdd, productsToRemove) => {
		const config = {
			method: 'POST',
			url: `${url}/${userId}/invitations`,
			data: {
				AddProducts: [],
				RemoveProducts: [],
			},
		};

		if (productsToAdd.length) {
			config.data.AddProducts = productsToAdd;
		}

		if (productsToRemove.length) {
			config.data.RemoveProducts = productsToRemove;
		}

		return $http(config);
	};

	// Revoke Access
	const revokeAccess = (type, id) => {
		const config = {
			method: 'DELETE',
			url: `${url}/${type}/${id}/flid`,
		};

		return $http(config);
	};

	// Grant application access
	const grantApplicationAccessToUser = (type, id, productIds) => {
		let config = {
			method: 'POST',
			url: `${url}/${type}/${id}/products`,
			params: {},
		};

		if (productIds) {
			config.params.product = productIds;
		}

		return $http(config);
	};

	// Remove application access
	const removeApplicationAccessForUser = (type, id, productIds) => {
		let config = {
			method: 'DELETE',
			url: `${url}/${type}/${id}/products`,
			params: {},
		};

		if (productIds) {
			config.params.product = productIds;
		}

		return $http(config);
	};

	const changeApplicationAccessForUser = (userId, productsToAdd, productsToRemove) => {
		const config = {
			method: 'POST',
			url: `${url}/${userId}/products`,
			data: {
				AddProducts: [],
				RemoveProducts: [],
			},
		};

		if (productsToAdd.length) {
			config.data.AddProducts = productsToAdd;
		}

		if (productsToRemove.length) {
			config.data.RemoveProducts = productsToRemove;
		}

		return $http(config);
	};

	// Bulk invite via ids
	const sendBulkInvitationIds = (ids, products) => {
		const config = {
			method: 'POST',
			url: `${url}/ids/invitations?${$httpParamSerializer(ids)}`,
			data: products,
		};

		return $http(config);
	};

	// Bulk invite via filter
	const sendBulkInvitationFilter = (filter, products) => {
		const config = {
			method: 'POST',
			url: `${url}/filter/invitations?${$httpParamSerializer(filter)}`,
			data: products,
		};

		return $http(config);
	};

	// Bulk revoke via ids
	const revokeBulkAccessIds = (ids) => {
		const config = {
			method: 'DELETE',
			url: `${url}/ids/flid?${$httpParamSerializer(ids)}`,
		};

		return $http(config);
	};

	// Bulk revoke via filter
	const revokeBulkAccessFilter = (filter) => {
		const config = {
			method: 'DELETE',
			url: `${url}/filter/flid?${$httpParamSerializer(filter)}`,
		};

		return $http(config);
	};

	const grantBulkApplicationAccessIds = (ids, products) => {
		const config = {
			method: 'POST',
			url: `${url}/ids/products?${$httpParamSerializer(ids)}`,
			data: {
				AddProducts: products
			},
		};

		return $http(config);
	};

	const grantBulkApplicationAccessFilter = (filter, products) => {
		const config = {
			method: 'POST',
			url: `${url}/filter/products?${$httpParamSerializer(filter)}`,
			data: {
				AddProducts: products
			},
		};

		return $http(config);
	};

	const removeBulkApplicationAccessIds = (ids, products) => {
		const config = {
			method: 'POST',
			url: `${url}/ids/products?${$httpParamSerializer(ids)}`,
			data: {
				RemoveProducts: products
			},
		};

		return $http(config);
	};

	const removeBulkApplicationAccessFilter = (filter, products) => {
		const config = {
			method: 'POST',
			url: `${url}/filter/products?${$httpParamSerializer(filter)}`,
			data: {
				RemoveProducts: products
			},
		};

		return $http(config);
	};

	const downloadUserCsv = (orgId) => {
		let config = {
			method: 'GET',
			url: `${url}/organizations/${orgId}/export`,
		};
		return $http(config);
	};

	// API endpoint methods
	return {
		getUsers,
		getUser,
		getUserById,
		sendInvitationToUser,
		sendBulkInvitationIds,
		sendBulkInvitationFilter,
		revokeAccess,
		revokeBulkAccessIds,
		revokeBulkAccessFilter,
		grantApplicationAccessToUser,
		grantBulkApplicationAccessIds,
		grantBulkApplicationAccessFilter,
		removeApplicationAccessForUser,
		removeBulkApplicationAccessIds,
		removeBulkApplicationAccessFilter,
		downloadUserCsv,
		changeApplicationAccessForUser,
	};

// #-- END API Endpoint (ssPositionsApi) --# //
// #---------------------------------------# //
}

angular.module('super-suit-apis').factory('ssUsersApi', usersApi);
