// #--------------------------------# //
// #---- Helper (ssUiModuleHlp) ----# //

/*@ngInject*/
function ssUiModuleHlp($location) { return { // eslint-disable-line

	// demo function, please remove
	getDetails(modName, manifest) {
		// default details to return if our lookup fails for any reason
		const details = {
			isValid: false,
			isUniversal: false,
			bootstrapName: null,
			productId: null,
		};

		// no module is defined
		if (modName == null || manifest == null) { return details; }
		if (modName.length === 0) { return details; }

		// calculate universal module name (so we can get for it)
		const productId = modName.split('-')[0];
		const universalModName = modName.replace(productId, 'universal');

		// manifest entries for module and universal module varient
		const modMani = manifest[modName];
		const uniMani = manifest[universalModName];

		// check for exact match as well as universal module
		const isUniversal = uniMani != null;
		const isValid = modMani != null || uniMani != null;

		Object.assign(details, {
			bootstrapName: (isUniversal ? universalModName : modName),
			isValid, isUniversal, productId
		});

		return details;
	},

	getCurrentModuleName() {
		var match = $location.path().match(/^\/([\w-]*)/i);
		if (match.length === 2) {
			return match[1];
		} else {
			return '';
		}
	}

};} // eslint-disable-line
// #-- END Helper (ssUiModuleHlp) --# //
// #--------------------------------# //

angular.module('super-suit-helpers').factory('ssUiModuleHlp', ssUiModuleHlp);
