var tempCache;

/*@ngInject*/
function ssTempCache($templateCache) {
// #-------------------------------# //
// #---- Utility (ssTempCache) ----# //

	// External utility injectable value
	return function (url) {
		return (tempCache || $templateCache).get(url);
	};

// #-- END Utility (ssTempCache) --# //
// #-------------------------------# //
}

/*@ngInject*/
function fillTempCache($templateCache) {
	tempCache = $templateCache;
}

angular.module('super-suit-utilities').factory('ssTempCache', ssTempCache);
angular.module('super').run(fillTempCache);
