/* globals sidekick */

/*@ngInject*/
function hookupNav($q, $rootScope, $location, $log, ssAppConfigSvc, ssPermissionsSvc, ssFeatureSvc, ssFlagCCSvc, ssFlagSvc) {

	const isExternalUrl = RegExp.prototype.test.bind((/^https?:\/\//));

	ssAppConfigSvc.getAsync('left-nav').then(function (leftNavCfg) {
		ssPermissionsSvc.ready.then(function () {
			Promise.all(leftNavCfg.menuItems.map(menuItem => {
				return resolveFlags(menuItem);
			})).then(() => {
				//> groupCollapsed('%cSidekick Left-Nav Permission Checks', 'color: #698;')
				leftNavCfg.menuItems = leftNavCfg.menuItems.filter(isPermittedMenuItem);
				//> groupEnd('%cSidekick Left-Nav Permission Checks', 'color: #698;')
				sidekick.store.set('nav', leftNavCfg);
			});
		});

		// this is a single page app, we want to let the $location service
		// handle the page change so we don't do a full refresh
		sidekick.events.on('nav.menu-item-clicked', function (item, evt) {
			// if the url points to an external server, let the browser perform
			// it's normal redirection
			if (isExternalUrl(item.url)) { return; }

			// if this is internal to supersuit, handle it with $location so we
			// don't do a full browser reload
			evt.preventDefault();
			$rootScope.$applyAsync(() => $location.url(item.url));
		});

		/**
		 * Recursively checks the visibility of a menu item (and its children) based upon the Feature Flag (legacy and new) and the permissions to the route
		 * @param {any} item A menu item object from the app specific menu json blob eg: employee-center.json
		 */
		function isPermittedMenuItem(item) {
			var permitted = false;

			// remove item according to feature flags
			// Check the flags first, as we may have routes that have children where we would want to hide the whole branch vs each individual leaf

			// If item has both old and new feature flag attributes then warn dev and use new
			if ((item.hideWhenFeatureOff || item.hideWhenFeatureOn) && (item.hideWhenFlagOff || item.hideWhenFlagOn)) {
				$log.warn(`Nav item '${item.title}' is using both current and deprecated feature flags. Please remove references to "hideWhenFeatureOff" and "hideWhenFeatureOn".`);
			}

			//New feature flags
			if (item.hideWhenFlagOn && item.hideWhenFlagOnResolved) { return false; }
			if (item.hideWhenFlagOff && !item.hideWhenFlagOffResolved) { return false; }

			// Old feature flags
			if (item.hideWhenFeatureOn && ssFeatureSvc.isOn(item.hideWhenFeatureOn)) { return false; }
			if (item.hideWhenFeatureOff && ssFeatureSvc.isOff(item.hideWhenFeatureOff)) { return false; }

			// If the node has children, recurse into the next level
			if (angular.isArray(item.children) && item.children.length) {
				item.children = item.children.filter(isPermittedMenuItem);
				// No children, no visibility
				permitted = !!item.children.length;
			} else {
				if (item.url) {
					permitted = ssPermissionsSvc.isPermittedRoute(item.url);
				} else {
					permitted = true;
				}
			}
			return permitted;
		}

		function resolveFlags(item){
			// If the node has children, recurse into the next level
			if (angular.isArray(item.children) && item.children.length){
				item.children.forEach(subItem => {
					resolveFlags(subItem);
				});
			}
			// Resolve LD Flags
			if (item.hideWhenFlagOn){
				item.hideWhenFlagOnResolved = ssFlagSvc.checkState(item.hideWhenFlagOn);
			}
			if (item.hideWhenFlagOff){
				item.hideWhenFlagOffResolved = ssFlagSvc.checkState(item.hideWhenFlagOff);
			}
			// Custom checks
			if (item.hideWhenCustomFlagOn){
				return customFlags[item.hideWhenCustomFlagOn]().then(enabled => {
					item.hideWhenFlagOn = true;
					item.hideWhenFlagOnResolved = enabled;
				});
			}
			if (item.hideWhenCustomFlagOff){
				return customFlags[item.hideWhenCustomFlagOff]().then(enabled => {
					item.hideWhenFlagOff = true;
					item.hideWhenFlagOffResolved = enabled;
				});
			}
			// Non-MIFD Flags
			if (item.hideWhenCCFlagOn){
				return ssFlagCCSvc.checkState(item.hideWhenCCFlagOn).then(enabled => {
					item.hideWhenFlagOn = true;
					item.hideWhenFlagOnResolved = enabled;
				});
			}
			if (item.hideWhenCCFlagOff){
				return ssFlagCCSvc.checkState(item.hideWhenCCFlagOff).then(enabled => {
					item.hideWhenFlagOff = true;
					item.hideWhenFlagOffResolved = enabled;
				});
			}
		}

		const customFlags = {
			isDataImporterEnabled: function () {
				return Promise.all([
					ssFlagCCSvc.checkState('fc_di_enable_data_importer'),
					ssFlagCCSvc.checkState('global_hrms')
				]).then(result => {
					const [dataImporter, hrms] = result;
					return hrms ? false : dataImporter;
				});
			},
			isGlobalOrSharedProfile: function () {
				return Promise.all([
					ssFlagCCSvc.checkState('global_fc_shared_employee'),
					ssFlagCCSvc.checkState('global_data_integration'),
				]).then(result => {
					const [sharedEmployee, isDataIntegrationsEnabled] = result;
					return sharedEmployee || isDataIntegrationsEnabled;
				});
			}
		};
	});
}

angular.module('super').run(hookupNav);
