import 'fluid-components/dist/fluid-plugin-angular';

// super setup module is only used by the super module
// it provides a place for code to run prior to the super module initializing
// because modules init before their parent module. used for setting up event
// listeners and other code that needs to run before the main module is run
angular.module('super-setup', []);

// main application module. the super module is the module that does the
// ui-module application bootstrapping and such. it's the controlling app
angular.module('super', [
	'super-suit',
	'super-setup',
	'super-templates',
	'ui.router',
	'ui.router.title',
	'flAuth'
]);

// super-suit module is the shared library that both the super module and the
// ui-modules utilize. this module contains most of the functionality.
angular.module('super-suit', [
	'super-templates',
	'ui.router',
	'ui.router.title',
	'ngAnimate',
	'ngTouch',
	'ngAria',
	'ngMask',
	'beauby.jsonApiDataStore',
	'toastr',
	'infinite-scroll',
	'angular-inview',
	'angularFileUpload',
	'fluid-components',
	'super-suit-config',
	'super-suit-utilities',
	'super-suit-helpers',
	'super-suit-components',
	'super-suit-decorators',
	'super-suit-enums',
	'super-suit-mutators',
	'super-suit-services',
	'super-suit-apis'
]);

// Separating the super-suit module into smaller modules allows us to bootstrap only the modules we need while unit testing.
angular.module('super-suit-utilities', []);
angular.module('super-suit-config', []);
angular.module('super-suit-helpers', []);
angular.module('super-suit-components', []);
angular.module('super-suit-decorators', []);
angular.module('super-suit-enums', []);
angular.module('super-suit-mutators', []);
angular.module('super-suit-services', []);
angular.module('super-suit-apis', []);
