import {Enums} from 'base-enum';

// #-----------------------------# //
// #---- Enums (/salaryType) ----# //

class SalaryTypeEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Annual Salary',
			value: 0
		}, {
			name: 'Hourly Rate',
			value: 1
		}];
		super(enums);
	}
}

// #---- End Enums (/salaryType) ----# //
// #---------------------------------# //

angular.module('super-suit-enums').factory('enumSalaryType', () => new SalaryTypeEnum());
