/* globals sidekick */

/*@ngInject*/
function hookupNotifications($rootScope, ssAppConfigSvc, ssEventSvc, ssNotificationsSvc) {
	ssAppConfigSvc.getAsync('notifications').then(function () {
		var notifications;

		ssEventSvc.on('notifications.updated', updateSidekickData, false);
		sidekick.events.on('notification.delete', remove);
		sidekick.events.on('notification.mark-as-read', markAsRead);
		ssNotificationsSvc.getNotifications().then(updateSidekickData);

		function updateSidekickData(newNotifications) {
			// if we have new data from the endpoint, mutate it to what we need
			if (newNotifications != null) {
				newNotifications = newNotifications.map(n => {
					return {
						id: n.id,
						timestamp: n._createdUtc,
						title: n._subject,
						content: n.shortContent,
						unread: !n.isRead
					};
				});
			}

			// if the new notification data is the same as the old, no need to go further
			if (angular.equals(notifications, newNotifications)) { return; }
			// if we have new notification data, save it for future use
			if (newNotifications != null) { notifications = newNotifications; }
			// update the store so the component will repaint
			sidekick.store.set('notifications', notifications);
		}

		function markAsRead(notification) {
			ssNotificationsSvc.markAsRead(notification.id);
			notifications.find(item => item.id === notification.id).unread = false;
			sidekick.store.set('notifications', notifications);
		}

		function remove(notification) {
			ssNotificationsSvc.remove(notification.id).then(res => {
				notifications = notifications.filter(item => item.id !== notification.id);
				updateSidekickData();
			});
		}
	});
}

angular.module('super').run(hookupNotifications);
