// Moment Date Filters
/*@ngInject*/
function fromNowFilter() {
	return function (date, precision, format) {
		const d = moment(date);
		const now = moment(Date.now());
		const duration = Math.abs(now.diff(d, precision));
		return moment.duration(duration, precision).format(format);
	};
}

angular.module('super-suit').filter('fromNow', fromNowFilter);
