import {Component} from 'base-component';
// #------------------------------------------------# //
// #---- Component (super-emp-profile-complete) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	id: '@?',
	employee: '=?',
	textPrefix: '@?'
};

// directive definition object overrides
const config = {};

/*@ngInject*/
function empProfileComplete() {

	class EmpProfileComplete extends Component {
		/*@ngInject*/
		controller($scope, ssAsync, ssEmployeeApi) {
			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				incompleteList: ''
			};

			ssAsync(function*() {
				var emp; // eslint-disable-line no-unused-vars
				try {
					if ($scope.employee == null) {
						emp = yield ssEmployeeApi.get({
							id: $scope.id,
							fields: 'firstname,lastname,birthDateUtc,gender,maritalStatus,race,ethnicity,hiredate,ssn',
							include: 'phoneNumbers,addresses,emails,positions,emergencyContacts,assignments,statusHistory'
						});
					} else {
						emp = $scope.employee;
					}

				} catch (e) {
					//> log('Error getting employee information', e);
				}
			})();
		}
	}

	return new EmpProfileComplete(props, 'emp-profile-complete', config);
}

// #-- END Component (super-emp-profile-complete) --# //
// #------------------------------------------------# //

angular.module('super-suit-components').directive('superEmpProfileComplete', empProfileComplete);
