/*@ngInject*/
function formInstancesApi($log, $http, $q, JsonApiDataStore, ssApiSvc, ssJsonApi, buildConfig, ssActiveUserSvc) {
// #-------------------------------------------# //
// #---- API Endpoint (ssFormInstancesApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.formTemplates;
	const url = (p) => `${baseUrl}/forminstances/${p.id}`;
	const queryUrl = (p) => `${baseUrl}/employees/${p.id}/forminstances`;

	const queryByOrgUrl = `${baseUrl}/organizations/:ORGID:/forminstances`;
	const queryDashboardUrl = `${baseUrl}/organizations/:ORGID:/dashboard`;
	const queryFormPacketViewSummaryUrl = `${baseUrl}/formtracking/formPacketView?organizationId=:ORGID:`;
	const formPacketViewCountsUrl = `${baseUrl}/formtracking/formPacketViewCounts?organizationId=:ORGID:`;
	const queryInProgressUrl = `${baseUrl}/organizations/:ORGID:/forminstances/inprogress`;
	const queryNotStartedUrl = `${baseUrl}/organizations/:ORGID:/forminstances/notstarted`;
	const postReminderUrl = (p) => `${baseUrl}/forminstances/${p.id}/reminder`;
	const updateStatusUrl = (p) => `${baseUrl}/forminstances/${p.id}/status/${p.newStatus}`;
	const querySubmittedFormsUrl = (p) => `${baseUrl}/employee/${p.id}/sentforminstances`;
	const queryCurrentFormsUrl = (p) => `${baseUrl}/employee/${p.id}/currentforminstances`;
	const postFormOverlayUrl = (p) => `${baseUrl}/forminstances/${p.formInstanceid}/textoverlay`;
	const batchFormInstancesUrl = `${baseUrl}/BatchRequests/formInstances`;
	const batchPacketInstancesUrl =` ${baseUrl}/BatchRequests/packetInstances`;
	const getAllBatchesUrl = `${baseUrl}/BatchRequests`;
	const getAllBatchStatusesUrl = `${baseUrl}/BatchStatuses`;
	const getBatchStatusUrl = (p) => `${baseUrl}/BatchStatuses/${p.id}`;
	const getFormCountsUrl = `${baseUrl}/organizations/:ORGID:/forminstances/counts`;
	const statusCsvUrl = `${baseUrl}/Export/trackform/statuscsv`;
	const responsesXlsxUrl = `${baseUrl}/Export/trackform/responsesxlsx`;
	const getPacketContentsUrl = (p) => `${baseUrl}/formtracking/packets/${p.id}/forms?organizationId=:ORGID:`;

	// model data type
	const model = 'formInstances';
	// possible relationships
	const rel = [
		'steps'
	];

	const get = ssApiSvc.endpoint({url, method: 'GET'}, model, rel);
	const query = ssApiSvc.endpoint({ url: queryUrl,  method: 'GET' }, model, rel);
	const post = ssApiSvc.endpoint({url, method: 'POST'}, model, rel);
	const postReminder = ssApiSvc.endpoint({url: postReminderUrl, method: 'POST'}, model, rel);
	const queryByOrg = ssApiSvc.endpoint({ url: queryByOrgUrl,  method: 'GET' }, model, rel);
	const queryDashboardSummary = ssApiSvc.endpoint({ url: queryDashboardUrl, method: 'GET' }, model, rel);
	const queryFormPacketViewSummary = ssApiSvc.endpoint({ url: queryFormPacketViewSummaryUrl, method: 'GET' }, model, rel);
	const queryFormPacketViewCounts = ssApiSvc.endpoint({ url: formPacketViewCountsUrl, method: 'GET' }, model, rel);
	const queryInProgress = ssApiSvc.endpoint({ url: queryInProgressUrl,  method: 'GET' }, model, rel);
	const queryNotStarted = ssApiSvc.endpoint({ url: queryNotStartedUrl,  method: 'GET' }, model, rel);
	const querySubmitted = ssApiSvc.endpoint({ url: querySubmittedFormsUrl,  method: 'GET' }, model, rel);
	const queryCurrent = ssApiSvc.endpoint({ url: queryCurrentFormsUrl,  method: 'GET' }, model, rel);
	const postFormOverlay = ssApiSvc.endpoint({ url: postFormOverlayUrl,  method: 'POST' }, model, rel);
	const getFormCounts = ssApiSvc.endpoint({ url: getFormCountsUrl,  method: 'GET' }, model, rel);
	const getPacketContents = ssApiSvc.endpoint({ url: getPacketContentsUrl, method: 'GET'}, model, rel);
	// batch form sending
	const postFormInstanceBatch = ssApiSvc.endpoint({ url: batchFormInstancesUrl,  method: 'POST' }, model, rel);
	const postPacketInstanceBatch = ssApiSvc.endpoint({ url: batchPacketInstancesUrl,  method: 'POST' }, model, rel);
	const getAllBatches = ssApiSvc.endpoint({ url: getAllBatchesUrl,  method: 'GET' }, model, rel);
	const getAllBatchStatuses = ssApiSvc.endpoint({ url: getAllBatchStatusesUrl,  method: 'GET' }, model, rel);
	const getBatchStatus = ssApiSvc.endpoint({ url: getBatchStatusUrl,  method: 'GET' }, model, rel);

	const downloadResponsesXlsx = (params, body) => {
		const config = {
			url: responsesXlsxUrl,
			method: 'POST',
			params,
			data: {
				data: body
			}
		};
		return $http(config);
	};

	const downloadStatusCsv = (params, body) => {
		let config = {
			url: statusCsvUrl,
			method: 'POST',
			params,
			data: {
				data: body
			}
		};
		return $http(config);
	};

	const updateStatus = (p) => $http({url: updateStatusUrl(p), method: 'PATCH'});

	const updateEmpRecord = (p) => {
		let url = `${baseUrl}/forminstances/${p.id}`;
		let config = {
			url,
			method: 'POST',
			data: {},
			params: p.params
		};

		return $http(config);

	};

	const updateCurrentStepId = (p) => {
		let url = `${baseUrl}/forminstances/${p.id}/currentStepId/${p.targetStep}`;
		let config = {
			url,
			method: 'PATCH',
			params: p.params
		};

		return $http(config);
	};

	const send = (params, filter) => {
		if (params == null || !angular.isArray(params.attributes)) {
			$log.error('params.attributes must be an array');
			return;
		}
		let url = `${baseUrl}/organizations/${ssActiveUserSvc.getOrgIdSync()}/forminstances`;
		let config = {
			url,
			method: 'POST',
			params: filter,
			data: {
				data: params
			}
		};
		const schema = {
			model,
			path: url,
			relationships: []
		};

		return $http(config).then(function (res) {
			const store = JsonApiDataStore.store.sync(res.data);
			return new ssJsonApi.Collection(store, schema);
		});
	};

	const bulkSend = data => {
		if (data == null || !angular.isArray(data.attributes)) {
			$log.error('data.attributes must be an array');
			return;
		}
		let url = `${baseUrl}/organizations/${ssActiveUserSvc.getOrgIdSync()}/forminstances/bulk`;
		let config = {
			url,
			method: 'POST',
			data: { data }
		};
		return $http(config).then(res => res);
	};

	const signature = text => {
		let url = `${baseUrl}/signature`;
		let config = {
			url,
			method: 'GET',
			params: {
				signature: text
			}
		};

		return $q((res, rej) => {
			ssActiveUserSvc.getIdentityByType('employee').then(userKey => {
				config.params.id = userKey;
				res($http(config));
			});
		});
	};

	const generatePDFOverlay = (p) => {
		let url = `${baseUrl}/forminstances/${p.formInstanceid}/textoverlayinprogress`;
		let config = {
			url,
			method: 'POST',
			params: {
				showSensitive: p.showSensitive,
				timeStamp: 'ticks'
			},
			data: p.formFieldValues
		};

		return $http(config).then(res => res);

	};

	// API endpoint methods
	return {
		get,
		query,
		send,
		bulkSend,
		post,
		postReminder,
		updateStatus,
		queryByOrg,
		queryDashboardSummary: function (props) {
			return ssActiveUserSvc.getIdentityByType('employee').then(userKey => queryDashboardSummary(
				Object.assign({employeeId: userKey}, props)
			));
		},
		queryFormPacketViewSummary,
		queryFormPacketViewCounts,
		queryInProgress,
		queryNotStarted,
		querySubmitted,
		queryCurrent,
		postFormOverlay,
		generatePDFOverlay,
		getFormCounts,
		updateEmpRecord,
		updateCurrentStepId,
		signature,
		postFormInstanceBatch,
		postPacketInstanceBatch,
		getAllBatches,
		getAllBatchStatuses,
		getBatchStatus,
		downloadStatusCsv,
		downloadResponsesXlsx,
		getPacketContents
	};

// #-- END API Endpoint (ssFormInstancesApi) --# //
// #-------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssFormInstancesApi', formInstancesApi);
