/*@ngInject*/
function formTemplatesApi($http, ssApiSvc, ssJsonApi, buildConfig, JsonApiDataStore, ssActiveUserSvc) {
	// #-------------------------------------------# //
	// #---- API Endpoint (ssFormTemplatesApi) ----# //

	const currentOrgId = ssActiveUserSvc.getOrgIdSync();

	// base endpoint url
	const baseUrl = buildConfig.endpoint.formTemplates;
	const url = (p) => `${baseUrl}/formtemplates/${p.id}`;
	const queryUrl = `${baseUrl}/organizations/:ORGID:/formtemplates`;
	const formTemplatesFilterUrl = `${baseUrl}/organizations/:ORGID:/templateObjectsForFilter`;
	const fileAttachmentUrl = `${baseUrl}/files`;
	const fileRecordsUrl = (p) => `${baseUrl}/files/detailsByIds`;
	const fileRecordUrl = (p) => `${baseUrl}/files/${p.id}`;
	const fileUrlUrl = (p) => `${baseUrl}/files/${p.id}/url`;
	const fileAssociationUrl = (p) => `${baseUrl}/formtemplates/${p.id}/files`;
	const fileRemovalUrl = (p) => `${baseUrl}/formtemplates/${p.id}/files/${p.fileId}`;
	const formTemplatesIds = `${baseUrl}/formtemplates/ids`;
	const frequentlySentUrl = `${baseUrl}/organizations/:ORGID:/formtemplates/frequentlySent`;
	const recentlySentUrl = `${baseUrl}/organizations/:ORGID:/formtemplates/recentlySent`;
	const frontlineShareUrl = (p) => `${baseUrl}/organizations/${currentOrgId}/formtemplates/${p.sourceTemplateId}/share`;
	const generatePDFOverlayUrl = (p) => `${baseUrl}/formtemplate/${p.formTemplateId}/textoverlay`;

	// model data type
	const model = 'formTemplates';
	// possible relationships
	const rel = [
		'steps',
		'formFields',
		'statusHistory',
		'files'
	];

	// generate default endpoints
	const get = ssApiSvc.endpoint({ url, method: 'GET' }, model, rel);
	const query = ssApiSvc.endpoint({ url: queryUrl, method: 'GET' }, model, rel);
	const add = ssApiSvc.endpoint({ url: queryUrl, method: 'POST' }, model, rel);
	const copy = ssApiSvc.endpoint({ url: url, method: 'POST' }, model, rel);

	const attachFile = ssApiSvc.endpoint({ url: fileAttachmentUrl, method: 'POST' }, model, rel);
	const getFiles = ssApiSvc.endpoint({ url: fileRecordsUrl, method: 'POST' }, model, rel);
	const getFile = ssApiSvc.endpoint({ url: fileRecordUrl, method: 'GET' }, model, rel);
	const associateFile = ssApiSvc.endpoint({ url: fileAssociationUrl, method: 'POST' }, model, rel);
	const getFileUrl = ssApiSvc.endpoint({ url: fileUrlUrl, method: 'GET' }, model, rel);
	const frequentlySent = ssApiSvc.endpoint({ url: frequentlySentUrl, method: 'GET' }, model, rel);
	const recentlySent = ssApiSvc.endpoint({ url: recentlySentUrl, method: 'GET' }, model, rel);
	const generatePDFOverlay = ssApiSvc.endpoint({ url: generatePDFOverlayUrl, method: 'POST' }, model, rel);
	const getFilterTemplates = ssApiSvc.endpoint({ url: formTemplatesFilterUrl, method: 'GET' }, model, rel);

	const getUploadFileUrl = (p) => {
		return `${baseUrl}/files/${p.id}`;
	};

	const editActive = template => {
		let config = {
			url: url({
				id: template.id
			}),
			method: 'PATCH',
			params: {
				version: template.$serialize().data.attributes.ownerVersionNumber
			},
			data: {
				data: {
					attributes: {},
					id: template.id,
					type: model
				}
			}
		};
		const schema = {
			model,
			path: url,
			relationships: []
		};

		return $http(config).then(res => jsonApiData(res.data, schema));
	};

	const removeFile = template => {
		let config = {
			url: fileRemovalUrl({
				id: template.id,
				fileId: template._files.find(file => file._fileId === template._printPreviewDefinition.templateFileId).id
			}),
			method: 'DELETE',
			params: {
				version: template.$serialize().data.attributes.ownerVersionNumber
			},
			data: {
				data: {
					attributes: {},
					id: template.id,
					type: model
				}
			}
		};

		return $http(config);
	};

	const formTemplatesByIds = (ids, params = {}) => {
		let config = {
			url: `${formTemplatesIds}?organizationId=${ssActiveUserSvc.getOrgIdSync()}`,
			method: 'POST',
			data: {
				data: {
					attributes: ids,
					type: model
				}
			},
			params
		};
		const schema = {
			model,
			path: url,
			relationships: []
		};
		return $http(config).then(res => jsonApiData(res.data, schema));
	};

	const shareToOrg = (sourceTemplateId, data) => {
		let config = {
			url: frontlineShareUrl({ sourceTemplateId }),
			method: 'POST',
			data
		};

		return $http(config);
	};



	// API endpoint methods
	return {
		get,
		query,
		add,
		copy,
		editActive,
		attachFile,
		generatePDFOverlay,
		getUploadFileUrl,
		getFiles,
		getFile,
		getFileUrl,
		associateFile,
		removeFile,
		formTemplatesByIds,
		frequentlySent,
		recentlySent,
		shareToOrg,
		getFilterTemplates
	};

	function jsonApiData(res, schema) {
		const store = JsonApiDataStore.store.sync(res);
		if (angular.isArray(res.data)) {
			return new ssJsonApi.Collection(store, schema);
		} else {
			return new ssJsonApi.Model(store, schema);
		}
	}

	// #-- END API Endpoint (ssFormTemplatesApi) --# //
	// #-------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssFormTemplatesApi', formTemplatesApi);
