/*@ngInject*/
function scormCloudApi($http, buildConfig) {
// #----------------------------------------# //
// #---- API Endpoint (ssScormCloudApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.scormCloud;
	const url = `${baseUrl}/course`;

	/**
	 * @description Private function to format an array of strings as "tags" parameters for attaching to a url string.
	 * @param {[string]} tagsArray An array of strings representing the library tags to filter on.
	 * @returns {string} Example: "?tags=abc&tags=def"
	 */
	function formatTagParameters ( tagsArray ) {
		let result = '';
		if (!Array.isArray(tagsArray)) {
			return result;
		}
		result += '?';
		tagsArray.forEach(function (tag) {
			if (result !== '?') {
				result += '&';
			}
			result += `tags=${tag}`;
		});
		return result;
	}

	/**
	 * @description Request a collection of all course summary information from the API.
	 */
	function getAllCourses() {
		return $http({
			method: 'GET',
			url: `${url}`,
		});
	}

	/**
	 * @description Request the information for a particular course from the API.
	 * @param {string} courseId The ID for the course you wish to get course information from the API.
	 */
	function getCourseById( courseId ) {
		return $http({
			method: 'GET',
			url: `${url}/${courseId}`,
		});
	}

	/**
	 * @description Request a collection of course information from the API, filtered by the library tags provided in the parameter array.
	 * @param {[string]} tagsArray An array of strings identifying the library catalog tags to filter the collection.
	 */
	function getCoursesByTags ( tagsArray ) {
		return $http({
			method: 'GET',
			url: `${url}/bytags` + formatTagParameters(tagsArray),
		});
	}

	/**
	 * @description Request a launch link for a course.  If the specified user (from the access token) has already registered for the specified course a link for that registration will be returned otherwise the user will be registered for the course and the new link returned.
	 * @param {string} courseId The course ID to get a registration/launch link for.
	 * @param {string} firstName First name of the user for record purposes.
	 * @param {string} lastName Last name of the user for record purposes.
	 * @param {string} redirectUrl URL to redirect to when the user exits the course viewer.
	 */
	function getCourseLaunchLink ( courseId, firstName, lastName, redirectUrl ) {
		return $http({
			method: 'GET',
			url: `${url}/getLaunchLink/${courseId}/${firstName}/${lastName}?redirectUrl=${redirectUrl}`,
		});
	}

	/**
	 * @description Request course details including the launch link for a course.  If the specified user (from the access token) has already registered for the specified course a link for that registration will be included but the user will not be automatically registered by this call.
	 * @param {string} courseId The course ID to get a registration/launch link for.
	 * @param {string} redirectUrl URL to redirect to when the user exits the course viewer.
	 */
	function getCourseDetails ( courseId, redirectUrl ) {
		return $http({
			method: 'GET',
			url: `${url}/getCourseDetails/${courseId}?redirectUrl=${redirectUrl}`,
		});
	}

	// API endpoint methods
	return {
		getAllCourses, // Get a resource containing a collection of all Courses available on the Scorm Cloud Application namespace.
		getCourseById, // Get a resource representing a single Course selected by the parameter.
		getCoursesByTags, // Get a resource containing a collection of Courses available on the Scorm Cloud Application namespace that contain a tag from the parameter array.
		getCourseLaunchLink, // If the user is not currently registered for the specified course, register the user then provide the launch link for the course.If the user is already registered, return the existing launch link for the course. : warning: * Use caution if you are testing with this endpoint as each new registration incurrs additional cost on our subscription with the vendor.*
		getCourseDetails // Get the details of a particular course including the registration launch link if it already exist.This will not register a user for a course if they do not currently have a registration.
	};

// #-- END API Endpoint (ssScormCloudApi) --# //
// #----------------------------------------# //
}

angular.module('super-suit-apis').factory('ssScormCloudApi', scormCloudApi);
