/*@ngInject*/
function enumByValueFilter(ssEnum) {

	return function(input, type) {
		if(input == null || type == null) {
			return '';
		}
		var enumObj = ssEnum[type].enumByValue(input);
		if (enumObj == null) {
			return '';
		} else {
			return ssEnum[type].enumByValue(input).name;
		}
	};
}

/*@ngInject*/
function enumByNameFilter(ssEnum) {

	return function(input, type) {
		if(input == null || type == null) {
			return '';
		}
		var enumObj = ssEnum[type].enumByName(input);
		if (enumObj == null) {
			return '';
		} else {
			return ssEnum[type].enumByName(input).value;
		}
	};
}


angular.module('super-suit').filter('enumByValue', enumByValueFilter);
angular.module('super-suit').filter('enumByName', enumByNameFilter);
