/*@ngInject*/
function ssLocationSvc(ssAsync, ssStorage, ssLocationsApi, ssActiveUserSvc, ssServiceLogicHlp, ssConfigurationHlp) {
// #---------------------------------# //
// #---- Service (ssLocationSvc) ----# //

	// list of IDM AppIds that use the microservice
	const usingLocationMsvc = ssServiceLogicHlp.productIdList([
		'EC'
	]);

	var locations = [];

	const ready = (ssAsync(function* () {
		try {
			const orgId = yield ssActiveUserSvc.getOrgId();
			if (orgId == null) { return []; }

			// resolve with nothing if this product doesn't use the locationSvc
			const pId = yield ssActiveUserSvc.getProductId();
			if (!usingLocationMsvc.includes(pId)) { return []; }

			locations = ssStorage.load(`${orgId}_locations`);

			if (locations == null) {
				locations = (yield ssLocationsApi.query({
					limit: ssConfigurationHlp.getLimit('locations')
				})).map(function (loc) {
					const obj = loc.$serialize().data.attributes;
					Object.keys(obj).forEach(function (attr) {
						obj[`_${attr}`] = obj[attr];
						delete obj[attr];
					});

					return Object.assign({id: loc.id}, obj);
				});
				ssStorage.save(`${orgId}_locations`, locations);
			}

			return locations;
		} catch (e) {
			/* eslint-disable no-console */
			if (console && console.error) {
				console.error('ERROR', e);
			}
			/* eslint-enable no-console */
		}
	})());

	// External service API
	return {
		getLocationById,
		getAll,
		ready
	};

	function getLocationById(id) {
		return locations.find(l => l.id === id);
	}

	function getAll() {
		return locations;
	}

// #-- END Service (ssLocationSvc) --# //
// #---------------------------------# //
}

angular.module('super-suit-services').factory('ssLocationSvc', ssLocationSvc);
