/*@ngInject*/
function packetInstancesApi($log, $http, JsonApiDataStore, ssJsonApi, ssApiSvc, buildConfig, ssActiveUserSvc) {
// #---------------------------------------------# //
// #---- API Endpoint (ssPacketInstancesApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.endpoint.formTemplates;
	const url = `${baseUrl}/organizations/:ORGID:/packetInstances`;
	const byIdUrl = (p) => `${baseUrl}/packetInstances/${p.id}`;
	// model data type
	const model = 'packetInstances';
	// possible relationships
	const rel = [];

	// generate default endpoints
	const {query, add} = ssApiSvc.defaultEndpoints({ url }, model, rel);
	const get = ssApiSvc.endpoint({ url: byIdUrl, method: 'GET' }, model, rel);
	const remove = ssApiSvc.endpoint({ url: byIdUrl, method: 'DELETE' }, model, rel);

	const send = (data) => {
		if (data == null || !angular.isArray(data.attributes)) {
			$log.error('data.attributes must be an array');
			return;
		}
		let url = `${baseUrl}/organizations/${ssActiveUserSvc.getOrgIdSync()}/packetinstances`;
		let config = {
			url,
			method: 'POST',
			data: { data }
		};
		const schema = {
			model,
			path: url,
			relationships: []
		};

		return $http(config).then(function (res) {
			const store = JsonApiDataStore.store.sync(res.data);
			return new ssJsonApi.Collection(store, schema);
		});
	};

	// API endpoint methods
	return {
		get, query, add, remove, send
	};

// #-- END API Endpoint (ssPacketInstancesApi) --# //
// #---------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssPacketInstancesApi', packetInstancesApi);
