/*@ngInject*/
function productAccessApi($http, buildConfig) {
	// #-------------------------------------------# //
	// #---- API Endpoint (ssProductAccessApi) ----# //

	// base endpoint url
	const baseUrl = buildConfig.idmGatewayUrl;
	const url = `${baseUrl}userservice/v0/api/products`;

	function getProductNameFromId(productId) {
		let config = {
			method: 'GET',
			url: `${url}/${productId}/name`
		};

		return $http(config);
	};

	// API endpoint methods
	return {
		getProductNameFromId,
	};

	// #-- END API Endpoint (ssProductAccessApi) --# //
	// #-------------------------------------------# //
}

angular.module('super-suit-apis').factory('ssProductAccessApi', productAccessApi);
